import { Material } from "./Material";
import { ColorRGB } from "../Math";
import { Nullable } from "../types";
import { Constants, Texture } from "../Engines";

/**
 * 
 */
export class MeshBasicMaterial extends Material {

    /**
     * @ignore
     */
    protected _color: ColorRGB = new ColorRGB();
    /**
     * @ignore
     */
    protected _map: Nullable<Texture> = null;
    /**
     * @ignore
     */
    protected _lightMap: Nullable<Texture> = null;
    /**
     * @ignore
     */
    protected _lightMapIntensity: number = 1.0;
    /**
     * @ignore
     */
    protected _aoMap: Nullable<Texture> = null;
    /**
     * @ignore
     */
    protected _aoMapIntensity: number = 1.0;
    /**
     * @ignore
     */
    protected _specularMap: Nullable<Texture> = null;
    /**
     * @ignore
     */
    protected _alphaMap: Nullable<Texture> = null;
    /**
     * @ignore
     */
    protected _envMap: Nullable<Texture> = null;
    /**
     * @ignore
     */
    protected _combine: number = Constants.MultiplyOperation;
    /**
     * @ignore
     */
    protected _reflectivity: number = 1;
    /**
     * @ignore
     */
    protected _refractionRatio: number = 0.98;
    /**
     * @ignore
     */
    protected _wireframe: boolean = false;
    /**
     * @ignore
     */
    protected _wireframeLineWidth: number = 1;
    /**
     * @ignore
     */
    protected _wireframeLineCap: string = 'round';
    /**
     * @ignore
     */
    protected _wireframeLineJoin: string = 'round';
    /**
     * @ignore
     */
    protected _skinning: boolean = false;
    /**
     * @ignore
     */
    protected _morphTargets: boolean = false;

    /**
     * 
     */
    constructor() {
        super();
    }

    /**
     * 
     */
    public get color() {
        return this._color;
    }
    public set color(value) {
        if (!this._color.equals(value)) {
            this._color.copy(value);
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get map() {
        return this._map;
    }
    public set map(value) {
        if (this._map != value) {
            if (this._map) {
                this._map.dispose();
            }
            this._map = value;
            if (this._map) {
                this._map.addReference();
            }
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get lightMap() {
        return this._lightMap;
    }
    public set lightMap(value) {
        if (this._lightMap != value) {
            if (this._lightMap) {
                this._lightMap.dispose();
            }
            this._lightMap = value;
            if (this._lightMap) {
                this._lightMap.addReference();
            }
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get lightMapIntensity() {
        return this._lightMapIntensity;
    }
    public set lightMapIntensity(value) {
        if (this._lightMapIntensity != value) {
            this._lightMapIntensity = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get aoMap() {
        return this._aoMap;
    }
    public set aoMap(value) {
        if (this._aoMap != value) {
            if (this._aoMap) {
                this._aoMap.dispose();
            }
            this._aoMap = value;
            if (this._aoMap) {
                this._aoMap.addReference();
            }
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get aoMapIntensity() {
        return this._aoMapIntensity;
    }
    public set aoMapIntensity(value) {
        if (this._aoMapIntensity != value) {
            this._aoMapIntensity = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get specularMap() {
        return this._specularMap;
    }
    public set specularMap(value) {
        if (this._specularMap != value) {
            if (this._specularMap) {
                this._specularMap.dispose();
            }
            this._specularMap = value;
            if (this._specularMap) {
                this._specularMap.addReference();
            }
            this.needsUpdate = true;
        }
    }

    /**
     * Alpha
     */
    public get alphaMap() {
        return this._alphaMap;
    }
    public set alphaMap(value) {
        if (this._alphaMap != value) {
            if (this._alphaMap) {
                this._alphaMap.dispose();
            }
            this._alphaMap = value;
            if (this._alphaMap) {
                this._alphaMap.addReference();
            }
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get envMap() {
        return this._envMap;
    }
    public set envMap(value) {
        if (this._envMap != value) {
            if (this._envMap) {
                this._envMap.dispose();
            }
            this._envMap = value;
            if (this._envMap) {
                this._envMap.addReference();
            }
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get combine() {
        return this._combine;
    }
    public set combine(value) {
        if (this._combine != value) {
            this._combine = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get reflectivity() {
        return this._reflectivity;
    }
    public set reflectivity(value) {
        if (this._reflectivity != value) {
            this._reflectivity = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get refractionRatio() {
        return this._refractionRatio;
    }
    public set refractionRatio(value) {
        if (this._refractionRatio != value) {
            this._refractionRatio = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get wireframe() {
        return this._wireframe;
    }
    public set wireframe(value) {
        if (this._wireframe != value) {
            this._wireframe = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get wireframeLineWidth() {
        return this._wireframeLineWidth;
    }
    public set wireframeLineWidth(value) {
        if (this._wireframeLineWidth != value) {
            this._wireframeLineWidth = value;
            this.needsUpdate = true;
        }
    }

    /**
     * LineCap
     */
    public get wireframeLineCap() {
        return this._wireframeLineCap;
    }
    public set wireframeLineCap(value) {
        if (this._wireframeLineCap != value) {
            this._wireframeLineCap = value;
            this.needsUpdate = true;
        }
    }

    /**
     * LineJoin
     */
    public get wireframeLineJoin() {
        return this._wireframeLineJoin;
    }
    public set wireframeLineJoin(value) {
        if (this._wireframeLineJoin != value) {
            this._wireframeLineJoin = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get skinning() {
        return this._skinning;
    }
    public set skinning(value) {
        if (this._skinning != value) {
            this._skinning = value;
            this.needsUpdate = true;
        }
    }

    /**
     * morphTargets
     */
    public get morphTargets() {
        return this._morphTargets;
    }
    public set morphTargets(value) {
        if (this._morphTargets != value) {
            this._morphTargets = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public clone(): MeshBasicMaterial {
        return new MeshBasicMaterial().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: MeshBasicMaterial): MeshBasicMaterial {
        super.copy(source);

        this._color.copy(source.color);

        this._map = source.map;

        this._lightMap = source.lightMap;
        this._lightMapIntensity = source.lightMapIntensity;

        this._aoMap = source.aoMap;
        this._aoMapIntensity = source.aoMapIntensity;

        this._specularMap = source.specularMap;
        this._alphaMap = source.alphaMap;

        this._envMap = source.envMap;
        this._combine = source.combine;
        this._reflectivity = source.reflectivity;
        this._refractionRatio = source.refractionRatio;

        this._wireframe = source.wireframe;
        this._wireframeLineWidth = source.wireframeLineWidth;
        this._wireframeLineCap = source.wireframeLineCap;
        this._wireframeLineJoin = source.wireframeLineJoin;

        this._skinning = source.skinning;
        this._morphTargets = source.morphTargets;

        return this;
    }
}
