import { Texture } from "./Texture";
import { Nullable } from "../../types";

/**
 * 2D
 */
export class Texture2D extends Texture {

    public buffer: Nullable<HTMLImageElement | ImageBitmap> = null;
    public deleteBuffer: boolean = false;
    public width: number;
    public height: number;
    public mipmaps: HTMLImageElement[] | ImageBitmap[];

    /**
     * 
     * @param generateMipmaps
     * @param flipY
     * @param buffer
     * @param deleteBuffer
     * @param format
     * @param wrapU
     * @param wrapV
     */
    constructor(buffer: Nullable<HTMLImageElement | ImageBitmap> = null, deleteBuffer: boolean = false, generateMipmaps: boolean = false, flipY: boolean = true,
                format?: number, wrapU?: number, wrapV?: number) {
        super();

        this.deleteBuffer = deleteBuffer;
        this.generateMipmaps = generateMipmaps;

        if (format) {
            this.format = format;
        }

        this._flipY = flipY;

        if (wrapU) {
            this._wrapU = wrapU;
        }
        if (wrapV) {
            this._wrapV = wrapV;
        }

        if (buffer) {
            this.buffer = buffer;
            this.width = buffer.width;
            this.height = buffer.height;
        }
    }

    /**
     * 
     */
    public get isReady(): boolean {
        return this.buffer != null && this.buffer.width > 0 && this.buffer.height > 0;
    }

    /**
     * Buffer
     * @param buffer
     */
    public updateBuffer(buffer: HTMLImageElement | ImageBitmap): void {
        this.buffer = buffer;
        this.width = buffer.width;
        this.height = buffer.height;
        this.unpackAlignment = (this.width % 4 != 0) ? 1 : 4;
        this.needsUpdate = true;
    }

    /**
     * Mipmaps
     * @param buffers
     */
    public updateMipmaps(buffers: HTMLImageElement[] | ImageBitmap[]): void {
        this.mipmaps = buffers;
        this.needsUpdate = true;
    }

    /**
     * 
     */
    public clone(): Texture2D {
        return new Texture2D(this.buffer, this.deleteBuffer).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: Texture2D): Texture2D {
        super.copy(source);

        return this;
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        if (this.deleteBuffer) {
            delete this.buffer;
        }
    }
}
