import { DeepImmutable, FloatArray, int, Nullable } from "../types";
import { Matrix3 } from "./Matrix3";
import { MathUtils } from "./MathUtils";
import { BinaryBuffer } from "../Misc/BinaryBuffer";
import { BufferAttribute } from "../Scene/Geometries/BufferAttribute";
import { MathTmp } from "./MathTmp";
import { InterleavedBufferAttribute } from "../Scene/Geometries/InterleavedBufferAttribute";

type InVector2 = Vector2 | DeepImmutable<Vector2>;

/**
 * 2D
 * @category Math
 */
export class Vector2 {

    /**
     * @ignore
     */
    private static _zeroReadOnly = new Vector2(0, 0) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private static _oneReadOnly = new Vector2(1, 1) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private static _oneInvReadOnly = new Vector2(-1, -1) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private static _unitXReadOnly = new Vector2(1, 0) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private static _unitYReadOnly = new Vector2(0, 1) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private static _unitXInvReadOnly = new Vector2(-1, 0) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private static _unitYInvReadOnly = new Vector2(0, -1) as DeepImmutable<Vector2>;
    /**
     * @ignore
     */
    private readonly _p: number[];
    /**
     * @ignore
     */
    private get _x() {return this._p[0]; }
    /**
     * @ignore
     */
    private set _x(value) {this._p[0] = value; }
    /**
     * @ignore
     */
    private get _y() {return this._p[1]; }
    /**
     * @ignore
     */
    private set _y(value) {this._p[1] = value; }
    /**
     * @ignore
     */
    private _notify: boolean;
    /**
     * @ignore
     */
    private _onNotify: any;

    /**
     * 
     * @param x
     * @param y
     */
    constructor(x: number = 0, y: number = 0) {
        this._p = [];
        this._p[0] = x;
        this._p[1] = y;
        this._notify = true;
        this._onNotify = null;
    }

    /**
     * 
     */
    public static Build() {
        return new Vector2();
    }

    /**
     * ZERO
     */
    public static get ZERO(): DeepImmutable<Vector2> {
        return Vector2._zeroReadOnly;
    }

    /**
     * ONE
     */
    public static get ONE(): DeepImmutable<Vector2> {
        return Vector2._oneReadOnly;
    }

    /**
     * INV_ONE
     */
    public static get INV_ONE(): DeepImmutable<Vector2> {
        return Vector2._oneInvReadOnly;
    }

    /**
     * UNIT_X
     */
    public static get UNIT_X(): DeepImmutable<Vector2> {
        return Vector2._unitXReadOnly;
    }

    /**
     * UNIT_Y
     */
    public static get UNIT_Y(): DeepImmutable<Vector2> {
        return Vector2._unitYReadOnly;
    }

    /**
     * UNIT_INV_X
     */
    public static get UNIT_INV_X(): DeepImmutable<Vector2> {
        return Vector2._unitXInvReadOnly;
    }

    /**
     * UNIT_INV_Y
     */
    public static get UNIT_INV_Y(): DeepImmutable<Vector2> {
        return Vector2._unitYInvReadOnly;
    }

    /**
     * 
     * 
     * ()
     * numinput1v[0]
     * v[0]v[1]
     * @param numInputs
     * @param v
     */
    public static computeOrthogonalComplement(numInputs: number, v: Vector2[]): number {

        if (numInputs == 1) {
            v[1] = new Vector2().copy(v[0]).perp().negate();
            return Vector2.orthonormalize(2, v);
        }

        return 0;
    }

    /**
     * Gram-Schmidt
     * 
     * ()
     * 1 <= numElements <= Nv[0]v[numElements-1]
     * v[0]v[numElements-1]
     * @param numInputs
     * @param v
     */
    public static orthonormalize(numInputs: number, v: Vector2[]): number {
        if (v && 1 <= numInputs && numInputs <= 3) {
            let minLength = v[0].length();
            v[0].normalize();

            for (let i = 1; i < numInputs; ++i) {
                for (let j = 0; j < i; ++j) {
                    let dot = v[i].dot(v[j]);
                    v[i].sub(v[j].multiplyScalar(dot));
                }

                const length = v[i].length();
                v[i].normalize();

                if (length < minLength) {
                    minLength = length;
                }
            }
            return minLength;
        }
        return 0;
    }

    /**
     * 
     */
    public get p() {
        return this._p;
    }

    /**
     * X
     */
    public get x() {
        return this._x;
    }
    public set x(value: number) {
        if (this._x != value) {
            this._x = value;
            this.triggerNotify();
        }
    }

    /**
     * Y
     */
    public get y() {
        return this._y;
    }
    public set y(value: number) {
        if (this._y != value) {
            this._y = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get width() {
        return this._x;
    }
    public set width(value: number) {
        if (this._x != value) {
            this._x = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get height() {
        return this._y;
    }
    public set height(value: number) {
        if (this._y != value) {
            this._y = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get notify() {
        return this._notify;
    }
    public set notify(value: boolean) {
        this._notify = value;
    }

    /**
     * 
     */
    public setNotifyFunc(value: any): Vector2 {
        this._onNotify = value;
        return this;
    }

    /**
     * 
     */
    private triggerNotify(): void {
        if (this._notify) {
            if (this._onNotify) {
                this._onNotify();
            }
        }
    }

    /**
     * XY
     * @param x
     * @param y
     */
    public set(x: number, y: number): Vector2 {
        this._x = x;
        this._y = y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public setScalar(scalar: number): Vector2 {
        this._x = scalar;
        this._y = scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * X
     * @param x
     */
    public setX(x: number): Vector2 {
        this._x = x;
        this.triggerNotify();
        return this;
    }

    /**
     * Y
     * @param y
     */
    public setY(y: number): Vector2 {
        this._y = y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): Vector2 {
        switch (index) {
            case 0: this.x = value; break;
            case 1: this.y = value; break;
            default: throw new Error(`: ${index}`);
        }
        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this.x;
            case 1: return this.y;
            default: throw new Error(`: ${index}`);
        }
    }

    /**
     * 
     */
    public clone(): Vector2 {
        return new Vector2(this._x, this._y);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InVector2): Vector2 {
        this._x = source.x;
        this._y = source.y;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     */
    public add(vec: InVector2): Vector2 {
        this._x += vec.x;
        this._y += vec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public addScalar(scalar: number): Vector2 {
        this._x += scalar;
        this._y += scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param aVec
     * @param bVec
     */
    public addVectors(aVec: InVector2, bVec: InVector2): Vector2 {
        this._x = aVec.x + bVec.x;
        this._y = aVec.y + bVec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     * @param scalar
     */
    public addScaledVector(vec: InVector2, scalar: number): Vector2 {
        this._x += vec.x * scalar;
        this._y += vec.y * scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     */
    public sub(vec: InVector2): Vector2 {
        this._x -= vec.x;
        this._y -= vec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param value
     */
    public subScalar(value: number): Vector2 {
        this._x -= value;
        this._y -= value;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param aVec
     * @param bVec
     */
    public subVectors(aVec: InVector2, bVec: InVector2): Vector2 {
        this._x = aVec.x - bVec.x;
        this._y = aVec.y - bVec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     */
    public multiply(vec: InVector2): Vector2 {
        this._x *= vec.x;
        this._y *= vec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public multiplyScalar(scalar: number): Vector2 {
        this._x *= scalar;
        this._y *= scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param aVec
     * @param bVec
     */
    public multiplyVectors(aVec: InVector2, bVec: InVector2): Vector2 {
        this._x = aVec.x * bVec.x;
        this._y = aVec.y * bVec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     */
    public divide(vec: InVector2): Vector2 {
        this._x /= vec.x;
        this._y /= vec.y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public divideScalar(scalar: number): Vector2 {
        return this.multiplyScalar(1 / scalar);
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public applyMatrix3(matrix3: Matrix3): Vector2 {
        const x = this.x;
        const y = this.y;
        const e = matrix3.elements;
        this._x = e[0] * x + e[3] * y + e[6];
        this._y = e[1] * x + e[4] * y + e[7];
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     */
    public min(vec: InVector2): Vector2 {
        this._x = Math.min(this.x, vec.x);
        this._y = Math.min(this.y, vec.y);
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec
     */
    public max(vec: InVector2): Vector2 {
        this._x = Math.max(this.x, vec.x);
        this._y = Math.max(this.y, vec.y);
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param minVec
     * @param maxVec
     */
    public clamp(minVec: InVector2, maxVec: InVector2): Vector2 {
        this._x = Math.max(minVec.x, Math.min(maxVec.x, this.x));
        this._y = Math.max(minVec.y, Math.min(maxVec.y, this.y));
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param minScalar
     * @param maxScalar
     */
    public clampScalar(minScalar: number, maxScalar: number): Vector2 {
        this._x = Math.max(minScalar, Math.min(maxScalar, this.x));
        this._y = Math.max(minScalar, Math.min(maxScalar, this.y));
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param minScalar
     * @param maxScalar
     */
    public clampLength(minScalar: number, maxScalar: number): Vector2 {
        const length = this.length();
        return this.divideScalar(length || 1).multiplyScalar(Math.max(minScalar, Math.min(maxScalar, length)));
    }

    /**
     * 
     */
    public floor(): Vector2 {
        this._x = Math.floor(this._x);
        this._y = Math.floor(this._y);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public ceil(): Vector2 {
        this._x = Math.ceil(this._x);
        this._y = Math.ceil(this._y);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public round(): Vector2 {
        this._x = Math.round(this._x);
        this._y = Math.round(this._y);
        this.triggerNotify();
        return this;
    }

    /**
     * 0
     */
    public roundToZero(): Vector2 {
        this._x = (this._x < 0) ? Math.ceil(this._x) : Math.floor(this._x);
        this._y = (this._y < 0) ? Math.ceil(this._y) : Math.floor(this._y);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public negate(): Vector2 {
        this._x = -this._x;
        this._y = -this._y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public dot(vec: InVector2): number {
        return this._x * vec.x + this._y * vec.y;
    }

    /**
     * 
     * @param vec
     */
    public cross(vec: InVector2): number {
        return this._x * vec.y - this._y * vec.x;
    }

    /**
     * SQ
     */
    public lengthSq(): number {
        return this._x * this._x + this._y * this._y;
    }

    /**
     * 
     */
    public length(): number {
        return Math.sqrt(this._x * this._x + this._y * this._y);
    }

    /**
     * 
     */
    public manhattanLength(): number {
        return Math.abs(this._x) + Math.abs(this._y);
    }

    /**
     * Vector2
     * @param vec
     */
    public manhattanDistanceTo(vec: InVector2): number {
        return Math.abs(this._x - vec.x) + Math.abs(this._y - vec.y);
    }

    /**
     * 
     */
    public normalize(): Vector2 {
        return this.divideScalar(this.length() || 1);
    }

    /**
     * x
     */
    public angle(): number {
        let angle = Math.atan2(this._y, this._x);
        if (angle < 0) { angle += 2 * Math.PI; }
        return angle;
    }

    /**
     * Vector2SQ
     * @param vec
     */
    public distanceToSquared(vec: InVector2): number {
        const dx = this._x - vec.x;
        const dy = this._y - vec.y;
        return dx * dx + dy * dy;
    }

    /**
     * Vector2
     * @param vec
     */
    public distanceTo(vec: InVector2): number {
        return Math.sqrt(this.distanceToSquared(vec));
    }

    /**
     * 
     * @param length
     */
    public setLength(length: number): Vector2 {
        return this.normalize().multiplyScalar(length);
    }

    /**
     * Vector2
     * @param vec
     * @param alpha
     */
    public lerp(vec: InVector2, alpha: number): Vector2 {
        this._x += (vec.x - this._x) * alpha;
        this._y += (vec.y - this._y) * alpha;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector2
     * @param vec1
     * @param vec2
     * @param alpha
     */
    public lerpVectors(vec1: InVector2, vec2: InVector2, alpha: number): Vector2 {
        this._x = vec1.x + (vec2.x - vec1.x) * alpha;
        this._y = vec1.y + (vec2.y - vec1.y) * alpha;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public perp(): Vector2 {
        const x = this.x;
        const y = this.y;
        this._x = y;
        this._y = -x;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public dotPerp(vec: InVector2): number {
        const p2 = MathTmp.Vector2[0].set(this._x, this._y).perp();
        return this.dot(p2);
    }

    /**
     * 
     * @param center
     * @param angle
     */
    public rotateAround(center: InVector2, angle: number): Vector2 {
        const c = Math.cos(angle);
        const s = Math.sin(angle);
        const x = this._x - center.x;
        const y = this._y - center.y;
        this._x = x * c - y * s + center.x;
        this._y = x * s + y * c + center.y;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public random() {
        this._x = Math.random();
        this._y = Math.random();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public equals(vec: InVector2): boolean {
        return (vec.x == this._x) && (vec.y == this._y);
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: InVector2, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        return MathUtils.equalsEpsilon(this._x, right.x, relativeEpsilon, absoluteEpsilon) && MathUtils.equalsEpsilon(this._y, right.y, relativeEpsilon, absoluteEpsilon);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): Vector2 {
        this._x = array[offset];
        this._y = array[offset + 1];
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {
        array[offset] = this._x;
        array[offset + 1] = this._y;
        return array;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Vector2 {
        this._x = dataBuffer.readFloat32();
        this._y = dataBuffer.readFloat32();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Vector2 {
        dataBuffer.writeFloat32(this._x);
        dataBuffer.writeFloat32(this._y);
        return this;
    }

    /**
     * Buffer
     * @param attribute
     * @param index
     */
    public fromBufferAttribute(attribute: BufferAttribute | InterleavedBufferAttribute, index: int): Vector2 {
        this._x = attribute.getX(index);
        this._y = attribute.getY(index);
        return this;
    }
}
