import { TextureCube } from "./TextureCube";

/**
 * Mipmap
 */
export interface MipmapRawCubeBuffer {
    buffer: ArrayBuffer | ArrayBufferView;
    width: number;
    height: number;
}

/**
 * 
 */
export class TextureRawCube extends TextureCube {

    public bufferRaws: ArrayBuffer[] | ArrayBufferView[] = [];
    public mipmapRaws: MipmapRawCubeBuffer[][];

    /**
     * 
     * @param buffers
     * @param size
     * @param deleteBuffer
     * @param generateMipmaps
     * @param flipY
     * @param format
     * @param wrapU
     * @param wrapV
     */
    constructor(buffers: ArrayBuffer[] | ArrayBufferView[], size: number, deleteBuffer: boolean = false, generateMipmaps: boolean = false, flipY: boolean = false,
                format?: number, wrapU?: number, wrapV?: number) {
        super(null, deleteBuffer, generateMipmaps, flipY, format, wrapU, wrapV);

        this.width = size;
        this.height = size;
        this.bufferRaws = buffers;
        this.unpackAlignment = (this.width % 4 != 0) ? 1 : 4;
    }

    /**
     * 
     */
    public get isReady(): boolean {
        return this.bufferRaws.length > 0;
    }

    /**
     * 
     */
    public clone(): TextureRawCube {
        return new TextureRawCube(this.bufferRaws, this.width, this.deleteBuffer).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TextureRawCube): TextureRawCube {
        super.copy(source);

        return this;
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        if (this.deleteBuffer) {
            this.bufferRaws.forEach((buffer: any, index: number) => {
                delete this.bufferRaws[index];
            });
            this.bufferRaws.length = 0;
        }
    }
}
