import { Vector2 } from "./Vector2";
import { Vector3 } from "./Vector3";
import { Vector4 } from "./Vector4";
import { Matrix3 } from "./Matrix3";
import { Matrix4 } from "./Matrix4";
import { Euler } from "./Euler";
import { Quaternion } from "./Quaternion";
import { Box2 } from "./Box2";
import { Box3 } from "./Box3";
import { Sphere } from "./Sphere";
import { ColorRGB } from "./ColorRGB";
import { ColorRGBA } from "./ColorRGBA";

/**
 * @ignore
 */
export abstract class MathTmp {
    [key: string]: (Vector2 | Vector3 | Vector4 | Matrix3 | Matrix4 | Euler | Quaternion | Box2 | Box3 | Sphere)[];

    private static _Vector2: Vector2[];
    private static _Vector3: Vector3[];
    private static _Vector4: Vector4[];
    private static _Matrix3: Matrix3[];
    private static _Matrix4: Matrix4[];
    private static _Euler: Euler[];
    private static _Quaternion: Quaternion[];

    private static _Box2: Box2[];
    private static _Box3: Box3[];
    private static _Sphere: Sphere[];

    private static _ColorRGB: ColorRGB[];
    private static _ColorRGBA: ColorRGBA[];

    public static get Vector2() {
        if (MathTmp._Vector2.length <= 0) {
            MathTmp._Vector2 = buildArray(10, Vector2.Build);
        }
        return MathTmp._Vector2;
    }
    public static get Vector3() {
        if (MathTmp._Vector3.length <= 0) {
            MathTmp._Vector3 = buildArray(10, Vector3.Build);
        }
        return MathTmp._Vector3;
    }
    public static get Vector4() {
        if (MathTmp._Vector4.length <= 0) {
            MathTmp._Vector4 = buildArray(10, Vector4.Build);
        }
        return MathTmp._Vector4;
    }
    public static get Matrix3() {
        if (MathTmp._Matrix3.length <= 0) {
            MathTmp._Matrix3 = buildArray(6, Matrix3.Build);
        }
        return MathTmp._Matrix3;
    }
    public static get Matrix4() {
        if (MathTmp._Matrix4.length <= 0) {
            MathTmp._Matrix4 = buildArray(6, Matrix4.Build);
        }
        return MathTmp._Matrix4;
    }
    public static get Euler() {
        if (MathTmp._Euler.length <= 0) {
            MathTmp._Euler = buildArray(6, Euler.Build);
        }
        return MathTmp._Euler;
    }
    public static get Quaternion() {
        if (MathTmp._Quaternion.length <= 0) {
            MathTmp._Quaternion = buildArray(6, Quaternion.Build);
        }
        return MathTmp._Quaternion;
    }

    public static get Box2() {
        if (MathTmp._Box2.length <= 0) {
            MathTmp._Box2 = buildArray(6, Box2.Build);
        }
        return MathTmp._Box2;
    }
    public static get Box3() {
        if (MathTmp._Box3.length <= 0) {
            MathTmp._Box3 = buildArray(6, Box3.Build);
        }
        return MathTmp._Box3;
    }
    public static get Sphere() {
        if (MathTmp._Sphere.length <= 0) {
            MathTmp._Sphere = buildArray(6, Sphere.Build);
        }
        return MathTmp._Sphere;
    }

    public static get ColorRGB() {
        if (MathTmp._ColorRGB.length <= 0) {
            MathTmp._ColorRGB = buildArray(6, ColorRGB.Build);
        }
        return MathTmp._ColorRGB;
    }
    public static get ColorRGBA() {
        if (MathTmp._ColorRGBA.length <= 0) {
            MathTmp._ColorRGBA = buildArray(6, ColorRGBA.Build);
        }
        return MathTmp._ColorRGBA;
    }
}

/**
 * @ignore
 * @param size
 * @param itemBuilder
 * @constructor
 */
function buildArray<T>(size: number, itemBuilder: () => T): Array<T> {
    const a: T[] = [];
    for (let i = 0; i < size; ++i) {
        a.push(itemBuilder());
    }
    return a;
}
