import { Curve } from "./Curve";
import { Vector3 } from "../Vector3";
import { Nullable } from "../../types";
import { BinaryBuffer } from "../../Misc/BinaryBuffer";

/**
 * 3D
 * @category Curve
 */
export class LineCurve3 extends Curve<Vector3> {

    public v1: Vector3 = new Vector3();
    public v2: Vector3 = new Vector3();

    /**
     *
     * @param v1
     * @param v2
     */
    constructor(v1: Nullable<Vector3> = null, v2: Nullable<Vector3> = null) {
        super();

        this.type = 'LineCurve3';
        this.arcLengthDivisions = 10;

        if (v1) { this.v1.copy(v1); }
        if (v2) { this.v2.copy(v2); }
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector3> = null): Vector3 {
        const point = optionalTarget ?? new Vector3();

        if (t == 1) {
            point.copy(this.v2);
        } else {
            point.copy(this.v2).sub(this.v1);
            point.multiplyScalar(t).add(this.v1);
        }

        return point;
    }

    /**
     * 
     * @param u
     * @param optionalTarget
     */
    public getPointAt(u: number, optionalTarget: Nullable<Vector3> = null): Vector3 {
        return this.getPoint(u, optionalTarget);
    }

    /**
     * 
     */
    public clone(): LineCurve3 {
        return new LineCurve3().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: LineCurve3): LineCurve3 {
        super.copy(source);

        this.v1.copy(source.v1);
        this.v2.copy(source.v2);

        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();
        data.v1 = this.v1.toArray();
        data.v2 = this.v2.toArray();

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): LineCurve3 {
        super.fromJSON(json);

        this.v1.fromArray(json.v1);
        this.v2.fromArray(json.v2);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): LineCurve3 {
        super.toDataBuffer(dataBuffer);

        this.v1.toDataBuffer(dataBuffer);
        this.v2.toDataBuffer(dataBuffer);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): LineCurve3 {
        super.fromDataBuffer(dataBuffer);

        this.v1.fromDataBuffer(dataBuffer);
        this.v2.fromDataBuffer(dataBuffer);
        return this;
    }
}
