import { Light } from "./Light";
import { Vector3 } from "../Math";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";

/**
 * 
 */
export class SpotLight extends Light {

    /**
     * @ignore
     */
    protected _target: Vector3;
    /**
     * @ignore
     */
    protected _distance: number = 0;
    /**
     * @ignore
     */
    protected _angle: number = Math.PI / 3;
    /**
     * @ignore
     */
    protected _penumbra: number = 0;
    /**
     * @ignore
     */
    protected _decay: number = 1;

    /**
     * 
     * @param scene
     * @param parent
     * @param name
     */
    constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '') {
        super(scene, parent, name);

        this._target = new Vector3();
        this._position.set(0, 1, 0);
    }

    /**
     * 
     */
    public get intensity() {
        return this._intensity * 4 * Math.PI;
    }
    public set intensity(value: number) {
        this._intensity = value / Math.PI;
    }

    /**
     * 
     */
    public get distance() {
        return this._distance;
    }
    public set distance(value: number) {
        this._distance = value;
    }

    /**
     * 
     */
    public get angle() {
        return this._angle;
    }
    public set angle(value: number) {
        this._angle = value;
    }

    /**
     * 
     */
    public get penumbra() {
        return this._penumbra;
    }
    public set penumbra(value: number) {
        this._penumbra = value;
    }

    /**
     * 
     */
    public get decay() {
        return this._decay;
    }
    public set decay(value: number) {
        this._decay = value;
    }
}
