import { Curve } from "./Curve";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { BezierCurveFunc } from "./BezierCurveFunc";
import { BinaryBuffer } from "../../Misc";

/**
 * 2D
 * @category Curve
 */
export class CubicBezierCurve extends Curve<Vector2> {

    public v0: Vector2 = new Vector2();
    public v1: Vector2 = new Vector2();
    public v2: Vector2 = new Vector2();
    public v3: Vector2 = new Vector2();

    /**
     * 
     * @param v0
     * @param v1
     * @param v2
     * @param v3
     */
    constructor(v0: Nullable<Vector2> = null, v1: Nullable<Vector2> = null, v2: Nullable<Vector2> = null, v3: Nullable<Vector2> = null) {
        super();

        this.type = 'CubicBezierCurve';

        if (v0) { this.v0.copy(v0); }
        if (v1) { this.v1.copy(v1); }
        if (v2) { this.v2.copy(v2); }
        if (v3) { this.v3.copy(v3); }
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector2> = null): Vector2 {
        const point = optionalTarget ?? new Vector2();

        const v0 = this.v0;
        const v1 = this.v1;
        const v2 = this.v2;
        const v3 = this.v3;

        point.set(
            BezierCurveFunc.CubicBezier(t, v0.x, v1.x, v2.x, v3.x),
            BezierCurveFunc.CubicBezier(t, v0.y, v1.y, v2.y, v3.y),
        );

        return point;
    }

    /**
     * 
     */
    public clone(): CubicBezierCurve {
        return new CubicBezierCurve().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: CubicBezierCurve): CubicBezierCurve {
        super.copy(source);

        this.v0.copy(source.v0);
        this.v1.copy(source.v1);
        this.v2.copy(source.v2);
        this.v3.copy(source.v3);

        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();

        data.v0 = this.v0.toArray();
        data.v1 = this.v1.toArray();
        data.v2 = this.v2.toArray();
        data.v3 = this.v3.toArray();

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): CubicBezierCurve {
        super.fromJSON(json);

        this.v0.fromArray(json.v0);
        this.v1.fromArray(json.v1);
        this.v2.fromArray(json.v2);
        this.v3.fromArray(json.v3);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): CubicBezierCurve {
        super.toDataBuffer(dataBuffer);

        this.v0.toDataBuffer(dataBuffer);
        this.v1.toDataBuffer(dataBuffer);
        this.v2.toDataBuffer(dataBuffer);
        this.v3.toDataBuffer(dataBuffer);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): CubicBezierCurve {
        super.fromDataBuffer(dataBuffer);

        this.v0.fromDataBuffer(dataBuffer);
        this.v1.fromDataBuffer(dataBuffer);
        this.v2.fromDataBuffer(dataBuffer);
        this.v3.fromDataBuffer(dataBuffer);

        return this;
    }
}
