import { Nullable } from "../../types";
import { Vector2 } from "../Vector2";
import { Vector3 } from "../Vector3";
import { Matrix4 } from "../Matrix4";
import { MathUtils } from "../MathUtils";
import { BinaryBuffer } from "../../Misc";

/**
 * 
 * @ignore
 */
export class Curve<T = Vector2 | Vector3> {

    public type: string;
    public arcLengthDivisions: number;
    protected needsUpdate: boolean = false;
    protected cacheArcLengths: number[];

    /**
     * 
     */
    constructor() {
        this.type = 'Curve';
        this.arcLengthDivisions = 200;
    }

    /**
     * 
     */
    public isEmpty(): boolean {
        return true;
    }

    /**
     * 
     */
    public getPoint(t: number, optionalTarget: Nullable<T> = null): T {
        // @ts-ignore
        return null;
    }

    /**
     * 
     * @param u
     * @param optionalTarget
     */
    public getPointAt(u: number, optionalTarget: Nullable<T> = null): T {
        const t = this.getUtoTmapping(u);
        return this.getPoint(t, optionalTarget);
    }

    /**
     * 
     * @param divisions
     */
    public getPoints(divisions: number = 5): T[] {

        const points = [];
        for (let d = 0; d <= divisions; d++) {
            points.push(this.getPoint(d / divisions));
        }
        return points;
    }

    /**
     * 
     * @param divisions
     */
    public getSpacedPoints(divisions: number = 5): T[] {

        const points = [];
        for (let d = 0; d <= divisions; d++) {
            points.push(this.getPointAt(d / divisions));
        }
        return points;
    }

    /**
     * 
     */
    public getLength(): number {
        const lengths = this.getLengths();
        return lengths[lengths.length - 1];
    }

    /**
     * 
     * @param divisions
     */
    public getLengths(divisions = this.arcLengthDivisions): number[] {

        if (this.cacheArcLengths &&
            (this.cacheArcLengths.length == divisions + 1) &&
            !this.needsUpdate) {
            return this.cacheArcLengths;
        }

        this.needsUpdate = false;

        let cache = [];
        let last = this.getPoint(0);
        let sum = 0;
        let current: T;

        cache.push(0);

        for (let p = 1; p <= divisions; p++) {
            current = this.getPoint(p / divisions);
            (current as any).distanceTo(last);
            cache.push(sum);
            last = current;
        }

        this.cacheArcLengths = cache;

        return cache;
    }

    /**
     * 
     */
    public updateArcLengths(): number[] {
        this.needsUpdate = true;
        return this.getLengths();
    }

    /**
     * UT
     * @param u
     * @param distance
     */
    public getUtoTmapping(u: number, distance: number = 0): number {
        let arcLengths = this.getLengths();

        let i = 0;
        let il = arcLengths.length;

        let targetArcLength; // The targeted u distance value to get

        if (distance) {
            targetArcLength = distance;
        } else {
            targetArcLength = u * arcLengths[il - 1];
        }

        // binary search for the index with largest value smaller than target u distance

        let low = 0;
        let high = il - 1;
        let comparison;

        while (low <= high) {
            i = Math.floor(low + (high - low) / 2); // less likely to overflow, though probably not issue here, JS doesn't really have integers, all numbers are floats

            comparison = arcLengths[i] - targetArcLength;

            if (comparison < 0) {
                low = i + 1;
            } else if (comparison > 0) {
                high = i - 1;
            } else {
                high = i;
                break;

                // DONE
            }
        }

        i = high;

        if (arcLengths[i] == targetArcLength) {
            return i / (il - 1);
        }

        // we could get finer grain at lengths, or use simple interpolation between two points

        let lengthBefore = arcLengths[i];
        let lengthAfter = arcLengths[i + 1];

        let segmentLength = lengthAfter - lengthBefore;

        // determine where we are between the 'before' and 'after' points

        let segmentFraction = (targetArcLength - lengthBefore) / segmentLength;

        // add that fractional amount to t

        let t = (i + segmentFraction) / (il - 1);

        return t;
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getTangent(t: number, optionalTarget: Nullable<T> = null): T {

        let delta = 0.0001;
        let t1 = t - delta;
        let t2 = t + delta;

        // Capping in case of danger

        if (t1 < 0) {
            t1 = 0;
        }
        if (t2 > 1) {
            t2 = 1;
        }

        let pt1 = this.getPoint(t1);
        let pt2 = this.getPoint(t2);

        let tangent: T;
        if (!optionalTarget) {
            if (pt1 instanceof Vector2) {
                tangent = new Vector2() as any;
            } else {
                tangent = new Vector3() as any;
            }
        } else {
            tangent = optionalTarget;
        }

        if (tangent) {
            (tangent as any).copy(pt2).sub(pt1).normalize();
        }

        return tangent;
    }

    /**
     * 
     * @param u
     * @param optionalTarget
     */
    public getTangentAt(u: number, optionalTarget: Nullable<T> = null): T {

        const t = this.getUtoTmapping(u);
        return this.getTangent(t, optionalTarget);
    }

    /**
     * Frenet
     * @param segments
     * @param closed
     */
    public computeFrenetFrames(segments: number, closed: boolean) {

        let normal = new Vector3();

        let tangents = [];
        let normals = [];
        let binormals = [];

        let vec = new Vector3();
        let mat = new Matrix4();

        let i;
        let u;
        let theta;

        // compute the tangent vectors for each segment on the curve

        for (i = 0; i <= segments; i++) {
            u = i / segments;

            tangents[ i ] = this.getTangentAt(u);
            (tangents[ i ] as any).normalize();
        }

        // select an initial normal vector perpendicular to the first tangent vector,
        // and in the direction of the minimum tangent xyz component

        normals[0] = new Vector3();
        binormals[0] = new Vector3();

        let min = Number.MAX_VALUE;
        let tx = Math.abs((tangents[0] as any).x);
        let ty = Math.abs((tangents[0] as any).y);
        let tz = Math.abs((tangents[0] as any).z);

        if (tx <= min) {
            min = tx;
            normal.set(1, 0, 0);
        }

        if (ty <= min) {
            min = ty;
            normal.set(0, 1, 0);
        }

        if (tz <= min) {
            normal.set(0, 0, 1);
        }

        vec.crossVectors(tangents[0] as any, normal).normalize();

        normals[0].crossVectors(tangents[0] as any, vec);
        binormals[0].crossVectors(tangents[0] as any, normals[0]);

        // compute the slowly-varying normal and binormal vectors for each segment on the curve

        for (i = 1; i <= segments; i++) {
            normals[i] = normals[i - 1].clone();

            binormals[i] = binormals[i - 1].clone();

            vec.crossVectors(tangents[i - 1] as any, tangents[i] as any);

            if (vec.length() > Number.EPSILON) {
                vec.normalize();

                theta = Math.acos(MathUtils.clamp((tangents[i - 1] as any).dot(tangents[i] as any), -1, 1)); // clamp for floating pt errors

                normals[i].applyMatrix4(mat.makeRotationAxis(vec, theta));
            }

            binormals[i].crossVectors(tangents[i] as any, normals[i]);
        }

        // if the curve is closed, postprocess the vectors so the first and last normal vectors are the same

        if (closed) {
            theta = Math.acos(MathUtils.clamp(normals[0].dot(normals[segments]), -1, 1));
            theta /= segments;

            if ((tangents[0] as any).dot(vec.crossVectors(normals[0], normals[segments])) > 0) {
                theta = -theta;
            }

            for (i = 1; i <= segments; i++) {
                // twist a little...
                normals[i].applyMatrix4(mat.makeRotationAxis(tangents[i] as any, theta * i));
                binormals[i].crossVectors(tangents[i] as any, normals[i]);
            }
        }

        return {
            tangents,
            normals,
            binormals,
        };
    }

    /**
     * 
     */
    public clone() {
        return new Curve<T>().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: Curve<T>): Curve<T> {
        this.arcLengthDivisions = source.arcLengthDivisions;
        return this;
    }

    /**
     * JSON
     */
    public toJSON() {
        const data: any = {};
        data.type = this.type;
        data.arcLengthDivisions = this.arcLengthDivisions;
        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): Curve<T> {
        this.arcLengthDivisions = json.arcLengthDivisions;
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Curve<T> {
        dataBuffer.writeString(this.type);
        dataBuffer.writeFloat32(this.arcLengthDivisions);
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Curve<T> {
        this.arcLengthDivisions = dataBuffer.readFloat32();
        return this;
    }
}
