import { Camera } from "./Camera";
import { MathUtils } from "../Math";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";

/**
 * 
 */
export class PerspectiveCamera extends Camera {

    /**
     * @ignore
     */
    protected _fov: number;
    /**
     * @ignore
     */
    protected _aspect: number;
    /**
     * @ignore
     */
    protected _near: number;
    /**
     * @ignore
     */
    protected _far: number;
    /**
     * @ignore
     */
    protected _filmGauge: number;
    /**
     * @ignore
     */
    protected _filmOffset: number;

    /**
     * 
     * @param scene
     * @param activeIfNoneActive
     * @param parent
     * @param name
     * @param fov
     * @param aspect
     * @param near
     * @param far
     */
    constructor(scene: Scene, activeIfNoneActive: boolean = true, parent: Nullable<AbstractObject> = null, name: string = '', fov: number = 50, aspect: number = 1, near: number = 0.1, far: number = 2000) {
        super(scene, activeIfNoneActive, parent, name);

        this._fov = fov;
        this._aspect = aspect;
        this._near = near;
        this._far = far;

        this._filmGauge = 35;
        this._filmOffset = 0;

        this.updateProjectionMatrix();
    }

    /**
     * 
     */
    public get fov() {
        return this._fov;
    }
    public set fov(value) {
        if (this._fov != value) {
            this._fov = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public get aspect() {
        return this._aspect;
    }
    public set aspect(value) {
        if (this._aspect != value) {
            this._aspect = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public get near() {
        return this._near;
    }
    public set near(value) {
        if (this._near != value) {
            this._near = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public get far() {
        return this._far;
    }
    public set far(value) {
        if (this._far != value) {
            this._far = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * filmGauge
     */
    public get filmGauge() {
        return this._filmGauge;
    }
    public set filmGauge(value) {
        if (this._filmGauge != value) {
            this._filmGauge = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * filmOffset
     */
    public get filmOffset() {
        return this._filmOffset;
    }
    public set filmOffset(value) {
        if (this._filmOffset != value) {
            this._filmOffset = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * FOV
     * @param focalLength
     */
    public setFocalLength(focalLength: number): PerspectiveCamera {
        const vExtentSlope = 0.5 * this.getFilmHeight() / focalLength;
        this.fov = MathUtils.RAD2DEG * 2 * Math.atan(vExtentSlope);
        this.updateProjectionMatrix();
        return this;
    }

    /**
     * 
     */
    public getFocalLength(): number {
        const vExtentSlope = Math.tan(MathUtils.DEG2RAD * 0.5 * this.fov);
        return 0.5 * this.getFilmHeight() / vExtentSlope;
    }

    /**
     * 
     */
    public getEffectiveFOV(): number {
        return MathUtils.RAD2DEG * 2 * Math.atan(Math.tan(MathUtils.DEG2RAD * 0.5 * this.fov));
    }

    /**
     * 
     */
    public getFilmWidth(): number {
        return this.filmGauge * Math.min(this.aspect, 1);
    }

    /**
     * 
     */
    public getFilmHeight() {
        return this.filmGauge / Math.max(this.aspect, 1);
    }

    /**
     * 
     */
    public updateProjectionMatrix(): void {

        this._needsUpdateCameraMatrix = false;

        let near = this.near;
        let top = near * Math.tan(MathUtils.DEG2RAD * 0.5 * this.fov) / this.zoom;
        let bottom = -top;
        let height = 2 * top;
        let width = this.aspect * height;
        let left = -0.5 * width;
        let right = -left;

        let skew = this.filmOffset;
        if (skew != 0) { left += near * skew / this.getFilmWidth(); }

        this._projectionMatrix.makePerspective(left, left + width, top, top - height, near, this.far);
        this._projectionMatrixInverse.getInverse(this._projectionMatrix);
        this._viewProjectionMatrix.multiplyMatrices(this._projectionMatrix, this._matrixWorldInverse);
        this._viewProjectionMatrixInverse.getInverse(this._viewProjectionMatrix);
        this._frustum.setFromProjectionMatrix(this._viewProjectionMatrix);
    }
}
