import { WebGLEngine } from "../WebGLEngine";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {
        createTransformFeedback(): WebGLTransformFeedback;
        deleteTransformFeedback(glObj: WebGLTransformFeedback): WebGLEngine;
        bindTransformFeedback(glObj: WebGLTransformFeedback): WebGLEngine;
        beginTransformFeedback(usePoints: boolean): WebGLEngine;
        endTransformFeedback(): WebGLEngine;
        pauseTransformFeedback(): WebGLEngine;
        resumeTransformFeedback(): WebGLEngine;
        setTranformFeedbackVaryings(glProgram: WebGLProgram, varyings: string[], separate: boolean): WebGLEngine;
        bindTransformFeedbackBuffer(glObj: WebGLBuffer, index: number): WebGLEngine;
    }
}

WebGLEngine.prototype.createTransformFeedback = function(): WebGLTransformFeedback {
    return this.gl.createTransformFeedback();
};

WebGLEngine.prototype.deleteTransformFeedback = function(glObj: WebGLTransformFeedback): WebGLEngine {
    this.gl.deleteTransformFeedback(glObj);
    return this;
};

WebGLEngine.prototype.bindTransformFeedback = function(glObj: WebGLTransformFeedback): WebGLEngine {
    this.gl.bindTransformFeedback(this.gl.TRANSFORM_FEEDBACK, glObj);
    return this;
};

WebGLEngine.prototype.beginTransformFeedback = function(usePoints: boolean = true): WebGLEngine {
    this.gl.beginTransformFeedback(usePoints ? this.gl.POINTS : this.gl.TRIANGLES);
    return this;
};

WebGLEngine.prototype.endTransformFeedback = function(): WebGLEngine {
    this.gl.endTransformFeedback();
    return this;
};

WebGLEngine.prototype.pauseTransformFeedback = function(): WebGLEngine {
    this.gl.pauseTransformFeedback();
    return this;
};

WebGLEngine.prototype.resumeTransformFeedback = function(): WebGLEngine {
    this.gl.resumeTransformFeedback();
    return this;
};

WebGLEngine.prototype.setTranformFeedbackVaryings = function(glProgram: WebGLProgram, varyings: string[], separate: boolean = false): WebGLEngine {
    this.gl.transformFeedbackVaryings(glProgram, varyings, separate ? this.gl.SEPARATE_ATTRIBS : this.gl.INTERLEAVED_ATTRIBS);
    return this;
};

WebGLEngine.prototype.bindTransformFeedbackBuffer = function(glObj: WebGLBuffer, index: number = 0): WebGLEngine {
    this.gl.bindBufferBase(this.gl.TRANSFORM_FEEDBACK_BUFFER, index, glObj);
    return this;
};
