import { Camera } from "./Camera";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";

/**
 * 
 */
export class OrthographicCamera extends Camera {

    /**
     * @ignore
     */
    protected _left: number;
    /**
     * @ignore
     */
    protected _right: number;
    /**
     * @ignore
     */
    protected _top: number;
    /**
     * @ignore
     */
    protected _bottom: number;
    /**
     * @ignore
     */
    protected _near: number;
    /**
     * @ignore
     */
    protected _far: number;
    /**
     * @ignore
     */
    protected _aspect: number;

    /**
     * 
     * @param scene
     * @param activeIfNoneActive
     * @param parent
     * @param name
     * @param left
     * @param right
     * @param top
     * @param bottom
     * @param near
     * @param far
     */
    constructor(scene: Scene, activeIfNoneActive: boolean = true, parent: Nullable<AbstractObject> = null, name: string = '',
                left: number = -1, right: number = 1, top: number = 1, bottom: number = -1,
                near: number = 0.1, far: number = 2000) {
        super(scene, activeIfNoneActive, parent, name);

        this.left = left;
        this.right = right;
        this.top = top;
        this.bottom = bottom;

        this.near = near;
        this.far = far;

        this.aspect = (this.right - this.left) / (this.top - this.bottom);
    }

    public get left() {
        return this._left;
    }
    public set left(value) {
        if (this._left != value) {
            this._left = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    public get right() {
        return this._right;
    }
    public set right(value) {
        if (this._right != value) {
            this._right = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    public get top() {
        return this._top;
    }
    public set top(value) {
        if (this._top != value) {
            this._top = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    public get bottom() {
        return this._bottom;
    }
    public set bottom(value) {
        if (this._bottom != value) {
            this._bottom = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public get near() {
        return this._near;
    }
    public set near(value) {
        if (this._near != value) {
            this._near = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public get far() {
        return this._far;
    }
    public set far(value) {
        if (this._far != value) {
            this._far = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public get aspect() {
        return this._aspect;
    }
    public set aspect(value) {
        if (this._aspect != value) {
            this._aspect = value;
            this._needsUpdateCameraMatrix = true;
        }
    }

    /**
     * 
     */
    public updateProjectionMatrix() {

        this._needsUpdateCameraMatrix = false;

        const dx = (this.right - this.left) / (2);
        const dy = (this.top - this.bottom) / (2);
        const cx = (this.right + this.left) / 2;
        const cy = (this.top + this.bottom) / 2;

        const left = cx - dx;
        const right = cx + dx;
        const top = cy + dy;
        const bottom = cy - dy;

        this._projectionMatrix.makeOrthographic(left, right, top, bottom, this.near, this.far);
        this._projectionMatrixInverse.getInverse(this._projectionMatrix);
        this._viewProjectionMatrix.multiplyMatrices(this._projectionMatrix, this._matrixWorldInverse);
        this._viewProjectionMatrixInverse.getInverse(this._viewProjectionMatrix);
        this._frustum.setFromProjectionMatrix(this._viewProjectionMatrix);
    }
}
