import { WebGLEngine } from "../WebGLEngine";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {

        stencilLocked: boolean;
        currentStencilTest: boolean;
        currentStencilMask: number;
        currentStencilFunc: number;
        currentStencilRef: number;
        currentStencilFuncMask: number;
        currentStencilFail: number;
        currentStencilZFail: number;
        currentStencilZPass: number;
        currentStencilClear: number;

        _initStencilState(): WebGLEngine;
        setStencilTest(test: boolean): WebGLEngine;
        setStencilhMask(mask: number): WebGLEngine;
        setStencilFunc(func: number, ref: number, mask: number): WebGLEngine;
        setStencilOp(fail: number, zFail: number, zPass: number): WebGLEngine;
        setStencilLock(lock: boolean): WebGLEngine;
        setStencilClear(depth: number): WebGLEngine;
        setStencilReset(): WebGLEngine;

        getStencilTest(): boolean;
    }
}

WebGLEngine.prototype._initStencilState = function(): WebGLEngine {

    this.stencilLocked = false;
    this.currentStencilTest = false;
    this.currentStencilMask = -1;
    this.currentStencilFunc = -1;
    this.currentStencilRef = -1;
    this.currentStencilFuncMask = -1;
    this.currentStencilFail = -1;
    this.currentStencilZFail = -1;
    this.currentStencilZPass = -1;
    this.currentStencilClear = -1;

    const gl = this.gl;

    gl.disable(gl.STENCIL_TEST);
    gl.stencilMask(0);

    return this;
};

WebGLEngine.prototype.setStencilTest = function(test: boolean): WebGLEngine {

    const gl = this.gl;

    if (!this.stencilLocked && this.currentStencilTest != test) {
        if (test) {
            gl.enable(gl.STENCIL_TEST);
        } else {
            gl.disable(gl.STENCIL_TEST);
        }
        this.currentStencilTest = test;
    }

    return this;
};

WebGLEngine.prototype.setStencilhMask = function(mask: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentStencilMask != mask && !this.stencilLocked) {
        gl.stencilMask(mask);
        this.currentStencilMask = mask;
    }

    return this;
};

WebGLEngine.prototype.setStencilFunc = function(func: number, ref: number, mask: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentStencilFunc != func ||
        this.currentStencilRef 	!= ref ||
        this.currentStencilFuncMask != mask) {

        gl.stencilFunc(func, ref, mask);

        this.currentStencilFunc = func;
        this.currentStencilRef = ref;
        this.currentStencilFuncMask = mask;
    }

    return this;
};

WebGLEngine.prototype.setStencilOp = function(fail: number, zFail: number, zPass: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentStencilFail != fail ||
        this.currentStencilZFail != zFail ||
        this.currentStencilZPass != zPass) {

        gl.stencilOp(fail, zFail, zPass);

        this.currentStencilFail = fail;
        this.currentStencilZFail = zFail;
        this.currentStencilZPass = zPass;
    }

    return this;
};

WebGLEngine.prototype.setStencilLock = function(lock: boolean): WebGLEngine {
    this.stencilLocked = lock;
    return this;
};

WebGLEngine.prototype.setStencilClear = function(stencil: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentStencilClear != stencil) {
        gl.clearStencil(stencil);
        this.currentStencilClear = stencil;
    }
    return this;
};

WebGLEngine.prototype.setStencilReset = function(): WebGLEngine {

    this.stencilLocked = false;
    this.currentStencilMask = -1;
    this.currentStencilFunc = -1;
    this.currentStencilRef = -1;
    this.currentStencilFuncMask = -1;
    this.currentStencilFail = -1;
    this.currentStencilZFail = -1;
    this.currentStencilZPass = -1;
    this.currentStencilClear = -1;

    return this;
};

WebGLEngine.prototype.getStencilTest = function(): boolean {
    return this.currentStencilTest;
};
