import { InterleavedBuffer } from "./InterleavedBuffer";
import { BufferArray } from "../../types";

/**
 * Buffer
 */
export class InstancedInterleavedBuffer extends InterleavedBuffer {

    public meshPerAttribute: number;

    /**
     * 
     * @param array
     * @param stride
     * @param meshPerAttribute
     */
    constructor(array: BufferArray, stride: number, meshPerAttribute: number = 1) {
        super(array, stride);

        this.meshPerAttribute = meshPerAttribute;
    }

    /**
     * 
     */
    public clone(): InstancedInterleavedBuffer {
        return new InstancedInterleavedBuffer(this.array, this.stride, this.meshPerAttribute).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InstancedInterleavedBuffer): InstancedInterleavedBuffer {
        super.copy(source);
        return this;
    }
}
