import { Nullable } from "../types";
import { Vector3 } from "./Vector3";
import { MathUtils } from "./MathUtils";

/**
 * 
 * @category Math
 */
export class Spherical {

    /**
     * 
     */
    public radius: number = 1;
    /**
     * 
     */
    public phi: number = 0;
    /**
     * 
     */
    public theta: number = 0;

    constructor(radius: Nullable<number> = null, phi: Nullable<number> = null, theta: Nullable<number> = null) {
        if (radius) {
            this.radius = radius;
        }
        if (phi) {
            this.phi = phi;
        }
        if (theta) {
            this.theta = theta;
        }
    }

    /**
     * 
     * @param radius
     * @param phi
     * @param theta
     */
    public set(radius: number, phi: number, theta: number): Spherical {
        this.radius = radius;
        this.phi = phi;
        this.theta = theta;
        return this;
    }

    /**
     * 
     * @param source
     */
    public copy(source: Spherical): Spherical {
        this.radius = source.radius;
        this.phi = source.phi;
        this.theta = source.theta;
        return this;
    }

    /**
     * 
     */
    public clone(): Spherical {
        return new Spherical().copy(this);
    }

    /**
     * 
     */
    public makeSafe(): Spherical {
        const EPS = 0.000001;
        this.phi = Math.max(EPS, Math.min(Math.PI - EPS, this.phi));
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public setFromVector3(vec: Vector3): Spherical {
        return this.setFromCartesianCoords(vec.x, vec.y, vec.z);
    }

    /**
     * XYZ
     * @param x
     * @param y
     * @param z
     */
    public setFromCartesianCoords(x: number, y: number, z: number): Spherical {
        this.radius = Math.sqrt(x * x + y * y + z * z);

        if (this.radius == 0) {
            this.theta = 0;
            this.phi = 0;
        } else {
            this.theta = Math.atan2(x, z);
            this.phi = Math.acos(MathUtils.clamp(y / this.radius, -1, 1));
        }
        return this;
    }
}
