import { BufferAttribute } from "./BufferAttribute";
import { BufferArray } from "../../types";

/**
 * Buffer
 */
export class InstancedBufferAttribute extends BufferAttribute {

    public meshPerAttribute: number;

    /**
     * 
     * @param array
     * @param itemSize
     * @param normalized
     * @param meshPerAttribute
     */
    constructor(array: BufferArray, itemSize: number, normalized: boolean = false, meshPerAttribute: number = 1) {
        super(array, itemSize, normalized);

        this.meshPerAttribute = meshPerAttribute;
    }

    /**
     * 
     */
    public clone(): InstancedBufferAttribute {
        return new InstancedBufferAttribute(this.array, this.itemSize, this.normalized, this.meshPerAttribute).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InstancedBufferAttribute): InstancedBufferAttribute {
        super.copy(source);
        return this;
    }
}
