import { Curve } from "./Curve";
import { Vector3 } from "../Vector3";
import { Nullable } from "../../types";
import { BezierCurveFunc } from "./BezierCurveFunc";
import { BinaryBuffer } from "../../Misc";

/**
 * 2D
 * @category Curve
 */
export class QuadraticBezierCurve3 extends Curve<Vector3> {

    public v0: Vector3 = new Vector3();
    public v1: Vector3 = new Vector3();
    public v2: Vector3 = new Vector3();

    /**
     * 
     * @param v0
     * @param v1
     * @param v2
     */
    constructor(v0: Nullable<Vector3> = null, v1: Nullable<Vector3> = null, v2: Nullable<Vector3> = null) {
        super();

        this.type = 'QuadraticBezierCurve3';

        if (v0) { this.v0.copy(v0); }
        if (v1) { this.v1.copy(v1); }
        if (v2) { this.v2.copy(v2); }
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector3> = null): Vector3 {
        const point = optionalTarget ?? new Vector3();

        const v0 = this.v0;
        const v1 = this.v1;
        const v2 = this.v2;

        point.set(
            BezierCurveFunc.QuadraticBezier(t, v0.x, v1.x, v2.x),
            BezierCurveFunc.QuadraticBezier(t, v0.y, v1.y, v2.y),
            BezierCurveFunc.QuadraticBezier(t, v0.z, v1.z, v2.z)
        );

        return point;
    }

    /**
     * 
     */
    public clone(): QuadraticBezierCurve3 {
        return new QuadraticBezierCurve3().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: QuadraticBezierCurve3): QuadraticBezierCurve3 {
        super.copy(source);

        this.v0.copy(source.v0);
        this.v1.copy(source.v1);
        this.v2.copy(source.v2);

        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();

        data.v0 = this.v0.toArray();
        data.v1 = this.v1.toArray();
        data.v2 = this.v2.toArray();

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): QuadraticBezierCurve3 {
        super.fromJSON(json);

        this.v0.fromArray(json.v0);
        this.v1.fromArray(json.v1);
        this.v2.fromArray(json.v2);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): QuadraticBezierCurve3 {
        super.toDataBuffer(dataBuffer);

        this.v0.toDataBuffer(dataBuffer);
        this.v1.toDataBuffer(dataBuffer);
        this.v2.toDataBuffer(dataBuffer);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): QuadraticBezierCurve3 {
        super.fromDataBuffer(dataBuffer);

        this.v0.fromDataBuffer(dataBuffer);
        this.v1.fromDataBuffer(dataBuffer);
        this.v2.fromDataBuffer(dataBuffer);

        return this;
    }
}
