import { MathUtils } from "../Math/MathUtils";
import { Nullable } from "../types";
import { EventDispatcher, Event } from "../Events";
import { Scene } from "./Scene";
import { VisibleLayers } from "../Misc/VisibleLayers";

/**
 * @ignore
 */
let objectPoolId = 0;

/**
 * 3D
 */
export abstract class AbstractObject extends EventDispatcher {

    /**
     * @ignore
     */
    [key: string]: any;

    /**
     * id
     */
    public id: number;
    /**
     * uuid
     */
    public uuid: string;
    /**
     * id
     */
    public ancestorId: string;
    /**
     * 
     */
    public name: string;

    /**
     * @ignore
     */
    protected _scene: Scene;

    /**
     * @ignore
     */
    protected _parent: Nullable<AbstractObject> = null;

    /**
     * @ignore
     */
    protected _children: AbstractObject[] = [];

    /**
     * @ignore
     */
    protected _visible: boolean = true;

    /**
     * @ignore
     */
    protected _visibleLayers: VisibleLayers = new VisibleLayers();

    /**
     * @ignore
     */
    private _sceneRootNodesIndex: number = -1;

    /**
     * 
     * @protected
     */
    protected constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '') {
        super();

        this.id = objectPoolId++;
        this.name = name;
        this.uuid = MathUtils.generateUUID();

        this._scene = scene;
        this._parent = parent;
        this._parent?.add(this);
    }

    /**
     * 
     */
    get isInScene(): boolean {
        return this._scene != null;
    }

    /**
     * 
     */
    get scene(): Scene {
        return this._scene;
    }

    /**
     * 
     */
    get children(): AbstractObject[] {
        return this._children;
    }

    /**
     * 
     */
    get parent(): Nullable<AbstractObject> {
        return this._parent;
    }

    /**
     * 
     */
    get visibleLayers() {
        return this._visibleLayers;
    }

    /**
     * 
     */
    get visible() {
        return this._visible;
    }
    set visible(value) {
        if (this._visible != value) {
            const tempVisible = this._visible;
            this._visible = value;
            this.onVisibleChanged(tempVisible, this._visible);
        }
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        this.dispatchEvent(new Event('dispose'));
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onVisibleChanged(oldValue: boolean, newValue: boolean): void {

    }

    /**
     * 
     * @param object
     */
    public add(object: AbstractObject): AbstractObject {

        this._children.push(object);

        object.onAddSelf();

        return this;
    }

    /**
     * 
     * @param object
     */
    public remove(object: AbstractObject): AbstractObject {

        const index = this._children.indexOf(object);

        if (index != -1) {

            object.onRemoveSelf();

            object._parent = null;

            this._children.splice(index, 1);
        }

        return this;
    }

    /**
     * 
     */
    public removeFromParent() {
        if (this._parent) {
            this._parent.remove(this);
        }
    }

    /**
     * 
     * @ignore
     */
    protected onAddSelf(): void {
    }

    /**
     * 
     * @ignore
     */
    protected onRemoveSelf(): void {
    }

    /**
     * 
     * @ignore
     */
    public onAddScene(): void {
    }

    /**
     * 
     * @ignore
     */
    public onRemoveScene(): void {
    }

    /**
     * Id
     * @param id
     */
    public getObjectById(id: number): Nullable<AbstractObject> {
        return this.getObjectByProperty('id', id);
    }

    /**
     * uuid
     * @param uuid
     */
    public getObjectByUUID(uuid: string): Nullable<AbstractObject> {
        return this.getObjectByProperty('uuid', uuid);
    }

    /**
     * 
     * @param name
     */
    public getObjectByName(name: string): Nullable<AbstractObject> {
        return this.getObjectByProperty('name', name);
    }

    /**
     * 
     * @param name
     * @param value
     */
    public getObjectByProperty<T>(name: string, value: T): Nullable<AbstractObject> {
        if (this[name] == value) { return this; }

        for (let i = 0, l = this.children.length; i < l; i++) {
            let child = this.children[i];
            let object = child.getObjectByProperty<T>(name, value);
            if (object) {
                return object;
            }
        }
        return null;
    }

    /**
     * 
     * @param func
     */
    public traverse(func: (obj: AbstractObject) => void): AbstractObject {

        func(this);

        const children = this.children;
        for (let i = 0, l = children.length; i < l; i++) {
            children[i].traverse(func);
        }

        return this;
    }

    /**
     * 
     * @param func
     */
    public traverseVisible(func: (obj: AbstractObject) => void): AbstractObject {

        if (!this._visible) {
            return this;
        }

        func(this);

        const children = this.children;
        for (let i = 0, l = children.length; i < l; i++) {
            children[i].traverseVisible(func);
        }

        return this;
    }

    /**
     * 
     * @param func
     */
    public traverseAncestors(func: (obj: AbstractObject) => void): AbstractObject {

        const parent = this._parent;
        if (parent != null) {
            func(parent);

            parent.traverseAncestors(func);
        }

        return this;
    }

    /**
     * @ignore
     */
    public _addToSceneRootNodes(): void {
        if (this._sceneRootNodesIndex == -1) {
            this._sceneRootNodesIndex = this._scene.rootNodes.length;
            this._scene.rootNodes.push(this);
        }
    }

    /**
     * @ignore
     */
    public _removeFromSceneRootNodes(): void {
        if (this._sceneRootNodesIndex != -1) {
            const rootNodes = this._scene.rootNodes;
            const lastIdx = rootNodes.length - 1;
            rootNodes[this._sceneRootNodesIndex] = rootNodes[lastIdx];
            rootNodes[this._sceneRootNodesIndex]._sceneRootNodesIndex = this._sceneRootNodesIndex;
            this._scene.rootNodes.pop();
            this._sceneRootNodesIndex = -1;
        }
    }

    /**
     * 
     */
    public abstract clone(): AbstractObject;

    /**
     * 
     * @param source
     */
    public copy(source: AbstractObject): AbstractObject {
        return this;
    }
}
