import { DeepImmutable, FloatArray, int, Nullable } from "../types";
import { Matrix4 } from "./Matrix4";
import { Quaternion } from "./Quaternion";
import { Matrix3 } from "./Matrix3";
import { MathUtils } from "./MathUtils";
import { BinaryBuffer, BufferAttribute } from "../Misc";

type InVector4 = Vector4 | DeepImmutable<Vector4>;

/**
 * 4D
 * @category Math
 */
export class Vector4 {

    /**
     * @ignore
     */
    private static _zeroReadOnly = new Vector4(0, 0, 0, 0) as DeepImmutable<Vector4>;
    /**
     * @ignore
     */
    private static _oneReadOnly = new Vector4(1, 1, 1, 1) as DeepImmutable<Vector4>;
    /**
     * @ignore
     */
    private static _oneInvReadOnly = new Vector4(-1, -1, -1, -1) as DeepImmutable<Vector4>;

    /**
     * @ignore
     */
    private readonly _p: number[];
    /**
     * @ignore
     */
    private get _x() {return this._p[0]; }
    /**
     * @ignore
     */
    private set _x(value) {this._p[0] = value; }
    /**
     * @ignore
     */
    private get _y() {return this._p[1]; }
    /**
     * @ignore
     */
    private set _y(value) {this._p[1] = value; }
    /**
     * @ignore
     */
    private get _z() {return this._p[2]; }
    /**
     * @ignore
     */
    private set _z(value) {this._p[2] = value; }
    /**
     * @ignore
     */
    private get _w() {return this._p[3]; }
    /**
     * @ignore
     */
    private set _w(value) {this._p[3] = value; }
    /**
     * @ignore
     */
    private _notify: boolean;
    /**
     * @ignore
     */
    private _onNotify: any;

    constructor(x: number = 0, y: number = 0, z: number = 0, w: number = 0) {
        this._p = [];
        this._p[0] = x;
        this._p[1] = y;
        this._p[2] = z;
        this._p[3] = w;
        this._notify = true;
        this._onNotify = null;
    }

    /**
     * 
     */
    public static Build() {
        return new Vector4();
    }

    /**
     * ZERO
     */
    public static get ZERO(): DeepImmutable<Vector4> {
        return Vector4._zeroReadOnly;
    }

    /**
     * ONE
     */
    public static get ONE(): DeepImmutable<Vector4> {
        return Vector4._oneReadOnly;
    }

    /**
     * INV_ONE
     */
    public static get INV_ONE(): DeepImmutable<Vector4> {
        return Vector4._oneInvReadOnly;
    }

    /**
     * 
     */
    public get p() {
        return this._p;
    }

    /**
     * X
     */
    public get x() {
        return this._x;
    }
    public set x(value: number) {
        if (this._x != value) {
            this._x = value;
            this.triggerNotify();
        }
    }

    /**
     * Y
     */
    public get y() {
        return this._y;
    }
    public set y(value: number) {
        if (this._y != value) {
            this._y = value;
            this.triggerNotify();
        }
    }

    /**
     * Z
     */
    public get z() {
        return this._z;
    }
    public set z(value: number) {
        if (this._z != value) {
            this._z = value;
            this.triggerNotify();
        }
    }

    /**
     * W
     */
    public get w() {
        return this._w;
    }
    public set w(value: number) {
        if (this._w != value) {
            this._w = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get notify() {
        return this._notify;
    }
    public set notify(value: boolean) {
        this._notify = value;
    }

    /**
     * 
     */
    public setNotifyFunc(value: any): Vector4 {
        this._onNotify = value;
        return this;
    }

    /**
     * 
     */
    private triggerNotify(): void {
        if (this._notify) {
            if (this._onNotify) {
                this._onNotify();
            }
        }
    }

    /**
     * XYZW
     * @param x
     * @param y
     * @param z
     * @param w
     */
    public set(x: number, y: number, z: number, w: number): Vector4 {
        this._x = x;
        this._y = y;
        this._z = z;
        this._w = w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public setScalar(scalar: number): Vector4 {
        this._x = scalar;
        this._y = scalar;
        this._z = scalar;
        this._w = scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * X
     * @param x
     */
    public setX(x: number): Vector4 {
        this._x = x;
        this.triggerNotify();
        return this;
    }

    /**
     * Y
     * @param y
     */
    public setY(y: number): Vector4 {
        this._y = y;
        this.triggerNotify();
        return this;
    }

    /**
     * Z
     * @param z
     */
    public setZ(z: number): Vector4 {
        this._z = z;
        this.triggerNotify();
        return this;
    }

    /**
     * W
     * @param w
     */
    public setW(w: number): Vector4 {
        this._w = w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): Vector4 {
        switch (index) {
            case 0: this.x = value; break;
            case 1: this.y = value; break;
            case 2: this.z = value; break;
            case 3: this.w = value; break;
            default: throw new Error(`index is out of range: ${index}`);
        }

        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this._x;
            case 1: return this._y;
            case 2: return this._z;
            case 3: return this._w;
            default: throw new Error(`index is out of range: ${index}`);
        }
    }

    /**
     * 
     */
    public clone(): Vector4 {
        return new Vector4(this._x, this._y, this._z, this._w);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InVector4): Vector4 {
        this._x = source.x;
        this._y = source.y;
        this._z = source.z;
        this._w = source.w;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector4
     * @param v
     */
    public add(v: InVector4): Vector4 {
        this._x += v.x;
        this._y += v.y;
        this._z += v.z;
        this._w += v.w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public addScalar(scalar: number): Vector4 {
        this._x += scalar;
        this._y += scalar;
        this._z += scalar;
        this._w += scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector4
     * @param aVec
     * @param bVec
     */
    public addVectors(aVec: InVector4, bVec: InVector4): Vector4 {
        this._x = aVec.x + bVec.x;
        this._y = aVec.y + bVec.y;
        this._z = aVec.z + bVec.z;
        this._w = aVec.w + bVec.w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     * @param scalar
     */
    public addScaledVector(vec: InVector4, scalar: number): Vector4 {
        this._x += vec.x * scalar;
        this._y += vec.y * scalar;
        this._z += vec.z * scalar;
        this._w += vec.w * scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public sub(vec: InVector4): Vector4 {
        this._x -= vec.x;
        this._y -= vec.y;
        this._z -= vec.z;
        this._w -= vec.w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public subScalar(scalar: number): Vector4 {
        this._x -= scalar;
        this._y -= scalar;
        this._z -= scalar;
        this._w -= scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param aVec
     * @param bVec
     */
    public subVectors(aVec: Vector4, bVec: Vector4): Vector4 {
        this._x = aVec.x - bVec.x;
        this._y = aVec.y - bVec.y;
        this._z = aVec.z - bVec.z;
        this._w = aVec.w - bVec.w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public multiplyScalar(scalar: number): Vector4 {
        this._x *= scalar;
        this._y *= scalar;
        this._z *= scalar;
        this._w *= scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Matrix4
     * @param matrix4
     */
    public applyMatrix4(matrix4: Matrix4): Vector4 {
        const x = this._x;
        const y = this._y;
        const z = this._z;
        const w = this._w;
        const e = matrix4.elements;
        this._x = e[0] * x + e[4] * y + e[8] * z + e[12] * w;
        this._y = e[1] * x + e[5] * y + e[9] * z + e[13] * w;
        this._z = e[2] * x + e[6] * y + e[10] * z + e[14] * w;
        this._w = e[3] * x + e[7] * y + e[11] * z + e[15] * w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public divideScalar(scalar: number): Vector4 {
        return this.multiplyScalar(1 / scalar);
    }

    /**
     * 
     * @param quaternion
     */
    public setAxisAngleFromQuaternion(quaternion: Quaternion): Vector4 {

        this._w = 2 * Math.acos(quaternion.w);

        const s = Math.sqrt(1 - quaternion.w * quaternion.w);
        if (s < 0.0001) {
            this._x = 1;
            this._y = 0;
            this._z = 0;
        } else {
            this._x = quaternion.x / s;
            this._y = quaternion.y / s;
            this._z = quaternion.z / s;
        }

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param matrix
     */
    public setAxisAngleFromRotationMatrix(matrix: Matrix3 | Matrix4): Vector4 {

        let angle: number;
        let x: number;
        let y: number;
        let z: number;

        const epsilon: number = 0.01;		// margin to allow for rounding errors
        const epsilon2: number = 0.1;		// margin to distinguish between 0 and 180 degrees

        const isMatrix4: boolean = matrix instanceof Matrix4;
        const te = matrix.elements;

        const m11: number = isMatrix4 ? te[0] : te[0]; const m12: number = isMatrix4 ? te[4] : te[3]; const m13: number = isMatrix4 ? te[8] : te[6];
        const m21: number = isMatrix4 ? te[1] : te[1]; const m22: number = isMatrix4 ? te[5] : te[4]; const m23: number = isMatrix4 ? te[9] : te[7];
        const m31: number = isMatrix4 ? te[2] : te[2]; const m32: number = isMatrix4 ? te[6] : te[5]; const m33: number = isMatrix4 ? te[10] : te[8];

        if ((Math.abs(m12 - m21) < epsilon) &&
            (Math.abs(m13 - m31) < epsilon) &&
            (Math.abs(m23 - m32) < epsilon)) {
            // singularity found
            // first check for identity matrix which must have +1 for all terms
            // in leading diagonal and zero in other terms

            if ((Math.abs(m12 + m21) < epsilon2) &&
                (Math.abs(m13 + m31) < epsilon2) &&
                (Math.abs(m23 + m32) < epsilon2) &&
                (Math.abs(m11 + m22 + m33 - 3) < epsilon2)) {
                // this singularity is identity matrix so angle = 0

                this.set(1, 0, 0, 0);

                return this; // zero angle, arbitrary axis
            }

            // otherwise this singularity is angle = 180

            angle = Math.PI;

            const xx = (m11 + 1) / 2;
            const yy = (m22 + 1) / 2;
            const zz = (m33 + 1) / 2;
            const xy = (m12 + m21) / 4;
            const xz = (m13 + m31) / 4;
            const yz = (m23 + m32) / 4;

            if ((xx > yy) && (xx > zz)) {
                // m11 is the largest diagonal term

                if (xx < epsilon) {
                    x = 0;
                    y = 0.707106781;
                    z = 0.707106781;
                } else {
                    x = Math.sqrt(xx);
                    y = xy / x;
                    z = xz / x;
                }
            } else if (yy > zz) {
                // m22 is the largest diagonal term

                if (yy < epsilon) {
                    x = 0.707106781;
                    y = 0;
                    z = 0.707106781;
                } else {
                    y = Math.sqrt(yy);
                    x = xy / y;
                    z = yz / y;
                }
            } else {
                // m33 is the largest diagonal term so base result on this

                if (zz < epsilon) {
                    x = 0.707106781;
                    y = 0.707106781;
                    z = 0;
                } else {
                    z = Math.sqrt(zz);
                    x = xz / z;
                    y = yz / z;
                }
            }

            this.set(x, y, z, angle);

            return this; // return 180 deg rotation
        }

        // as we have reached here there are no singularities so we can handle normally

        let s: number = Math.sqrt((m32 - m23) * (m32 - m23) + (m13 - m31) * (m13 - m31) + (m21 - m12) * (m21 - m12)); // used to normalize

        if (Math.abs(s) < 0.001) { s = 1; }

        // prevent divide by zero, should not happen if matrix is orthogonal and should be
        // caught by singularity test above, but I've left it in just in case

        this._x = (m32 - m23) / s;
        this._y = (m13 - m31) / s;
        this._z = (m21 - m12) / s;
        this._w = Math.acos((m11 + m22 + m33 - 1) / 2);
        this.triggerNotify();

        return this;
    }

    /**
     * Vector4
     * @param vec
     */
    public min(vec: InVector4): Vector4 {
        this._x = Math.min(this._x, vec.x);
        this._y = Math.min(this._y, vec.y);
        this._z = Math.min(this._z, vec.z);
        this._w = Math.min(this._w, vec.w);
        this.triggerNotify();
        return this;
    }

    /**
     * Vector4
     * @param vec
     */
    public max(vec: InVector4): Vector4 {
        this._x = Math.max(this._x, vec.x);
        this._y = Math.max(this._y, vec.y);
        this._z = Math.max(this._z, vec.z);
        this._w = Math.max(this._w, vec.w);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param minVec
     * @param maxVec
     */
    public clamp(minVec: InVector4, maxVec: InVector4): Vector4 {
        this._x = Math.max(minVec.x, Math.min(maxVec.x, this._x));
        this._y = Math.max(minVec.y, Math.min(maxVec.y, this._y));
        this._z = Math.max(minVec.z, Math.min(maxVec.z, this._z));
        this._w = Math.max(minVec.w, Math.min(maxVec.w, this._w));
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param minScalar
     * @param maxScalar
     */
    public clampScalar(minScalar: number, maxScalar: number): Vector4 {
        this._x = Math.max(minScalar, Math.min(maxScalar, this._x));
        this._y = Math.max(minScalar, Math.min(maxScalar, this._y));
        this._z = Math.max(minScalar, Math.min(maxScalar, this._z));
        this._w = Math.max(minScalar, Math.min(maxScalar, this._w));
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param minScalar
     * @param maxScalar
     */
    public clampLength(minScalar: number, maxScalar: number): Vector4 {
        const length: number = this.length();
        return this.divideScalar(length || 1).multiplyScalar(Math.max(minScalar, Math.min(maxScalar, length)));
    }

    /**
     * 
     */
    public floor() {
        this._x = Math.floor(this._x);
        this._y = Math.floor(this._y);
        this._z = Math.floor(this._z);
        this._w = Math.floor(this._w);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public ceil(): Vector4 {
        this._x = Math.ceil(this._x);
        this._y = Math.ceil(this._y);
        this._z = Math.ceil(this._z);
        this._w = Math.ceil(this._w);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public round(): Vector4 {
        this._x = Math.round(this._x);
        this._y = Math.round(this._y);
        this._z = Math.round(this._z);
        this._w = Math.round(this._w);
        this.triggerNotify();
        return this;
    }

    /**
     * 0
     */
    public roundToZero(): Vector4 {
        this._x = (this._x < 0) ? Math.ceil(this._x) : Math.floor(this._x);
        this._y = (this._y < 0) ? Math.ceil(this._y) : Math.floor(this._y);
        this._z = (this._z < 0) ? Math.ceil(this._z) : Math.floor(this._z);
        this._w = (this._w < 0) ? Math.ceil(this._w) : Math.floor(this._w);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public negate(): Vector4 {
        this._x = -this._x;
        this._y = -this._y;
        this._z = -this._z;
        this._w = -this._w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public dot(vec: InVector4): number {
        return this._x * vec.x + this._y * vec.y + this._z * vec.z + this._w * vec.w;
    }

    /**
     * SQ
     */
    public lengthSq(): number {
        return this._x * this._x + this._y * this._y + this._z * this._z + this._w * this._w;
    }

    /**
     * 
     */
    public length(): number {
        return Math.sqrt(this._x * this._x + this._y * this._y + this._z * this._z + this._w * this._w);
    }

    /**
     * 
     */
    public manhattanLength(): number {
        return Math.abs(this._x) + Math.abs(this._y) + Math.abs(this._z) + Math.abs(this._w);
    }

    /**
     * Vector4
     * @param vec
     */
    public manhattanDistanceTo(vec: InVector4): number {
        return Math.abs(this._x - vec.x) + Math.abs(this._y - vec.y) + Math.abs(this._z - vec.z) + Math.abs(this._w - vec.w);
    }

    /**
     * 
     */
    public normalize(): Vector4 {
        return this.divideScalar(this.length() || 1);
    }

    /**
     * 
     * @param length
     */
    public setLength(length: number): Vector4 {
        return this.normalize().multiplyScalar(length);
    }

    /**
     * V
     * @param vec
     * @param alpha
     */
    public lerp(vec: InVector4, alpha: number): Vector4 {
        this._x += (vec.x - this._x) * alpha;
        this._y += (vec.y - this._y) * alpha;
        this._z += (vec.z - this._z) * alpha;
        this._w += (vec.w - this._w) * alpha;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec1
     * @param vec2
     * @param alpha
     */
    public lerpVectors(vec1: InVector4, vec2: InVector4, alpha: number): Vector4 {
        this._x = vec1.x + (vec2.x - vec1.x) * alpha;
        this._y = vec1.y + (vec2.y - vec1.y) * alpha;
        this._z = vec1.z + (vec2.z - vec1.z) * alpha;
        this._w = vec1.w + (vec2.w - vec1.w) * alpha;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public random(): Vector4 {
        this._x = Math.random();
        this._y = Math.random();
        this._z = Math.random();
        this._w = Math.random();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     * @returns {boolean}
     */
    public equals(vec: InVector4): boolean {
        return ((vec.x == this._x) && (vec.y == this._y) && (vec.z == this._z) && (vec.w == this._w));
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: InVector4, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        return MathUtils.equalsEpsilon(this._x, right.x, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._y, right.y, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._z, right.z, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._w, right.w, relativeEpsilon, absoluteEpsilon);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): Vector4 {
        this._x = array[offset];
        this._y = array[offset + 1];
        this._z = array[offset + 2];
        this._w = array[offset + 3];
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {
        array[offset] = this._x;
        array[offset + 1] = this._y;
        array[offset + 2] = this._z;
        array[offset + 3] = this._w;
        return array;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Vector4 {
        this._x = dataBuffer.readFloat32();
        this._y = dataBuffer.readFloat32();
        this._z = dataBuffer.readFloat32();
        this._w = dataBuffer.readFloat32();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Vector4 {
        dataBuffer.writeFloat32(this._x);
        dataBuffer.writeFloat32(this._y);
        dataBuffer.writeFloat32(this._z);
        dataBuffer.writeFloat32(this._w);
        return this;
    }

    /**
     * Buffer
     * @param attribute
     * @param index
     */
    public fromBufferAttribute(attribute: BufferAttribute, index: int): Vector4 {
        this._x = attribute.getX(index);
        this._y = attribute.getY(index);
        this._z = attribute.getZ(index);
        this._w = attribute.getW(index);
        return this;
    }
}
