import { Curve } from "./Curve";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { BezierCurveFunc } from "./BezierCurveFunc";
import { BinaryBuffer } from "../../Misc";

/**
 * 
 * @category Curve
 */
export class SplineCurve extends Curve<Vector2> {

    public points: Vector2[];

    /**
     * 
     * @param points
     */
    constructor(points: Nullable<Vector2[]> = null) {
        super();

        this.type = 'SplineCurve';
        this.points = points ?? [];
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector2> = null): Vector2 {

        let point = optionalTarget || new Vector2();

        let points = this.points;
        let p = (points.length - 1) * t;

        let intPoint = Math.floor(p);
        let weight = p - intPoint;

        let p0 = points[intPoint == 0 ? intPoint : intPoint - 1];
        let p1 = points[intPoint];
        let p2 = points[intPoint > points.length - 2 ? points.length - 1 : intPoint + 1];
        let p3 = points[intPoint > points.length - 3 ? points.length - 1 : intPoint + 2];

        point.set(
            BezierCurveFunc.CatmullRom(weight, p0.x, p1.x, p2.x, p3.x),
            BezierCurveFunc.CatmullRom(weight, p0.y, p1.y, p2.y, p3.y),
        );

        return point;
    }

    /**
     * 
     */
    public clone(): SplineCurve {
        return new SplineCurve(this.points);
    }

    /**
     * 
     * @param source
     */
    public copy(source: SplineCurve): SplineCurve {
        super.copy(source);

        this.points = [];
        for (let i = 0, l = source.points.length; i < l; i++) {
            let point = source.points[i];
            this.points.push(point.clone());
        }
        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();
        data.points = [];

        for (let i = 0, l = this.points.length; i < l; i++) {
            let point = this.points[i];
            data.points.push(point.toArray());
        }
        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): SplineCurve {
        super.fromJSON(json);

        this.points = [];

        for (let i = 0, l = json.points.length; i < l; i++) {
            let point = json.points[i];
            this.points.push(new Vector2().fromArray(point));
        }
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): SplineCurve {
        super.toDataBuffer(dataBuffer);

        dataBuffer.writeUint16(this.points.length);
        this.points.forEach((point) => point.toDataBuffer(dataBuffer));

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): SplineCurve {
        super.fromDataBuffer(dataBuffer);

        const count = dataBuffer.readUint16();
        for (let i = 0; i < count; ++i) {
            this.points.push(new Vector2().fromDataBuffer(dataBuffer));
        }
        return this;
    }
}
