import { ColorRGB } from "./ColorRGB";
import { DeepImmutable, FloatArray, int, Nullable } from "../types";
import { MathUtils } from "./MathUtils";
import { BinaryBuffer, BufferAttribute } from "../Misc";

type InColorRGBA = ColorRGBA | ColorRGB | DeepImmutable<ColorRGB>;

/**
 * RGBA
 * @category Math
 */
export class ColorRGBA extends ColorRGB{

    /**
     * 
     */
    public a: number = 1.0;

    /**
     * 
     * @param r
     * @param g
     * @param b
     * @param a
     */
    constructor(r: null | number | string | InColorRGBA = null, g: Nullable<number> = null, b: Nullable<number> = null, a: Nullable<number> = null) {
        super(r, g, b);

        if (a) {
            this.a = a;
        }
    }

    /**
     * 
     */
    public static Build() {
        return new ColorRGBA();
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): ColorRGBA {
        switch (index) {
            case 0: this.r = value; break;
            case 1: this.g = value; break;
            case 2: this.b = value; break;
            case 3: this.a = value; break;
            default: throw new Error(`index is out of range: ${index}`);
        }
        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this.r;
            case 1: return this.g;
            case 2: return this.b;
            case 3: return this.a;
            default: throw new Error(`index is out of range: ${index}`);
        }
    }

    /**
     * 
     */
    public maximumComponent(): number {
        return Math.max(Math.max(Math.max(this.r, this.g), this.b), this.a);
    }

    /**
     * 
     */
    public minimumComponent(): number {
        return Math.min(Math.min(Math.min(this.r, this.g), this.b), this.a);
    }

    /**
     * 
     */
    public clone(): ColorRGBA {
        return new ColorRGBA(this.r, this.g, this.b, this.a);
    }

    /**
     * 
     * @param color
     */
    public copy(color: InColorRGBA): ColorRGBA {
        this.r = color.r;
        this.g = color.g;
        this.b = color.b;

        if (color instanceof ColorRGBA) {
            this.a = color.a;
        }
        return this;
    }

    /**
     * RGBA
     * @param r
     * @param g
     * @param b
     * @param a
     */
    public setRGBA(r: number, g: number, b: number, a: number): ColorRGBA {
        this.r = r;
        this.g = g;
        this.b = b;
        this.a = a;
        return this;
    }

    /**
     * CSS
     * @param style
     */
    public setStyle(style: string): ColorRGBA {

        let m;

        if ((m = /^((?:rgb|hsl)a?)\(\s*([^\)]*)\)/.exec(style))) {
            // rgb / hsl

            let color;
            let name = m[1];
            let components = m[2];

            switch (name) {
                case 'rgba':

                    if ((color = /^(\d+)\s*,\s*(\d+)\s*,\s*(\d+)\s*(,\s*([0-9]*\.?[0-9]+)\s*)?$/.exec(components))) {

                        this.r = Math.min(255, parseInt(color[1], 10)) / 255;
                        this.g = Math.min(255, parseInt(color[2], 10)) / 255;
                        this.b = Math.min(255, parseInt(color[3], 10)) / 255;
                        this.a = Math.min(1.0, parseFloat(color[4]));

                        return this;
                    }

                    if ((color = /^(\d+)\%\s*,\s*(\d+)\%\s*,\s*(\d+)\%\s*(,\s*([0-9]*\.?[0-9]+)\s*)?$/.exec(components))) {

                        this.r = Math.min(100, parseInt(color[1], 10)) / 100;
                        this.g = Math.min(100, parseInt(color[2], 10)) / 100;
                        this.b = Math.min(100, parseInt(color[3], 10)) / 100;
                        this.a = Math.min(1.0, parseFloat(color[4]));

                        return this;
                    }

                    break;

                case 'hsla':

                    if ((color = /^([0-9]*\.?[0-9]+)\s*,\s*(\d+)\%\s*,\s*(\d+)\%\s*(,\s*([0-9]*\.?[0-9]+)\s*)?$/.exec(components))) {

                        const h = parseFloat(color[1]) / 360;
                        const s = parseInt(color[2], 10) / 100;
                        const l = parseInt(color[3], 10) / 100;

                        this.a = Math.min(1.0, parseFloat(color[4]));

                        this.setHSL(h, s, l);

                        return this;
                    }

                    break;
            }
        }

        super.setStyle(style);

        return this;
    }

    /**
     * CSS
     */
    public getStyle(): string {
        return `rgba(${(this.r * 255) | 0},${(this.g * 255) | 0},${(this.b * 255) | 0},${this.a})`;
    }

    /**
     * 
     * @param color
     * @param alpha
     */
    public lerp(color: ColorRGBA, alpha: number): ColorRGBA {
        this.r += (color.r - this.r) * alpha;
        this.g += (color.g - this.g) * alpha;
        this.b += (color.b - this.b) * alpha;
        this.a += (color.a - this.a) * alpha;
        return this;
    }

    /**
     * 
     * @param color
     */
    public equals(color: InColorRGBA): boolean {
        if (color instanceof ColorRGBA) {
            return (color.r == this.r) && (color.g == this.g) && (color.b == this.b) && (color.a == this.a);
        }
        return super.equals(color);
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: InColorRGBA, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        if (right instanceof ColorRGBA) {
            return MathUtils.equalsEpsilon(this.r, right.r, relativeEpsilon, absoluteEpsilon) &&
                MathUtils.equalsEpsilon(this.g, right.g, relativeEpsilon, absoluteEpsilon) &&
                MathUtils.equalsEpsilon(this.b, right.b, relativeEpsilon, absoluteEpsilon) &&
                MathUtils.equalsEpsilon(this.a, right.a, relativeEpsilon, absoluteEpsilon);
        }
        return super.equalsEpsilon(right, relativeEpsilon, absoluteEpsilon);
    }

    /**
     * Buffer
     * @param attribute
     * @param index
     */
    public fromBufferAttribute(attribute: BufferAttribute, index: int): ColorRGB {

        this.r = attribute.getX(index);
        this.g = attribute.getY(index);
        this.b = attribute.getZ(index);
        this.a = attribute.getW(index);

        if (attribute.normalized) {
            this.r /= 255;
            this.g /= 255;
            this.b /= 255;
            this.a /= 255;
        }
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {

        array[offset] = this.r;
        array[offset + 1] = this.g;
        array[offset + 2] = this.b;
        array[offset + 3] = this.a;

        return array;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): ColorRGBA {

        this.r = array[offset];
        this.g = array[offset + 1];
        this.b = array[offset + 2];
        this.a = array[offset + 3];

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): ColorRGBA {
        this.r = dataBuffer.readFloat32();
        this.g = dataBuffer.readFloat32();
        this.b = dataBuffer.readFloat32();
        this.a = dataBuffer.readFloat32();

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): ColorRGBA {
        dataBuffer.writeFloat32(this.r);
        dataBuffer.writeFloat32(this.g);
        dataBuffer.writeFloat32(this.b);
        dataBuffer.writeFloat32(this.a);

        return this;
    }
}
