import { InterleavedBuffer } from "./InterleavedBuffer";
import { int } from "../../types";
import { Matrix3, Matrix4, Vector2, Vector3 } from "../../Math";
import { InterleavedBufferClone } from "./InterleavedBufferClone";
import { IDisposable } from "../../Misc/IDisposable";

/**
 * @ignore
 */
const _vector3 = new Vector3();
/**
 * @ignore
 */
const _vector2 = new Vector2();

/**
 * Buffer
 */
export class InterleavedBufferAttribute implements IDisposable {

    public buffer: InterleavedBuffer;
    public itemSize: int;
    public offset: int;
    public normalized: boolean;

    /**
     * 
     * @param interleavedBuffer
     * @param itemSize
     * @param offset
     * @param normalized
     */
    constructor(interleavedBuffer: InterleavedBuffer, itemSize: int, offset: int, normalized: boolean = false) {

        this.buffer = interleavedBuffer;
        this.itemSize = itemSize;
        this.offset = offset;
        this.normalized = normalized;
    }

    public get glBuffer() {
        return this.buffer.glBuffer;
    }
    public set glBuffer(value) {
        this.buffer.glBuffer = value;
    }

    public get target() {
        return this.buffer.target;
    }

    public get usage() {
        return this.buffer.usage;
    }

    public get updateRange() {
        return this.buffer.updateRange;
    }

    /**
     * 
     */
    public get version() {
        return this.buffer.version;
    }

    /**
     * 
     */
    public get count() {
        return this.buffer.count;
    }

    /**
     * 
     */
    public get array() {
        return this.buffer.array;
    }

    /**
     * 
     * @param value
     */
    public set needsUpdate(value: boolean) {
        if (value) {
            this.buffer.needsUpdate = value;
        }
    }

    /**
     * 
     */
    public dispose() {
        this.buffer.dispose();
    }

    /**
     * 
     */
    public addReference() {
        this.buffer.addReference();
    }

    /**
     * 
     * @param cache
     */
    public clone(cache: InterleavedBufferClone): InterleavedBufferAttribute {

        let cloneBuffer = cache.interleavedBuffers[this.buffer.uuid];
        if (!cloneBuffer) {
            cloneBuffer = this.buffer.clone(cache);
            cache.interleavedBuffers[this.buffer.uuid] = cloneBuffer;
        }

        return new InterleavedBufferAttribute(cloneBuffer, this.itemSize, this.offset, this.normalized);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InterleavedBufferAttribute): InterleavedBufferAttribute {
        this.buffer = source.buffer;
        this.itemSize = source.itemSize;
        this.offset = source.offset;
        this.normalized = source.normalized;
        return this;
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public applyMatrix3(matrix3: Matrix3): InterleavedBufferAttribute {

        if (this.itemSize == 2) {

            for (let i = 0, l = this.count; i < l; i++) {
                _vector2.fromBufferAttribute(this, i);
                _vector2.applyMatrix3(matrix3);
                this.setXY(i, _vector2.x, _vector2.y);
            }

        } else if (this.itemSize == 3) {

            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                _vector3.applyMatrix3(matrix3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }

        }
        return this;
    }

    /**
     * Matrix4
     * @param matrix4
     */
    public applyMatrix4(matrix4: Matrix4): InterleavedBufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                _vector3.applyMatrix4(matrix4);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;
    }

    /**
     * 
     * @param matrix3
     */
    public applyNormalMatrix(matrix3: Matrix3): InterleavedBufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                _vector3.applyNormalMatrix(matrix3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;

    }

    /**
     * 
     * @param matrix4
     */
    public transformDirection(matrix4: Matrix4): InterleavedBufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                matrix4.transformVector3(_vector3, _vector3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;
    }

    /**
     * 
     * @param matrix4
     */
    public transformPoint(matrix4: Matrix4): InterleavedBufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                matrix4.transformPoint(_vector3, _vector3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;
    }

    /**
     * Value
     * @param offset
     */
    public get(offset: number): number {
        return this.array[offset];
    }

    /**
     * Value
     * @param value
     * @param offset
     */
    public set(value: number[], offset: int = 0): InterleavedBufferAttribute {
        this.array.set(value, offset);
        return this;
    }

    /**
     * itemSizeX
     * @param index
     */
    public getX(index: int): number {
        return this.array[index * this.itemSize];
    }

    /**
     * itemSizeX
     * @param index
     * @param x
     */
    public setX(index: int, x: number): InterleavedBufferAttribute {
        this.array[index * this.itemSize] = x;
        return this;
    }

    /**
     * itemSizeY
     * @param index
     */
    public getY(index: int): number {
        return this.array[index * this.itemSize + 1];
    }

    /**
     * itemSizeY
     * @param index
     * @param y
     */
    public setY(index: int, y: number): InterleavedBufferAttribute {
        this.array[index * this.itemSize + 1] = y;
        return this;
    }

    /**
     * itemSizeZ
     * @param index
     */
    public getZ(index: int): number {
        return this.array[index * this.itemSize + 2];
    }

    /**
     * itemSizeZ
     * @param index
     * @param z
     */
    public setZ(index: int, z: number): InterleavedBufferAttribute {
        this.array[index * this.itemSize + 2] = z;
        return this;
    }

    /**
     * itemSizeW
     * @param index
     */
    public getW(index: int): number {
        return this.array[index * this.itemSize + 3];
    }

    /**
     * itemSizeW
     * @param index
     * @param W
     */
    public setW(index: int, W: number): InterleavedBufferAttribute {
        this.array[index * this.itemSize + 3] = W;
        return this;
    }

    /**
     * itemSizeXY
     * @param index
     * @param x
     * @param y
     */
    public setXY(index: int, x: number, y: number): InterleavedBufferAttribute {
        index *= this.itemSize;
        this.array[index + 0] = x;
        this.array[index + 1] = y;
        return this;
    }

    /**
     * itemSizeXYZ
     * @param index
     * @param x
     * @param y
     * @param z
     */
    public setXYZ(index: int, x: number, y: number, z: number): InterleavedBufferAttribute {
        index *= this.itemSize;
        this.array[index + 0] = x;
        this.array[index + 1] = y;
        this.array[index + 2] = z;
        return this;
    }

    /**
     * itemSizeXYZW
     * @param index
     * @param x
     * @param y
     * @param z
     * @param w
     */
    public setXYZW(index: int, x: number, y: number, z: number, w: number): InterleavedBufferAttribute {
        index *= this.itemSize;
        this.array[index + 0] = x;
        this.array[index + 1] = y;
        this.array[index + 2] = z;
        this.array[index + 3] = w;
        return this;
    }
}
