import { Box3, MathUtils, Matrix4, Quaternion, Sphere, Vector2, Vector3 } from "../../Math";
import { BufferAttribute } from "./BufferAttribute";
import { BufferArray, FloatArray, int, Nullable } from "../../types";
import { GeometryGroup } from "./GeometryGroup";
import { GeometryDrawRange } from "./GeometryDrawRange";
import { InterleavedBufferAttribute } from "./InterleavedBufferAttribute";
import { Float32BufferAttribute, UInt16BufferAttribute, UInt32BufferAttribute } from "./TypeBufferAttribute";
import { BufferAttributeKind } from "./BufferAttributeKind";
import { DataTypeFormat } from "../../Misc";
import { InterleavedBufferClone } from "./InterleavedBufferClone";
import { EventDispatcher, Event } from "../../Events";
import { ArrayUtils } from "../../Misc/ArrayUtils";
import { Constants } from "../../Engines";

/**
 * @ignore
 */
const _tempMatrix = new Matrix4();
/**
 * @ignore
 */
const _tempVector1 = new Vector3();
/**
 * @ignore
 */
const _tempVector2 = new Vector3();
/**
 * @ignore
 */
const _tempBox1 = new Box3();
/**
 * @ignore
 */
const _tempBox2 = new Box3();
/**
 * @ignore
 */
let geometryPoolId = 0;

/**
 * 
 */
export class Geometry extends EventDispatcher {

    public id: number;
    public uuid: string;
    public name: string;

    public indexAttrbute: Nullable<BufferAttribute>;
    public attributes: {[key: string]: BufferAttribute | InterleavedBufferAttribute};

    public morphAttributes: {[key: string]: (BufferAttribute | InterleavedBufferAttribute)[] };
    public morphTargetsRelative: boolean;

    public boundingBox: Nullable<Box3>;
    public boundingSphere: Nullable<Sphere>;

    public groups: GeometryGroup[];
    public drawRange: GeometryDrawRange;
    public version: number;
    public reference: number;

    /**
     * 
     */
    constructor() {
        super();

        this.uuid = MathUtils.generateUUID();
        this.id = geometryPoolId += 2;
        this.name = '';

        this.indexAttrbute = null;
        this.attributes = {};

        this.morphAttributes = {};
        this.morphTargetsRelative = false;

        this.boundingBox = null;
        this.boundingSphere = null;

        this.groups = [];
        this.drawRange = {start: 0, count: Infinity};
        this.version = 0;
        this.reference = 0;
    }

    /**
     * 
     */
    public dispose() {
        this.reference--;
        if (this.reference == 0) {
            super.dispose();

            if (this.indexAttrbute) {
                this.indexAttrbute.dispose();
                this.indexAttrbute = null;
            }

            let attributes = this.attributes;
            for (let key in attributes) {
                if (attributes[key]) {
                    attributes[key].dispose();
                    delete attributes[key];
                }
            }

            const morphAttributes = this.morphAttributes;
            for (let key in morphAttributes) {
                if (morphAttributes[key]) {
                    let attributes = morphAttributes[key];
                    for (let index in attributes) {
                        attributes[index].dispose();
                        delete attributes[index];
                    }
                    delete morphAttributes[key];
                }
            }
        }
    }

    /**
     * 
     */
    public addReference() {
        this.reference++;
    }

    /**
     * 
     */
    public getIndexAttrbute(): Nullable<BufferAttribute> {
        return this.indexAttrbute;
    }

    /**
     * 
     * @param index
     */
    public setIndexAttrbute(index: BufferAttribute | number[]): Geometry {

        if (this.indexAttrbute != index) {
            if (this.indexAttrbute) {
                this.indexAttrbute.dispose();
                this.indexAttrbute = null;
            }

            if (Array.isArray(index)) {
                this.indexAttrbute = new (ArrayUtils.arrayMax(index) > 65535 ? UInt32BufferAttribute : UInt16BufferAttribute)(index, 1);
            } else {
                this.indexAttrbute = index;
            }

            this.indexAttrbute.target = Constants.ElementBufferTarget;
            this.indexAttrbute.addReference();
        }
        return this;
    }

    /**
     * 
     */
    public isEmptyAttribute(): boolean {
        return Object.keys(this.attributes).length <= 0;
    }

    /**
     * 
     * @param name
     */
    public haveAttribute(name: string): boolean {
        let attribute;
        if (name == BufferAttributeKind.IndicesKind) {
            attribute = this.indexAttrbute;
        }
        else {
            attribute = this.attributes[name];
        }
        return attribute != null;
    }

    /**
     * 
     * @param name
     */
    public getAttribute(name: string): Nullable<BufferAttribute | InterleavedBufferAttribute> {

        if (name == BufferAttributeKind.IndicesKind) {
            return this.indexAttrbute;
        }
        return this.attributes[name];
    }

    /**
     * 
     * @param name
     * @param attribute
     */
    public setAttribute(name: string, attribute: BufferAttribute | InterleavedBufferAttribute): Geometry {

        if (attribute instanceof BufferAttribute) {
            if (name == BufferAttributeKind.IndicesKind) {
                return this.setIndexAttrbute(attribute);
            }
        }

        if (this.attributes[name] != attribute) {

            if (this.attributes[name]) {
                this.attributes[name].dispose();
            }

            this.attributes[name] = attribute;

            if (this.attributes[name]) {
                this.attributes[name].addReference();
            }
        }
        return this;
    }

    /**
     * 
     * @param name
     */
    public removeAttribute(name: string): Geometry {
        if (this.attributes[name]) {
            this.attributes[name].dispose();
            delete this.attributes[name];
        }
        return this;
    }

    /**
     * 
     */
    public forceUpdateAttributes(): Geometry {
        if (this.indexAttrbute) {
            this.indexAttrbute.needsUpdate = true;
        }

        const attributes = this.attributes;
        for (let key in attributes) {
            if (this.attributes[key]) {
                this.attributes[key].needsUpdate = true;
            }
        }
        return this;
    }

    /**
     * 
     * @param start
     * @param count
     * @param materialIndex
     */
    public addGroup(start: number, count: number, materialIndex: number = 0): Geometry {
        this.groups.push({
            start,
            count,
            materialIndex
        });
        return this;
    }

    /**
     * 
     */
    public clearGroups(): Geometry {
        this.groups.length = 0;
        return this;
    }

    /**
     * 
     */
    public sortGroupByMaterial(): Geometry {
        this.groups.sort((a, b) => a.materialIndex - b.materialIndex);
        return this;
    }

    /**
     * 
     */
    public sortGroupByStart(): Geometry {
        this.groups.sort((a, b) => a.start - b.start);
        return this;
    }

    /**
     * 
     * @param start
     * @param count
     */
    public setDrawRange(start: number, count: number): Geometry {
        this.drawRange.start = start;
        this.drawRange.count = count;
        return this;
    }

    /**
     * 
     * @param matrix4
     */
    public applyMatrix(matrix4: Matrix4): Geometry {

        const position = this.attributes.position;
        if (position) {
            position.applyMatrix4(matrix4);
            position.needsUpdate = true;
        }

        const normal = this.attributes.normal;
        if (normal) {
            normal.transformDirection(matrix4);
            normal.needsUpdate = true;
        }

        const tangent = this.attributes.tangent;
        if (tangent) {
            tangent.transformDirection(matrix4);
            tangent.needsUpdate = true;
        }

        if (this.boundingBox) {
            this.computeBoundingBox();
        }

        if (this.boundingSphere) {
            this.computeBoundingSphere();
        }

        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public applyQuaternion(quaternion: Quaternion): Geometry {

        const position = this.attributes.position;

        if (position) {
            quaternion.applyToBufferAttribute(position);
            position.needsUpdate = true;
        }

        const normal = this.attributes.normal;
        if (normal) {
            quaternion.applyToBufferAttribute(normal);
            normal.needsUpdate = true;
        }

        const tangent = this.attributes.tangent;
        if (tangent) {
            quaternion.applyToBufferAttribute(tangent);
            normal.needsUpdate = true;
        }

        if (this.boundingBox) {
            this.computeBoundingBox();
        }

        if (this.boundingSphere) {
            this.computeBoundingSphere();
        }

        return this;
    }

    /**
     * X
     * @param angle
     */
    public rotateX(angle: number): Geometry {
        _tempMatrix.makeRotationX(angle);
        this.applyMatrix(_tempMatrix);
        return this;
    }

    /**
     * Y
     * @param angle
     */
    public rotateY(angle: number): Geometry {
        _tempMatrix.makeRotationY(angle);
        this.applyMatrix(_tempMatrix);
        return this;
    }

    /**
     * Z
     * @param angle
     */
    public rotateZ(angle: number): Geometry {
        _tempMatrix.makeRotationZ(angle);
        this.applyMatrix(_tempMatrix);
        return this;
    }

    /**
     * 
     * @param offset
     */
    public translate(offset: Vector3): Geometry {
        _tempMatrix.makeTranslationVector(offset);
        this.applyMatrix(_tempMatrix);
        return this;
    }

    /**
     * 
     * @param scale
     */
    public scale(scale: Vector3): Geometry {
        _tempMatrix.makeScaleVector(scale);
        this.applyMatrix(_tempMatrix);
        return this;
    }

    /**
     * 
     * @param target
     */
    public lookAt(target: Vector3): Geometry {
        _tempMatrix.lookAt(Vector3.ZERO, target, Vector3.UNIT_Y);
        this.applyMatrix(_tempMatrix);
        return this;
    }

    /**
     * 
     */
    public moveToOrigin(): Geometry {
        this.computeBoundingBox();
        this.boundingBox?.getCenter(_tempVector1).negate();
        this.translate(_tempVector1);
        return this;
    }

    /**
     * 
     * @param points
     */
    public setFromPoints(points: Vector3[] | Vector2[]): Geometry {
        const position = [];

        for (let i = 0, l = points.length; i < l; i++) {
            let point = points[i];
            position.push(point.x, point.y, point instanceof Vector2 ? 0 : point.z);
        }

        this.setAttribute(BufferAttributeKind.PositionKind, new Float32BufferAttribute(position, 3));
        return this;
    }

    /**
     * 
     */
    public computeBoundingBox(): Geometry {

        if (this.boundingBox == null) {
            this.boundingBox = new Box3();
        }

        const position = this.attributes[BufferAttributeKind.PositionKind];
        const morphAttributesPosition = this.morphAttributes[BufferAttributeKind.PositionKind];

        if (position) {

            this.boundingBox.setFromBufferAttribute(position);

            if (morphAttributesPosition) {

                for (let i = 0, il = morphAttributesPosition.length; i < il; i++) {

                    const morphAttribute = morphAttributesPosition[ i ];
                    _tempBox1.setFromBufferAttribute(morphAttribute);

                    if (this.morphTargetsRelative) {

                        _tempVector1.addVectors(this.boundingBox.min, _tempBox1.min);
                        this.boundingBox.expandByPoint(_tempVector1);

                        _tempVector1.addVectors(this.boundingBox.max, _tempBox1.max);
                        this.boundingBox.expandByPoint(_tempVector1);

                    } else {

                        this.boundingBox.expandByPoint(_tempBox1.min);
                        this.boundingBox.expandByPoint(_tempBox1.max);

                    }

                }

            }

        } else {

            this.boundingBox.makeEmpty();

        }

        if (isNaN(this.boundingBox.min.x) || isNaN(this.boundingBox.min.y) || isNaN(this.boundingBox.min.z)) {
            console.warn('computeBoundingBox: Computed min/max have NaN values. The "position" attribute is likely to have NaN values.');
        }

        return this;
    }

    /**
     * 
     */
    public computeBoundingSphere(): Geometry {

        if (this.boundingSphere == null) {
            this.boundingSphere = new Sphere();
        }

        const position = this.attributes[BufferAttributeKind.PositionKind];
        const morphAttributesPosition = this.morphAttributes[BufferAttributeKind.PositionKind];

        if (position) {

            const center = this.boundingSphere.center;

            _tempBox1.setFromBufferAttribute(position);

            if (morphAttributesPosition) {

                for (let i = 0, il = morphAttributesPosition.length; i < il; i++) {

                    const morphAttribute = morphAttributesPosition[i];
                    _tempBox2.setFromBufferAttribute(morphAttribute);

                    if (this.morphTargetsRelative) {

                        _tempVector1.addVectors(_tempBox1.min, _tempBox2.min);
                        _tempBox1.expandByPoint(_tempVector1);

                        _tempVector1.addVectors(_tempBox1.max, _tempBox2.max);
                        _tempBox1.expandByPoint(_tempVector1);

                    } else {

                        _tempBox1.expandByPoint(_tempBox2.min);
                        _tempBox1.expandByPoint(_tempBox2.max);

                    }

                }

            }

            _tempBox1.getCenter(center);

            let maxRadiusSq = 0;

            for (let i = 0, il = position.count; i < il; i++) {
                _tempVector1.fromBufferAttribute(position, i);
                maxRadiusSq = Math.max(maxRadiusSq, center.distanceToSquared(_tempVector1));
            }

            if (morphAttributesPosition) {

                for (let i = 0, il = morphAttributesPosition.length; i < il; i++) {

                    const morphAttribute = morphAttributesPosition[i];
                    const morphTargetsRelative = this.morphTargetsRelative;

                    for (let j = 0, jl = morphAttribute.count; j < jl; j++) {

                        _tempVector1.fromBufferAttribute(morphAttribute, j);

                        if (morphTargetsRelative) {
                            _tempVector2.fromBufferAttribute(position, j);
                            _tempVector1.add(_tempVector2);
                        }

                        maxRadiusSq = Math.max(maxRadiusSq, center.distanceToSquared(_tempVector1));

                    }

                }

            }

            this.boundingSphere.radius = Math.sqrt(maxRadiusSq);

            if (isNaN(this.boundingSphere.radius)) {

                console.error('VISION.BufferGeometry.computeBoundingSphere(): Computed radius is NaN. The "position" attribute is likely to have NaN values.', this);

            }
        }

        return this;
    }

    /**
     * 
     */
    public computeVertexNormals(): Geometry {

        const indexAttribute = this.getAttribute(BufferAttributeKind.IndicesKind);
        const positionAttribute = this.getAttribute(BufferAttributeKind.PositionKind);
        if (positionAttribute) {

            let normalAttribute = this.getAttribute(BufferAttributeKind.NormalKind);
            if (!normalAttribute) {

                normalAttribute = new BufferAttribute(new Float32Array(positionAttribute.count * 3), 3);
                this.setAttribute(BufferAttributeKind.NormalKind, normalAttribute);
            }
            else {
                for (let i = 0, il = normalAttribute.count; i < il; i++) {
                    normalAttribute.setXYZ(i, 0, 0, 0);
                }
            }

            const pA = new Vector3(), pB = new Vector3(), pC = new Vector3();
            const nA = new Vector3(), nB = new Vector3(), nC = new Vector3();
            const cb = new Vector3(), ab = new Vector3();

            if (indexAttribute) {

                for (let i = 0, il = indexAttribute.count; i < il; i += 3) {

                    const vA = indexAttribute.getX(i + 0);
                    const vB = indexAttribute.getX(i + 1);
                    const vC = indexAttribute.getX(i + 2);

                    pA.fromBufferAttribute(positionAttribute, vA);
                    pB.fromBufferAttribute(positionAttribute, vB);
                    pC.fromBufferAttribute(positionAttribute, vC);

                    cb.subVectors(pC, pB);
                    ab.subVectors(pA, pB);
                    cb.cross(ab);

                    nA.fromBufferAttribute(normalAttribute, vA);
                    nB.fromBufferAttribute(normalAttribute, vB);
                    nC.fromBufferAttribute(normalAttribute, vC);

                    nA.add(cb);
                    nB.add(cb);
                    nC.add(cb);

                    normalAttribute.setXYZ(vA, nA.x, nA.y, nA.z);
                    normalAttribute.setXYZ(vB, nB.x, nB.y, nB.z);
                    normalAttribute.setXYZ(vC, nC.x, nC.y, nC.z);

                }
            }
            else {

                for (let i = 0, il = positionAttribute.count; i < il; i += 3) {

                    pA.fromBufferAttribute(positionAttribute, i + 0);
                    pB.fromBufferAttribute(positionAttribute, i + 1);
                    pC.fromBufferAttribute(positionAttribute, i + 2);

                    cb.subVectors(pC, pB);
                    ab.subVectors(pA, pB);
                    cb.cross(ab);

                    normalAttribute.setXYZ(i + 0, cb.x, cb.y, cb.z);
                    normalAttribute.setXYZ(i + 1, cb.x, cb.y, cb.z);
                    normalAttribute.setXYZ(i + 2, cb.x, cb.y, cb.z);

                }
            }

            this.normalizeNormals();
        }
        return this;
    }

    /**
     * 
     */
    public normalizeNormals(): Geometry {

        const normals = this.attributes.normal;

        for (let i = 0, il = normals.count; i < il; i++) {
            _tempVector1.fromBufferAttribute(normals, i);
            _tempVector1.normalize();
            normals.setXYZ(i, _tempVector1.x, _tempVector1.y, _tempVector1.z);
        }

        normals.needsUpdate = true;
        return this;
    }

    /**
     * 
     * @param geometry
     * @param offset
     */
    public mergeAttributes(geometry: Geometry, offset: int = 0): Geometry {

        const attributes = this.attributes;

        for (let key in attributes) {

            if (!geometry.attributes[key]) { continue; }

            const attribute1 = attributes[key];
            const attributeArray1 = attribute1.array;

            const attribute2 = geometry.attributes[key];
            const attributeArray2 = attribute2.array;

            const attributeOffset = attribute2.itemSize * offset;
            const length = Math.min(attributeArray2.length, attributeArray1.length - attributeOffset);

            for (let i = 0, j = attributeOffset; i < length; i++, j++) {
                attributeArray1[j] = attributeArray2[i];
            }
        }

        return this;
    }

    /**
     * 
     */
    public toNonIndexed(): Geometry {

        const convertBufferAttribute = (attribute: BufferAttribute, indices: FloatArray) => {
            const array = attribute.array;
            const itemSize = attribute.itemSize;
            const normalized = attribute.normalized;
            const array2: BufferArray = DataTypeFormat.createTypedArrayFromDataType(attribute.type, indices.length * itemSize)!;

            let index = 0;
            let index2 = 0;

            for (let i = 0, l = indices.length; i < l; i++) {
                index = indices[i] * itemSize;
                for (let j = 0; j < itemSize; j++) {
                    array2[index2++] = array[index++];
                }
            }

            return new BufferAttribute(array2, itemSize, normalized);
        };

        if (!this.haveAttribute(BufferAttributeKind.IndicesKind)) {
            return this;
        }

        const geometry2 = new Geometry();

        const indicesAttribute = this.getAttribute(BufferAttributeKind.IndicesKind)!;

        const attributes = this.attributes;
        for (let name in attributes) {
            const attribute = attributes[name];
            if (attribute instanceof BufferAttribute) {
                const newAttribute = convertBufferAttribute(attribute, indicesAttribute.array);
                geometry2.setAttribute(name, newAttribute);
            }
        }

        const morphAttributes = this.morphAttributes;
        for (let name in morphAttributes) {
            const morphArray = [];
            const morphAttribute = morphAttributes[name];

            for (let i = 0, il = morphAttribute.length; i < il; i++) {
                const attribute = morphAttribute[i];
                if (attribute instanceof BufferAttribute) {
                    const newAttribute = convertBufferAttribute(attribute, indicesAttribute.array);
                    morphArray.push(newAttribute);
                }
            }

            geometry2.morphAttributes[name] = morphArray;
        }

        geometry2.morphTargetsRelative = this.morphTargetsRelative;

        const groups = this.groups;
        for (let i = 0, l = groups.length; i < l; i++) {
            const group = groups[i];
            geometry2.addGroup(group.start, group.count, group.materialIndex);
        }

        return geometry2;
    }

    /**
     * 
     */
    public clone(): Geometry {
        return new Geometry().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: Geometry): Geometry {

        this.name = source.name;

        const index = source.getIndexAttrbute();
        if (index) {
            this.setIndexAttrbute(index.clone());
        }

        const interleavedBufferClone: InterleavedBufferClone = {
            cacheKey: new Map<BufferArray, string>(),
            arrayBuffers: {},
            interleavedBuffers: {}
        };

        const attributes = source.attributes;
        for (let name in attributes) {
            const attribute = attributes[name];
            this.setAttribute(name, attribute.clone(interleavedBufferClone));
        }

        const morphAttributes = source.morphAttributes;
        for (let name in morphAttributes) {
            const array = [];
            const morphAttribute = morphAttributes[name];

            for (let i = 0, l = morphAttribute.length; i < l; i++) {
                array.push(morphAttribute[ i ].clone(interleavedBufferClone));
            }

            this.morphAttributes[name] = array;
        }

        this.morphTargetsRelative = source.morphTargetsRelative;

        const groups = source.groups;
        for (let i = 0, l = groups.length; i < l; i++) {
            const group = groups[i];
            this.addGroup(group.start, group.count, group.materialIndex);
        }

        const boundingBox = source.boundingBox;
        if (boundingBox) {
            this.boundingBox = boundingBox.clone();
        }

        const boundingSphere = source.boundingSphere;
        if (boundingSphere) {
            this.boundingSphere = boundingSphere.clone();
        }

        this.drawRange.start = source.drawRange.start;
        this.drawRange.count = source.drawRange.count;

        return this;
    }


}
