import { Plane } from "./Plane";
import { Nullable } from "../types";
import { Matrix4 } from "./Matrix4";
import { Sphere } from "./Sphere";
import { Vector3 } from "./Vector3";
import { FrustumIntersect } from "./InterfaceType/IntersectResult";
import { Box3 } from "./Box3";
import { Mesh } from "../Scene";

/**
 * @ignore
 */
const _tempVec = new Vector3();
/**
 * @ignore
 */
const _tempSphere = new Sphere();

/**
 * 
 * @category Math
 */
export class Frustum {

    /**
     * @ignore
     */
    private readonly _p: Plane[];

    /**
     * @ignore
     */
    private readonly _planeData: number[];

    /**
     * 
     * @param p0
     * @param p1
     * @param p2
     * @param p3
     * @param p4
     * @param p5
     */
    constructor(p0: Nullable<Plane> = null, p1: Nullable<Plane> = null, p2: Nullable<Plane> = null, p3: Nullable<Plane> = null, p4: Nullable<Plane> = null, p5: Nullable<Plane> = null) {
        this._p = [new Plane(), new Plane(), new Plane(), new Plane(), new Plane(), new Plane()];
        this._planeData = [];
        if (p0) { this._p[0].copy(p0); }
        if (p1) { this._p[1].copy(p1); }
        if (p2) { this._p[2].copy(p2); }
        if (p3) { this._p[3].copy(p3); }
        if (p4) { this._p[4].copy(p4); }
        if (p5) { this._p[5].copy(p5); }
        this._updateArrayPlaneData();
    }

    /**
     * 
     */
    public get planes() {
        return this._p;
    }

    /**
     * 
     */
    public get planeData() {
        return this._planeData;
    }

    /**
     * 6
     * @param p0
     * @param p1
     * @param p2
     * @param p3
     * @param p4
     * @param p5
     */
    public set(p0: Plane, p1: Plane, p2: Plane, p3: Plane, p4: Plane, p5: Plane): Frustum {
        this._p[0].copy(p0);
        this._p[1].copy(p1);
        this._p[2].copy(p2);
        this._p[3].copy(p3);
        this._p[4].copy(p4);
        this._p[5].copy(p5);
        this._updateArrayPlaneData();
        return this;
    }

    /**
     * 
     * @param matrix
     */
    public setFromProjectionMatrix(matrix: Matrix4): Frustum {
        const planes = this.planes;
        const me = matrix.elements;
        const me0 = me[0]; const me1 = me[1]; const me2 = me[2]; const me3 = me[3];
        const me4 = me[4]; const me5 = me[5]; const me6 = me[6]; const me7 = me[7];
        const me8 = me[8]; const me9 = me[9]; const me10 = me[10]; const me11 = me[11];
        const me12 = me[12]; const me13 = me[13]; const me14 = me[14]; const me15 = me[15];

        planes[0].setFromXYZW(me3 - me0, me7 - me4, me11 - me8, me15 - me12).normalize();
        planes[1].setFromXYZW(me3 + me0, me7 + me4, me11 + me8, me15 + me12).normalize();
        planes[2].setFromXYZW(me3 + me1, me7 + me5, me11 + me9, me15 + me13).normalize();
        planes[3].setFromXYZW(me3 - me1, me7 - me5, me11 - me9, me15 - me13).normalize();
        planes[4].setFromXYZW(me3 - me2, me7 - me6, me11 - me10, me15 - me14).normalize();
        planes[5].setFromXYZW(me3 + me2, me7 + me6, me11 + me10, me15 + me14).normalize();

        this._updateArrayPlaneData();

        return this;
    }

    /**
     * 
     */
    public clone(): Frustum {
        return new Frustum().copy(this);
    }

    /**
     * 
     * @param frustum
     */
    public copy(frustum: Frustum): Frustum {
        const planes = this.planes;
        for (let i = 0; i < 6; i++) {
            planes[i].copy(frustum.planes[i]);
        }
        this._updateArrayPlaneData();
        return this;
    }

    /**
     * 
     */
    public intersectsObject(mesh: Mesh): boolean {
        _tempSphere.copy(mesh.boundingSphere).applyMatrix4(mesh.matrixWorld);
        return this.intersectsSphere(_tempSphere);
    }

    /**
     * 
     * @param sphere
     * @param result
     */
    public intersectsSphere(sphere: Sphere, result: Nullable<FrustumIntersect> = null): boolean {

        const planes = this.planes;
        const center = sphere.center;
        const negRadius = -sphere.radius;

        let fullVisible = 0;

        for (let i = 0; i < 6; i++) {
            const distance = planes[i].distanceToPoint(center);
            if (distance < negRadius) {
                return false;
            }

            if (result) {
                result.planes.push({
                    index: i,
                    distance,
                });

                if (distance >= sphere.radius) {
                    fullVisible++;
                }
            }
        }

        if (result) {
            result.fullVisible = fullVisible == 6;
        }
        return true;
    }

    /**
     * 
     * @param box
     * @param result
     */
    public intersectsBox(box: Box3, result: Nullable<FrustumIntersect> = null): boolean {

        box.getBoundingSphere(_tempSphere);

        const planes = this.planes;

        let fullVisible = 0;

        for (let i = 0; i < 6; i++) {

            const plane = planes[i];

            _tempVec.x = plane.normal.x > 0 ? box.max.x : box.min.x;
            _tempVec.y = plane.normal.y > 0 ? box.max.y : box.min.y;
            _tempVec.z = plane.normal.z > 0 ? box.max.z : box.min.z;

            const distance = plane.distanceToPoint(_tempVec);
            if (distance < 0) {
                return false;
            }

            if (result) {
                result.planes.push({
                    index: i,
                    distance,
                });

                if (distance >= _tempSphere.radius) {
                    fullVisible++;
                }
            }
        }

        if (result) {
            result.fullVisible = fullVisible == 6;
        }
        return true;
    }

    /**
     * @ignore
     */
    private _updateArrayPlaneData() {

        this._planeData.length = 0;

        const planes = this.planes;
        for (let i = 0; i < 6; i++) {
            this._planeData.push(planes[i].normal.x, planes[i].normal.x, planes[i].normal.x, planes[i].constant);
        }
    }
}
