WebGLEngine.prototype._uploadTextureRaw = function(texture: TextureRaw): boolean {
    const gl = this.gl;
    const glTex = texture.glTexture;

    gl.pixelStorei(gl.UNPACK_FLIP_Y_WEBGL, texture.flipY);
    gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, texture.premultiplyAlpha);
    gl.pixelStorei(gl.UNPACK_ALIGNMENT, texture.unpackAlignment);

    this._updateTextureFilterMode(texture);
    this._updateTextureWrapMode(texture);

    const internalSizedFomat = this._getRGBABufferInternalSizedFormat(texture.type, texture.format);
    const internalFormat = this._getInternalFormat(texture.format);
    const textureType = this._getWebGLTextureType(texture.type);

    if (glTex!.isCompression) {
        gl.compressedTexImage2D(gl.TEXTURE_2D, 0, texture.format, glTex!.width, glTex!.height, 0, texture.buffer);
    }
    else {
        gl.texImage2D(gl.TEXTURE_2D, 0, internalSizedFomat, glTex!.width, glTex!.height, 0, internalFormat, textureType, texture.buffer);
    }

    if (texture.mipmaps.length > 0) {
        for (let i = 0, n = texture.mipmaps.length; i < n; ++i) {
            if (glTex!.isCompression) {
                gl.compressedTexImage2D(gl.TEXTURE_2D, i + 1, texture.format, texture.mipmaps[i].width, texture.mipmaps[i].height, 0, texture.mipmaps[i].buffer);
            } else {
                gl.texImage2D(gl.TEXTURE_2D, i + 1, internalSizedFomat, texture.mipmaps[i].width, texture.mipmaps[i].height, 0, internalFormat, textureType, texture.mipmaps[i].buffer);
            }
        }
    }

    if (texture.generateMipmaps) {
        gl.generateMipmap(gl.TEXTURE_2D);
    }

    return true;
};

WebGLEngine.prototype._uploadTextureRawCube = function(texture: TextureRawCube): boolean {

    const gl = this.gl;
    const glTex = texture.glTexture;

    gl.pixelStorei(gl.UNPACK_FLIP_Y_WEBGL, texture.flipY);
    gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, texture.premultiplyAlpha);
    gl.pixelStorei(gl.UNPACK_ALIGNMENT, texture.unpackAlignment);

    this._updateTextureFilterMode(texture);
    this._updateTextureWrapMode(texture);

    const internalSizedFomat = this._getRGBABufferInternalSizedFormat(texture.type, texture.format);
    const internalFormat = this._getInternalFormat(texture.format);
    const textureType = this._getWebGLTextureType(texture.type);

    for (let i = 0, n = texture.bufferRaws.length; i < n; ++i) {
        if (glTex!.isCompression) {
            gl.compressedTexImage2D(gl.TEXTURE_CUBE_MAP_POSITIVE_X + i, 0, texture.format, glTex!.width, glTex!.height, 0, texture.bufferRaws[i]);
        }
        else {
            gl.texImage2D(gl.TEXTURE_CUBE_MAP_POSITIVE_X + i, 0, internalSizedFomat, glTex!.width, glTex!.height, 0, internalFormat, textureType, texture.bufferRaws[i]);
        }
    }

    if (texture.mipmapRaws.length > 0) {
        for (let i = 0, n = texture.mipmapRaws.length; i < n; ++i) {
            for (let j = 0, m = texture.mipmapRaws[i].length; j < m; ++j) {
                if (glTex!.isCompression) {
                    gl.compressedTexImage2D(gl.TEXTURE_CUBE_MAP_POSITIVE_X + i, j + 1, texture.format, texture.mipmapRaws[i][j].width, texture.mipmapRaws[i][j].height, 0, texture.bufferRaws[i]);
                }
                else {
                    gl.texImage2D(gl.TEXTURE_CUBE_MAP_POSITIVE_X + i, j + 1, internalSizedFomat, texture.mipmapRaws[i][j].width, texture.mipmapRaws[i][j].height, 0, internalFormat, textureType, texture.mipmapRaws[i][j].buffer);
                }
            }
        }
    }

    if (texture.generateMipmaps) {
        gl.generateMipmap(gl.TEXTURE_CUBE_MAP);
    }

    return true;
};

