import { int } from "../types";
import { encode, decode } from "./Utf8";

/**
 * IO
 * @category Libs
 */
export class IOBuffer {
    public littleEndian: boolean;
    public lastWrittenByte: number;
    public length: number;
    public byteLength: number;
    public byteOffset: number;
    public offset: number;
    public buffer: ArrayBuffer | Float32Array;

    protected _data: DataView;
    protected _mark: number;
    protected _marks: number[];

    /**
     * 
     * @param data
     */
    constructor(data: IOBuffer | ArrayBuffer | Float32Array | number) {
        let dataIsGiven = false;
        if (typeof data == 'number') {
            data = new ArrayBuffer(data);
        }
        else {
            dataIsGiven = true;
            this.lastWrittenByte = data.byteLength;
        }

        const offset = 0;
        const byteLength = data.byteLength - offset;

        let dvOffset = offset;
        if (ArrayBuffer.isView(data) || data instanceof IOBuffer) {
            if (data.byteLength != data.buffer.byteLength) {
                dvOffset = data.byteOffset + offset;
            }
            data = data.buffer;
        }
        if (dataIsGiven) {
            this.lastWrittenByte = byteLength;
        }
        else {
            this.lastWrittenByte = 0;
        }
        this.buffer = data;
        this.length = byteLength;
        this.byteLength = byteLength;
        this.byteOffset = dvOffset;
        this.offset = 0;
        this.littleEndian = true;
        this._data = new DataView(this.buffer, dvOffset, byteLength);
        this._mark = 0;
        this._marks = [];
    }

    /**
     * 
     * @param byteLength
     */
    public available(byteLength: number = 1): boolean {
        return this.offset + byteLength <= this.length;
    }

    /**
     * 
     */
    public isLittleEndian(): boolean {
        return this.littleEndian;
    }

    /**
     * 
     */
    public setLittleEndian(): IOBuffer {
        this.littleEndian = true;
        return this;
    }

    /**
     * 
     */
    public isBigEndian(): boolean {
        return !this.littleEndian;
    }

    /**
     * 
     */
    public setBigEndian(): IOBuffer {
        this.littleEndian = false;
        return this;
    }

    /**
     * n
     * @param n
     */
    public skip(n: number = 1): IOBuffer {
        this.offset += n;
        return this;
    }

    /**
     * 
     * @param offset
     */
    public seek(offset: number): IOBuffer {
        this.offset = offset;
        return this;
    }

    /**
     * 
     */
    public mark(): IOBuffer {
        this._mark = this.offset;
        return this;
    }

    /**
     * 
     */
    public reset(): IOBuffer {
        this.offset = this._mark;
        return this;
    }

    /**
     * 
     */
    public pushMark(): IOBuffer {
        this._marks.push(this.offset);
        return this;
    }

    /**
     * 
     */
    public popMark(): IOBuffer {
        const offset = this._marks.pop();
        if (offset == undefined) {
            throw new Error('Mark stack empty');
        }
        this.seek(offset);
        return this;
    }

    /**
     * 0
     */
    public rewind(): IOBuffer {
        this.offset = 0;
        return this;
    }

    /**
     * byteLength
     * (byteLength + current offset)()
     * @param byteLength
     */
    public ensureAvailable(byteLength: number = 1): IOBuffer {
        if (!this.available(byteLength)) {
            const lengthNeeded = this.offset + byteLength;
            const newLength = lengthNeeded * 2;
            const newArray = new Uint8Array(newLength);
            newArray.set(new Uint8Array(this.buffer));
            this.buffer = newArray.buffer;
            this.length = this.byteLength = newLength;
            this._data = new DataView(this.buffer);
        }
        return this;
    }

    /**
     * 0falsetrue
     */
    public readBoolean() {
        return this.readUint8() != 0;
    }

    /**
     * 81
     */
    public readInt8() {
        return this._data.getInt8(this.offset++);
    }

    /**
     * 8,1
     */
    public readUint8() {
        return this._data.getUint8(this.offset++);
    }

    /**
     * 1,1
     */
    public readByte() {
        return this.readUint8();
    }

    /**
     * n,n
     */
    public readBytes(n: number = 1): Uint8Array {
        const bytes = new Uint8Array(n);
        for (let i = 0; i < n; i++) {
            bytes[i] = this.readByte();
        }
        return bytes;
    }

    /**
     * 162
     */
    public readInt16(): int {
        const value = this._data.getInt16(this.offset, this.littleEndian);
        this.offset += 2;
        return value;
    }

    /**
     * 162
     */
    public readUint16(): int {
        const value = this._data.getUint16(this.offset, this.littleEndian);
        this.offset += 2;
        return value;
    }

    /**
     * 324
     */
    public readInt32(): int {
        const value = this._data.getInt32(this.offset, this.littleEndian);
        this.offset += 4;
        return value;
    }

    /**
     * 324
     */
    public readUint32(): int {
        const value = this._data.getUint32(this.offset, this.littleEndian);
        this.offset += 4;
        return value;
    }

    /**
     * 324
     */
    public readFloat32(): number {
        const value = this._data.getFloat32(this.offset, this.littleEndian);
        this.offset += 4;
        return value;
    }

    /**
     * 648
     */
    public readFloat64(): number {
        const value = this._data.getFloat64(this.offset, this.littleEndian);
        this.offset += 8;
        return value;
    }

    /**
     * 1ASCII1
     */
    public readChar(): string {
        return String.fromCharCode(this.readInt8());
    }

    /**
     * n1ASCIIn
     */
    public readChars(n: number = 1): string {
        let result = '';
        for (let i = 0; i < n; i++) {
            result += this.readChar();
        }
        return result;
    }

    /**
     * nUTF-8n
     */
    public readUtf8(n: number = 1): string {
        const bString = this.readChars(n);
        return decode(bString);
    }

    /**
     * , 0xff0x001
     */
    public writeBoolean(value: boolean): IOBuffer {
        this.writeUint8(value ? 0xff : 0x00);
        return this;
    }

    /**
     * 81
     */
    public writeInt8(value: int): IOBuffer {
        this.ensureAvailable(1);
        this._data.setInt8(this.offset++, value);
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 81
     */
    public writeUint8(value: int): IOBuffer {
        this.ensureAvailable(1);
        this._data.setUint8(this.offset++, value);
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 1
     * @param value
     */
    public writeByte(value: int): IOBuffer {
        return this.writeUint8(value);
    }

    /**
     * nm
     */
    public writeBytes(bytes: int[]): IOBuffer {
        this.ensureAvailable(bytes.length);
        for (let i = 0; i < bytes.length; i++) {
            this._data.setUint8(this.offset++, bytes[i]);
        }
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 162
     */
    public writeInt16(value: int): IOBuffer {
        this.ensureAvailable(2);
        this._data.setInt16(this.offset, value, this.littleEndian);
        this.offset += 2;
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 162
     */
    public writeUint16(value: int): IOBuffer {
        this.ensureAvailable(2);
        this._data.setUint16(this.offset, value, this.littleEndian);
        this.offset += 2;
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 324
     */
    public writeInt32(value: int): IOBuffer {
        this.ensureAvailable(4);
        this._data.setInt32(this.offset, value, this.littleEndian);
        this.offset += 4;
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 324
     * @param value
     */
    public writeUint32(value: int): IOBuffer {
        this.ensureAvailable(4);
        this._data.setUint32(this.offset, value, this.littleEndian);
        this.offset += 4;
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 324
     */
    public writeFloat32(value: number): IOBuffer {
        this.ensureAvailable(4);
        this._data.setFloat32(this.offset, value, this.littleEndian);
        this.offset += 4;
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 648
     */
    public writeFloat64(value: number): IOBuffer {
        this.ensureAvailable(8);
        this._data.setFloat64(this.offset, value, this.littleEndian);
        this.offset += 8;
        this._updateLastWrittenByte();
        return this;
    }

    /**
     * 1
     */
    public writeChar(str: string): IOBuffer {
        return this.writeUint8(str.charCodeAt(0));
    }

    /**
     * nn
     */
    public writeChars(str: string): IOBuffer {
        for (let i = 0; i < str.length; i++) {
            this.writeUint8(str.charCodeAt(i));
        }
        return this;
    }

    /**
     * UTF8length
     */
    public writeUtf8(str: string): IOBuffer {
        const bString = encode(str);
        return this.writeChars(bString);
    }

    /**
     * Uint8Array
     */
    public toArray(): Uint8Array {
        return new Uint8Array(this.buffer, this.byteOffset, this.lastWrittenByte);
    }

    /**
     * 
     */
    private _updateLastWrittenByte(): void {
        if (this.offset > this.lastWrittenByte) {
            this.lastWrittenByte = this.offset;
        }
    }
}
