import { TransformObject } from "../Scene/TransformObject";
import { VisibleLayers } from "../Misc/VisibleLayers";
import { ColorRGB } from "../Math";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";
import { Renderable } from "../Scene/Renderable";

export abstract class Light extends TransformObject {

    /**
     * @ignore
     */
    protected _maskLayers: VisibleLayers = new VisibleLayers();
    /**
     * @ignore
     */
    protected _color: ColorRGB = new ColorRGB(1.0, 1.0, 1.0);
    /**
     * @ignore
     */
    protected _castShadow: boolean = false;
    /**
     * @ignore
     */
    protected _intensity: number = 1.0;

    protected constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '') {
        super(scene, parent, name);

        this._maskLayers.enableAll();

        this.scene.addLight(this);
    }

    /**
     * 
     */
    public get maskLayers() {
        return this._maskLayers;
    }

    /**
     * 
     */
    public get color() {
        return this._color;
    }
    public set color(value) {
        this._color.copy(value);
    }

    /**
     * 
     */
    public get intensity() {
        return this._intensity;
    }
    public set intensity(value) {
        this._intensity = value;
    }

    /**
     * 
     */
    public get castShadow() {
        return this._castShadow;
    }
    public set castShadow(value: boolean) {
        this._castShadow = value;
    }

    /**
     * Mesh
     * @param mesh
     */
    public canAffectMesh(mesh: Renderable): boolean {
        return this._maskLayers.test(mesh.lightLayers);
    }
}
