import { Vector3 } from "../../../Math";
import { Geometry } from "../Geometry";
import { Float32BufferAttribute } from "../TypeBufferAttribute";

export abstract class BoxGeometryBuilder {
    /**
     * 
     * @param width
     * @param height
     * @param depth
     * @param widthSegments
     * @param heightSegments
     * @param depthSegments
     * @param normalizeUV
     */
    public static build(width: number = 1, height: number = 1, depth: number = 1, widthSegments: number = 1, heightSegments: number = 1, depthSegments: number = 1, normalizeUV: boolean = true): Geometry {

        const indices = new Array<number>();
        const vertices = new Array<number>();
        const normals = new Array<number>();
        const uvs = new Array<number>();

        let numberOfVertices = 0;
        let groupStart = 0;
        let geometry = new Geometry();

        function buildPlane(u: number, v: number, w: number, udir: number, vdir: number, width: number, height: number, depth: number, gridX: number, gridY: number, materialIndex: number) {
            let segmentWidth = width / gridX;
            let segmentHeight = height / gridY;

            let widthHalf = width / 2;
            let heightHalf = height / 2;
            let depthHalf = depth / 2;

            let gridX1 = gridX + 1;
            let gridY1 = gridY + 1;

            let vertexCounter = 0;
            let groupCount = 0;

            let ix;
            let iy;

            let vector = new Vector3();

            // generate vertices, normals and uvs

            for (iy = 0; iy < gridY1; iy++) {
                let y = iy * segmentHeight - heightHalf;

                for (ix = 0; ix < gridX1; ix++) {
                    let x = ix * segmentWidth - widthHalf;

                    // set values to correct vector component

                    vector.setComponent(u, x * udir);
                    vector.setComponent(v, y * vdir);
                    vector.setComponent(w, depthHalf);

                    // now apply vector to vertex buffer

                    vertices.push(vector.x, vector.y, vector.z);

                    // set values to correct vector component

                    vector.setComponent(u, 0);
                    vector.setComponent(v, 0);
                    vector.setComponent(w, depth > 0 ? 1 : -1);

                    // now apply vector to normal buffer

                    normals.push(vector.x, vector.y, vector.z);

                    // uvs

                    uvs.push(ix / gridX);
                    uvs.push(1 - (iy / gridY));

                    if (normalizeUV == false) {
                        uvs[uvs.length - 2] = uvs[uvs.length - 2] * width;
                        uvs[uvs.length - 1] = uvs[uvs.length - 1] * height;
                    }

                    // counters

                    vertexCounter += 1;
                }
            }

            // indices

            // 1. you need three indices to draw a single face
            // 2. a single segment consists of two faces
            // 3. so we need to generate six (2*3) indices per segment

            for (iy = 0; iy < gridY; iy++) {
                for (ix = 0; ix < gridX; ix++) {
                    let a = numberOfVertices + ix + gridX1 * iy;
                    let b = numberOfVertices + ix + gridX1 * (iy + 1);
                    let c = numberOfVertices + (ix + 1) + gridX1 * (iy + 1);
                    let d = numberOfVertices + (ix + 1) + gridX1 * iy;

                    // faces

                    indices.push(a, b, d);
                    indices.push(b, c, d);

                    // increase counter

                    groupCount += 6;
                }
            }

            // add a group to the geometry. this will ensure multi material support

            geometry.addGroup(groupStart, groupCount, materialIndex);

            // calculate new start value for groups

            groupStart += groupCount;

            // update total number of vertices

            numberOfVertices += vertexCounter;
        }

        buildPlane(2, 1, 0, -1, -1, depth, height, width, depthSegments, heightSegments, 0); // px
        buildPlane(2, 1, 0, 1, -1, depth, height, -width, depthSegments, heightSegments, 1); // nx
        buildPlane(0, 2, 1, 1, 1, width, depth, height, widthSegments, depthSegments, 2); // py
        buildPlane(0, 2, 1, 1, -1, width, depth, -height, widthSegments, depthSegments, 3); // ny
        buildPlane(0, 1, 2, 1, -1, width, height, depth, widthSegments, heightSegments, 4); // pz
        buildPlane(0, 1, 2, -1, -1, width, height, -depth, widthSegments, heightSegments, 5); // nz

        geometry.setIndexAttrbute(indices);
        geometry.setAttribute('position', new Float32BufferAttribute(vertices, 3));
        geometry.setAttribute('normal', new Float32BufferAttribute(normals, 3));
        geometry.setAttribute('uv', new Float32BufferAttribute(uvs, 2));

        return geometry;
    }
}
