import { BufferArray, BufferArrayInterface, DataArray, Nullable } from "../types";
import { Constants, GLConstants } from "../Engines";

/**
 * 
 * @category Misc
 */
export abstract class DataTypeFormat {

    /**
     * 
     * @param arrayOrType
     */
    public static dataTypeFromTypedArray(arrayOrType: DataArray | BufferArray): number {
        const type = ArrayBuffer.isView(arrayOrType) ? arrayOrType.constructor : arrayOrType;
        switch (type) {
            case Float64Array:
                return Constants.DoubleType;
            case Float32Array:
                return Constants.FloatType;
            case Uint16Array:
                return Constants.UnsignedShortType;
            case Uint32Array:
                return Constants.UnsignedIntType;
            case Uint8Array:
                return Constants.UnsignedByteType;
            case Uint8ClampedArray:
                return Constants.UnsignedByteType;
            case Int8Array:
                return Constants.ByteType;
            case Int16Array:
                return Constants.ShortType;
            case Int32Array:
                return Constants.IntType;
            default:
                return 0;
        }
    }

    /**
     * 
     * @param dataType
     */
    public static typedArrayFromDataType(dataType: number): Nullable<BufferArrayInterface> {
        switch (dataType) {
            case Constants.DoubleType:
                return Float64Array;
            case Constants.FloatType:
                return Float32Array;
            case Constants.UnsignedShortType:
            case Constants.UnsignedShort565Type:
            case Constants.UnsignedShort4444Type:
            case Constants.UnsignedShort5551Type:
                return Uint16Array;
            case Constants.UnsignedIntType:
                return Uint32Array;
            case Constants.UnsignedByteType:
                return Uint8Array;
            case Constants.ByteType:
                return Int8Array;
            case Constants.ShortType:
                return Int16Array;
            case Constants.IntType:
                return Int32Array;
            default:
                return null;
        }
    }

    /**
     * 
     * @param dataType
     * @param length
     */
    public static createTypedArrayFromDataType(dataType: number, length: number): Nullable<BufferArray> {
        switch (dataType) {
            case GLConstants.BYTE:
                return new Int8Array(length);
            case GLConstants.UNSIGNED_BYTE:
                return new Uint8Array(length);
            case GLConstants.SHORT:
                return new Int16Array(length);
            case GLConstants.UNSIGNED_SHORT:
                return new Uint16Array(length);
            case GLConstants.INT:
                return new Int32Array(length);
            case GLConstants.UNSIGNED_INT:
                return new Uint32Array(length);
            case GLConstants.FLOAT:
                return new Float32Array(length);
            case GLConstants.DOUBLE:
                return new Float64Array(length);
            default:
                return null;
        }
    }

    /**
     * 
     * @param dataType
     * @param values
     */
    public static createTypedArrayFromDataTypeValues(dataType: number, values: ArrayLike<number> | ArrayBufferLike): Nullable<BufferArray> {
        switch (dataType) {
            case GLConstants.BYTE:
                return new Int8Array(values);
            case GLConstants.UNSIGNED_BYTE:
                return new Uint8Array(values);
            case GLConstants.SHORT:
                return new Int16Array(values);
            case GLConstants.UNSIGNED_SHORT:
                return new Uint16Array(values);
            case GLConstants.INT:
                return new Int32Array(values);
            case GLConstants.UNSIGNED_INT:
                return new Uint32Array(values);
            case GLConstants.FLOAT:
                return new Float32Array(values);
            case GLConstants.DOUBLE:
                return new Float64Array(values);
            default:
                return null;
        }
    }

    /**
     * GL
     * @param name
     */
    public static glDataTypeFromName(name: string): number {

        switch (name) {
            case 'BYTE':
                return GLConstants.BYTE;
            case 'UNSIGNED_BYTE':
                return GLConstants.UNSIGNED_BYTE;
            case 'SHORT':
                return GLConstants.SHORT;
            case 'UNSIGNED_SHORT':
                return GLConstants.UNSIGNED_SHORT;
            case 'INT':
                return GLConstants.INT;
            case 'UNSIGNED_INT':
                return GLConstants.UNSIGNED_INT;
            case 'FLOAT':
                return GLConstants.FLOAT;
            case 'DOUBLE':
                return GLConstants.DOUBLE;
            default:
                throw 0;
        }
    }

    /**
     * GL
     * @param dataType
     */
    public static glDataTypeInBytes(dataType: number): number {

        switch (dataType) {
            case GLConstants.BYTE:
                return Int8Array.BYTES_PER_ELEMENT;
            case GLConstants.UNSIGNED_BYTE:
                return Uint8Array.BYTES_PER_ELEMENT;
            case GLConstants.SHORT:
                return Int16Array.BYTES_PER_ELEMENT;
            case GLConstants.UNSIGNED_SHORT:
                return Uint16Array.BYTES_PER_ELEMENT;
            case GLConstants.INT:
                return Int32Array.BYTES_PER_ELEMENT;
            case GLConstants.UNSIGNED_INT:
                return Uint32Array.BYTES_PER_ELEMENT;
            case GLConstants.FLOAT:
                return Float32Array.BYTES_PER_ELEMENT;
            case GLConstants.DOUBLE:
                return Float64Array.BYTES_PER_ELEMENT;
            default:
                return 0;
        }
    }

    /**
     * GL
     * @param dataType
     */
    public static validateGLDataType(dataType: number): boolean {
        return (dataType == GLConstants.BYTE ||
            dataType == GLConstants.UNSIGNED_BYTE ||
            dataType == GLConstants.SHORT ||
            dataType == GLConstants.UNSIGNED_SHORT ||
            dataType == GLConstants.INT ||
            dataType == GLConstants.UNSIGNED_INT ||
            dataType == GLConstants.FLOAT ||
            dataType == GLConstants.DOUBLE);
    }

    /**
     * GL
     * @param dataType
     * @param length
     */
    public static createTypedArrayFromGLDataType(dataType: number, length: number): Nullable<BufferArray> {
        switch (dataType) {
            case GLConstants.BYTE:
                return new Int8Array(length);
            case GLConstants.UNSIGNED_BYTE:
                return new Uint8Array(length);
            case GLConstants.SHORT:
                return new Int16Array(length);
            case GLConstants.UNSIGNED_SHORT:
                return new Uint16Array(length);
            case GLConstants.INT:
                return new Int32Array(length);
            case GLConstants.UNSIGNED_INT:
                return new Uint32Array(length);
            case GLConstants.FLOAT:
                return new Float32Array(length);
            case GLConstants.DOUBLE:
                return new Float64Array(length);
            default:
                return null;
        }
    }

    /**
     * GL
     * @param dataType
     * @param buffer
     * @param byteOffset
     * @param length
     */
    public static createArrayBufferFromGLDataType(dataType: number, buffer: ArrayBuffer, byteOffset: Nullable<number> = null, length: Nullable<number> = null): Nullable<BufferArray> {

        if (byteOffset == null) {
            byteOffset = 0;
        }
        if (length == null) {
            length = (buffer.byteLength - byteOffset) / DataTypeFormat.glDataTypeInBytes(dataType);
        }

        switch (dataType) {
            case GLConstants.BYTE:
                return new Int8Array(buffer, byteOffset, length);
            case GLConstants.UNSIGNED_BYTE:
                return new Uint8Array(buffer, byteOffset, length);
            case GLConstants.SHORT:
                return new Int16Array(buffer, byteOffset, length);
            case GLConstants.UNSIGNED_SHORT:
                return new Uint16Array(buffer, byteOffset, length);
            case GLConstants.INT:
                return new Int32Array(buffer, byteOffset, length);
            case GLConstants.UNSIGNED_INT:
                return new Uint32Array(buffer, byteOffset, length);
            case GLConstants.FLOAT:
                return new Float32Array(buffer, byteOffset, length);
            case GLConstants.DOUBLE:
                return new Float64Array(buffer, byteOffset, length);
            default:
                return null;
        }
    }
}
