import { ColorRGB } from "../ColorRGB";
import { Path } from "./Path";
import { Nullable } from "../../types";
import { Vector2 } from "../Vector2";
import { Shape } from "./Shape";
import { ShapeUtils } from "./ShapeUtils";
import { BinaryBuffer } from "../../Misc";

/**
 * 2D
 * @category Shape
 */
export class ShapePath {

    public color: ColorRGB;
    public subPaths: Path[];
    public currentPath: Nullable<Path>;

    /**
     * 
     */
    constructor() {
        this.color = new ColorRGB();
        this.subPaths = [];
        this.currentPath = null;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public moveTo(x: number, y: number): ShapePath {
        this.currentPath = new Path();
        this.subPaths.push(this.currentPath);
        this.currentPath.moveTo(x, y);
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public lineTo(x: number, y: number): ShapePath {
        this.currentPath?.lineTo(x, y);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param aRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     */
    public arc(aX: number, aY: number, aRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean): ShapePath {
        this.currentPath?.arc(aX, aY, aRadius, aStartAngle, aEndAngle, aClockwise);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param aRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     */
    public absarc(aX: number, aY: number, aRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean): ShapePath {
        this.currentPath?.absarc(aX, aY, aRadius, aStartAngle, aEndAngle, aClockwise);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param xRadius
     * @param yRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     * @param aRotation
     */
    public ellipse(aX: number, aY: number, xRadius: number, yRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean, aRotation: number = 0): ShapePath {
        this.currentPath?.ellipse(aX, aY, xRadius, yRadius, aStartAngle, aEndAngle, aClockwise, aRotation);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param xRadius
     * @param yRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     * @param aRotation
     */
    public absellipse(aX: number, aY: number, xRadius: number, yRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean, aRotation: number = 0): ShapePath {
        this.currentPath?.absellipse(aX, aY, xRadius, yRadius, aStartAngle, aEndAngle, aClockwise, aRotation);
        return this;
    }

    /**
     * 
     * @param aCPx
     * @param aCPy
     * @param aX
     * @param aY
     */
    public quadraticCurveTo(aCPx: number, aCPy: number, aX: number, aY: number): ShapePath {
        this.currentPath?.quadraticCurveTo(aCPx, aCPy, aX, aY);
        return this;
    }

    /**
     * 
     * @param aCP1x
     * @param aCP1y
     * @param aCP2x
     * @param aCP2y
     * @param aX
     * @param aY
     */
    public bezierCurveTo(aCP1x: number, aCP1y: number, aCP2x: number, aCP2y: number, aX: number, aY: number): ShapePath {
        this.currentPath?.bezierCurveTo(aCP1x, aCP1y, aCP2x, aCP2y, aX, aY);
        return this;
    }

    /**
     * 
     * @param pts
     */
    public splineThru(pts: Vector2[]): ShapePath {
        this.currentPath?.splineThru(pts);
        return this;
    }

    /**
     * 
     * @param isCCW
     * @param noHoles
     */
    public toShapes(isCCW: boolean, noHoles: boolean) {

        function toShapesNoHoles(inSubpaths: Path[]) {
            let shapes = [];

            for (let i = 0, l = inSubpaths.length; i < l; i++) {
                let tmpPath = inSubpaths[i];

                let tmpShape = new Shape();
                tmpShape.curves = tmpPath.curves;

                shapes.push(tmpShape);
            }

            return shapes;
        }

        function isPointInsidePolygon(inPt: Vector2, inPolygon: Vector2[]) {

            let polyLen = inPolygon.length;

            let inside = false;
            for (let p = polyLen - 1, q = 0; q < polyLen; p = q++) {
                let edgeLowPt = inPolygon[p];
                let edgeHighPt = inPolygon[q];

                let edgeDx = edgeHighPt.x - edgeLowPt.x;
                let edgeDy = edgeHighPt.y - edgeLowPt.y;

                if (Math.abs(edgeDy) > Number.EPSILON) {
                    // not parallel
                    if (edgeDy < 0) {
                        edgeLowPt = inPolygon[q]; edgeDx = -edgeDx;
                        edgeHighPt = inPolygon[p]; edgeDy = -edgeDy;
                    }
                    if ((inPt.y < edgeLowPt.y) || (inPt.y > edgeHighPt.y)) {
                        continue;
                    }

                    if (inPt.y == edgeLowPt.y) {
                        if (inPt.x == edgeLowPt.x) {
                            return true;
                        }		// inPt is on contour ?
                    } else {
                        let perpEdge = edgeDy * (inPt.x - edgeLowPt.x) - edgeDx * (inPt.y - edgeLowPt.y);
                        if (perpEdge == 0) {
                            return true;
                        }		// inPt is on contour ?

                        if (perpEdge < 0) {
                            continue;
                        }
                        inside = !inside;		// true intersection left of inPt
                    }
                } else {
                    // parallel or collinear
                    if (inPt.y != edgeLowPt.y) {
                        continue;
                    }			// parallel

                    // edge lies on the same horizontal line as inPt
                    if (((edgeHighPt.x <= inPt.x) && (inPt.x <= edgeLowPt.x)) || ((edgeLowPt.x <= inPt.x) && (inPt.x <= edgeHighPt.x))) {
                        return true;
                    }	// inPt: Point on contour !
                }
            }

            return	inside;
        }

        let isClockWise = ShapeUtils.isClockWise;

        let subPaths = this.subPaths;
        if (subPaths.length == 0) {
            return [];
        }

        if (noHoles) {
            return	toShapesNoHoles(subPaths);
        }

        let solid; let tmpPath; let tmpShape; let shapes = [];

        if (subPaths.length == 1) {
            tmpPath = subPaths[0];
            tmpShape = new Shape();
            tmpShape.curves = tmpPath.curves;
            shapes.push(tmpShape);
            return shapes;
        }

        let holesFirst = !isClockWise(subPaths[0].getPoints());
        holesFirst = isCCW ? !holesFirst : holesFirst;

        let betterShapeHoles = [];
        let newShapes = [];
        let newShapeHoles = [];
        let mainIdx = 0;
        let tmpPoints;

        newShapes[mainIdx] = undefined;
        newShapeHoles[mainIdx] = [];

        for (let i = 0, l = subPaths.length; i < l; i++) {
            tmpPath = subPaths[i];
            tmpPoints = tmpPath.getPoints();
            solid = isClockWise(tmpPoints);
            solid = isCCW ? !solid : solid;

            if (solid) {
                if ((!holesFirst) && (newShapes[mainIdx]))	{ mainIdx++; }

                newShapes[mainIdx] = {
                    s: new Shape(),
                    p: tmpPoints
                };

                // @ts-ignore
                newShapes[mainIdx].s.curves = tmpPath.curves;

                if (holesFirst)	{ mainIdx++; }
                newShapeHoles[mainIdx] = [];

            } else {
                newShapeHoles[mainIdx].push({
                    // @ts-ignore
                    h: tmpPath,
                    // @ts-ignore
                    p: tmpPoints[0]
                });
            }
        }

        // only Holes? -> probably all Shapes with wrong orientation
        if (!newShapes[0])	{
            return toShapesNoHoles(subPaths);
        }

        if (newShapes.length > 1) {
            let ambiguous = false;
            let toChange = [];

            for (let sIdx = 0, sLen = newShapes.length; sIdx < sLen; sIdx++) {
                betterShapeHoles[sIdx] = [];
            }

            for (let sIdx = 0, sLen = newShapes.length; sIdx < sLen; sIdx++) {
                let sho = newShapeHoles[sIdx];

                for (let hIdx = 0; hIdx < sho.length; hIdx++) {
                    let ho = sho[hIdx];
                    let hole_unassigned = true;

                    for (let s2Idx = 0; s2Idx < newShapes.length; s2Idx++) {
                        // @ts-ignore
                        if (isPointInsidePolygon(ho.p, newShapes[s2Idx].p)) {
                            if (sIdx != s2Idx)	{
                                toChange.push({
                                    froms: sIdx,
                                    tos: s2Idx,
                                    hole: hIdx
                                });
                            }
                            if (hole_unassigned) {
                                hole_unassigned = false;
                                betterShapeHoles[s2Idx].push(ho);
                            } else {
                                ambiguous = true;
                            }
                        }
                    }
                    if (hole_unassigned) {
                        betterShapeHoles[sIdx].push(ho);
                    }
                }
            }
            if (toChange.length > 0) {
                if (!ambiguous)	{ newShapeHoles = betterShapeHoles; }
            }
        }

        let tmpHoles;

        for (let i = 0, il = newShapes.length; i < il; i++) {
            // @ts-ignore
            tmpShape = newShapes[i].s;
            shapes.push(tmpShape);
            tmpHoles = newShapeHoles[i];

            for (let j = 0, jl = tmpHoles.length; j < jl; j++) {
                // @ts-ignore
                tmpShape.holes.push(tmpHoles[j].h);
            }
        }

        return shapes;
    }

    /**
     * JSON
     */
    public toJSON(): any {

        const data = {
            subPaths: new Array<any>()
        };

        for (let i = 0, l = this.subPaths.length; i < l; i++) {
            const path = this.subPaths[i];
            data.subPaths.push(path.toJSON());
        }

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): ShapePath {

        this.subPaths = [];

        for (let i = 0, l = json.subPaths.length; i < l; i++) {
            const path = json.subPaths[i];
            this.subPaths.push(new Path().fromJSON(path));
            this.currentPath = this.subPaths[this.subPaths.length - 1];
        }

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): ShapePath {

        dataBuffer.writeUint16(this.subPaths.length);

        this.subPaths.forEach((path) => {
            path.toDataBuffer(dataBuffer);
        });
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): ShapePath {

        const count = dataBuffer.readUint16();
        for (let i = 0; i < count; ++i) {
            const path = new Path().fromDataBuffer(dataBuffer);
            this.subPaths.push(path);
            this.currentPath = this.subPaths[this.subPaths.length - 1];
        }

        return this;
    }
}
