import { Renderable } from "./Renderable";
import { Geometry } from "./Geometries/Geometry";
import { Material } from "../Materials/Material";
import { MeshBasicMaterial } from "../Materials/MeshBasicMaterial";
import { Scene } from "./Scene";
import { Nullable } from "../types";
import { AbstractObject } from "./AbstractObject";
import { Box3, Sphere } from "../Math";

/**
 * 
 */
export class Mesh extends Renderable {

    public _geometry: Geometry;
    public _material: Material;

    /**
     * 
     * @param scene
     * @param parent
     * @param name
     * @param geometry
     * @param material
     */
    constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '', geometry: Geometry = new Geometry(), material: Material = new MeshBasicMaterial()) {
        super(scene, parent, name);

        this.geometry = geometry;
        this.material = material;

        this.scene.addMesh(this);
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        this._geometry.dispose();
        this._material.dispose();
    }

    /**
     * 
     */
    public get material() {
        return this._material;
    }
    public set material(value) {
        if (this._material != value) {
            this._material.dispose();
            this._material = value;
            this._material.addReference();
        }
    }

    /**
     * 
     */
    public get geometry() {
        return this._geometry;
    }
    public set geometry(value) {
        if (this._geometry != value) {
            this._geometry.dispose();
            this._geometry = value;
            this._geometry.addReference();
        }
    }

    /**
     * 
     */
    public get boundingSphere(): Sphere {
        if (!this._geometry.boundingSphere) {
            this._geometry.computeBoundingSphere();
        }
        return this._geometry.boundingSphere!;
    }

    /**
     * 
     */
    public get boundingBox(): Box3 {
        if (!this._geometry.boundingBox) {
            this._geometry.computeBoundingBox();
        }
        return this._geometry.boundingBox!;
    }

    /**
     * 
     */
    public clone(): Mesh {
        return new Mesh(this._scene, this._parent).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: Mesh): Mesh {
        return this;
    }

    /**
     * 
     * @param enableAlphaMode
     */
    public render(enableAlphaMode: boolean): Mesh {
        return this;
    }
}
