import { Texture } from "./Texture";
import { Nullable } from "../../types";

/**
 * 
 */
export class TextureCube extends Texture {

    public buffers: HTMLImageElement[] | ImageBitmap[] = [];
    public deleteBuffer: boolean = false;
    public width: number;
    public height: number;
    public mipmaps: HTMLImageElement[][] | ImageBitmap[][];

    /**
     * 
     * @param buffers
     * @param deleteBuffer
     * @param generateMipmaps
     * @param flipY
     * @param format
     * @param wrapU
     * @param wrapV
     */
    constructor(buffers: Nullable<HTMLImageElement[] | ImageBitmap[]> = null, deleteBuffer: boolean = false, generateMipmaps: boolean = false, flipY: boolean = true,
                format?: number, wrapU?: number, wrapV?: number) {
        super();

        this.deleteBuffer = deleteBuffer;
        this.generateMipmaps = generateMipmaps;

        if (format) {
            this.format = format;
        }

        this._flipY = flipY;

        if (wrapU) {
            this._wrapU = wrapU;
        }
        if (wrapV) {
            this._wrapV = wrapV;
        }

        if (buffers && buffers.length > 0) {
            this.buffers = buffers;
            this.width = buffers[0].width;
            this.height = buffers[0].height;
            this.unpackAlignment = (this.width % 4 != 0) ? 1 : 4;
        }
    }

    /**
     * 
     */
    public get isReady(): boolean {
        return this.buffers.length > 0 && this.buffers[0].width > 0 && this.buffers[0].height > 0;
    }

    /**
     * Buffer
     */
    public updateBuffer(buffers: HTMLImageElement[] | ImageBitmap[]): void {
        this.buffers = buffers;
        this.width = buffers[0].width;
        this.height = buffers[0].height;
        this.unpackAlignment = (this.width % 4 != 0) ? 1 : 4;
        this.needsUpdate = true;
    }

    /**
     * Mipmaps
     */
    public updateMipmaps(buffers: HTMLImageElement[][] | ImageBitmap[][]): void {
        this.mipmaps = buffers;
        this.needsUpdate = true;
    }

    /**
     * 
     */
    public clone(): TextureCube {
        return new TextureCube(this.buffers, this.deleteBuffer).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TextureCube): TextureCube {
        super.copy(source);

        return this;
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        if (this.deleteBuffer) {
            this.buffers.forEach((buffer: any, index: number) => {
                delete this.buffers[index];
            });
            this.buffers.length = 0;
        }
    }
}
