import { Curve } from "./Curve";
import { Vector2 } from "../Vector2";
import { Vector3 } from "../Vector3";
import { EllipseCurve } from "./EllipseCurve";
import { BinaryBuffer } from "../../Misc/BinaryBuffer";
import { LineCurve } from "./LineCurve";
import { SplineCurve } from "./SplineCurve";
import { LineCurve3 } from "./LineCurve3";
import { createCurve } from "./Curves";

/**
 * 
 * @category Curve
 */
export class CurvePath<T = Vector2 | Vector3> extends Curve<T> {

    public curves: Curve<T>[];
    public autoClose: boolean;
    protected cacheLengths: number[];

    constructor() {
        super();

        this.type = 'CurvePath';
        this.curves = [];
        this.autoClose = false;
    }

    public isEmpty(): boolean {
        return this.curves.length <= 0;
    }

    /**
     * 
     * @param curve
     */
    public add(curve: Curve<T>): CurvePath<T> {
        this.curves.push(curve);
        return this;
    }

    /**
     * 
     */
    public closePath(): CurvePath<T> {

        const startPoint = this.curves[0].getPoint(0);
        const endPoint = this.curves[this.curves.length - 1].getPoint(1);

        if (!(startPoint as any).equals(endPoint)) {

            if (startPoint instanceof Vector2 && endPoint instanceof Vector2) {
                this.curves.push(new LineCurve(endPoint, startPoint) as any);
            }
            else if (startPoint instanceof Vector3 && endPoint instanceof Vector3) {
                this.curves.push(new LineCurve3(endPoint, startPoint) as any);
            }
        }
        return this;
    }

    /**
     * 
     */
    public getLength(): number {
        const lens = this.getCurveLengths();
        return lens[lens.length - 1];
    }

    /**
     * 
     */
    public updateArcLengths(): number[] {
        this.needsUpdate = true;
        this.cacheLengths.length = 0;
        return this.getCurveLengths();
    }

    /**
     * 
     */
    public getCurveLengths(): number[] {
        // We use cache values if curves and cache array are same length

        if (this.cacheLengths && this.cacheLengths.length == this.curves.length) {
            return this.cacheLengths;
        }

        // Get length of sub-curve
        // Push sums into cached array

        let lengths = [];
        let sums = 0;

        for (let i = 0, l = this.curves.length; i < l; i++) {
            sums += this.curves[i].getLength();
            lengths.push(sums);
        }

        this.cacheLengths = lengths;

        return lengths;
    }

    /**
     * 
     * @param divisions
     */
    public getSpacedPoints(divisions: number = 40): T[] {

        let points = [];
        for (let i = 0; i <= divisions; i++) {
            points.push(this.getPoint(i / divisions));
        }

        if (this.autoClose) {
            points.push(points[0]);
        }

        return points;
    }

    /**
     * 
     * @param divisions
     */
    public getPoints(divisions: number = 12): T[] {

        let points = [];
        let last;

        for (let i = 0, curves = this.curves; i < curves.length; i++) {
            let curve = curves[i];
            let resolution = (curve instanceof EllipseCurve) ? divisions * 2
                : (curve && (curve instanceof LineCurve || curve instanceof LineCurve3)) ? 1
                    : (curve && curve instanceof SplineCurve) ? divisions * curve.points.length
                        : divisions;

            let pts = curve.getPoints(resolution);

            for (let j = 0; j < pts.length; j++) {
                let point = pts[j];

                if (last && (last as any).equals(point)) { continue; } // ensures no consecutive points are duplicates

                points.push(point);
                last = point;
            }
        }

        if (this.autoClose && points.length > 1 && !((points[points.length - 1] as any).equals(points[0]))) {
            points.push(points[0]);
        }

        return points;
    }

    /**
     * 
     */
    public clone(): CurvePath<T> {
        return new CurvePath<T>().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: CurvePath<T>): CurvePath<T> {
        super.copy(source);

        this.curves = [];

        for (let i = 0, l = source.curves.length; i < l; i++) {
            let curve = source.curves[i];
            this.curves.push(curve.clone());
        }

        this.autoClose = source.autoClose;
        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();
        data.autoClose = this.autoClose;
        data.curves = [];

        for (let i = 0, l = this.curves.length; i < l; i++) {
            let curve = this.curves[i];
            data.curves.push(curve.toJSON());
        }
        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): CurvePath<T> {
        super.fromJSON(json);

        this.autoClose = json.autoClose;
        this.curves = [];

        for (let i = 0, l = json.curves.length; i < l; i++) {
            let curve = json.curves[i];
            let obj = createCurve(curve.type);
            this.curves.push(obj.fromJSON(curve));
        }

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): CurvePath<T> {
        super.toDataBuffer(dataBuffer);

        dataBuffer.writeBoolean(this.autoClose);

        dataBuffer.writeUint16(this.curves.length);

        for (let i = 0, l = this.curves.length; i < l; i++) {
            const curve = this.curves[i];
            curve.toDataBuffer(dataBuffer);
        }

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): CurvePath<T> {
        super.fromDataBuffer(dataBuffer);

        this.autoClose = dataBuffer.readBoolean();

        const count = dataBuffer.readUint16();
        for (let i = 0; i < count; ++i) {
            const type = dataBuffer.readString();
            let obj = createCurve(type);
            this.curves.push(obj.fromDataBuffer(dataBuffer));
        }

        return this;
    }
}
