    /**
     * 
     * @param matrix4
     */
    public getInverse(matrix4: InMatrix4): Matrix4 {

        // based on http://www.euclideanspace.com/maths/algebra/matrix/functions/inverse/fourD/index.htm
        const te = this.elements;
        const me = matrix4.elements;

        const n11 = me[0]; const n21 = me[1]; const n31 = me[2]; const n41 = me[3];
        const n12 = me[4]; const n22 = me[5]; const n32 = me[6]; const n42 = me[7];
        const n13 = me[8]; const n23 = me[9]; const n33 = me[10]; const n43 = me[11];
        const n14 = me[12]; const n24 = me[13]; const n34 = me[14]; const n44 = me[15];

        const t11 = n23 * n34 * n42 - n24 * n33 * n42 + n24 * n32 * n43 - n22 * n34 * n43 - n23 * n32 * n44 + n22 * n33 * n44;
        const t12 = n14 * n33 * n42 - n13 * n34 * n42 - n14 * n32 * n43 + n12 * n34 * n43 + n13 * n32 * n44 - n12 * n33 * n44;
        const t13 = n13 * n24 * n42 - n14 * n23 * n42 + n14 * n22 * n43 - n12 * n24 * n43 - n13 * n22 * n44 + n12 * n23 * n44;
        const t14 = n14 * n23 * n32 - n13 * n24 * n32 - n14 * n22 * n33 + n12 * n24 * n33 + n13 * n22 * n34 - n12 * n23 * n34;

        const det = n11 * t11 + n21 * t12 + n31 * t13 + n41 * t14;

        if (det == 0) {
            return this.identity();
        }

        const detInv = 1 / det;

        te[0] = t11 * detInv;
        te[1] = (n24 * n33 * n41 - n23 * n34 * n41 - n24 * n31 * n43 + n21 * n34 * n43 + n23 * n31 * n44 - n21 * n33 * n44) * detInv;
        te[2] = (n22 * n34 * n41 - n24 * n32 * n41 + n24 * n31 * n42 - n21 * n34 * n42 - n22 * n31 * n44 + n21 * n32 * n44) * detInv;
        te[3] = (n23 * n32 * n41 - n22 * n33 * n41 - n23 * n31 * n42 + n21 * n33 * n42 + n22 * n31 * n43 - n21 * n32 * n43) * detInv;

        te[4] = t12 * detInv;
        te[5] = (n13 * n34 * n41 - n14 * n33 * n41 + n14 * n31 * n43 - n11 * n34 * n43 - n13 * n31 * n44 + n11 * n33 * n44) * detInv;
        te[6] = (n14 * n32 * n41 - n12 * n34 * n41 - n14 * n31 * n42 + n11 * n34 * n42 + n12 * n31 * n44 - n11 * n32 * n44) * detInv;
        te[7] = (n12 * n33 * n41 - n13 * n32 * n41 + n13 * n31 * n42 - n11 * n33 * n42 - n12 * n31 * n43 + n11 * n32 * n43) * detInv;

        te[8] = t13 * detInv;
        te[9] = (n14 * n23 * n41 - n13 * n24 * n41 - n14 * n21 * n43 + n11 * n24 * n43 + n13 * n21 * n44 - n11 * n23 * n44) * detInv;
        te[10] = (n12 * n24 * n41 - n14 * n22 * n41 + n14 * n21 * n42 - n11 * n24 * n42 - n12 * n21 * n44 + n11 * n22 * n44) * detInv;
        te[11] = (n13 * n22 * n41 - n12 * n23 * n41 - n13 * n21 * n42 + n11 * n23 * n42 + n12 * n21 * n43 - n11 * n22 * n43) * detInv;

        te[12] = t14 * detInv;
        te[13] = (n13 * n24 * n31 - n14 * n23 * n31 + n14 * n21 * n33 - n11 * n24 * n33 - n13 * n21 * n34 + n11 * n23 * n34) * detInv;
        te[14] = (n14 * n22 * n31 - n12 * n24 * n31 - n14 * n21 * n32 + n11 * n24 * n32 + n12 * n21 * n34 - n11 * n22 * n34) * detInv;
        te[15] = (n12 * n23 * n31 - n13 * n22 * n31 + n13 * n21 * n32 - n11 * n23 * n32 - n12 * n21 * n33 + n11 * n22 * n33) * detInv;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     * @param z
     */
    public translateXYZ(x: number, y: number, z: number): Matrix4 {
        const te = this.elements;
        const px = x;
        const py = y;
        const pz = z;

        te[12] += px;
        te[13] += py;
        te[14] += pz;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public translateVector(vec: InVector3): Matrix4 {
        return this.translateXYZ(vec.x, vec.y, vec.z);
    }

    /**
     * 
     * @param x
     * @param y
     * @param z
     */
    public scaleXYZ(x: number, y: number, z: number): Matrix4 {
        const te = this.elements;
        const px = x;
        const py = y;
        const pz = z;

        te[0] *= px; te[4] *= py; te[8] *= pz;
        te[1] *= px; te[5] *= py; te[9] *= pz;
        te[2] *= px; te[6] *= py; te[10] *= pz;
        te[3] *= px; te[7] *= py; te[11] *= pz;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public scaleVector(vec: InVector3): Matrix4 {
        return this.scaleXYZ(vec.x, vec.y, vec.z);
    }

    /**
     * 3
     */
    public getMaxScaleOnAxis(): number {

        const te = this.elements;
        const scaleXSq = te[0] * te[0] + te[1] * te[1] + te[2] * te[2];
        const scaleYSq = te[4] * te[4] + te[5] * te[5] + te[6] * te[6];
        const scaleZSq = te[8] * te[8] + te[9] * te[9] + te[10] * te[10];
        return Math.sqrt(Math.max(scaleXSq, scaleYSq, scaleZSq));
    }

    /**
     * 
     * @param x
     * @param y
     * @param z
     */
    public makeTranslationXYZ(x: number, y: number, z: number): Matrix4 {
        this.set(
            1, 0, 0, x,
            0, 1, 0, y,
            0, 0, 1, z,
            0, 0, 0, 1,
        );
        return this;
    }

