import { MathUtils } from "./MathUtils";
import { Nullable } from "../types";

/**
 * HeadingPitchRang
 * @category Math
 */
export class HeadingPitchRang {

    public heading: number;
    public pitch: number;
    public range: number;

    /**
     * 
     * @param heading
     * @param pitch
     * @param range
     */
    constructor(heading: number = 0, pitch: number = 0, range: number = 0) {
        this.heading = heading;
        this.pitch = pitch;
        this.range = range;
    }

    /**
     * 
     * @param source
     */
    public copy(source: HeadingPitchRang): HeadingPitchRang {
        this.heading = source.heading;
        this.pitch = source.pitch;
        this.range = source.range;
        return this;
    }

    /**
     * 
     */
    public clone(): HeadingPitchRang {
        return new HeadingPitchRang(this.heading, this.pitch, this.range);
    }

    /**
     * 
     * @param right
     */
    public equals(right: HeadingPitchRang): boolean {
        return (this.heading == right.heading) && (this.pitch == right.pitch) && (this.range == right.range);
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: HeadingPitchRang, relativeEpsilon: number, absoluteEpsilon: Nullable<number>) {
        return MathUtils.equalsEpsilon(this.heading, right.heading, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this.pitch, right.pitch, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this.range, right.range, relativeEpsilon, absoluteEpsilon);
    }
}
