import { IOBuffer } from "../Libs";
import { ColorRGB } from "../Math/ColorRGB";
import { ColorRGBA } from "../Math/ColorRGBA";
import { Vector2 } from "../Math/Vector2";
import { Vector3 } from "../Math/Vector3";
import { Vector4 } from "../Math/Vector4";
import { Matrix3 } from "../Math/Matrix3";
import { Matrix4 } from "../Math/Matrix4";
import { Quaternion } from "../Math/Quaternion";
import { Euler } from "../Math/Euler";
import { Box2 } from "../Math/Box2";
import { Box3 } from "../Math/Box3";
import { int } from "../types";
import { encode } from "../Libs/Utf8";

/**
 * 
 * @category Misc
 */
export class BinaryBuffer extends IOBuffer {
    private _growCount: number;

    /**
     * 
     * @param data
     */
    constructor(data: IOBuffer | ArrayBuffer | Float32Array | number = 1024 * 100) {
        super(data);

        this._growCount = 0;
    }

    /**
     * 
     */
    public readChars(): string {
        const length = this.readUint32();
        if (length > 0) {
            return super.readChars(length);
        }
        return '';
    }

    /**
     * UTF8
     */
    public readString(): string {
        const length = this.readUint32();
        if (length > 0) {
            return this.readUtf8(length);
        }
        return '';
    }

    /**
     * ColorRGB
     * @param obj
     */
    public readColorRGB(obj: ColorRGB = new ColorRGB()): ColorRGB {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * ColorRGBA
     * @param obj
     */
    public readColorRGBA(obj: ColorRGBA = new ColorRGBA()): ColorRGBA {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Vector2
     * @param obj
     */
    public readVector2(obj: Vector2 = new Vector2()): Vector2 {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Vector3
     * @param obj
     */
    public readVector3(obj: Vector3 = new Vector3()): Vector3 {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Vector4
     * @param obj
     */
    public readVector4(obj: Vector4 = new Vector4()): Vector4 {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Matrix3
     * @param obj
     */
    public readMatrix3(obj: Matrix3 = new Matrix3()): Matrix3 {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Matrix4
     * @param obj
     */
    public readMatrix4(obj: Matrix4 = new Matrix4()): Matrix4 {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Quaternion
     * @param obj
     */
    public readQuaternion(obj: Quaternion = new Quaternion()): Quaternion {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * Euler
     * @param obj
     */
    public readEuler(obj: Euler = new Euler()): Euler {
        obj.fromDataBuffer(this);
        return obj;
    }

    /**
     * dBox2
     * @param obj
     */
    public readBox2(obj: Box2 = new Box2()): Box2 {
        obj.min.fromDataBuffer(this);
        obj.max.fromDataBuffer(this);
        return obj;
    }

    /**
     * dBox3
     * @param obj
     */
    public readBox3(obj: Box3 = new Box3()): Box3 {
        obj.min.fromDataBuffer(this);
        obj.max.fromDataBuffer(this);
        return obj;
    }

    /**
     * , 0xff0x001
     */
    public writeBoolean(value: boolean): IOBuffer {
        this._growBuffer(1);
        return super.writeBoolean(value);
    }

    /**
     * 81
     */
    public writeInt8(value: int): IOBuffer {
        this._growBuffer(1);
        return super.writeInt8(value);
    }

    /**
     * 81
     */
    public writeUint8(value: int): IOBuffer {
        this._growBuffer(1);
        return super.writeUint8(value);
    }

    /**
     * 1
     * @param value
     */
    public writeByte(value: int): IOBuffer {
        this._growBuffer(1);
        return super.writeByte(value);
    }

    /**
     * nm
     */
    public writeBytes(bytes: int[]): IOBuffer {
        this._growBuffer(bytes.length);
        return super.writeBytes(bytes);
    }

    /**
     * 162
     */
    public writeInt16(value: int): IOBuffer {
        this._growBuffer(2);
        return super.writeInt16(value);
    }

    /**
     * 162
     */
    public writeUint16(value: int): IOBuffer {
        this._growBuffer(2);
        return super.writeUint16(value);
    }

    /**
     * 324
     */
    public writeInt32(value: int): IOBuffer {
        this._growBuffer(4);
        return super.writeInt32(value);
    }

    /**
     * 324
     * @param value
     */
    public writeUint32(value: int): IOBuffer {
        this._growBuffer(4);
        return super.writeUint32(value);
    }

    /**
     * 324
     */
    public writeFloat32(value: number): IOBuffer {
        this._growBuffer(4);
        return super.writeFloat32(value);
    }

    /**
     * 648
     */
    public writeFloat64(value: number): IOBuffer {
        this._growBuffer(8);
        return super.writeFloat64(value);
    }

    /**
     * 1
     */
    public writeChar(str: string): IOBuffer {
        this._growBuffer(1);
        return super.writeChar(str);
    }

    /**
     * nn
     */
    public writeChars(str: string): IOBuffer {
        this._growBuffer(str.length);
        this.writeUint32(str.length);
        return super.writeChars(str);
    }

    /**
     * UTF8
     * @param str
     */
    public writeString(str: string): IOBuffer {
        const utf8 = encode(str);
        this._growBuffer(utf8.length);
        this.writeUint32(utf8.length);
        this.writeChars(utf8);
        return this;
    }

    /**
     * ColorRGB
     * @param obj
     */
    public writeColorRGB(obj: ColorRGB): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * ColorRGBA
     * @param obj
     */
    public writeColorRGBA(obj: ColorRGBA): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Vector2
     * @param obj
     */
    public writeVector2(obj: Vector2): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Vector3
     * @param obj
     */
    public writeVector3(obj: Vector3): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Vector4
     * @param obj
     */
    public writeVector4(obj: Vector4): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Matrix3
     * @param obj
     */
    public writeMatrix3(obj: Matrix3): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Matrix4
     * @param obj
     */
    public writeMatrix4(obj: Matrix4): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Quaternion
     * @param obj
     */
    public writeQuaternion(obj: Quaternion): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Euler
     * @param obj
     */
    public writeEuler(obj: Euler): IOBuffer {
        obj.toDataBuffer(this);
        return this;
    }

    /**
     * Box2
     * @param obj
     */
    public writeBox2(obj: Box2): IOBuffer {
        obj.min.toDataBuffer(this);
        obj.max.toDataBuffer(this);
        return this;
    }

    /**
     * Box3
     * @param obj
     */
    public writeBox3(obj: Box3): IOBuffer {
        obj.min.toDataBuffer(this);
        obj.max.toDataBuffer(this);
        return this;
    }

    /**
     * 
     * @param byteLength
     * @private
     */
    private _growBuffer(byteLength: number): void {
        if (!this.available(byteLength)) {
            this._growCount++;
            const grow = Math.max(1024 * 100 * this._growCount, byteLength);
            this.ensureAvailable(((byteLength < grow) ? grow : byteLength * 2));
        }
    }
}
