import { TransformObject } from "./TransformObject";
import { int, Nullable } from "../types";
import { VisibleLayers } from "../Misc/VisibleLayers";
import { Scene } from "./Scene";
import { AbstractObject } from "./AbstractObject";
import { Light } from "../Lights";
import { RenderingGroup } from "./Rendering/RenderingGroup";

/**
 * 
 */
export abstract class Renderable extends TransformObject {

    /**
     * @ignore
     */
    public _classifyID: int = 0;
    /**
     * @ignore
     */
    public _castShadow: boolean = false;
    /**
     * @ignore
     */
    public _receiveShadow: boolean = false;
    /**
     * @ignore
     */
    public _frustumCulled: boolean = true;
    /**
     * @ignore
     */
    public _renderOrder: int = 0;
    /**
     * @ignore
     */
    public _pickable: boolean = true;

    /**
     * @ignore
     */
    protected _lightLayers: VisibleLayers = new VisibleLayers();

    /**
     * @ignore
     */
    public _lightSources: Light[] = new Array<Light>();

    /**
     * @ignore
     */
    public _renderingGroup: Nullable<RenderingGroup> = null;

    /**
     * @ignore
     */
    public _distanceToCamera: number = 0;

    /**
     * @ignore
     */
    public _renderingGroupId: number = 0;

    /**
     * 
     */
    protected constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '') {
        super(scene, parent, name, false);
    }

    /**
     * 
     */
    public get lightLayers() {
        return this._lightLayers;
    }

    /**
     * 
     */
    public get lightSources() {
        return this._lightSources;
    }

    /**
     * 
     */
    public get renderingGroupId() {
        return this._renderingGroupId;
    }
    public set renderingGroupId(value) {
        this._renderingGroupId = value;
    }

    /**
     * ID
     */
    public get classifyID() {
        return this._classifyID;
    }
    public set classifyID(value) {
        if (this._classifyID != value) {
            const oldValue = this._classifyID;
            this._classifyID = value;
            this.onClassifyIDChanged(oldValue, this._classifyID);
        }
    }

    /**
     * 
     */
    public get castShadow() {
        return this._castShadow;
    }
    public set castShadow(value) {
        if (this._castShadow != value) {
            const oldValue = this._castShadow;
            this._castShadow = value;
            this.onCastShadowChanged(oldValue, this._castShadow);
        }
    }

    /**
     * 
     */
    public get receiveShadow() {
        return this._receiveShadow;
    }
    public set receiveShadow(value) {
        if (this._receiveShadow != value) {
            const oldValue = this._receiveShadow;
            this._receiveShadow = value;
            this.onReceiveShadowChanged(oldValue, this._receiveShadow);
        }
    }

    /**
     * 
     */
    public get frustumCulled() {
        return this._frustumCulled;
    }
    public set frustumCulled(value) {
        if (this._frustumCulled != value) {
            const oldValue = this._frustumCulled;
            this._frustumCulled = value;
            this.onFrustumCulledChanged(oldValue, this._frustumCulled);
        }
    }

    /**
     * 
     */
    public get renderOrder() {
        return this._renderOrder;
    }
    public set renderOrder(value) {
        if (this._renderOrder != value) {
            const oldValue = this._renderOrder;
            this._renderOrder = value;
            this.onRenderOrderChanged(oldValue, this._renderOrder);
        }
    }

    /**
     * 
     */
    public get pickable() {
        return this._pickable;
    }
    public set pickable(value) {
        if (this._pickable != value) {
            const oldValue = this._pickable;
            this._pickable = value;
            this.onPickableChanged(oldValue, this._pickable);
        }
    }

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     */
    protected onClassifyIDChanged(oldValue: int, newValue: int): void {
    }

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     */
    protected onCastShadowChanged(oldValue: boolean, newValue: boolean): void {
    }

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     */
    protected onReceiveShadowChanged(oldValue: boolean, newValue: boolean): void {
    }

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     */
    protected onFrustumCulledChanged(oldValue: boolean, newValue: boolean): void {
    }

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     */
    protected onRenderOrderChanged(oldValue: int, newValue: int): void {
    }

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     */
    protected onPickableChanged(oldValue: boolean, newValue: boolean): void {
    }

    /**
     * 
     * @ignore
     */
    public _resyncLightSources(): void {

        this._lightSources.length = 0;

        for (const light of this.getScene().lights) {
            if (!light.visible) {
                continue;
            }

            if (light.canAffectMesh(this)) {
                this._lightSources.push(light);
            }
        }
    }

    /**
     * 
     * @param light
     * @ignore
     */
    public _resyncLightSource(light: Light): void {

        const isIn = light.visible && light.canAffectMesh(this);

        const index = this._lightSources.indexOf(light);
        let removed = false;
        if (index == -1) {
            if (!isIn) {
                return;
            }
            this._lightSources.push(light);
        } else {
            if (isIn) {
                return;
            }
            removed = true;
            this._lightSources.splice(index, 1);
        }
    }

    /**
     * 
     * @ignore
     */
    public _removeLightSource(light: Light, dispose: boolean): void {

        const index = this._lightSources.indexOf(light);
        if (index == -1) {
            return;
        }

        this._lightSources.splice(index, 1);
    }

    /**
     * 
     * @param enableAlphaMode
     */
    public render(enableAlphaMode: boolean): Renderable {
        return this;
    }
}
