import {CSG} from './CSG';
import {CSGVector2D} from './CSGVector2D';

/**
 * CAG
 */
export class CAGVertex {
    /**
     * 
     */
    public pos: CSGVector2D;

    /**
     * 
     */
    public tag: number;

    /**
     * 
     * @param pos
     */
    constructor(pos: CSGVector2D) {
        this.pos = pos;
    }

    /**
     * 
     */
    public toString(): string {
        return `(${this.pos.x.toFixed(2)},${this.pos.y.toFixed(2)})`;
    }

    /**
     * 
     */
    public getTag(): number {
        let result = this.tag;
        if (!result) {
            result = CSG.getTag();
            this.tag = result;
        }
        return result;
    }
}
