import { InVector3, Vector3 } from "./Vector3";
import { PlanePolygonIntersectType } from "../Enums/PlanePolygonIntersectType";
import { Vector4 } from "./Vector4";
import { Sphere } from "./Sphere";
import { Matrix4 } from "./Matrix4";
import { Matrix3 } from "./Matrix3";
import { Nullable } from "../types";
import { Line3 } from "./Line3";
import { Triangle } from "./Triangle";
import { OBB } from "./OBB";
import {
    PlanePlaneIntersect,
    PlanePolygonIntersect,
    PlaneSphereIntersect,
    PlaneTriangleIntersect
} from "./InterfaceType/IntersectResult";

/**
 * @ignore
 */
const _tempVec1 = new Vector3();
/**
 * @ignore
 */
const _tempVec2 = new Vector3();
/**
 * @ignore
 */
const _normalMatrix = new Matrix3();

/**
 * 
 * @category Math
 */
export class Plane {

    /**
     * 
     */
    public normal: Vector3 = new Vector3();
    /**
     * 
     */
    public constant: number = 0;

    /**
     * 
     * @param normal
     * @param constant
     */
    constructor(normal: InVector3 = Vector3.UNIT_X, constant = 0) {
        this.normal.copy(normal);
        this.constant = constant;
    }

    /**
     * 
     * @param normal
     * @param constant
     */
    public set(normal: Vector3, constant: number): Plane {
        this.normal.copy(normal);
        this.constant = constant;
        return this;
    }

    /**
     * XYZW
     * @param x
     * @param y
     * @param z
     * @param w
     */
    public setFromXYZW(x: number, y: number , z: number, w: number): Plane {
        this.normal.set(x, y, z);
        this.constant = w;
        return this;
    }

    /**
     * Vector4
     * @param vec4
     */
    public setFromVector4(vec4: Vector4): Plane {
        return this.setFromXYZW(vec4.x, vec4.y, vec4.z, vec4.w);
    }

    /**
     * 
     * @param normal
     * @param point
     */
    public setFromNormalAndCoplanarPoint(normal: Vector3, point: Vector3): Plane {
        this.normal.copy(normal);
        this.constant = point.dot(this.normal);
        return this;
    }

    /**
     * 3
     * @param a
     * @param b
     * @param c
     */
    public setFromCoplanarPoints(a: Vector3, b: Vector3, c: Vector3): Plane {

        const normal = _tempVec1.subVectors(c, b).cross(_tempVec2.subVectors(a, b)).normalize();
        this.setFromNormalAndCoplanarPoint(normal, a);
        return this;
    }

    /**
     * 
     */
    public clone(): Plane {
        return new Plane().copy(this);
    }

    /**
     * 
     * @param plane
     */
    public copy(plane: Plane): Plane {
        this.normal.copy(plane.normal);
        this.constant = plane.constant;
        return this;
    }

    /**
     * 
     */
    public normalize(): Plane {

        const inverseNormalLength = 1.0 / this.normal.length();
        this.normal.multiplyScalar(inverseNormalLength);
        this.constant *= inverseNormalLength;
        return this;
    }

    /**
     * 
     */
    public negate(): Plane {
        this.constant *= -1;
        this.normal.negate();
        return this;
    }

    /**
     * ()
     * @param point
     * @param target
     */
    public projectPoint(point: Vector3, target: Vector3 = new Vector3()): Vector3 {
        return target.copy(this.normal).multiplyScalar(-this.distanceToPoint(point)).add(point);
    }

    /**
     * 
     * @param target
     */
    public coplanarPoint(target = new Vector3()): Vector3 {
        return target.copy(this.normal).multiplyScalar(this.constant);
    }

    /**
     * Matrix4
     * @param matrix
     * @param normalMatrix
     */
    public applyMatrix4(matrix: Matrix4, normalMatrix: Nullable<Matrix3> = null): Plane {

        normalMatrix = normalMatrix || _normalMatrix.getNormalMatrix(matrix);
        const referencePoint = this.coplanarPoint(_tempVec1).applyMatrix4(matrix);
        const normal = this.normal.applyMatrix3(normalMatrix).normalize();
        this.constant = referencePoint.dot(normal);
        return this;
    }

    /**
     * 
     * @param offset
     */
    public translate(offset: Vector3) {
        this.constant += offset.dot(this.normal);
        return this;
    }

    /**
     * 
     * @param point
     */
    public distanceToPoint(point: Vector3): number {
        return this.normal.dot(point) - this.constant;
    }

    /**
     * 
     * @param sphere
     */
    public distanceToSphere(sphere: Sphere): number {
        return this.distanceToPoint(sphere.center) - sphere.radius;
    }

    /**
     * 
     * @param plane
     */
    public intersectsPlane(plane: Plane): boolean {

        const plane0 = this;
        const plane1 = plane;

        const dot = plane0.normal.dot(plane1.normal);
        if (Math.abs(dot) < 1) {
            return true;
        }

        let cDiff;
        if (dot >= 0) {
            cDiff = plane0.constant - plane1.constant;
        }
        else {
            cDiff = plane0.constant + plane1.constant;
        }

        return Math.abs(cDiff) == 0;
    }

    /**
     * 
     * @param plane
     */
    public intersectPlane(plane: Plane): PlanePlaneIntersect {

        const plane0 = this;
        const plane1 = plane;

        const result = {
            intersect: false,
            isLine: false,
            line: new Line3(),
            plane: new Plane()
        };

        const dot = plane0.normal.dot(plane1.normal);
        if (Math.abs(dot) >= 1) {
            let cDiff;
            if (dot >= 0) {
                cDiff = plane0.constant - plane1.constant;
            }
            else {
                cDiff = plane0.constant + plane1.constant;
            }

            if (Math.abs(cDiff) == 0) {
                // The planes are coplanar.
                result.intersect = true;
                result.isLine = false;
                result.plane = plane0;
                return result;
            }

            // The planes are parallel but distinct.
            result.intersect = false;
            return result;
        }

        const invDet = 1 / (1 - dot * dot);
        const c0 = (plane0.constant - dot * plane1.constant) * invDet;
        const c1 = (plane1.constant - dot * plane0.constant) * invDet;

        result.intersect = true;
        result.isLine = true;
        result.line.origin.addVectors(_tempVec1.copy(plane0.normal).multiplyScalar(c0), _tempVec2.copy(plane1.normal).multiplyScalar(c1));
        result.line.direction.crossVectors(plane0.normal, plane1.normal).normalize();
        return result;
    }

    /**
     * 
     * @param polygon
     */
    public intersectsPolygon(polygon: Vector3[]): boolean {

        const numVertices = polygon.length;
        if (numVertices < 3) {
            return false;
        }

        let numPositive = 0, numNegative = 0, numZero = 0;
        for (let i = 0; i < numVertices; ++i) {
            const h = this.normal.dot(polygon[i]) - this.constant;
            if (h > 0) {
                ++numPositive;
            }
            else if (h < 0) {
                ++numNegative;
            }
            else {
                ++numZero;
            }
        }

        if (numPositive > 0) {
            if (numNegative > 0) {
               return true;
            }
            else if (numZero == 0) {
                return false;
            }
            else if (numZero == 1) {
                return true;
            }
            else {
                return true;
            }
        }
        else if (numNegative > 0) {
            if (numZero == 0) {
                return false;
            }
            else if (numZero == 1) {
                return true;
            }
            else {
                return true;
            }
        }
        else {
            return true;
        }
    }

    /**
     * 
     * @param polygon
     */
    public intersectPolygon(polygon: Vector3[]): PlanePolygonIntersect {
        const result = {
            type: PlanePolygonIntersectType.INVALID_POLYGON,
            intersection: new Array<Vector3>(),
            positivePolygon: new Array<Vector3>(),
            negativePolygon: new Array<Vector3>()
        };

        const numVertices = polygon.length;
        if (numVertices < 3) {
            return result;
        }

        const height = new Array<number>(numVertices);
        const zeroHeightIndices = [];

        let numPositive = 0, numNegative = 0;
        let maxPosHeight = -Infinity;
        let maxNegHeight = Infinity;
        let maxPosIndex = Infinity;
        let maxNegIndex = Infinity;

        for (let i = 0; i < numVertices; ++i) {
            height[i] = this.normal.dot(polygon[i]) - this.constant;
            if (height[i] > 0) {
                ++numPositive;
                if (height[i] > maxPosHeight) {
                    maxPosHeight = height[i];
                    maxPosIndex = i;
                }
            }
            else if (height[i] < 0) {
                ++numNegative;
                if (height[i] < maxNegHeight) {
                    maxNegHeight = height[i];
                    maxNegIndex = i;
                }
            }
            else {
                zeroHeightIndices.push(i);
            }
        }

        if (numPositive > 0) {
            if (numNegative > 0) {
                result.type = PlanePolygonIntersectType.SPLIT;

                const doSwap = (maxPosHeight < -maxNegHeight);
                if (doSwap) {
                    height.forEach((v, i) => {
                        height[i] = -height[i];
                    });

                    let t = maxPosIndex;
                    maxPosIndex = maxNegIndex;
                    maxNegIndex = t ;
                }

                splitPolygon(polygon, height, maxPosIndex, result);

                if (doSwap) {
                    let t = result.positivePolygon;
                    result.positivePolygon = result.negativePolygon;
                    result.negativePolygon = t ;
                }
            }
            else {
                const numZero = zeroHeightIndices.length;
                if (numZero == 0) {
                    result.type = PlanePolygonIntersectType.POSITIVE_SIDE_STRICT;
                }
                else if (numZero == 1) {
                    result.type = PlanePolygonIntersectType.POSITIVE_SIDE_VERTEX;
                    result.intersection.push(polygon[zeroHeightIndices[0]]);
                }
                else {
                    result.type = PlanePolygonIntersectType.POSITIVE_SIDE_EDGE;
                    result.intersection.push(polygon[zeroHeightIndices[0]], polygon[zeroHeightIndices[1]]);
                }
                result.positivePolygon = polygon;
            }
        }
        else if (numNegative > 0) {
            const numZero = zeroHeightIndices.length;
            if (numZero == 0) {
                result.type = PlanePolygonIntersectType.NEGATIVE_SIDE_STRICT;
            }
            else if (numZero == 1) {
                result.type = PlanePolygonIntersectType.NEGATIVE_SIDE_VERTEX;
                result.intersection.push(
                    polygon[zeroHeightIndices[0]]
                );
            }
            else {
                result.type = PlanePolygonIntersectType.NEGATIVE_SIDE_EDGE;
                result.intersection.push(
                    polygon[zeroHeightIndices[0]],
                    polygon[zeroHeightIndices[1]]
                );
            }
            result.negativePolygon = polygon;
        }
        else {
            result.type = PlanePolygonIntersectType.CONTAINED;
            result.intersection = polygon;
        }

        return result;
    }

    /**
     * 
     * @param sphere
     */
    public intersectsSphere(sphere: Sphere): boolean {
        return Math.abs(this.distanceToPoint(sphere.center)) <= sphere.radius;
    }

    /**
     * 
     * @param sphere
     */
    public intersectSphere(sphere: Sphere): PlaneSphereIntersect {

        const signedDistance = this.distanceToPoint(sphere.center);
        const distance = Math.abs(signedDistance);
        if (distance < sphere.radius) {

            const sum = sphere.radius + distance;
            const dif = sphere.radius - distance;
            const arg = sum * dif;

            const circle = new Sphere();
            circle.center.subVectors(sphere.center, _tempVec1.copy(this.normal).multiplyScalar(signedDistance));
            circle.radius = Math.sqrt(arg);

            return {
                intersect: true,
                isCircle: true,
                circle
            };
        }
        else if (distance == sphere.radius) {
            return {
                intersect: true,
                isCircle: false,
                point: new Vector3().subVectors(sphere.center, _tempVec1.copy(this.normal).multiplyScalar(signedDistance))
            };
        }
        else {
            return {
                intersect: false
            };
        }
    }

    /**
     * 
     * @param triangle
     */
    public intersectsTriangle(triangle: Triangle): boolean {

        const s = [];

        let numPositive = 0, numNegative = 0, numZero = 0;
        for (let i = 0; i < 3; ++i) {
            s[i] = this.normal.dot(triangle.p[i]) - this.constant;
            if (s[i] > 0) {
                ++numPositive;
            }
            else if (s[i] < 0) {
                ++numNegative;
            }
            else {
                ++numZero;
            }
        }

        if (numZero == 0 && numPositive > 0 && numNegative > 0) {
            return true;
        }

        if (numZero > 0) {
            return true;
        }
        return false;
    }

    /**
     * 
     * @param triangle
     */
    public intersectTriangle(triangle: Triangle): PlaneTriangleIntersect {
        const result = {
            intersect: false,
            numIntersections: 0,
            isInterior: false,
            points: new Array<Vector3>()
        };

        const s = [];

        let numPositive = 0, numNegative = 0, numZero = 0;
        for (let i = 0; i < 3; ++i) {
            s[i] = this.normal.dot(triangle.p[i]) - this.constant;
            if (s[i] > 0) {
                ++numPositive;
            }
            else if (s[i] < 0) {
                ++numNegative;
            }
            else {
                ++numZero;
            }
        }

        if (numZero == 0 && numPositive > 0 && numNegative > 0) {
            result.intersect = true;
            result.numIntersections = 2;
            result.isInterior = true;

            const sign = 3 - numPositive * 2;
            for (let i0 = 0; i0 < 3; ++i0) {
                if (sign * s[i0] > 0) {
                    let i1 = (i0 + 1) % 3, i2 = (i0 + 2) % 3;
                    let t1 = s[i1] / (s[i1] - s[i0]);
                    let t2 = s[i2] / (s[i2] - s[i0]);
                    result.points[0] = new Vector3().copy(triangle.p[i1]).add(_tempVec1.subVectors(triangle.p[i0], triangle.p[i1]).multiplyScalar(t1));
                    result.points[1] = new Vector3().copy(triangle.p[i2]).add(_tempVec2.subVectors(triangle.p[i0], triangle.p[i2]).multiplyScalar(t2));
                    break;
                }
            }
            return result;
        }

        if (numZero == 1) {
            result.intersect = true;
            for (let i0 = 0; i0 < 3; ++i0) {
                if (s[i0] == 0) {
                    let i1 = (i0 + 1) % 3, i2 = (i0 + 2) % 3;
                    result.points[0] = new Vector3().copy(triangle.p[i0]);
                    if (numPositive == 2 || numNegative == 2) {
                        result.numIntersections = 1;
                    }
                    else {
                        result.numIntersections = 2;
                        result.isInterior = true;
                        let t = s[i1] / (s[i1] - s[i2]);
                        result.points[1] = new Vector3().copy(triangle.p[i1]).add(_tempVec1.subVectors(triangle.p[i2], triangle.p[i1]).multiplyScalar(t));
                    }
                    break;
                }
            }
            return result;
        }

        if (numZero == 2) {
            result.intersect = true;
            result.numIntersections = 2;
            result.isInterior = false;
            for (let i0 = 0; i0 < 3; ++i0) {
                if (s[i0] != 0) {
                    let i1 = (i0 + 1) % 3, i2 = (i0 + 2) % 3;
                    result.points[0] = new Vector3().copy(triangle.p[i1]);
                    result.points[1] = new Vector3().copy(triangle.p[i2]);
                    break;
                }
            }
            return result;
        }

        if (numZero == 3) {
            result.intersect = true;
            result.numIntersections = 3;
            result.points[0] = new Vector3().copy(triangle.a);
            result.points[1] = new Vector3().copy(triangle.b);
            result.points[2] = new Vector3().copy(triangle.c);
        }
        else {
            result.intersect = false;
            result.numIntersections = 0;

        }
        return result;
    }

    /**
     * OBB
     * @param box
     */
    public intersectsOBB(box: OBB): boolean {

        const radius =
            Math.abs(box.extent.x * this.normal.dot(box.axis[0])) +
            Math.abs(box.extent.y * this.normal.dot(box.axis[1])) +
            Math.abs(box.extent.z * this.normal.dot(box.axis[2]));

        const distance = this.distanceToPoint(box.center);
        return Math.abs(distance) <= radius;
    }

    /**
     * 
     * @param plane
     */
    public equals(plane: Plane): boolean {
        return plane.normal.equals(this.normal) && (plane.constant == this.constant);
    }
}

/**
 * @ignore
 */
function splitPolygon(polygon: Vector3[], height: number[], maxPosIndex: number, result: PlanePolygonIntersect): void {

    const numVertices = polygon.length;
    const positiveList = [];
    positiveList.push(polygon[maxPosIndex]);
    let end0 = maxPosIndex;
    let end0prev = Infinity;
    for (let i = 0; i < numVertices; ++i) {
        end0prev = (end0 + numVertices - 1) % numVertices;
        if (height[end0prev] >= 0) {
            positiveList.splice(0, 0, polygon[end0prev]);
            end0 = end0prev;
        }
        else {
            break;
        }
    }

    let end1 = maxPosIndex;
    let end1next = Infinity;
    for (let i = 0; i < numVertices; ++i) {
        end1next = (end1 + 1) % numVertices;
        if (height[end1next] >= 0) {
            positiveList.push(polygon[end1next]);
            end1 = end1next;
        }
        else {
            break;
        }
    }

    let index = end1next;
    const negativeList = [];
    for (let i = 0; i < numVertices; ++i) {
        negativeList.push(polygon[index]);
        index = (index + 1) % numVertices;
        if (index == end0) {
            break;
        }
    }

    if (height[end0] > 0) {
        const t = -height[end0prev] / (height[end0] - height[end0prev]);
        const omt = 1 - t;
        const V = new Vector3().addVectors(_tempVec1.copy(polygon[end0prev]).multiplyScalar(omt), _tempVec2.copy(polygon[end0]).multiplyScalar(t));
        positiveList.splice(0, 0, V);
        negativeList.push(V);
        result.intersection.push(V);
    }
    else {
        negativeList.push(polygon[end0]);
        result.intersection.push(polygon[end0]);
    }

    if (height[end1] > 0) {
        const t = -height[end1next] / (height[end1] - height[end1next]);
        const omt = 1 - t;
        const V = new Vector3().addVectors(_tempVec1.copy(polygon[end1next]).multiplyScalar(omt), _tempVec2.copy(polygon[end1]).multiplyScalar(t));
        positiveList.push(V);
        negativeList.splice(0, 0, V);
        result.intersection.push(V);
    }
    else {
        negativeList.splice(0, 0, polygon[end1]);
        result.intersection.push(polygon[end1]);
    }

    result.positivePolygon = positiveList;
    result.negativePolygon = negativeList;
}
