import { TransformObject } from "../Scene/TransformObject";
import { Frustum, Matrix4 } from "../Math";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";
import { Viewport } from "../Math/Viewport";

/**
 * 
 * @category Camera
 */
export abstract class Camera extends TransformObject {

    /**
     * @ignore
     */
    private _viewport: Viewport = new Viewport(0, 0, 1, 1);
    /**
     * @ignore
     */
    protected _frustum: Frustum = new Frustum();
    /**
     * @ignore
     */
    protected _projectionMatrix: Matrix4 = new Matrix4();
    /**
     * @ignore
     */
    protected _projectionMatrixInverse: Matrix4 = new Matrix4();
    /**
     * @ignore
     */
    protected _viewProjectionMatrix: Matrix4 = new Matrix4();
    /**
     * @ignore
     */
    protected _viewProjectionMatrixInverse: Matrix4 = new Matrix4();
    /**
     * @ignore
     */
    protected _needsUpdateCameraMatrix: boolean = true;

    /**
     * 
     * @param scene
     * @param activeIfNoneActive
     * @param parent
     * @param name
     * @protected
     */
    protected constructor(scene: Scene, activeIfNoneActive: boolean = true, parent: Nullable<AbstractObject> = null, name: string = '') {
        super(scene, parent, name);

        this._visibleLayers.enableAll();

        this.scene.addCamera(this);

        if (activeIfNoneActive && !this.scene.activeCamera) {
            this.scene.activeCamera = this;
        }
    }

    /**
     * 
     */
    public get viewport() {
        return this._viewport;
    }
    public set viewport(value) {
        this._viewport = value;
    }

    public get frustum() {
        if (this._needsUpdateCameraMatrix) {
            this.updateProjectionMatrix();
        }
        return this._frustum;
    }

    /**
     * PM
     */
    public get projectionMatrix() {
        if (this._needsUpdateCameraMatrix) {
            this.updateProjectionMatrix();
        }
        return this._projectionMatrix;
    }

    /**
     * IPM
     */
    public get projectionMatrixInverse() {
        if (this._needsUpdateCameraMatrix) {
            this.updateProjectionMatrix();
        }
        return this._projectionMatrixInverse;
    }

    /**
     * VP
     */
    public get viewProjectionMatrix() {
        if (this._needsUpdateCameraMatrix) {
            this.updateProjectionMatrix();
        }
        return this._viewProjectionMatrix;
    }

    /**
     * IVP
     */
    public get viewProjectionMatrixInverse() {
        if (this._needsUpdateCameraMatrix) {
            this.updateProjectionMatrix();
        }
        return this._viewProjectionMatrixInverse;
    }

    /**
     * 
     */
    public abstract updateProjectionMatrix(): void;

    /**
     * @ignore
     * @param oldValue
     * @param newValue
     * @protected
     */
    protected onMatrixWorldChanged(oldValue: Matrix4, newValue: Matrix4) {
        super.onMatrixWorldChanged(oldValue, newValue);
        this._needsUpdateCameraMatrix = true;
    }
}
