import { MathTmp } from "../MathTmp";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { CurvePath } from "../Curves/CurvePath";
import { LineCurve } from "../Curves/LineCurve";
import { QuadraticBezierCurve } from "../Curves/QuadraticBezierCurve";
import { CubicBezierCurve } from "../Curves/CubicBezierCurve";
import { SplineCurve } from "../Curves/SplineCurve";
import { EllipseCurve } from "../Curves/EllipseCurve";
import { BinaryBuffer } from "../../Misc/BinaryBuffer";

/**
 * 2D
 * @category Shape
 */
export class Path extends CurvePath<Vector2> {

    public currentPoint: Vector2;

    /**
     * 
     * @param points
     */
    constructor(points: Nullable<Vector2[]> = null) {
        super();

        this.type = 'Path';
        this.currentPoint = new Vector2();

        if (points) { this.setFromPoints(points); }
    }

    /**
     * 
     * @param points
     */
    public setFromPoints(points: Vector2[]): Path {
        this.moveTo(points[0].x, points[0].y);

        for (let i = 1, l = points.length; i < l; i++) {
            this.lineTo(points[i].x, points[i].y);
        }
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public moveTo(x: number, y: number): Path {
        this.currentPoint.set(x, y);
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public lineTo(x: number, y: number): Path {
        const curve = new LineCurve(this.currentPoint, MathTmp.Vector2[0].set(x, y));
        this.curves.push(curve);
        this.currentPoint.set(x, y);
        return this;
    }

    /**
     * 
     * @param aCPx
     * @param aCPy
     * @param aX
     * @param aY
     */
    public quadraticCurveTo(aCPx: number, aCPy: number, aX: number, aY: number): Path {
        const curve = new QuadraticBezierCurve(
            this.currentPoint,
            MathTmp.Vector2[0].set(aCPx, aCPy),
            MathTmp.Vector2[1].set(aX, aY),
        );

        this.curves.push(curve);
        this.currentPoint.set(aX, aY);
        return this;
    }

    /**
     * 
     * @param aCP1x
     * @param aCP1y
     * @param aCP2x
     * @param aCP2y
     * @param aX
     * @param aY
     */
    public bezierCurveTo(aCP1x: number, aCP1y: number, aCP2x: number, aCP2y: number, aX: number, aY: number): Path {
        const curve = new CubicBezierCurve(
            this.currentPoint.clone(),
            new Vector2(aCP1x, aCP1y),
            new Vector2(aCP2x, aCP2y),
            new Vector2(aX, aY),
        );

        this.curves.push(curve);
        this.currentPoint.set(aX, aY);
        return this;
    }

    /**
     * 
     * @param pts
     */
    public splineThru(pts: Vector2[]): Path {
        const npts = [this.currentPoint.clone()].concat(pts);

        const curve = new SplineCurve(npts);
        this.curves.push(curve);
        this.currentPoint.copy(pts[pts.length - 1]);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param aRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     */
    public arc(aX: number, aY: number, aRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean): Path {
        const x0 = this.currentPoint.x;
        const y0 = this.currentPoint.y;

        this.absarc(aX + x0, aY + y0, aRadius, aStartAngle, aEndAngle, aClockwise);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param aRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     */
    public absarc(aX: number, aY: number, aRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean): Path {
        this.absellipse(aX, aY, aRadius, aRadius, aStartAngle, aEndAngle, aClockwise);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param xRadius
     * @param yRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     * @param aRotation
     */
    public ellipse(aX: number, aY: number, xRadius: number, yRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean, aRotation: number = 0): Path {
        const x0 = this.currentPoint.x;
        const y0 = this.currentPoint.y;

        this.absellipse(aX + x0, aY + y0, xRadius, yRadius, aStartAngle, aEndAngle, aClockwise, aRotation);
        return this;
    }

    /**
     * 
     * @param aX
     * @param aY
     * @param xRadius
     * @param yRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     * @param aRotation
     */
    public absellipse(aX: number, aY: number, xRadius: number, yRadius: number, aStartAngle: number, aEndAngle: number, aClockwise: boolean, aRotation: number = 0): Path {
        const curve = new EllipseCurve(aX, aY, xRadius, yRadius, aStartAngle, aEndAngle, aClockwise, aRotation);

        if (this.curves.length > 0) {

            const firstPoint = curve.getPoint(0);
            if (!firstPoint.equals(this.currentPoint)) {
                this.lineTo(firstPoint.x, firstPoint.y);
            }
        }

        this.curves.push(curve);

        const lastPoint = curve.getPoint(1);
        this.currentPoint.copy(lastPoint);
        return this;
    }

    /**
     * 
     * @param source
     */
    public copy(source: Path): Path {
        super.copy(source);

        this.currentPoint.copy(source.currentPoint);
        return this;
    }

    /**
     * 
     */
    public clone(): Path {
        return new Path().copy(this);
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();

        data.currentPoint = this.currentPoint.toArray();
        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): Path {
        super.fromJSON(json);

        this.currentPoint.fromArray(json.currentPoint);
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Path {
        super.toDataBuffer(dataBuffer);

        this.currentPoint.toDataBuffer(dataBuffer);
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Path {
        super.fromDataBuffer(dataBuffer);

        this.currentPoint.fromDataBuffer(dataBuffer);
        return this;
    }
}
