import { DeepImmutable, float, FloatArray, int, Nullable } from "../types";
import { MathUtils } from "./MathUtils";
import { Euler } from "./Euler";
import { EulerOrder } from "../Enums";
import { Vector3 } from "./Vector3";
import { Matrix3 } from "./Matrix3";
import { Matrix4 } from "./Matrix4";
import { BinaryBuffer, BufferAttribute } from "../Misc";
import { MathTmp } from "./MathTmp";
import { InterleavedBufferAttribute } from "../Scene/Geometries/InterleavedBufferAttribute";

type InVector3 = Vector3 | DeepImmutable<Vector3>;

/**
 * 
 * @category Math
 */
export class Quaternion {
    /**
     * @ignore
     */
    private _x: number;
    /**
     * @ignore
     */
    private _y: number;
    /**
     * @ignore
     */
    private _z: number;
    /**
     * @ignore
     */
    private _w: number;
    /**
     * @ignore
     */
    private _notify: boolean;
    /**
     * @ignore
     */
    private _onNotify: any;

    /**
     * 
     * @param qa
     * @param qb
     * @param qm
     * @param t
     */
    public static slerp(qa: Quaternion, qb: Quaternion, qm: Quaternion, t: number): Quaternion {
        return qm.copy(qa).slerp(qb, t);
    }

    /**
     * 
     * @param dst
     * @param dstOffset
     * @param src0
     * @param srcOffset0
     * @param src1
     * @param srcOffset1
     * @param t
     */
    public static slerpFlat(dst: FloatArray, dstOffset: int, src0: FloatArray, srcOffset0: int, src1: FloatArray, srcOffset1: int, t: number): void {
        let x0 = src0[srcOffset0 + 0];
        let y0 = src0[srcOffset0 + 1];
        let z0 = src0[srcOffset0 + 2];
        let w0 = src0[srcOffset0 + 3];

        let x1 = src1[srcOffset1 + 0];
        let y1 = src1[srcOffset1 + 1];
        let z1 = src1[srcOffset1 + 2];
        let w1 = src1[srcOffset1 + 3];

        if (w0 != w1 || x0 != x1 || y0 != y1 || z0 != z1) {

            let s = 1 - t;
            let cos = x0 * x1 + y0 * y1 + z0 * z1 + w0 * w1;
            let dir = (cos >= 0 ? 1 : -1);
            let sqrSin = 1 - cos * cos;

            // Skip the Slerp for tiny steps to avoid numeric problems:
            if (sqrSin > MathUtils.EPSILON14) {
                let sin = Math.sqrt(sqrSin);
                let len = Math.atan2(sin, cos * dir);

                s = Math.sin(s * len) / sin;
                t = Math.sin(t * len) / sin;
            }

            let tDir = t * dir;

            x0 = x0 * s + x1 * tDir;
            y0 = y0 * s + y1 * tDir;
            z0 = z0 * s + z1 * tDir;
            w0 = w0 * s + w1 * tDir;

            // Normalize in case we just did a lerp:
            if (s == 1 - t) {
                let f = 1 / Math.sqrt(x0 * x0 + y0 * y0 + z0 * z0 + w0 * w0);

                x0 *= f;
                y0 *= f;
                z0 *= f;
                w0 *= f;
            }
        }

        dst[dstOffset] = x0;
        dst[dstOffset + 1] = y0;
        dst[dstOffset + 2] = z0;
        dst[dstOffset + 3] = w0;
    }

    /**
     * 
     * @param dst
     * @param dstOffset
     * @param src0
     * @param srcOffset0
     * @param src1
     * @param srcOffset1
     * @param t
     */
    public static multiplyQuaternionsFlat(dst: FloatArray, dstOffset: int, src0: FloatArray, srcOffset0: int, src1: FloatArray, srcOffset1: int, t: float): void {

        let x0 = src0[ srcOffset0 + 0 ],
            y0 = src0[ srcOffset0 + 1 ],
            z0 = src0[ srcOffset0 + 2 ],
            w0 = src0[ srcOffset0 + 3 ],

            x1 = src1[ srcOffset1 + 0 ],
            y1 = src1[ srcOffset1 + 1 ],
            z1 = src1[ srcOffset1 + 2 ],
            w1 = src1[ srcOffset1 + 3 ];

        if (w0 != w1 || x0 != x1 || y0 != y1 || z0 != z1) {

            let s = 1 - t,

                cos = x0 * x1 + y0 * y1 + z0 * z1 + w0 * w1,

                dir = (cos >= 0 ? 1 : -1),
                sqrSin = 1 - cos * cos;

            // Skip the Slerp for tiny steps to avoid numeric problems:
            if (sqrSin > MathUtils.EPSILON14) {

                let sin = Math.sqrt(sqrSin), len = Math.atan2(sin, cos * dir);

                s = Math.sin(s * len) / sin;
                t = Math.sin(t * len) / sin;

            }

            let tDir = t * dir;

            x0 = x0 * s + x1 * tDir;
            y0 = y0 * s + y1 * tDir;
            z0 = z0 * s + z1 * tDir;
            w0 = w0 * s + w1 * tDir;

            // Normalize in case we just did a lerp:
            if (s == 1 - t) {

                let f = 1 / Math.sqrt(x0 * x0 + y0 * y0 + z0 * z0 + w0 * w0);

                x0 *= f;
                y0 *= f;
                z0 *= f;
                w0 *= f;

            }

        }

        dst[ dstOffset ] = x0;
        dst[ dstOffset + 1 ] = y0;
        dst[ dstOffset + 2 ] = z0;
        dst[ dstOffset + 3 ] = w0;
    }

    /**
     * 
     * @param x
     * @param y
     * @param z
     * @param w
     */
    constructor(x: number = 0, y: number = 0, z: number = 0, w: number = 1) {
        this._x = x;
        this._y = y;
        this._z = z;
        this._w = w;
        this._notify = true;
        this._onNotify = null;
    }

    /**
     * 
     */
    public static Build() {
        return new Quaternion();
    }

    /**
     * X
     */
    public get x() {
        return this._x;
    }
    public set x(value: number) {
        if (this._x != value) {
            this._x = value;
            this.triggerNotify();
        }
    }

    /**
     * Y
     */
    public get y() {
        return this._y;
    }
    public set y(value: number) {
        if (this._y != value) {
            this._y = value;
            this.triggerNotify();
        }
    }

    /**
     * Z
     */
    public get z() {
        return this._z;
    }
    public set z(value: number) {
        if (this._z != value) {
            this._z = value;
            this.triggerNotify();
        }
    }

    /**
     * W
     */
    public get w() {
        return this._w;
    }
    public set w(value: number) {
        if (this._w != value) {
            this._w = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get notify() {
        return this._notify;
    }
    public set notify(value: boolean) {
        this._notify = value;
    }

    /**
     * 
     */
    public setNotifyFunc(value: any): Quaternion {
        this._onNotify = value;
        return this;
    }

    /**
     * 
     */
    private triggerNotify(): void {
        if (this._notify) {
            if (this._onNotify) {
                this._onNotify();
            }
        }
    }

    /**
     * XYZW
     * @param x
     * @param y
     * @param z
     * @param w
     */
    public set(x: number, y: number, z: number, w: number): Quaternion {
        this._x = x;
        this._y = y;
        this._z = z;
        this._w = w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): Quaternion {
        switch (index) {
            case 0: this.x = value; break;
            case 1: this.y = value; break;
            case 2: this.z = value; break;
            case 3: this.w = value; break;
            default: throw new Error(`index is out of range: ${index}`);
        }

        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this._x;
            case 1: return this._y;
            case 2: return this._z;
            case 3: return this._w;
            default: throw new Error(`index is out of range: ${index}`);
        }
    }

    /**
     * 
     */
    public clone(): Quaternion {
        return new Quaternion(this._x, this._y, this._z, this._w);
    }

    /**
     * 
     */
    public copy(source: Quaternion): Quaternion {
        this._x = source.x;
        this._y = source.y;
        this._z = source.z;
        this._w = source.w;
        this.triggerNotify();
        return this;
    }

    /**
     * Quaternion
     * @param quaternion
     */
    public add(quaternion: Quaternion): Quaternion {
        this._x += quaternion.x;
        this._y += quaternion.y;
        this._z += quaternion.z;
        this._w += quaternion.w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param aQuat
     * @param bQuat
     */
    public addQuaternions(aQuat: Quaternion, bQuat: Quaternion): Quaternion {
        this._x = aQuat.x + bQuat.x;
        this._y = aQuat.y + bQuat.y;
        this._z = aQuat.z + bQuat.z;
        this._w = aQuat.w + bQuat.w;
        return this;
    }

    /**
     * 
     * @param euler
     */
    public setFromEuler(euler: Euler): Quaternion {

        const x = euler.x;
        const y = euler.y;
        const z = euler.z;
        const order = euler.order;

        // http://www.mathworks.com/matlabcentral/fileexchange/
        // 	20696-function-to-convert-between-dcm-euler-angles-quaternions-and-euler-vectors/
        //	content/SpinCalc.m

        const cos = Math.cos;
        const sin = Math.sin;

        const c1 = cos(x / 2);
        const c2 = cos(y / 2);
        const c3 = cos(z / 2);

        const s1 = sin(x / 2);
        const s2 = sin(y / 2);
        const s3 = sin(z / 2);

        switch (order) {

            case EulerOrder.XYZ:
                this._x = s1 * c2 * c3 + c1 * s2 * s3;
                this._y = c1 * s2 * c3 - s1 * c2 * s3;
                this._z = c1 * c2 * s3 + s1 * s2 * c3;
                this._w = c1 * c2 * c3 - s1 * s2 * s3;
                break;

            case EulerOrder.YXZ:
                this._x = s1 * c2 * c3 + c1 * s2 * s3;
                this._y = c1 * s2 * c3 - s1 * c2 * s3;
                this._z = c1 * c2 * s3 - s1 * s2 * c3;
                this._w = c1 * c2 * c3 + s1 * s2 * s3;
                break;

            case EulerOrder.ZXY:
                this._x = s1 * c2 * c3 - c1 * s2 * s3;
                this._y = c1 * s2 * c3 + s1 * c2 * s3;
                this._z = c1 * c2 * s3 + s1 * s2 * c3;
                this._w = c1 * c2 * c3 - s1 * s2 * s3;
                break;

            case EulerOrder.ZYX:
                this._x = s1 * c2 * c3 - c1 * s2 * s3;
                this._y = c1 * s2 * c3 + s1 * c2 * s3;
                this._z = c1 * c2 * s3 - s1 * s2 * c3;
                this._w = c1 * c2 * c3 + s1 * s2 * s3;
                break;

            case EulerOrder.YZX:
                this._x = s1 * c2 * c3 + c1 * s2 * s3;
                this._y = c1 * s2 * c3 + s1 * c2 * s3;
                this._z = c1 * c2 * s3 - s1 * s2 * c3;
                this._w = c1 * c2 * c3 - s1 * s2 * s3;
                break;

            case EulerOrder.XZY:
                this._x = s1 * c2 * c3 - c1 * s2 * s3;
                this._y = c1 * s2 * c3 - s1 * c2 * s3;
                this._z = c1 * c2 * s3 + s1 * s2 * c3;
                this._w = c1 * c2 * c3 + s1 * s2 * s3;
                break;
        }

        this.triggerNotify();

        return this;
    }

    /**
     * 
     * @param axis
     * @param angle
     */
    public setFromAxisAngle(axis: InVector3, angle: number): Quaternion {

        const halfAngle = angle / 2;
        const s = Math.sin(halfAngle);

        this._x = axis.x * s;
        this._y = axis.y * s;
        this._z = axis.z * s;
        this._w = Math.cos(halfAngle);

        this.triggerNotify();

        return this;
    }

    /**
     * 
     * @param matrix
     */
    public setFromRotationMatrix(matrix: Matrix3 | Matrix4): Quaternion {

        const isMatrix4: boolean = matrix instanceof Matrix4;
        const te = matrix.elements;

        const m11 = isMatrix4 ? te[0] : te[0]; const m12 = isMatrix4 ? te[4] : te[3]; const m13 = isMatrix4 ? te[8] : te[6];
        const m21 = isMatrix4 ? te[1] : te[1]; const m22 = isMatrix4 ? te[5] : te[4]; const m23 = isMatrix4 ? te[9] : te[7];
        const m31 = isMatrix4 ? te[2] : te[2]; const m32 = isMatrix4 ? te[6] : te[5]; const m33 = isMatrix4 ? te[10] : te[8];

        const trace = m11 + m22 + m33;
        let s;

        if (trace > 0) {
            s = 0.5 / Math.sqrt(trace + 1.0);

            this._w = 0.25 / s;
            this._x = (m32 - m23) * s;
            this._y = (m13 - m31) * s;
            this._z = (m21 - m12) * s;
        } else if (m11 > m22 && m11 > m33) {
            s = 2.0 * Math.sqrt(1.0 + m11 - m22 - m33);

            this._w = (m32 - m23) / s;
            this._x = 0.25 * s;
            this._y = (m12 + m21) / s;
            this._z = (m13 + m31) / s;
        } else if (m22 > m33) {
            s = 2.0 * Math.sqrt(1.0 + m22 - m11 - m33);

            this._w = (m13 - m31) / s;
            this._x = (m12 + m21) / s;
            this._y = 0.25 * s;
            this._z = (m23 + m32) / s;
        } else {
            s = 2.0 * Math.sqrt(1.0 + m33 - m11 - m22);

            this._w = (m21 - m12) / s;
            this._x = (m13 + m31) / s;
            this._y = (m23 + m32) / s;
            this._z = 0.25 * s;
        }

        this.triggerNotify();

        return this;
    }

    /**
     * 
     * @param vFrom
     * @param vTo
     */
    public setFromUnitVectors(vFrom: InVector3, vTo: InVector3): Quaternion {
        // assumes direction vectors vFrom and vTo are normalized

        const EPS = 0.000001;

        let r = vFrom.dot(vTo) + 1;

        if (r < EPS) {
            r = 0;

            if (Math.abs(vFrom.x) > Math.abs(vFrom.z)) {
                this._x = -vFrom.y;
                this._y = vFrom.x;
                this._z = 0;
                this._w = r;
            } else {
                this._x = 0;
                this._y = -vFrom.z;
                this._z = vFrom.y;
                this._w = r;
            }
        } else {
            // crossVectors( vFrom, vTo ); // inlined to avoid cyclic dependency on Vector3

            this._x = vFrom.y * vTo.z - vFrom.z * vTo.y;
            this._y = vFrom.z * vTo.x - vFrom.x * vTo.z;
            this._z = vFrom.x * vTo.y - vFrom.y * vTo.x;
            this._w = r;
        }

        return this.normalize();
    }

    /**
     * 
     * @param quaternion
     */
    public angleTo(quaternion: Quaternion): number {
        return 2 * Math.acos(Math.abs(MathUtils.clamp(this.dot(quaternion), -1, 1)));
    }

    /**
     * 
     * @param quaternion
     * @param step
     */
    public rotateTowards(quaternion: Quaternion, step: number): Quaternion {

        const angle = this.angleTo(quaternion);
        if (angle == 0) { return this; }

        const t = Math.min(1, step / angle);
        this.slerp(quaternion, t);
        return this;
    }

    /**
     * 
     */
    public inverse(): Quaternion {
        return this.conjugate();
    }

    /**
     * 
     */
    public conjugate(): Quaternion {
        this._x *= -1;
        this._y *= -1;
        this._z *= -1;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public dot(quaternion: Quaternion): number {
        return this._x * quaternion._x + this._y * quaternion._y + this._z * quaternion._z + this._w * quaternion._w;
    }

    /**
     * SQ
     */
    public lengthSq(): number {
        return this._x * this._x + this._y * this._y + this._z * this._z + this._w * this._w;
    }

    /**
     * 
     */
    public length(): number {
        return Math.sqrt(this._x * this._x + this._y * this._y + this._z * this._z + this._w * this._w);
    }

    /**
     * 
     */
    public normalize(): Quaternion {
        let l = this.length();

        if (l == 0) {
            this._x = 0;
            this._y = 0;
            this._z = 0;
            this._w = 1;
        } else {
            l = 1 / l;

            this._x = this._x * l;
            this._y = this._y * l;
            this._z = this._z * l;
            this._w = this._w * l;
        }
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public multiply(quaternion: Quaternion): Quaternion {
        return this.multiplyQuaternions(this, quaternion);
    }

    /**
     * 
     * @param quaternion
     */
    public premultiply(quaternion: Quaternion): Quaternion {
        return this.multiplyQuaternions(quaternion, this);
    }

    /**
     * 
     * @param aQuat
     * @param bQuat
     */
    public multiplyQuaternions(aQuat: Quaternion, bQuat: Quaternion): Quaternion {

        const qax = aQuat._x;
        const qay = aQuat._y;
        const qaz = aQuat._z;
        const qaw = aQuat._w;

        const qbx = bQuat._x;
        const qby = bQuat._y;
        const qbz = bQuat._z;
        const qbw = bQuat._w;

        this._x = qax * qbw + qaw * qbx + qay * qbz - qaz * qby;
        this._y = qay * qbw + qaw * qby + qaz * qbx - qax * qbz;
        this._z = qaz * qbw + qaw * qbz + qax * qby - qay * qbx;
        this._w = qaw * qbw - qax * qbx - qay * qby - qaz * qbz;

        this.triggerNotify();

        return this;
    }

    /**
     * 
     * @param quaternion
     * @param t
     */
    public slerp(quaternion: Quaternion, t: number): Quaternion {
        if (t == 0) { return this; }
        if (t == 1) { return this.copy(quaternion); }

        const x = this._x;
        const y = this._y;
        const z = this._z;
        const w = this._w;

        // http://www.euclideanspace.com/maths/algebra/realNormedAlgebra/quaternions/slerp/

        let cosHalfTheta = w * quaternion._w + x * quaternion._x + y * quaternion._y + z * quaternion._z;
        if (cosHalfTheta < 0) {
            this._w = -quaternion._w;
            this._x = -quaternion._x;
            this._y = -quaternion._y;
            this._z = -quaternion._z;

            cosHalfTheta = -cosHalfTheta;

        } else {
            this.copy(quaternion);
        }

        if (cosHalfTheta >= 1.0) {
            this._w = w;
            this._x = x;
            this._y = y;
            this._z = z;

            return this;
        }

        const sqrSinHalfTheta = 1.0 - cosHalfTheta * cosHalfTheta;

        if (sqrSinHalfTheta <= MathUtils.EPSILON14) {
            const s = 1 - t;
            this._w = s * w + t * this._w;
            this._x = s * x + t * this._x;
            this._y = s * y + t * this._y;
            this._z = s * z + t * this._z;

            return this.normalize();
        }

        const sinHalfTheta = Math.sqrt(sqrSinHalfTheta);
        const halfTheta = Math.atan2(sinHalfTheta, cosHalfTheta);
        const ratioA = Math.sin((1 - t) * halfTheta) / sinHalfTheta;
        const ratioB = Math.sin(t * halfTheta) / sinHalfTheta;

        this._w = (w * ratioA + this._w * ratioB);
        this._x = (x * ratioA + this._x * ratioB);
        this._y = (y * ratioA + this._y * ratioB);
        this._z = (z * ratioA + this._z * ratioB);

        this.triggerNotify();

        return this;
    }

    /**
     * Buffer
     * @param attribute
     */
    public applyToBufferAttribute(attribute: BufferAttribute | InterleavedBufferAttribute): BufferAttribute | InterleavedBufferAttribute {
        for (let i = 0, l = attribute.count; i < l; i++) {
            MathTmp.Vector3[0].x = attribute.getX(i);
            MathTmp.Vector3[0].y = attribute.getY(i);
            MathTmp.Vector3[0].z = attribute.getZ(i);
            MathTmp.Vector3[0].applyQuaternion(this);
            attribute.setXYZ(i, MathTmp.Vector3[0].x, MathTmp.Vector3[0].y, MathTmp.Vector3[0].z);
        }
        return attribute;
    }

    /**
     * 
     * @param quaternion
     */
    public equals(quaternion: Quaternion): boolean {
        return (quaternion._x == this._x) && (quaternion._y == this._y) && (quaternion._z == this._z) && (quaternion._w == this._w);
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: Quaternion, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        return MathUtils.equalsEpsilon(this._x, right.x, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._y, right.y, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._z, right.z, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._w, right.w, relativeEpsilon, absoluteEpsilon);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): Quaternion {
        this._x = array[offset];
        this._y = array[offset + 1];
        this._z = array[offset + 2];
        this._w = array[offset + 3];
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {
        array[offset] = this._x;
        array[offset + 1] = this._y;
        array[offset + 2] = this._z;
        array[offset + 3] = this._w;
        return array;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Quaternion {
        this._x = dataBuffer.readFloat32();
        this._y = dataBuffer.readFloat32();
        this._z = dataBuffer.readFloat32();
        this._w = dataBuffer.readFloat32();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Quaternion {
        dataBuffer.writeFloat32(this._x);
        dataBuffer.writeFloat32(this._y);
        dataBuffer.writeFloat32(this._z);
        dataBuffer.writeFloat32(this._w);
        return this;
    }

    /**
     * Buffer
     * @param attribute
     * @param index
     */
    public fromBufferAttribute(attribute: BufferAttribute, index: int): Quaternion {
        this._x = attribute.getX(index);
        this._y = attribute.getY(index);
        this._z = attribute.getZ(index);
        this._w = attribute.getW(index);
        return this;
    }
}
