import { TextureRaw } from "./TextureRaw";
import { Constants } from "../Constants";
import { Nullable } from "../../types";

/**
 * 2D
 */
export class TextureRaw2DArray extends TextureRaw {

    public depth: number;

    /**
     * 
     * @param data
     * @param width
     * @param height
     * @param depth
     * @param deleteBuffer
     * @param generateMipmaps
     * @param format
     * @param type
     * @param flipY
     */
    constructor(data: Nullable<ArrayBuffer | ArrayBufferView>, width: number, height: number, depth: number, deleteBuffer: boolean = false,
                generateMipmaps: boolean = true, format: number = Constants.RGBAFormat, type: number = Constants.UnsignedByteType,
                flipY: boolean = false) {
        super(data, width, height, deleteBuffer, generateMipmaps, format, type, flipY);

        this.depth = depth;
    }

    /**
     * 
     */
    public clone(): TextureRaw2DArray {
        return new TextureRaw2DArray(this.buffer, this.width, this.height, this.depth, this.deleteBuffer).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TextureRaw2DArray): TextureRaw2DArray {
        super.copy(source);

        return this;
    }
}
