import { Vector2 } from "./Vector2";
import { Nullable } from "../types";
import { Line2Line2Distance, Line2Ray2Distance, Line2Segment2Distance } from "./InterfaceType/DistanceResult";
import {
    Line2Box2Intersect,
    Line2Line2Intersect,
    Line2Ray2Intersect,
    Line2Segment2Intersect
} from "./InterfaceType/IntersectResult";
import { LineSegment2 } from "./LineSegment2";
import { Ray2 } from "./Ray2";
import { Box2 } from "./Box2";

/**
 * @ignore
 */
const _tempVec1 = new Vector2();
/**
 * @ignore
 */
const _tempVec2 = new Vector2();
/**
 * @ignore
 */
const _tempVec3 = new Vector2();
/**
 * @ignore
 */
const _tempVec4 = new Vector2();
/**
 * @ignore
 */
const _tempVec5 = new Vector2();
/**
 * @ignore
 */
const _tempVec6 = new Vector2();
/**
 * @ignore
 */
const _tempVec7 = new Vector2();

/**
 * 2D
 * @category Math
 */
export class Line2 {

    /**
     * 
     */
    public origin: Vector2;
    /**
     * 
     */
    public direction: Vector2;

    /**
     * 
     * @param origin
     * @param direction
     */
    constructor(origin: Nullable<Vector2> = null, direction: Nullable<Vector2> = null) {
        this.origin = new Vector2();
        this.direction = new Vector2(-1, 0);

        if (origin) {
            this.origin.copy(origin);
        }
        if (direction) {
            this.direction.copy(direction);
        }
    }

    /**
     * 
     * @param origin
     * @param direction
     */
    public set(origin: Vector2, direction: Vector2): Line2 {
        this.origin.copy(origin);
        this.direction.copy(direction);
        return this;
    }

    /**
     * 
     */
    public clone(): Line2 {
        return new Line2().copy(this);
    }

    /**
     * 
     * @param ray
     */
    public copy(ray: Line2): Line2 {
        this.origin.copy(ray.origin);
        this.direction.copy(ray.direction);
        return this;
    }

    /**
     * 
     * @param point
     * @param target
     */
    public closestPointToPoint(point: Vector2, target = new Vector2()): Vector2 {
        target.subVectors(point, this.origin);

        const directionDistance = target.dot(this.direction);
        return target.copy(this.direction).multiplyScalar(directionDistance).add(this.origin);
    }

    /**
     * SQ
     * @param point
     * @param lineClosest
     */
    public distanceSqToPoint(point: Vector2, lineClosest: Nullable<Vector2> = null): number {

        const diff = _tempVec1.subVectors(point, this.origin);
        const lineParameter = this.direction.dot(diff);
        _tempVec2.copy(this.direction).multiplyScalar(lineParameter).add(this.origin);

        if (lineClosest) {
            lineClosest.copy(_tempVec2);
        }

        diff.subVectors(point, _tempVec2);
        return diff.dot(diff);
    }

    /**
     * 
     * @param point
     * @param lineClosest
     */
    public distanceToPoint(point: Vector2, lineClosest: Nullable<Vector2> = null): number {
        return Math.sqrt(this.distanceSqToPoint(point, lineClosest));
    }

    /**
     * SQ
     * @param ray
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToRay(ray: Ray2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Line2Ray2Distance> = null): number {

        const diff = _tempVec1.subVectors(this.origin, ray.origin);
        let a01 = -this.direction.dot(ray.direction);
        let b0 = diff.dot(this.direction);
        let s0, s1;

        if (Math.abs(a01) < 1) {
            const b1 = -diff.dot(ray.direction);
            s1 = a01 * b0 - b1;

            if (s1 >= 0) {
                // Two interior points are closest, one on line and one
                // on ray.
                const det = 1 - a01 * a01;
                s0 = (a01 * b1 - b0) / det;
                s1 /= det;
            }
            else {
                // Origin of ray and interior point of line are closest.
                s0 = -b0;
                s1 = 0;
            }
        }
        else {
            // Lines are parallel, closest pair with one point at ray
            // origin.
            s0 = -b0;
            s1 = 0;
        }

        _tempVec2.copy(this.direction).multiplyScalar(s0).add(this.origin);
        _tempVec3.copy(ray.direction).multiplyScalar(s1).add(ray.origin);

        if (closestPoint0) {
            closestPoint0.copy(_tempVec2);
        }
        if (closestPoint1) {
            closestPoint1.copy(_tempVec3);
        }
        if (parameter) {
            parameter.parameter = [s0, s1];
        }
        diff.subVectors(_tempVec2, _tempVec3);
        return diff.dot(diff);
    }

    /**
     * 
     * @param ray
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToRay(ray: Ray2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Line2Ray2Distance> = null): number {
        return Math.sqrt(this.distanceSqToRay(ray, closestPoint0, closestPoint1, parameter));
    }

    /**
     * SQ
     * @param segment
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToSegment(segment: LineSegment2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Line2Segment2Distance> = null): number {

        const segCenter = _tempVec1;
        const segDirection = _tempVec2;
        const segExtent = segment.getCenteredForm(segCenter, segDirection);

        const diff = _tempVec3.subVectors(this.origin, segCenter);
        let a01 = -this.direction.dot(segDirection);
        let b0 = diff.dot(this.direction);
        let s0, s1;

        if (Math.abs(a01) < 1) {
            // The line and segment are not parallel.
            const det = 1 - a01 * a01;
            const extDet = segExtent * det;
            const b1 = -diff.dot(segDirection);
            s1 = a01 * b0 - b1;

            if (s1 >= -extDet) {
                if (s1 <= extDet) {
                    // Two interior points are closest, one on the line
                    // and one on the segment.
                    s0 = (a01 * b1 - b0) / det;
                    s1 /= det;
                }
                else {
                    // The endpoint e1 of the segment and an interior
                    // point of the line are closest.
                    s1 = segExtent;
                    s0 = -(a01 * s1 + b0);
                }
            }
            else {
                // The endpoint e0 of the segment and an interior point
                // of the line are closest.
                s1 = -segExtent;
                s0 = -(a01 * s1 + b0);
            }
        }
        else {
            // The line and segment are parallel.  Choose the closest pair
            // so that one point is at segment origin.
            s1 = 0;
            s0 = -b0;
        }

        _tempVec4.copy(this.direction).multiplyScalar(s0).add(this.origin);
        _tempVec5.copy(segDirection).multiplyScalar(s1).add(segCenter);

        if (closestPoint0) {
            closestPoint0.copy(_tempVec4);
        }
        if (closestPoint1) {
            closestPoint1.copy(_tempVec5);
        }

        if (parameter) {
            parameter.parameter = [s0, s1];
        }

        diff.subVectors(_tempVec4, _tempVec5);
        return diff.dot(diff);
    }

    /**
     * 
     * @param segment
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToSegment(segment: LineSegment2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Line2Segment2Distance> = null): number {
        return Math.sqrt(this.distanceSqToSegment(segment, closestPoint0, closestPoint1, parameter));
    }

    /**
     * SQ
     * @param line
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToLine(line: Line2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Line2Line2Distance> = null): number {

        const line0 = this;
        const line1 = line;

        const diff = _tempVec1.subVectors(line0.origin, line1.origin);
        let a01 = -line0.direction.dot(line1.direction);
        let b0 = diff.dot(line0.direction);
        let s0, s1;

        if (Math.abs(a01) < 1) {
            // Lines are not parallel.
            const det = 1 - a01 * a01;
            const b1 = -diff.dot(line1.direction);
            s0 = (a01 * b1 - b0) / det;
            s1 = (a01 * b0 - b1) / det;
        }
        else {
            // Lines are parallel, select any pair of closest points.
            s0 = -b0;
            s1 = 0;
        }

        _tempVec4.copy(line0.direction).multiplyScalar(s0).add(line0.origin);
        _tempVec5.copy(line1.direction).multiplyScalar(s1).add(line1.origin);

        if (closestPoint0) {
            closestPoint0.copy(_tempVec4);
        }
        if (closestPoint1) {
            closestPoint1.copy(_tempVec5);
        }
        if (parameter) {
            parameter.parameter = [s0, s1];
        }

        diff.subVectors(_tempVec4, _tempVec5);
        return diff.dot(diff);
    }

    /**
     * 
     * @param line
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToLine(line: Line2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Line2Line2Distance> = null): number {
        return Math.sqrt(this.distanceSqToLine(line, closestPoint0, closestPoint1, parameter));
    }

    /**
     * 
     * @param box
     */
    public intersectsBox(box: Box2): boolean {

        const boxCenter = box.getCenter(_tempVec1);
        const boxExtent = box.getExtent(_tempVec2);

        const lineOrigin = _tempVec3.subVectors(this.origin, boxCenter);
        return Line2.intersectsBox(lineOrigin, this.direction, boxExtent);
    }

    /**
     * @ignore
     * @param lineOrigin
     * @param lineDirection
     * @param boxExtent
     */
    public static intersectsBox(lineOrigin: Vector2, lineDirection: Vector2, boxExtent: Vector2): boolean {

        const LHS = Math.abs(lineDirection.dotPerp(lineOrigin));
        const RHS = boxExtent.p[0] * Math.abs(lineDirection.p[1]) + boxExtent.p[1] * Math.abs(lineDirection.p[0]);
        return (LHS <= RHS);
    }

    /**
     * 
     * @param box
     */
    public intersectBox(box: Box2): Line2Box2Intersect {

        const boxCenter = box.getCenter(_tempVec1);
        const boxExtent = box.getExtent(_tempVec2);

        const lineOrigin = _tempVec3.subVectors(this.origin, boxCenter);

        const result = {
            intersect: false,
            numIntersections: 0,
            parameter: new Array<number>(2),
            point: new Array<Vector2>(2)
        };

        Line2.intersectBox(lineOrigin, this.direction, boxExtent, result);

        for (let i = 0; i < result.numIntersections; ++i) {
            result.point[i] = new Vector2().copy(this.direction).multiplyScalar(result.parameter[i]).add(this.origin);
        }

        return result;
    }

    /**
     * @ignore
     * @param lineOrigin
     * @param lineDirection
     * @param boxExtent
     * @param result
     */
    public static intersectBox(lineOrigin: Vector2, lineDirection: Vector2, boxExtent: Vector2, result: Line2Box2Intersect): void {

        const clip = (denom: number, numer: number, t: number[]): boolean => {

            if (denom > 0) {
                if (numer > denom * t[1]) {
                    return false;
                }
                if (numer > denom * t[0]) {
                    t[0] = numer / denom;
                }
                return true;
            }
            else if (denom < 0) {
                if (numer > denom * t[0]) {
                    return false;
                }
                if (numer > denom * t[1]) {
                    t[1] = numer / denom;
                }
                return true;
            }
            else {
                return numer <= 0;
            }
        };

        const t = [-Infinity, Infinity];

        if (clip(+lineDirection.p[0], -lineOrigin.p[0] - boxExtent.p[0], t) &&
            clip(-lineDirection.p[0], +lineOrigin.p[0] - boxExtent.p[0], t) &&
            clip(+lineDirection.p[1], -lineOrigin.p[1] - boxExtent.p[1], t) &&
            clip(-lineDirection.p[1], +lineOrigin.p[1] - boxExtent.p[1], t))
        {
            result.intersect = true;
            if (t[1] > t[0])
            {
                result.numIntersections = 2;
                result.parameter[0] = t[0];
                result.parameter[1] = t[1];
            }
            else
            {
                result.numIntersections = 1;
                result.parameter[0] = t[0];
                result.parameter[1] = t[0];  // Used by derived classes.
            }
            return;
        }

        result.intersect = false;
        result.numIntersections = 0;
    }

    /**
     * 
     * @param line
     */
    public intersectsLine(line: Line2): boolean {

        const line0 = this;
        const line1 = line;

        const diff = _tempVec1.subVectors(line1.origin, line0.origin);
        const D0DotPerpD1 = line0.direction.dotPerp(line1.direction);

        if (D0DotPerpD1 != 0) {
            return true;
        }
        else {
            diff.normalize();
            const diffNDotPerpD1 = diff.dotPerp(line1.direction);
            if (diffNDotPerpD1 != 0) {
                return false;
            }
            else {
                return true;
            }
        }
    }

    /**
     * 
     * @param line
     */
    public intersectLine(line: Line2): Line2Line2Intersect {

        const line0 = this;
        const line1 = line;

        const diff = _tempVec1.subVectors(line1.origin, line0.origin);
        const D0DotPerpD1 = line0.direction.dotPerp(line1.direction);

        const result = {
            intersect: false,
            numIntersections: 0,
            line0Parameter: new Array<number>(2),
            line1Parameter: new Array<number>(2),
            point: new Vector2()
        };

        if (D0DotPerpD1 != 0)
        {
            // The lines are not parallel.
            result.intersect = true;
            result.numIntersections = 1;

            const invD0DotPerpD1 = 1 / D0DotPerpD1;
            const diffDotPerpD0 = diff.dotPerp(line0.direction);
            const diffDotPerpD1 = diff.dotPerp(line1.direction);
            const s0 = diffDotPerpD1 * invD0DotPerpD1;
            const s1 = diffDotPerpD0 * invD0DotPerpD1;
            result.line0Parameter[0] = s0;
            result.line1Parameter[0] = s1;
            result.point.copy(line0.direction).multiplyScalar(s0).add(line0.origin);
        }
        else
        {
            // The lines are parallel.
            diff.normalize();
            const diffNDotPerpD1 = diff.dotPerp(line1.direction);
            if (Math.abs(diffNDotPerpD1) != 0)
            {
                // The lines are parallel but distinct.
                result.intersect = false;
                result.numIntersections = 0;
            }
            else
            {
                // The lines are the same.
                result.intersect = true;
                result.numIntersections = Infinity;

                const maxReal = Infinity;
                result.line0Parameter[0] = -maxReal;
                result.line0Parameter[1] = +maxReal;
                result.line1Parameter[0] = -maxReal;
                result.line1Parameter[1] = +maxReal;
            }
        }

        return result;
    }

    /**
     * 
     * @param segment
     */
    public intersectsSegment(segment: LineSegment2): boolean {

        const segOrigin = _tempVec1;
        const segDirection = _tempVec2;
        const segExtent = segment.getCenteredForm(segOrigin, segDirection);

        const llResult = this.intersectLine(new Line2(segOrigin, segDirection));
        if (llResult.numIntersections == 1) {
            if (Math.abs(llResult.line1Parameter[0]) <= segExtent) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return llResult.intersect;
        }
    }

    /**
     * 
     * @param segment
     */
    public intersectSegment(segment: LineSegment2): Line2Segment2Intersect {

        const segOrigin = _tempVec1;
        const segDirection = _tempVec2;
        const segExtent = segment.getCenteredForm(segOrigin, segDirection);

        const result = {
            intersect: false,
            numIntersections: 0,
            lineParameter: new Array<number>(2),
            segmentParameter: new Array<number>(2),
            point: new Vector2()
        };
        const llResult = this.intersectLine(new Line2(segOrigin, segDirection));

        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the ray.
            if (Math.abs(llResult.line1Parameter[0]) <= segExtent)
            {
                result.intersect = true;
                result.numIntersections = 1;
                result.lineParameter[0] = llResult.line0Parameter[0];
                result.segmentParameter[0] = llResult.line1Parameter[0];
                result.point = llResult.point;
            }
            else
            {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            result.intersect = true;
            result.numIntersections = Infinity;

            const maxReal = Infinity;
            result.lineParameter[0] = -maxReal;
            result.lineParameter[1] = +maxReal;
            result.segmentParameter[0] = -segExtent;
            result.segmentParameter[1] = +segExtent;
        }
        else
        {
            result.intersect = false;
            result.numIntersections = 0;
        }
        return result;
    }

    /**
     * 
     * @param ray
     */
    public intersectsRay(ray: Ray2): boolean {

        const llResult = this.intersectLine(new Line2(ray.origin, ray.direction));
        if (llResult.numIntersections == 1) {
            // Test whether the line-line intersection is on the ray.
            if (llResult.line1Parameter[0] >= 0) {
               return true;
            }
            else {
                return false;
            }
        }
        else {
            return llResult.intersect;
        }

    }

    /**
     * 
     * @param ray
     */
    public intersectRay(ray: Ray2): Line2Ray2Intersect {

        const result = {
            intersect: false,
            numIntersections: 0,
            lineParameter: new Array<number>(2),
            rayParameter: new Array<number>(2),
            point: new Vector2()
        };
        const llResult = this.intersectLine(new Line2(ray.origin, ray.direction));

        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the ray.
            if (llResult.line1Parameter[0] >= 0)
            {
                result.intersect = true;
                result.numIntersections = 1;
                result.lineParameter[0] = llResult.line0Parameter[0];
                result.rayParameter[0] = llResult.line1Parameter[0];
                result.point = llResult.point;
            }
            else
            {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            result.intersect = true;
            result.numIntersections = Infinity;

            const maxReal = Infinity;
            result.lineParameter[0] = -maxReal;
            result.lineParameter[1] = +maxReal;
            result.rayParameter[0] = 0;
            result.rayParameter[1] = +maxReal;
        }
        else
        {
            result.intersect = false;
            result.numIntersections = 0;
        }
        return result;
    }

    /**
     * 
     * @param line
     */
    public equals(line: Line2): boolean {
        return line.origin.equals(this.origin) && line.direction.equals(this.direction);
    }
}
