import { int, Nullable } from "../../types";

/**
 * LCP
 * @category Math
 */
export enum LCPResultType
{
    HAS_TRIVIAL_SOLUTION,
    HAS_NONTRIVIAL_SOLUTION,
    NO_SOLUTION,
    FAILED_TO_CONVERGE,
    INVALID_INPUT
}

/**
 * LCP
 * @category Math
 */
export interface LCPResult {
    result: LCPResultType;
}

/**
 * @ignore
 */
interface LCPVariable {
    name: string;
    index: int;
    complementary: int;
    tuple: number[];
}

/**
 * @ignore
 */
class LCPSolverShared {
    protected mDimension: int = 0;
    protected mMaxIterations: int = 0;
    protected mNumIterations: int = 0;

    protected mVarBasic: LCPVariable[];
    protected mVarNonbasic: LCPVariable[];

    protected mNumCols: int = 0;

    protected mAugmented: number[];
    protected mQMin: number[];
    protected mMinRatio: number[];
    protected mRatio: number[];
    protected mPoly: number[];

    protected mZero: number = 0;
    protected mOne: number = 1;

    constructor(n: number, zero: number = 0, one: number = 1) {
        this.mZero = zero;
        this.mOne = one;

        if (n > 0) {
            this.mDimension = n;
            this.mMaxIterations = n * n;
        }
        else {
            this.mDimension = 0;
            this.mMaxIterations = 0;
        }
    }

    public getNumIterations(): number {
        return this.mNumIterations;
    }

    public setMaxIterations(maxIterations: int): void {
        this.mMaxIterations = (maxIterations > 0 ? maxIterations : this.mDimension * this.mDimension);
    }

    public solve(q: number[], M: number[][], w: number[], z: number[], result: Nullable<LCPResult>): boolean {

        for (let r = 0; r < this.mDimension; ++r) {
            this.mPoly[r] = this.getAugmentedIndex(r, this.mDimension + 1);
            this.makeZero(this.mPoly[r]);
            this.setAugmentedValueByIndex(this.mPoly[r], q[r]);
            this.setAugmentedValueByIndex(this.mPoly[r] + r + 1, this.mOne);
        }

        this.copyAugmented(this.mPoly[0], this.mQMin);

        let basic = 0;
        for (let r = 1; r < this.mDimension; ++r) {
            if (this.lessThanAugmented(this.mPoly[r], this.mQMin)) {
                this.copyAugmented(this.mPoly[r], this.mQMin);
                basic = r;
            }
        }

        if (!this.lessThanZero(this.mQMin)) {
            for (let r = 0; r < this.mDimension; ++r) {
                w[r] = q[r];
                z[r] = this.mZero;
            }
            if (result) {
                result.result = LCPResultType.HAS_TRIVIAL_SOLUTION;
            }
            return true;
        }

        for (let r = 0; r < this.mDimension; ++r) {
            for (let c = 0; c < this.mDimension; ++c) {
                this.setAugmentedValue(r, c, M[this.mDimension * r][c]);
            }
            this.setAugmentedValue(r, this.mDimension, this.mOne);
        }

        for (let i = 0; i <= this.mDimension; ++i) {
            this.mVarBasic[i].name = 'w';
            this.mVarBasic[i].index = i;
            this.mVarBasic[i].complementary = i;
            this.mVarBasic[i].tuple = w;
            this.mVarNonbasic[i].name = 'z';
            this.mVarNonbasic[i].index = i;
            this.mVarNonbasic[i].complementary = i;
            this.mVarNonbasic[i].tuple = z;
        }

        let driving = this.mDimension;
        for (let r = 0; r < this.mDimension; ++r) {
            if (r != basic) {
                for (let c = 0; c < this.mNumCols; ++c) {
                    if (c != this.mDimension) {
                        const a = this.getAugmentedValue(r, c);
                        const b = this.getAugmentedValue(basic, c);
                        this.setAugmentedValue(r, c, a - b);
                    }
                }
            }
        }

        for (let c = 0; c < this.mNumCols; ++c) {
            if (c != this.mDimension) {
                this.setAugmentedValue(basic, c, -this.getAugmentedValue(basic, c));
            }
        }

        this.mNumIterations = 0;
        for (let i = 0; i < this.mMaxIterations; ++i, ++this.mNumIterations) {
            let nextDriving = this.mVarBasic[basic].complementary;
            this.mVarNonbasic[nextDriving].complementary = driving;

            let t = this.mVarBasic[basic];
            this.mVarBasic[basic] = this.mVarNonbasic[driving];
            this.mVarNonbasic[driving] = t;

            if (this.mVarNonbasic[driving].index == this.mDimension) {
                for (let r = 0; r < this.mDimension; ++r) {
                    this.mVarBasic[r].tuple[this.mVarBasic[r].index] = this.getAugmentedValueByIndex(r);
                }

                for (let c = 0; c <= this.mDimension; ++c) {
                    let index = this.mVarNonbasic[c].index;
                    if (index < this.mDimension) {
                        this.mVarNonbasic[c].tuple[index] = this.mZero;
                    }
                }

                if (result) {
                    result.result = LCPResultType.HAS_NONTRIVIAL_SOLUTION;
                }
                return true;
            }

            driving = nextDriving;
            basic = -1;
            for (let r = 0; r < this.mDimension; ++r) {
                if (this.getAugmentedValue(r, driving) < this.mZero) {
                    let factor = -this.mOne / this.getAugmentedValue(r, driving);

                    this.multiplyAugmented(this.mPoly[r], factor, this.mRatio);

                    if (basic == -1 || this.lessThan(this.mRatio, this.mMinRatio)) {
                        this.copy(this.mRatio, this.mMinRatio);
                        basic = r;
                    }
                }
            }

            if (basic == -1) {
                for (let r = 0; r < this.mDimension; ++r) {
                    w[r] = this.mZero;
                    z[r] = this.mZero;
                }

                if (result) {
                    result.result = LCPResultType.NO_SOLUTION;
                }
                return false;
            }

            let invDenom = this.mOne / this.getAugmentedValue(basic, driving);
            for (let r = 0; r < this.mDimension; ++r) {
                if (r != basic && this.getAugmentedValue(r, driving) != this.mZero) {
                    let multiplier = this.getAugmentedValue(r, driving) * invDenom;
                    for (let c = 0; c < this.mNumCols; ++c) {
                        if (c != driving) {
                            let a = this.getAugmentedValue(r, c);
                            let b = this.getAugmentedValue(basic, c) * multiplier;
                            this.setAugmentedValue(r, c, a - b);
                        }
                        else {
                            this.setAugmentedValue(r, driving, multiplier);
                        }
                    }
                }
            }

            for (let c = 0; c < this.mNumCols; ++c) {
                if (c != driving) {
                    this.setAugmentedValue(basic, c, -this.getAugmentedValue(basic, c) * invDenom);
                }
                else {
                    this.setAugmentedValue(basic, driving, invDenom);
                }
            }
        }

        if (result) {
            result.result = LCPResultType.FAILED_TO_CONVERGE;
        }
        return false;
    }

    protected getAugmentedIndex(row: int, col: int): number {
        return col + this.mNumCols * row;
    }

    protected getAugmentedValue(row: int, col: int): number {
        return this.mAugmented[col + this.mNumCols * row];
    }
    protected setAugmentedValue(row: int, col: int, value: number): void {
        this.mAugmented[col + this.mNumCols * row] = value;
    }

    protected setAugmentedValueByIndex(index: int, value: number): void {
        this.mAugmented[index] = value;
    }
    protected getAugmentedValueByIndex(index: int): number {
        return this.mAugmented[index];
    }

    protected makeZero(poly: number): void {
        for (let i = 0; i <= this.mDimension; ++i) {
            this.mAugmented[poly + i] = this.mZero;
        }
    }
    protected copyAugmented(poly0: number, poly1: number[]): void {
        for (let i = 0; i <= this.mDimension; ++i) {
            poly1[i] = this.mAugmented[poly0 + i];
        }
    }
    protected copy(poly0: number[], poly1: number[]): void {
        for (let i = 0; i <= this.mDimension; ++i) {
            poly1[i] = poly0[i];
        }
    }
    protected lessThanAugmented(poly0: number, poly1: number[]): boolean {
        for (let i = 0; i <= this.mDimension; ++i) {
            if (this.mAugmented[poly0 + i] < poly1[i]) {
                return true;
            }
            if (this.mAugmented[poly0 + i] > poly1[i]) {
                return false;
            }
        }
        return false;
    }
    protected lessThan(poly0: number[], poly1: number[]): boolean {
        for (let i = 0; i <= this.mDimension; ++i) {
            if (poly0[i] < poly1[i]) {
                return true;
            }
            if (poly0[i] > poly1[i]) {
                return false;
            }
        }
        return false;
    }
    protected lessThanZeroAugmented(poly: number): boolean {
        for (let i = 0; i <= this.mDimension; ++i) {
            if (this.mAugmented[poly + i] < this.mZero) {
                return true;
            }
            if (this.mAugmented[poly + i] > this.mZero) {
                return false;
            }
        }
        return false;
    }
    protected lessThanZero(poly: number[]): boolean {
        for (let i = 0; i <= this.mDimension; ++i) {
            if (poly[i] < this.mZero) {
                return true;
            }
            if (poly[i] > this.mZero) {
                return false;
            }
        }
        return false;
    }
    protected multiplyAugmented(poly: number, scalar: number, product: number[]): void {
        for (let i = 0; i <= this.mDimension; ++i) {
            product[i] = this.mAugmented[poly + i] * scalar;
        }
    }
    protected multiply(poly: number[], scalar: number, product: number[]): void {
        for (let i = 0; i <= this.mDimension; ++i) {
            product[i] = poly[i] * scalar;
        }
    }
}

/**
 * LCP
 * @category Math
 */
export class LCPSolver extends LCPSolverShared {
    constructor(n: number) {
        super(n);

        if (n > 0) {
            this.mVarBasic = new Array<LCPVariable>(n + 1);
            this.mVarNonbasic = new Array<LCPVariable>(n + 1);

            this.mNumCols = 2 * (n + 1);
            this.mAugmented = new Array<number>(2 * (n + 1) * n);

            this.mQMin = new Array<number>(n + 1);
            this.mMinRatio = new Array<number>(n + 1);
            this.mRatio = new Array<number>(n + 1);
            this.mPoly = new Array<number>(n);
        }
    }

    public solve(q: number[], M: number[][], w: number[], z: number[], result: Nullable<LCPResult> = null): boolean {

        if (this.mDimension > q.length || this.mDimension * this.mDimension > M.length) {
            if (result) {
                result.result = LCPResultType.INVALID_INPUT;
            }
            return false;
        }

        if (this.mDimension > w.length) {
            if (result) {
                result.result = LCPResultType.INVALID_INPUT;
            }
            return false;
        }

        if (this.mDimension > z.length) {
            if (result) {
                result.result = LCPResultType.INVALID_INPUT;
            }
            return false;
        }

        return super.solve(q, M, w, z, result);
    }
}
