/**
 * @ignore
 */
const stringFromCharCode = String.fromCharCode;

/**
 * @ignore
 */
let byteArray: number[];
/**
 * @ignore
 */
let byteCount: number;
/**
 * @ignore
 */
let byteIndex: number;

/**
 * @ignore
 */
function ucs2decode(string: string): number[] {
    let output = [];
    let counter = 0;
    let length = string.length;

    let value;
    let extra;
    while (counter < length) {
        if (typeof (string) == 'string') {
            value = string.charCodeAt(counter++);
        }
        else {
            value = string[counter++];
        }
        if (value >= 0xD800 && value <= 0xDBFF && counter < length) {
            // high surrogate, and there is a next character
            if (typeof (string) == 'string') {
                extra = string.charCodeAt(counter++);
            }
            else {
                extra = string[counter++];
            }
            if ((extra & 0xFC00) == 0xDC00) { // low surrogate
                output.push(((value & 0x3FF) << 10) + (extra & 0x3FF) + 0x10000);
            } else {
                // unmatched surrogate; only append this code unit, in case the next
                // code unit is the high surrogate of a surrogate pair
                output.push(value);
                counter--;
            }
        } else {
            output.push(value);
        }
    }
    return output;
}

/**
 * @ignore
 */
function ucs2encode(array: number[]): string {
    let length = array.length;
    let index = -1;
    let value;
    let output = '';
    while (++index < length) {
        value = array[index];
        if (value > 0xFFFF) {
            value -= 0x10000;
            output += stringFromCharCode(value >>> 10 & 0x3FF | 0xD800);
            value = 0xDC00 | value & 0x3FF;
        }
        output += stringFromCharCode(value);
    }
    return output;
}

/**
 * @ignore
 */
function checkScalarValue(codePoint: number): void {
    if (codePoint >= 0xD800 && codePoint <= 0xDFFF) {
        throw Error(
            'Lone surrogate U+' + codePoint.toString(16).toUpperCase() +
            ' is not a scalar value'
        );
    }
}

/**
 * @ignore
 */
function createByte(codePoint: number, shift: number): string {
    return stringFromCharCode(((codePoint >> shift) & 0x3F) | 0x80);
}

/**
 * @ignore
 */
function encodeCodePoint(codePoint: number): string {
    if ((codePoint & 0xFFFFFF80) == 0) { // 1-byte sequence
        return stringFromCharCode(codePoint);
    }
    let symbol = '';
    if ((codePoint & 0xFFFFF800) == 0) { // 2-byte sequence
        symbol = stringFromCharCode(((codePoint >> 6) & 0x1F) | 0xC0);
    }
    else if ((codePoint & 0xFFFF0000) == 0) { // 3-byte sequence
        checkScalarValue(codePoint);
        symbol = stringFromCharCode(((codePoint >> 12) & 0x0F) | 0xE0);
        symbol += createByte(codePoint, 6);
    }
    else if ((codePoint & 0xFFE00000) == 0) { // 4-byte sequence
        symbol = stringFromCharCode(((codePoint >> 18) & 0x07) | 0xF0);
        symbol += createByte(codePoint, 12);
        symbol += createByte(codePoint, 6);
    }
    symbol += stringFromCharCode((codePoint & 0x3F) | 0x80);
    return symbol;
}

/**
 * @ignore
 */
function utf8encode(string: string): string {
    let codePoints = ucs2decode(string);
    let length = codePoints.length;
    let index = -1;
    let codePoint;
    let byteString = '';
    while (++index < length) {
        codePoint = codePoints[index];
        byteString += encodeCodePoint(codePoint);
    }
    return byteString;
}

/**
 * @ignore
 */
function readContinuationByte(): number {
    if (byteIndex >= byteCount) {
        throw Error('Invalid byte index');
    }

    let continuationByte = byteArray[byteIndex] & 0xFF;
    byteIndex++;

    if ((continuationByte & 0xC0) == 0x80) {
        return continuationByte & 0x3F;
    }

    // If we end up here, its not a continuation byte
    throw Error('Invalid continuation byte');
}

/**
 * @ignore
 */
function decodeSymbol(): number | boolean {
    let byte1;
    let byte2;
    let byte3;
    let byte4;
    let codePoint;

    if (byteIndex > byteCount) {
        throw Error('Invalid byte index');
    }

    if (byteIndex == byteCount) {
        return false;
    }

    // Read first byte
    byte1 = byteArray[byteIndex] & 0xFF;
    byteIndex++;

    // 1-byte sequence (no continuation bytes)
    if ((byte1 & 0x80) == 0) {
        return byte1;
    }

    // 2-byte sequence
    if ((byte1 & 0xE0) == 0xC0) {
        byte2 = readContinuationByte();
        codePoint = ((byte1 & 0x1F) << 6) | byte2;
        if (codePoint >= 0x80) {
            return codePoint;
        } else {
            throw Error('Invalid continuation byte');
        }
    }

    // 3-byte sequence (may include unpaired surrogates)
    if ((byte1 & 0xF0) == 0xE0) {
        byte2 = readContinuationByte();
        byte3 = readContinuationByte();
        codePoint = ((byte1 & 0x0F) << 12) | (byte2 << 6) | byte3;
        if (codePoint >= 0x0800) {
            checkScalarValue(codePoint);
            return codePoint;
        } else {
            throw Error('Invalid continuation byte');
        }
    }

    // 4-byte sequence
    if ((byte1 & 0xF8) == 0xF0) {
        byte2 = readContinuationByte();
        byte3 = readContinuationByte();
        byte4 = readContinuationByte();
        codePoint = ((byte1 & 0x07) << 0x12) | (byte2 << 0x0C) | (byte3 << 0x06) | byte4;
        if (codePoint >= 0x010000 && codePoint <= 0x10FFFF) {
            return codePoint;
        }
    }

    throw Error('Invalid UTF-8 detected');
}

/**
 * @ignore
 */
function utf8decode(byteString: string): string {
    byteArray = ucs2decode(byteString);
    byteCount = byteArray.length;
    byteIndex = 0;

    let codePoints: number[] = [];
    let tmp;
    while ((tmp = decodeSymbol()) != false) {
        codePoints.push(Number(tmp));
    }
    return ucs2encode(codePoints);
}

/*--------------------------------------------------------------------------*/
/**
 * @ignore
 */
let encode = utf8encode;

/**
 * @ignore
 */
let decode = utf8decode;

export { encode, decode };
