import { SphereSphereIntersectType } from "../Enums/SphereSphereIntersectType";
import { Vector3 } from "./Vector3";
import { FloatArray, Nullable } from "../types";
import { Box3 } from "./Box3";
import { Matrix4 } from "./Matrix4";
import { Triangle } from "./Triangle";
import { SphereSphereIntersect, SphereTriangleIntersect } from "./InterfaceType/IntersectResult";
import { MathTmp } from "./MathTmp";

/**
 * 3D
 * @category Math
 */
export class Sphere {

    /**
     * 
     */
    public center: Vector3 = new Vector3();
    /**
     * 
     */
    public radius: number = 0;

    /**
     *
     * @param center
     * @param radius
     */
    constructor(center: Nullable<Vector3> = null, radius: Nullable<number> = null) {
        if (center) {
            this.center.copy(center);
        }
        if (radius) {
            this.radius = radius;
        }
    }

    /**
     * 
     * @constructor
     */
    public static Build() {
        return new Sphere();
    }

    /**
     * 
     * @param center
     * @param radius
     */
    public set(center: Vector3, radius: number): Sphere {
        this.center.copy(center);
        this.radius = radius;
        return this;
    }

    /**
     * 
     * @param points
     * @param optionalCenter
     */
    public setFromPoints(points: Vector3[], optionalCenter: Nullable<Vector3> = null): Sphere {

        const center = this.center;

        if (optionalCenter) {
            center.copy(optionalCenter);
        } else {
            MathTmp.Box3[0].setFromPoints(points).getCenter(center);
        }

        let maxRadiusSq = 0;
        for (let i = 0, il = points.length; i < il; i++) {
            maxRadiusSq = Math.max(maxRadiusSq, center.distanceToSquared(points[ i ]));
        }
        this.radius = Math.sqrt(maxRadiusSq);
        return this;
    }

    /**
     * 
     */
    public clone(): Sphere {
        return new Sphere().copy(this);
    }

    /**
     * 
     * @param sphere
     */
    public copy(sphere: Sphere): Sphere {
        this.center.copy(sphere.center);
        this.radius = sphere.radius;
        return this;
    }

    /**
     * 
     */
    public empty(): boolean {
        return (this.radius <= 0);
    }

    /**
     * 
     */
    public makeEmpty(): Sphere {
        this.center.set(0, 0, 0);
        this.radius = 0;
        return this;
    }

    /**
     * 
     * @param point
     */
    public containsPoint(point: Vector3): boolean {
        return (point.distanceToSquared(this.center) <= (this.radius * this.radius));
    }

    /**
     * 
     * @param sphere
     */
    public containsSphere(sphere: Sphere): boolean {
        return this.containsPoint(sphere.center) && this.radius >= sphere.radius;
    }

    /**
     * 
     * @param box
     */
    public containsBox(box: Box3): boolean {
        const sphere = new Sphere();
        box.getBoundingSphere(sphere);
        return this.containsSphere(sphere);
    }

    /**
     * 
     * @param point
     */
    public distanceToPoint(point: Vector3): number {
        return (point.distanceTo(this.center) - this.radius);
    }

    /**
     * 
     * @param sphere
     */
    public intersectsSphere(sphere: Sphere): boolean {
        const radiusSum = this.radius + sphere.radius;
        return sphere.center.distanceToSquared(this.center) <= (radiusSum * radiusSum);
    }

    /**
     * 
     * @param sphere
     */
    public intersectSphere(sphere: Sphere): SphereSphereIntersect {

        const sphere0 = this;
        const sphere1 = sphere;

        const c1mC0 = MathTmp.Vector3[0].subVectors(sphere1.center, sphere0.center);
        const sqrLen = c1mC0.dot(c1mC0);
        const r0 = sphere0.radius, r1 = sphere1.radius;
        const rSum = r0 + r1;
        const rSumSqr = rSum * rSum;

        if (sqrLen > rSumSqr) {
            return {
                intersect: false,
                type: SphereSphereIntersectType.NONE
            };
        }

        if (sqrLen == rSumSqr) {
            return {
                intersect: true,
                type: SphereSphereIntersectType.POINT,
                point: new Vector3().copy(sphere0.center).add(c1mC0.normalize().multiplyScalar(r0))
            };
        }

        const rDif = r0 - r1;
        const rDifSqr = rDif * rDif;
        if (sqrLen < rDifSqr) {
            return {
                intersect: true,
                type: (rDif <= 0 ? SphereSphereIntersectType.SPHERE : SphereSphereIntersectType.SPHERE1_IN_SPHERE0),
                point: new Vector3().addVectors(sphere0.center, sphere1.center).multiplyScalar(0.5)
            };
        }

        if (sqrLen == rDifSqr) {
            const result = {
                intersect: true,
                type: 0,
                point: new Vector3()
            };
            if (rDif <= 0) {
                result.type = SphereSphereIntersectType.SPHERE0_IN_SPHERE1_POINT;
                result.point.copy(sphere1.center).add(c1mC0.normalize().multiplyScalar(r1));
            }
            else {
                result.type = SphereSphereIntersectType.SPHERE1_IN_SPHERE0_POINT;
                result.point.copy(sphere0.center).add(c1mC0.normalize().multiplyScalar(r0));
            }
            return result;
        }

        // Compute t for which the circle of intersection has center
        // K = C0 + t*(C1 - C0).
        const t = (0.5) * (1 + rDif * rSum / sqrLen);

        const result = {
            intersect: true,
            type: SphereSphereIntersectType.SPHERE,
            circle: new Sphere()
        };

        // Compute the center and radius of the circle of intersection.
        result.circle.center.copy(sphere0.center).add(MathTmp.Vector3[0].copy(c1mC0).multiplyScalar(t));
        result.circle.radius = Math.sqrt(Math.max(r0 * r0 - t * t * sqrLen, 0));

        return result;
    }

    /**
     * 
     * @param triangle
     */
    public intersecsTriangle(triangle: Triangle): boolean {
        const distance = triangle.distanceToPoint(this.center, MathTmp.Vector3[0]);
        return distance < this.radius;
    }

    /**
     * 
     * @param triangle
     */
    public intersectTriangle(triangle: Triangle): SphereTriangleIntersect {

        const closestPoint = new Vector3();
        const distance = triangle.distanceToPoint(this.center, closestPoint);

        return {
            intersect: distance < this.radius,
            point: closestPoint
        };
    }

    /**
     * 
     * @param point
     * @param target
     */
    public clampPoint(point: Vector3, target = new Vector3()): Vector3 {

        const deltaLengthSq = this.center.distanceToSquared(point);

        target.copy(point);

        if (deltaLengthSq > (this.radius * this.radius)) {
            target.sub(this.center).normalize();
            target.multiplyScalar(this.radius).add(this.center);
        }

        return target;
    }

    /**
     * 
     * @param target
     */
    public getBoundingBox(target = new Box3()): Box3 {

        target.set(this.center, this.center);
        target.expandByScalar(this.radius);
        return target;
    }

    /**
     * Matrix4
     * @param matrix
     */
    public applyMatrix4(matrix: Matrix4): Sphere {
        this.center.applyMatrix4(matrix);
        this.radius = this.radius * matrix.getMaxScaleOnAxis();
        return this;
    }

    /**
     * 
     * @param offset
     */
    public translate(offset: Vector3): Sphere {
        this.center.add(offset);
        return this;
    }

    /**
     * 
     * @param sphere
     */
    public equals(sphere: Sphere): boolean {
        return sphere.center.equals(this.center) && (sphere.radius == this.radius);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray, offset: number = 0): FloatArray {

        this.center.toArray(array, offset);
        array[offset + 3] = this.radius;
        return array;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray = [], offset: number = 0): Sphere {

        this.center.fromArray(array, offset);
        this.radius = array[offset + 3];
        return this;
    }
}
