import { WebGLEngine } from "../WebGLEngine";
import { Constants } from "../Constants";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {

        currentCullFace: number;
        currentFrontFace: boolean;
        currentPolygonOffset: boolean;
        currentPolygonOffsetFactor: number;
        currentPolygonOffsetUnits: number;

        _initCullState(): WebGLEngine;
        setCullFace(cullFace: number): WebGLEngine;
        setFrontFace(cw: boolean): WebGLEngine;
        setPolygonOffset(polygonOffset: boolean, factor: number, units: number): WebGLEngine;
    }
}

WebGLEngine.prototype._initCullState = function(): WebGLEngine {
    this.currentCullFace = Constants.CullFaceBack;
    this.currentFrontFace = false;
    this.currentPolygonOffset = false;
    this.currentPolygonOffsetFactor = 0;
    this.currentPolygonOffsetUnits = 0;

    const gl = this.gl;
    gl.enable(gl.CULL_FACE);
    gl.cullFace(gl.BACK);
    gl.frontFace(gl.CCW);
    gl.disable(gl.POLYGON_OFFSET_FILL);

    return this;
};

WebGLEngine.prototype.setCullFace = function(cullFace: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentCullFace != cullFace) {

        if (cullFace != Constants.CullFaceNone) {
            gl.enable(gl.CULL_FACE);

            if (cullFace == Constants.CullFaceBack) {
                gl.cullFace(gl.BACK);
            } else if (cullFace == Constants.CullFaceFront) {
                gl.cullFace(gl.FRONT);
            } else {
                gl.cullFace(gl.FRONT_AND_BACK);
            }

        } else {
            gl.disable(gl.CULL_FACE);
        }

        this.currentCullFace = cullFace;
    }
    return this;
};

WebGLEngine.prototype.setFrontFace = function(cw: boolean): WebGLEngine {

    const gl = this.gl;

    if (this.currentFrontFace != cw) {
        if (cw) {
            gl.frontFace(gl.CW);
        } else {
            gl.frontFace(gl.CCW);
        }

        this.currentFrontFace = cw;
    }
    return this;
};

WebGLEngine.prototype.setPolygonOffset = function(polygonOffset: boolean, factor: number, units: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentPolygonOffset != polygonOffset) {

        if (polygonOffset) {
            gl.enable(gl.POLYGON_OFFSET_FILL);

            if (this.currentPolygonOffsetFactor != factor || this.currentPolygonOffsetUnits != units) {
                gl.polygonOffset(factor, units);

                this.currentPolygonOffsetFactor = factor;
                this.currentPolygonOffsetUnits = units;
            }
        } else {
            gl.disable(gl.POLYGON_OFFSET_FILL);
        }

        this.currentPolygonOffset = polygonOffset;
    }
    return this;
};
