import { BaseEngine } from "./BaseEngine";
import { Nullable } from "../types";
import { GLTextureSource } from "./GLTextureSource";

/**
 * GL
 */
export class GLTexture {

    public isCube: boolean = false;
    public is3D: boolean = false;
    public is2DArray: boolean = false;
    public isCompression: boolean = false;

    public baseWidth: number = 0;
    public baseHeight: number = 0;
    public baseDepth: number = 0;

    public width: number = 0;
    public height: number = 0;
    public depth: number = 0;

    public format: number = -1;
    public type: number = -1;

    public _version: number = -1;
    public _source: GLTextureSource;
    public _references: number = 0;
    public _engine: BaseEngine;

    public _framebuffer: Nullable<WebGLFramebuffer> = null;

    public _depthStencilBuffer: Nullable<GLTexture>;
    public _colorBuffer: Nullable<GLTexture>;

    public _MSAAFramebuffer: Nullable<WebGLFramebuffer> = null;
    public _MSAARenderBuffer: Nullable<WebGLRenderbuffer> = null;

    public _glTexture: Nullable<WebGLTexture> = null;
    public _glRenderbuffer: Nullable<WebGLRenderbuffer> = null;

    public _colorBuffers: Nullable<GLTexture[]> = null;
    public _colorAttachments: Nullable<number[]> = null;

    /**
     * 
     * @param engine
     * @param source
     */
    constructor(engine: BaseEngine, source: GLTextureSource) {
        this._engine = engine;
        this._source = source;
    }

    /**
     * 
     */
    public addReference(): void {
        this._references++;
    }

    /**
     * 
     */
    public dispose(): void {
        this._references--;
        if (this._references == 0) {
            this._engine._releaseTexture(this);
        }
    }
}
