/**
 * 
 * @category Event
 */
export class Event {

    /**
     * 
     */
    public type: string;

    /**
     * 
     */
    public removed: boolean;

    /**
     * 
     */
    public target: any;

    /**
     * 
     */
    public propagationStopped: boolean;

    /**
     * 
     * @param type
     */
    constructor(type: string) {
        this.type = type;
        this.removed = false;
        this.propagationStopped = false;
    }

    /**
     * 
     */
    remove() {
        this.removed = true;
        return this;
    }
}
