import { Quaternion } from "./Quaternion";
import { MathUtils } from "./MathUtils";
import { Nullable } from "../types";

/**
 * HeadingPitchRoll
 * @category Math
 */
export class HeadingPitchRoll {

    public heading: number;
    public pitch: number;
    public roll: number;

    /**
     * 
     * @param heading
     * @param pitch
     * @param roll
     */
    constructor(heading: number = 0, pitch: number = 0, roll: number = 0) {
        this.heading = heading;
        this.pitch = pitch;
        this.roll = roll;
    }

    /**
     * 
     * @param heading
     * @param pitch
     * @param roll
     */
    public setFromDegrees(heading: number, pitch: number, roll: number): HeadingPitchRoll {
        this.heading = MathUtils.degToRad(heading);
        this.pitch = MathUtils.degToRad(pitch);
        this.roll = MathUtils.degToRad(roll);
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public setFromQuaternion(quaternion: Quaternion): HeadingPitchRoll {
        const test = 2 * (quaternion.w * quaternion.z - quaternion.y * quaternion.x);
        const denominatorRoll = 1 - 2 * (quaternion.x * quaternion.x + quaternion.z * quaternion.z);
        const numeratorRoll = 2 * (quaternion.w * quaternion.x + quaternion.z * quaternion.y);
        const denominatorHeading = 1 - 2 * (quaternion.z * quaternion.z + quaternion.y * quaternion.y);
        const numeratorHeading = 2 * (quaternion.w * quaternion.y + quaternion.x * quaternion.z);
        this.heading = -Math.atan2(numeratorHeading, denominatorHeading);
        this.roll = Math.atan2(numeratorRoll, denominatorRoll);
        this.pitch = -MathUtils.asinClamped(test);
        return this;
    }

    /**
     * 
     * @param source
     */
    public copy(source: HeadingPitchRoll): HeadingPitchRoll {
        this.heading = source.heading;
        this.pitch = source.pitch;
        this.roll = source.roll;
        return this;
    }

    /**
     * 
     */
    public clone(): HeadingPitchRoll {
        return new HeadingPitchRoll(this.heading, this.pitch, this.roll);
    }

    /**
     * 
     * @param right
     */
    public equals(right: HeadingPitchRoll): boolean {
        return (this.heading == right.heading) && (this.pitch == right.pitch) && (this.roll == right.roll);
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: HeadingPitchRoll, relativeEpsilon: number, absoluteEpsilon: Nullable<number>) {
        return MathUtils.equalsEpsilon(this.heading, right.heading, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this.pitch, right.pitch, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this.roll, right.roll, relativeEpsilon, absoluteEpsilon);
    }
}
