import { InterleavedBufferAttribute } from "./InterleavedBufferAttribute";
import { InstancedInterleavedBuffer } from "./InstancedInterleavedBuffer";
import { int } from "../../types";

/**
 * Buffer
 */
export class InstancedInterleavedBufferAttribute extends InterleavedBufferAttribute {

    public meshPerAttribute: number;

    /**
     * 
     * @param interleavedBuffer
     * @param itemSize
     * @param offset
     * @param normalized
     * @param meshPerAttribute
     */
    constructor(interleavedBuffer: InstancedInterleavedBuffer, itemSize: int, offset: int, normalized: boolean = false, meshPerAttribute: number = 1) {
        super(interleavedBuffer, itemSize, offset, normalized);

        this.meshPerAttribute = meshPerAttribute;
    }

    /**
     * 
     */
    public clone(): InstancedInterleavedBufferAttribute {
        return new InstancedInterleavedBufferAttribute(<InstancedInterleavedBuffer>this.buffer, this.itemSize, this.offset, this.normalized, this.meshPerAttribute);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InstancedInterleavedBufferAttribute): InstancedInterleavedBufferAttribute {
        super.copy(source);
        return this;
    }
}
