    public distanceSqToTriangle(triangle: Triangle, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null, parameter: Nullable<Ray3TriangleDistance> = null): number {

        const ltResult = {
            lineParameter: 0,
            triangleParameter: []
        };
        const line = new Line3(this.origin, this.direction);
        let sqrDistance = line.distanceSqToTriangle(triangle, closestPoint0, closestPoint1, ltResult);

        if (ltResult.lineParameter >= 0) {
            if (parameter) {
                parameter.rayParameter = ltResult.lineParameter;
                parameter.triangleParameter[0] = ltResult.triangleParameter[0];
                parameter.triangleParameter[1] = ltResult.triangleParameter[1];
                parameter.triangleParameter[2] = ltResult.triangleParameter[2];
            }
            return sqrDistance;
        }

        const ptResult = {
            parameter: []
        };
        sqrDistance = triangle.distanceSqToPoint(this.origin, closestPoint1, ptResult);
        if (closestPoint0) {
            closestPoint0.copy(this.origin);
        }
        if (parameter) {
            parameter.rayParameter = 0;
            parameter.triangleParameter[0] = ptResult.parameter[0];
            parameter.triangleParameter[1] = ptResult.parameter[1];
            parameter.triangleParameter[2] = ptResult.parameter[2];
        }
        return sqrDistance;
    }

