    public static intersecBox(rayOrigin: Vector3, rayDirection: Vector3, boxExtent: Vector3, result: Ray3Box3Intersect) {

        Line3.intersectBox(rayOrigin, rayDirection, boxExtent, result);

        if (result.intersect) {
            if (result.lineParameter[1] >= 0) {
                if (result.lineParameter[0] < 0) {
                    result.lineParameter[0] = 0;
                }
            }
            else {
                result.intersect = false;
                result.numPoints = 0;
            }
        }
    }

