        const rayOrigin = _tempVec2.set(diff.dot(box.axis[0]), diff.dot(box.axis[1]), diff.dot(box.axis[2]));
        const rayDirection = _tempVec3.set(this.direction.dot(box.axis[0]), this.direction.dot(box.axis[1]), this.direction.dot(box.axis[2]));
        const boxExtent = box.getExtent(_tempVec4);

