import { EulerOrder } from "../Enums/EulerOrder";
import { BinaryBuffer } from "../Misc/BinaryBuffer";
import { FloatArray, int, Nullable } from "../types";
import { Matrix3 } from "./Matrix3";
import { Matrix4 } from "./Matrix4";
import { MathUtils } from "./MathUtils";
import { Quaternion } from "./Quaternion";
import { Vector3 } from "./Vector3";
import { MathTmp } from "./MathTmp";

/**
 * 
 * @category Math
 */
export class Euler {
    /**
     * @ignore
     */
    private _x: number;
    /**
     * @ignore
     */
    private _y: number;
    /**
     * @ignore
     */
    private _z: number;
    /**
     * @ignore
     */
    private _order: EulerOrder;
    /**
     * @ignore
     */
    private _notify: boolean;
    /**
     * @ignore
     */
    private _onNotify: any;

    /**
     * 
     * @param x
     * @param y
     * @param z
     * @param order
     */
    constructor(x: number = 0, y: number = 0, z: number = 0, order: EulerOrder = EulerOrder.YXZ) {
        this._x = x;
        this._y = y;
        this._z = z;
        this._order = order;
        this._notify = true;
        this._onNotify = null;
    }

    /**
     * 
     */
    public static Build() {
        return new Euler();
    }

    /**
     * X
     */
    public get x() {
        return this._x;
    }
    public set x(value: number) {
        if (this._x != value) {
            this._x = value;
            this.triggerNotify();
        }
    }

    /**
     * Y
     */
    public get y() {
        return this._y;
    }
    public set y(value: number) {
        if (this._y != value) {
            this._y = value;
            this.triggerNotify();
        }
    }

    /**
     * Z
     */
    public get z() {
        return this._z;
    }
    public set z(value: number) {
        if (this._z != value) {
            this._z = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get order() {
        return this._order;
    }
    public set order(value: number) {
        if (this._order != value) {
            this._order = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get notify() {
        return this._notify;
    }
    public set notify(value: boolean) {
        this._notify = value;
    }

    /**
     * 
     */
    public setNotifyFunc(value: any): Euler {
        this._onNotify = value;
        return this;
    }

    /**
     * 
     */
    private triggerNotify(): void {
        if (this._notify) {
            if (this._onNotify) {
                this._onNotify();
            }
        }
    }

    /**
     * XYZ
     * @param x
     * @param y
     * @param z
     * @param order
     */
    public set(x: number, y: number, z: number, order: Nullable<EulerOrder> = null): Euler {
        this._x = x;
        this._y = y;
        this._z = z;
        this._order = order || this._order;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): Euler {
        switch (index) {
            case 0: this.x = value; break;
            case 1: this.y = value; break;
            case 2: this.z = value; break;
            default: throw new Error(`index is out of range: ${index}`);
        }
        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this.x;
            case 1: return this.y;
            case 2: return this.z;
            default: throw new Error(`index is out of range: ${index}`);
        }
    }

    /**
     * 
     */
    public clone(): Euler {
        return new Euler(this._x, this._y, this._z, this._order);
    }

    /**
     * 
     * @param euler
     */
    public copy(euler: Euler): Euler {
        this._x = euler._x;
        this._y = euler._y;
        this._z = euler._z;
        this._order = euler._order;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param matrix
     * @param order
     */
    public setFromRotationMatrix(matrix: Matrix3 | Matrix4, order: Nullable<EulerOrder>): Euler {

        const clamp = MathUtils.clamp;

        const te = matrix.elements;
        const m11 = te[0];
        const m12 = te[4];
        const m13 = te[8];
        const m21 = te[1];
        const m22 = te[5];
        const m23 = te[9];
        const m31 = te[2];
        const m32 = te[6];
        const m33 = te[10];

        order = order || this._order;

        switch (order) {

            case EulerOrder.XYZ:

                this._y = Math.asin(clamp(m13, -1, 1));

                if (Math.abs(m13) < 0.9999999) {

                    this._x = Math.atan2(-m23, m33);
                    this._z = Math.atan2(-m12, m11);

                } else {

                    this._x = Math.atan2(m32, m22);
                    this._z = 0;

                }

                break;

            case EulerOrder.YXZ:

                this._x = Math.asin(-clamp(m23, -1, 1));

                if (Math.abs(m23) < 0.9999999) {

                    this._y = Math.atan2(m13, m33);
                    this._z = Math.atan2(m21, m22);

                } else {

                    this._y = Math.atan2(-m31, m11);
                    this._z = 0;

                }

                break;

            case EulerOrder.ZXY:

                this._x = Math.asin(clamp(m32, -1, 1));

                if (Math.abs(m32) < 0.9999999) {

                    this._y = Math.atan2(-m31, m33);
                    this._z = Math.atan2(-m12, m22);

                } else {

                    this._y = 0;
                    this._z = Math.atan2(m21, m11);

                }

                break;

            case EulerOrder.ZYX:

                this._y = Math.asin(-clamp(m31, -1, 1));

                if (Math.abs(m31) < 0.9999999) {

                    this._x = Math.atan2(m32, m33);
                    this._z = Math.atan2(m21, m11);

                } else {

                    this._x = 0;
                    this._z = Math.atan2(-m12, m22);

                }

                break;

            case EulerOrder.YZX:

                this._z = Math.asin(clamp(m21, -1, 1));

                if (Math.abs(m21) < 0.9999999) {

                    this._x = Math.atan2(-m23, m22);
                    this._y = Math.atan2(-m31, m11);

                } else {

                    this._x = 0;
                    this._y = Math.atan2(m13, m33);

                }

                break;

            case EulerOrder.XZY:

                this._z = Math.asin(-clamp(m12, -1, 1));

                if (Math.abs(m12) < 0.9999999) {

                    this._x = Math.atan2(m32, m22);
                    this._y = Math.atan2(m13, m11);

                } else {

                    this._x = Math.atan2(-m23, m33);
                    this._y = 0;

                }

                break;

        }

        this._order = order;

        this.triggerNotify();

        return this;
    }

    /**
     * 
     * @param quaternion
     * @param order
     */
    public setFromQuaternion(quaternion: Quaternion, order: Nullable<EulerOrder>): Euler {
        MathTmp.Matrix4[0].makeRotationFromQuaternion(quaternion);
        return this.setFromRotationMatrix(MathTmp.Matrix4[0], order);
    }

    /**
     * Vector3
     * @param vec
     * @param order
     */
    public setFromVector3(vec: Vector3, order: Nullable<EulerOrder>): Euler {
        return this.set(vec.x, vec.y, vec.z, order || this._order);
    }

    /**
     * 
     * @param newOrder
     */
    public reorder(newOrder: EulerOrder): Euler {
        MathTmp.Quaternion[0].setFromEuler(this);
        return this.setFromQuaternion(MathTmp.Quaternion[0], newOrder);
    }

    /**
     * 
     * @param euler
     */
    public equals(euler: Euler): boolean {
        return (euler._x == this._x) && (euler._y == this._y) && (euler._z == this._z) && (euler._order == this._order);
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: Euler, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        return MathUtils.equalsEpsilon(this._x, right.x, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._y, right.y, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._z, right.z, relativeEpsilon, absoluteEpsilon) &&
            this._order == right._order;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): Euler {
        this._x = array[offset];
        this._y = array[offset + 1];
        this._z = array[offset + 2];
        this._order = array[offset + 3];
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {
        array[offset] = this._x;
        array[offset + 1] = this._y;
        array[offset + 2] = this._z;
        array[offset + 3] = this._order;
        return array;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Euler {
        this._x = dataBuffer.readFloat32();
        this._y = dataBuffer.readFloat32();
        this._z = dataBuffer.readFloat32();
        this._order = dataBuffer.readInt8();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Euler {
        dataBuffer.writeFloat32(this._x);
        dataBuffer.writeFloat32(this._y);
        dataBuffer.writeFloat32(this._z);
        dataBuffer.writeInt8(this._order);
        return this;
    }
}
