import { EllipseCurve } from "./EllipseCurve";

/**
 * 
 * @category Curve
 */
export class ArcCurve extends EllipseCurve {

    /**
     * 
     * @param aX
     * @param aY
     * @param aRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     */
    constructor(aX: number = 0, aY: number = 0, aRadius: number = 1,
                aStartAngle: number = 0, aEndAngle: number = 2 * Math.PI,
                aClockwise: boolean = false) {
        super(aX, aY, aRadius, aRadius, aStartAngle, aEndAngle, aClockwise);

        this.type = 'ArcCurve';
    }

    /**
     * 
     */
    public clone(): ArcCurve {
        return new ArcCurve().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: ArcCurve): ArcCurve {
        return super.copy(source);
    }
}
