import { Texture } from "./Texture";
import { Nullable } from "../../types";

/**
 * 
 */
export class TextureVideo extends Texture {

    public autoUpdateTexture: boolean;
    public autoPlay: boolean;
    public loop: boolean;
    public video: HTMLVideoElement;
    public currentSrc: Nullable<string | string[] | HTMLVideoElement>;
    public frameId: number;

    /**
     * 
     * @param src
     * @param autoplay
     * @param loop
     * @param generateMipMaps
     * @param flip
     */
    constructor(src: string | string[] | HTMLVideoElement, autoplay: boolean = true, loop: boolean = true,
                generateMipMaps: boolean = false, flip: boolean = false) {
        super();

        this.currentSrc = src;
        this.video = this._getVideo(src);
        this.video.autoplay = true;
        this.video.loop = true;
        this.video.muted = true;
        this.video.setAttribute("playsinline", "");
    }

    public get width() {
        return this.video?.width;
    }

    public get height() {
        return this.video?.height;
    }

    /**
     * @ignore
     * @param src
     * @private
     */
    private _getVideo(src: string | string[] | HTMLVideoElement): HTMLVideoElement {
        if (src instanceof HTMLVideoElement) {
            return src;
        }
        const video: HTMLVideoElement = document.createElement("video");
        if (typeof src == "string") {
            video.src = src;
        } else {
            src.forEach((url) => {
                const source = document.createElement("source");
                source.src = url;
                video.appendChild(source);
            });
        }
        return video;
    }

    /**
     * 
     */
    public get isReady(): boolean {
        return this.video != null;
    }

    /**
     * 
     */
    public clone(): TextureVideo {
        return new TextureVideo(this.currentSrc!, this.autoPlay, this.loop).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TextureVideo): TextureVideo {
        super.copy(source);

        this.autoUpdateTexture = source.autoUpdateTexture;
        return this;
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        this.currentSrc = null;
        this.video.pause();
    }
}
