import { WebGLEngine } from "../WebGLEngine";
import { Constants } from "../Constants";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {

        depthLocked: boolean;
        currentDepthTest: boolean;
        currentDepthMask: boolean;
        currentDepthClear: number;
        currentDepthFunc: number;

        _initDepthState(): WebGLEngine;
        setDepthTest(test: boolean): WebGLEngine;
        setDepthMask(mask: boolean): WebGLEngine;
        setDepthFunc(func: number): WebGLEngine;
        setDepthLock(lock: boolean): WebGLEngine;
        setDepthClear(depth: number): WebGLEngine;
        setDepthReset(): WebGLEngine;
    }
}

WebGLEngine.prototype._initDepthState = function(): WebGLEngine {
    this.depthLocked = false;
    this.currentDepthTest = true;
    this.currentDepthMask = true;
    this.currentDepthClear = 0;
    this.currentDepthFunc = Constants.LessEqualDepth;

    const gl = this.gl;

    gl.enable(gl.DEPTH_TEST);
    gl.depthMask(true);
    gl.depthFunc(gl.LEQUAL);

    return this;
};

WebGLEngine.prototype.setDepthTest = function(test: boolean): WebGLEngine {

    const gl = this.gl;

    if (this.currentDepthTest != test) {
        if (test) {
            gl.enable(gl.DEPTH_TEST);
        } else {
            gl.disable(gl.DEPTH_TEST);
        }
        this.currentDepthTest = test;
    }

    return this;
};

WebGLEngine.prototype.setDepthMask = function(mask: boolean): WebGLEngine {

    const gl = this.gl;

    if (this.currentDepthMask != mask && !this.depthLocked) {
        gl.depthMask(mask);
        this.currentDepthMask = mask;
    }
    return this;
};

WebGLEngine.prototype.setDepthFunc = function(func: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentDepthFunc != func) {
        if (func) {
            switch (func) {
                case Constants.NeverDepth:

                    gl.depthFunc(gl.NEVER);
                    break;

                case Constants.AlwaysDepth:

                    gl.depthFunc(gl.ALWAYS);
                    break;

                case Constants.LessDepth:

                    gl.depthFunc(gl.LESS);
                    break;

                case Constants.LessEqualDepth:

                    gl.depthFunc(gl.LEQUAL);
                    break;

                case Constants.EqualDepth:

                    gl.depthFunc(gl.EQUAL);
                    break;

                case Constants.GreaterEqualDepth:

                    gl.depthFunc(gl.GEQUAL);
                    break;

                case Constants.GreaterDepth:

                    gl.depthFunc(gl.GREATER);
                    break;

                case Constants.NotEqualDepth:

                    gl.depthFunc(gl.NOTEQUAL);
                    break;

                default:

                    gl.depthFunc(gl.LEQUAL);
            }
        } else {
            gl.depthFunc(gl.LEQUAL);
        }

        this.currentDepthFunc = func;
    }
    return this;
};

WebGLEngine.prototype.setDepthLock = function(lock: boolean): WebGLEngine {
    this.depthLocked = lock;
    return this;
};

WebGLEngine.prototype.setDepthClear = function(depth: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentDepthClear != depth) {
        gl.clearDepth(depth);
        this.currentDepthClear = depth;
    }
    return this;
};

WebGLEngine.prototype.setDepthReset = function(): WebGLEngine {

    this.depthLocked = false;

    this.currentDepthMask = true;
    this.currentDepthFunc = -1;
    this.currentDepthClear = -1;

    return this;
};
