import { Nullable } from "../../types";
import { MathUtils, Matrix3, Vector2 } from "../../Math";
import { Constants } from "../Constants";
import { GLTexture } from "../GLTexture";

/**
 * 
 * @category Engine
 */
export class Texture {

    /**
     * uuid
     */
    public uuid: string;
    /**
     * 
     */
    public name: string;

    /**
     * 
     */
    public width: number;
    /**
     * 
     */
    public height: number;
    /**
     * 
     */
    public compression: boolean;

    /**
     * 
     */
    public format: number;
    /**
     * 
     */
    public type: number;
    /**
     * 
     */
    public mapping: number;
    /**
     * 
     */
    public encoding: number;
    /**
     * 
     */
    public unpackAlignment: number;
    /**
     * Mipmaps
     */
    public generateMipmaps: boolean;
    /**
     * Alpha
     */
    public premultiplyAlpha: boolean;

    /**
     * @ignore
     */
    protected _wrapU: number;
    /**
     * @ignore
     */
    protected _wrapV: number;
    /**
     * @ignore
     */
    protected _wrapR: number;
    /**
     * @ignore
     */
    protected _filter: number;
    /**
     * @ignore
     */
    protected _flipY: boolean;
    /**
     * @ignore
     */
    protected _anisotropy: number;

    /**
     * @ignore
     */
    protected _offset: Vector2;
    /**
     * @ignore
     */
    protected _repeat: Vector2;
    /**
     * @ignore
     */
    protected _center: Vector2;
    /**
     * @ignore
     */
    protected _rotation: number;
    /**
     * @ignore
     */
    protected _matrix: Matrix3;
    /**
     * @ignore
     */
    protected _needsUpdateMatrix: boolean;

    /**
     * @ignore
     */
    protected _version: number;
    /**
     * @ignore
     */
    protected _reference: number;
    /**
     * @ignore
     */
    protected _glTexture: Nullable<GLTexture>;

    /**
     * 
     */
    constructor() {

        this.uuid = MathUtils.generateUUID();
        this.name = '';

        this.format = Constants.RGBAFormat;
        this.type = Constants.UnsignedByteType;
        this.encoding = Constants.LinearEncoding;
        this.mapping = Constants.UVMapping;
        this.unpackAlignment = 4;
        this.generateMipmaps = true;
        this.premultiplyAlpha = false;

        this._wrapU = Constants.ClampToEdgeWrapping;
        this._wrapV = Constants.ClampToEdgeWrapping;
        this._wrapR = Constants.ClampToEdgeWrapping;
        this._filter = Constants.LinearLinearMipLinearFilter;
        this._anisotropy = 1;

        this._flipY = true;
        this._offset = new Vector2(0, 0);
        this._repeat = new Vector2(1, 1);
        this._center = new Vector2(0, 0);
        this._rotation = 0;
        this._matrix = new Matrix3();
        this._needsUpdateMatrix = false;

        this._version = 0;
        this._reference = 0;
        this._glTexture = null;
    }

    /**
     * 
     */
    public get isReady(): boolean {
        return true;
    }

    /**
     * UV
     */
    public get wrapU() {
        return this._wrapU;
    }
    public set wrapU(value) {
        if (this._wrapU != value) {
            this._wrapU = value;
            this.needsUpdate = true;
        }
    }

    /**
     * UV
     */
    public get wrapV() {
        return this._wrapV;
    }
    public set wrapV(value) {
        if (this._wrapV != value) {
            this._wrapV = value;
            this.needsUpdate = true;
        }
    }

    /**
     * UV
     */
    public get wrapR() {
        return this._wrapR;
    }
    public set wrapR(value) {
        if (this._wrapR != value) {
            this._wrapR = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get filter() {
        return this._filter;
    }
    public set filter(value) {
        if (this._filter != value) {
            this._filter = value;
            this.needsUpdate = true;
        }
    }

    /**
     * Y
     */
    public get flipY() {
        return this._flipY;
    }
    public set flipY(value) {
        if (this._flipY != value) {
            this._flipY = value;
            this.needsUpdate = true;
        }
    }

    /**
     * 
     */
    public get anisotropy() {
        return this._anisotropy;
    }
    public set anisotropy(value) {
        if (this._anisotropy != value) {
            this._anisotropy = value;
            this.needsUpdate = true;
        }
    }

    /**
     * UV
     */
    public get offset() {
        return this._offset;
    }
    public set offset(value) {
        if (!this._offset.equals(value)) {
            this._offset = value;
            this._needsUpdateMatrix = true;
        }
    }

    /**
     * UV
     */
    public get repeat() {
        return this._repeat;
    }
    public set repeat(value) {
        if (!this._repeat.equals(value)) {
            this._repeat = value;
            this._needsUpdateMatrix = true;
        }
    }

    /**
     * UV
     */
    public get center() {
        return this._center;
    }
    public set center(value) {
        if (!this._center.equals(value)) {
            this._center = value;
            this._needsUpdateMatrix = true;
        }
    }

    /**
     * UV
     */
    public get rotation() {
        return this._rotation;
    }
    public set rotation(value) {
        if (this._rotation != value) {
            this._rotation = value;
            this._needsUpdateMatrix = true;
        }
    }

    /**
     * UV
     */
    public get matrix() {
        if (this._needsUpdateMatrix) {
            this.updateMatrix();
        }
        return this._matrix;
    }
    public set matrix(value) {
        this._matrix.copy(value);
    }

    /**
     * 
     */
    public get version() {
        return this._version;
    }

    /**
     * GL
     */
    public get glTexture() {
        return this._glTexture;
    }
    public set glTexture(value) {
        if (this._glTexture != value) {
            if (this._glTexture) {
                this._glTexture.addReference();
            }
            this._glTexture = value;
            if (this._glTexture) {
                this._glTexture.dispose();
            }
        }
    }

    /**
     * 
     * @param value
     */
    public set needsUpdate(value: boolean) {
        if (value) {
            this._version++;
        }
    }

    /**
     * UV
     */
    public updateMatrix(): void {
        this._needsUpdateMatrix = false;
        this._matrix.setUvTransform(this._offset.x, this._offset.y, this._repeat.x, this._repeat.y, this._rotation, this._center.x, this._center.y);
    }

    /**
     * 
     */
    public addReference(): void {
        this._reference++;
    }

    /**
     * GL
     */
    public releaseGLTexture(): void {
        if (this._glTexture) {
            this._glTexture.dispose();
            this._glTexture = null;
        }
    }

    /**
     * 
     */
    public clone(): Texture {
        return new Texture().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: Texture): Texture {
        this.name = source.name;

        this.format = source.format;
        this.type = source.type;
        this.mapping = source.mapping;
        this.generateMipmaps = source.generateMipmaps;
        this.premultiplyAlpha = source.premultiplyAlpha;
        this.unpackAlignment = source.unpackAlignment;
        this.encoding = source.encoding;

        this._offset.copy(source.offset);
        this._repeat.copy(source.repeat);
        this._center.copy(source.center);
        this._rotation = source.rotation;
        this._matrix.copy(source.matrix);

        this._flipY = source.flipY;
        this._wrapU = source.wrapU;
        this._wrapV = source.wrapV;
        this._wrapR = source.wrapR;
        this._filter = source.filter;
        this._anisotropy = source.anisotropy;

        return this;
    }

    /**
     * 
     */
    public dispose(): void {
        this._reference--;
        if (this._reference == 0) {
            this.releaseGLTexture();
        }
    }
}
