import { EllipseCurve } from "./EllipseCurve";
import { ArcCurve } from "./ArcCurve";
import { CubicBezierCurve } from "./CubicBezierCurve";
import { CubicBezierCurve3 } from "./CubicBezierCurve3";
import { LineCurve } from "./LineCurve";
import { LineCurve3 } from "./LineCurve3";
import { QuadraticBezierCurve } from "./QuadraticBezierCurve";
import { QuadraticBezierCurve3 } from "./QuadraticBezierCurve3";
import { SplineCurve } from "./SplineCurve";
import { CatmullRomCurve } from "./CatmullRomCurve";
import { CatmullRomCurve3 } from "./CatmullRomCurve3";

/**
 * @ignore
 */
export function createCurve(type: string): any {
    switch (type) {
        case 'EllipseCurve': return new EllipseCurve();
        case 'ArcCurve': return new ArcCurve();
        case 'CubicBezierCurve': return new CubicBezierCurve();
        case 'CubicBezierCurve3': return new CubicBezierCurve3();
        case 'LineCurve': return new LineCurve();
        case 'LineCurve3': return new LineCurve3();
        case 'QuadraticBezierCurve': return new QuadraticBezierCurve();
        case 'QuadraticBezierCurve3': return new QuadraticBezierCurve3();
        case 'SplineCurve': return new SplineCurve();
        case 'CatmullRomCurve': return new CatmullRomCurve();
        case 'CatmullRomCurve3': return new CatmullRomCurve3();
        default:
            throw Error('createCurve error!');
    }
}
