import { WebGLEngine } from "../WebGLEngine";
import { Vector4 } from "../../Math/Vector4";
import { MathTmp } from "../../Math/MathTmp";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {

        colorLocked: boolean;
        colorState: Vector4;
        currentColorMask: Vector4;
        currentColorClear: Vector4;

        _initColorState(): WebGLEngine;
        setColorStateMask(rMask: boolean, gMask?: boolean, bMask?: boolean, aMask?: boolean): WebGLEngine;
        setColorStateLocked(lock: boolean): WebGLEngine;
        setColorStateClear(r: number, g: number, b: number, a: number, premultipliedAlpha: boolean): WebGLEngine;
        setColorStateReset(): WebGLEngine;
    }
}

WebGLEngine.prototype._initColorState = function(): WebGLEngine {

    const gl = this.gl;
    this.colorLocked = false;
    this.colorState = new Vector4();
    this.currentColorMask = new Vector4(1, 1, 1, 1);
    this.currentColorClear = new Vector4(1, 1, 1, 1);
    gl.colorMask(true, true, true, true);
    gl.clearColor(1, 1, 1, 1);

    return this;
};

WebGLEngine.prototype.setColorStateMask = function(rMask: boolean, gMask?: boolean, bMask?: boolean, aMask?: boolean): WebGLEngine {

    const gl = this.gl;

    gMask = gMask ?? rMask;
    bMask = bMask ?? rMask;
    aMask = aMask ?? rMask;

    MathTmp.Vector4[0].set(rMask ? 1 : 0, gMask ? 1 : 0, bMask ? 1 : 0, aMask ? 1 : 0);

    if (!this.currentColorMask.equals(MathTmp.Vector4[0]) && !this.colorLocked) {
        gl.colorMask(rMask, gMask, bMask, aMask);
        this.currentColorMask.copy(MathTmp.Vector4[0]);
    }
    return this;
};

WebGLEngine.prototype.setColorStateLocked = function(lock: boolean): WebGLEngine {
    this.colorLocked = lock;
    return this;
};

WebGLEngine.prototype.setColorStateClear = function(r: number, g: number, b: number, a: number, premultipliedAlpha: boolean): WebGLEngine {

    const gl = this.gl;

    if (premultipliedAlpha) {
        r *= a; g *= a; b *= a;
    }

    this.colorState.set(r, g, b, a);

    if (!this.currentColorClear.equals(this.colorState)) {
        gl.clearColor(r, g, b, a);
        this.currentColorClear.copy(this.colorState);
    }
    return this;
};

WebGLEngine.prototype.setColorStateReset = function(): WebGLEngine {

    this.colorLocked = false;
    this.currentColorMask.set(-1, 0, 0, 0);
    this.currentColorClear.set(-1, 0, 0, 0);
    return this;
};
