import { IDisposable } from "../Misc/IDisposable";
import { Event } from "./Event";

/**
 * @category Event
 */
interface EventListenerObject {
    handleEvent(evt: Event): void;
}

/**
 * @category Event
 */
interface EventListener {
    (evt: Event): void;
}

/**
 * @category Event
 */
declare type EventListenerOrEventListenerObject = EventListener | EventListenerObject;
/**
 * @category Event
 */
declare type EventListeners = Map<string, Array<EventListenerOrEventListenerObject>>;

/**
 * 
 * @category Event
 */
export class EventDispatcher implements IDisposable {

    private _listeners: EventListeners;

    /**
     * 
     */
    constructor() {
        this._listeners = new Map<string, Array<EventListenerOrEventListenerObject>>();
    }

    /**
     * 
     * @param type
     */
    hasEventListener(type: string): boolean {
        const listeners = this._listeners;
        return listeners.has(type);
    }

    /**
     * 
     * @param type
     * @param listener
     */
    addEventListener(type: string, listener: EventListenerOrEventListenerObject): EventListenerOrEventListenerObject {

        const listeners: EventListeners = this._listeners;

        let arr = listeners.get(type);
        if (arr) {
            this.removeEventListener(type, listener);
            arr = listeners.get(type);
        }
        if (!arr) {
            arr = new Array<EventListenerOrEventListenerObject>();
            listeners.set(type, arr);
        }
        arr.push(listener);
        return listener;
    }

    /**
     * 
     * @param type
     * @param listener
     */
    removeEventListener(type: string, listener: EventListenerOrEventListenerObject): void {

        const listeners: EventListeners = this._listeners;
        if (!listeners) {
            return;
        }

        const arr = listeners.get(type);
        if (!arr) {
            return;
        }

        const idx = arr.indexOf(listener);
        if (idx >= 0) {
            arr.splice(idx, 1);
        }

        if (arr.length <= 0) {
            listeners.delete(type);
        }
    }

    /**
     * 
     * @param type
     */
    removeAllEventListeners(type: string | null): void {
        if (type) {
            if (this._listeners) { this._listeners.delete(type); }
        } else {
            this._listeners.clear();
        }
    }

    /**
     * 
     * @param event
     */
    dispatchEvent(event: Event): void {

        event.target = this;
        event.removed = false;

        const listeners: EventListeners = this._listeners;

        let arr = listeners.get(event.type);
        if (!arr || arr.length <= 0) {
            return;
        }

        arr = arr.slice();

        let l = arr.length;
        for (let i = 0; i < l && !event.propagationStopped; i++) {
            let o = arr[i];

            if ((o as EventListenerObject).handleEvent != undefined) {
                (o as EventListenerObject).handleEvent(event);
            }
            else {
                o = o as EventListener;
                o(event);
            }

            if (event.removed) {
                this.removeEventListener(event.type, o);
                event.removed = false;
            }
        }

    }

    /**
     * 
     */
    dispose(): void {
        this._listeners.clear();
        delete this._listeners;
    }
}
