import { WebGLEngine } from "../WebGLEngine";
import { GLQuery } from "../GLQuery";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {
        _updateQuery(): void;
        createQuery(conservative: boolean): GLQuery;
        deleteQuery(query: GLQuery): WebGLEngine;
        beginQuery(query: GLQuery): boolean;
        endQuery(query: GLQuery): boolean;
    }
}

WebGLEngine.prototype._updateQuery = function(): void {
    this._cachedQueries.forEach((query) => {
        if (query.pending && this.gl.getQueryParameter(query.handler, this.gl.QUERY_RESULT_AVAILABLE)) {
            query.result = this.gl.getQueryParameter(query.handler, this.gl.QUERY_RESULT);
            query.pending = false;
        }
    });
};

WebGLEngine.prototype.createQuery = function(conservative: boolean): GLQuery {
    const query = new GLQuery(this.gl.createQuery(), conservative ? this.gl.ANY_SAMPLES_PASSED_CONSERVATIVE : this.gl.ANY_SAMPLES_PASSED);
    this._cachedQueries.push(query);
    return query;
};

WebGLEngine.prototype.deleteQuery = function(query: GLQuery): WebGLEngine {
    const index = this._cachedQueries.indexOf(query);
    this._cachedQueries.splice(index, 1);
    this.gl.deleteQuery(query.handler);
    return this;
};

WebGLEngine.prototype.beginQuery = function(query: GLQuery): boolean {

    if (!query.pending) {

        this.gl.beginQuery(query.target, query.handler);

        return true;
    }

    return false;
};

WebGLEngine.prototype.endQuery = function(query: GLQuery): boolean {

    if (!query.pending) {

        query.pending = true;

        this.gl.endQuery(query.target);

        return true;
    }

    return false;
};
