import { Nullable } from "../types";
import { MathUtils } from "./MathUtils";
import { Segment2Point2Distance, Segment2Segment2Distance } from "./InterfaceType/DistanceResult";
import { Vector2 } from "./Vector2";
import { Box2 } from "./Box2";
import { Segment2Box2Intersect, Segment2Segment2Intersect } from "./InterfaceType/IntersectResult";
import { Line2 } from "./Line2";
import { Intervals } from "./Algorithm/Intervals";

/**
 * @ignore
 */
const _tempVec1 = new Vector2();
/**
 * @ignore
 */
const _tempVec2 = new Vector2();
/**
 * @ignore
 */
const _tempVec3 = new Vector2();
/**
 * @ignore
 */
const _tempVec4 = new Vector2();
/**
 * @ignore
 */
const _tempVec5 = new Vector2();
/**
 * @ignore
 */
const _tempVec6 = new Vector2();
/**
 * @ignore
 */
let mA: number, mB: number, mC: number, mD: number, mE: number;

/**
 * @ignore
 */
let mF00: number, mF10: number, mF01: number, mF11: number;

/**
 * @ignore
 */
let mG00: number, mG10: number, mG01: number, mG11: number;

/**
 * 2D
 * @category Math
 */
export class LineSegment2 {

    /**
     * @ignore
     */
    private readonly _p: Vector2[];

    /**
     * 
     * @param start
     * @param end
     */
    constructor(start: Nullable<Vector2> = null, end: Nullable<Vector2> = null) {
        this._p = [new Vector2(), new Vector2()];

        if (start) {
            this._p[0].copy(start);
        }
        if (end) {
            this._p[1].copy(end);
        }
    }

    /**
     * 
     */
    get p() {
        return this._p;
    }

    /**
     * 
     */
    get start() {
        return this._p[0];
    }
    set start(value) {
        this._p[0].copy(value);
    }

    /**
     * 
     */
    get end() {
        return this._p[1];
    }
    set end(value) {
        this._p[1].copy(value);
    }

    /**
     * 
     * @param start
     * @param end
     */
    public set(start: Vector2, end: Vector2): LineSegment2 {
        this.start.copy(start);
        this.end.copy(end);
        return this;
    }

    /**
     * 
     */
    public clone(): LineSegment2 {
        return new LineSegment2().copy(this);
    }

    /**
     * 
     * @param line
     */
    public copy(line: LineSegment2): LineSegment2 {
        this.start.copy(line.start);
        this.end.copy(line.end);
        return this;
    }

    /**
     * 
     * @param target
     */
    public getCenter(target = new Vector2()): Vector2 {
        return target.addVectors(this.start, this.end).multiplyScalar(0.5);
    }

    /**
     * 
     * @param target
     */
    public delta(target = new Vector2()): Vector2 {
        return target.subVectors(this.end, this.start);
    }

    /**
     * SQ
     */
    public distanceSq(): number {
        return this.start.distanceToSquared(this.end);
    }

    /**
     * 
     */
    public distance(): number {
        return this.start.distanceTo(this.end);
    }

    /**
     * 
     * @param t
     * @param target
     */
    public at(t: number, target = new Vector2()): Vector2 {
        return this.delta(target).multiplyScalar(t).add(this.start);
    }

    /**
     * 
     * @param point
     * @param clampToLine
     */
    public closestPointToPointParameter(point: Vector2, clampToLine: boolean = false): number {

        _tempVec1.subVectors(point, this.start);
        _tempVec2.subVectors(this.end, this.start);

        const startEnd2 = _tempVec2.dot(_tempVec2);
        const startEnd_startP = _tempVec2.dot(_tempVec1);

        let t = startEnd_startP / startEnd2;

        if (clampToLine) {
            t = MathUtils.clamp(t, 0, 1);
        }
        return t;
    }

    /**
     * 
     * @param point
     * @param clampToLine
     * @param target
     */
    public closestPointToPoint(point: Vector2, clampToLine: boolean = false, target = new Vector2()): Vector2 {
        const t = this.closestPointToPointParameter(point, clampToLine);
        return this.delta(target).multiplyScalar(t).add(this.start);
    }

    /**
     * 
     * @param center
     * @param direction
     * @param extent
     */
    public setCenteredForm(center: Vector2, direction: Vector2, extent: number): LineSegment2 {
        this._p[0].copy(direction).multiplyScalar(extent).subVectors(center, this._p[0]);
        this._p[1].copy(direction).multiplyScalar(extent).add(center);
        return this;
    }

    /**
     * 
     * @param center
     * @param direction
     */
    public getCenteredForm(center: Vector2, direction: Vector2): number {
        center.addVectors(this._p[0], this._p[1]).multiplyScalar(0.5);
        direction.subVectors(this._p[1], this._p[0]);
        const extent = direction.length() * 0.5;
        direction.normalize();
        return extent;
    }

    /**
     * SQ
     * @param point
     * @param closestPoint
     * @param parameter
     */
    public distanceSqToPoint(point: Vector2, closestPoint: Nullable<Vector2> = null, parameter: Nullable<Segment2Point2Distance> = null): number {

        const direction = _tempVec1.subVectors(this.p[1], this.p[0]);
        const diff = _tempVec2.subVectors(point, this.p[1]);
        let t = direction.dot(diff);

        let segmentParameter = 0;
        let segmentClosest;

        if (t >= 0)
        {
            segmentParameter = 1;
            segmentClosest = this.p[1];
        }
        else
        {
            diff.subVectors(point, this.p[0]);
            t = direction.dot(diff);
            if (t <= 0)
            {
                segmentParameter = 0;
                segmentClosest = this.p[0];
            }
            else
            {
                const sqrLength = direction.dot(direction);
                if (sqrLength > 0)
                {
                    t /= sqrLength;
                    segmentParameter = t;
                    segmentClosest = _tempVec3.copy(direction).multiplyScalar(t).add(this.p[0]);
                }
                else
                {
                    segmentParameter = 0;
                    segmentClosest = this.p[0];
                }
            }
        }

        if (closestPoint) {
            closestPoint.copy(segmentClosest);
        }
        if (parameter) {
            parameter.segmentParameter = segmentParameter;
        }

        diff.subVectors(point, segmentClosest);
        return diff.dot(diff);
    }

    /**
     * 
     * @param point
     * @param segmentClosest
     * @param parameter
     */
    public distanceToPoint(point: Vector2, segmentClosest: Nullable<Vector2> = null, parameter: Nullable<Segment2Point2Distance> = null): number {
        return Math.sqrt(this.distanceSqToPoint(point, segmentClosest, parameter));
    }

    /**
     * SQ
     * @param segment
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToSegment(segment: LineSegment2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Segment2Segment2Distance> = null): number {
        const P0 = this.start;
        const P1 = this.end;

        const Q0 = segment.start;
        const Q1 = segment.end;

        const P1mP0 = _tempVec1.subVectors(P1, P0);
        const Q1mQ0 = _tempVec2.subVectors(Q1, Q0);
        const P0mQ0 = _tempVec3.subVectors(P0, Q0);
        mA = P1mP0.dot(P1mP0);
        mB = P1mP0.dot(Q1mQ0);
        mC = Q1mQ0.dot(Q1mQ0);
        mD = P1mP0.dot(P0mQ0);
        mE = Q1mQ0.dot(P0mQ0);

        mF00 = mD;
        mF10 = mF00 + mA;
        mF01 = mF00 - mB;
        mF11 = mF10 - mB;

        mG00 = -mE;
        mG10 = mG00 - mB;
        mG01 = mG00 + mC;
        mG11 = mG10 + mC;

        const ssparameter = [0, 0];

        if (mA > 0 && mC > 0) {
            // Compute the solutions to dR/ds(s0,0) = 0 and
            // dR/ds(s1,1) = 0.  The location of sI on the s-axis is
            // stored in classifyI (I = 0 or 1).  If sI <= 0, classifyI
            // is -1.  If sI >= 1, classifyI is 1.  If 0 < sI < 1,
            // classifyI is 0.  This information helps determine where to
            // search for the minimum point (s,t).  The fij values are
            // dR/ds(i,j) for i and j in {0,1}.

            const sValue = [0, 0];
            sValue[0] = getClampedRoot(mA, mF00, mF10);
            sValue[1] = getClampedRoot(mA, mF01, mF11);

            const classify = [0, 0];
            for (let i = 0; i < 2; ++i) {
                if (sValue[i] <= 0) {
                    classify[i] = -1;
                }
                else if (sValue[i] >= 1) {
                    classify[i] = +1;
                }
                else {
                    classify[i] = 0;
                }
            }

            if (classify[0] == -1 && classify[1] == -1) {
                // The minimum must occur on s = 0 for 0 <= t <= 1.
                ssparameter[0] = 0;
                ssparameter[1] = getClampedRoot(mC, mG00, mG01);
            }
            else if (classify[0] == +1 && classify[1] == +1) {
                // The minimum must occur on s = 1 for 0 <= t <= 1.
                ssparameter[0] = 1;
                ssparameter[1] = getClampedRoot(mC, mG10, mG11);
            }
            else {
                // The line dR/ds = 0 intersects the domain [0,1]^2 in a
                // nondegenerate segment.  Compute the endpoints of that
                // segment, end[0] and end[1].  The edge[i] flag tells you
                // on which domain edge end[i] lives: 0 (s=0), 1 (s=1),
                // 2 (t=0), 3 (t=1).
                const edge = [0, 0];
                const end = [[0, 0], [0, 0]];

                computeIntersection(sValue, classify, edge, end);

                // The directional derivative of R along the segment of
                // intersection is
                //   H(z) = (end[1][1]-end[1][0]) *
                //          dR/dt((1-z)*end[0] + z*end[1])
                // for z in [0,1].  The formula uses the fact that
                // dR/ds = 0 on the segment.  Compute the minimum of
                // H on [0,1].
                computeMinimumParameters(edge, end, ssparameter);
            }
        }
        else {
            if (mA > 0) {
                // The Q-segment is degenerate (Q0 and Q1 are the same
                // point) and the quadratic is R(s,0) = a*s^2 + 2*d*s + f
                // and has (half) first derivative F(t) = a*s + d.  The
                // closest P-point is interior to the P-segment when
                // F(0) < 0 and F(1) > 0.
                ssparameter[0] = getClampedRoot(mA, mF00, mF10);
                ssparameter[1] = 0;
            }
            else if (mC > 0) {
                // The P-segment is degenerate (P0 and P1 are the same
                // point) and the quadratic is R(0,t) = c*t^2 - 2*e*t + f
                // and has (half) first derivative G(t) = c*t - e.  The
                // closest Q-point is interior to the Q-segment when
                // G(0) < 0 and G(1) > 0.
                ssparameter[0] = 0;
                ssparameter[1] = getClampedRoot(mC, mG00, mG01);
            }
            else {
                // P-segment and Q-segment are degenerate.
                ssparameter[0] = 0;
                ssparameter[1] = 0;
            }
        }

        _tempVec4.addVectors(_tempVec1.copy(P0).multiplyScalar(1 - ssparameter[0]), _tempVec2.copy(P1).multiplyScalar(ssparameter[0]));
        _tempVec5.addVectors(_tempVec1.copy(Q0).multiplyScalar(1 - ssparameter[1]), _tempVec2.copy(Q1).multiplyScalar(ssparameter[1]));

        if (closestPoint0) {
            closestPoint0.copy(_tempVec4);
        }
        if (closestPoint1) {
            closestPoint1.copy(_tempVec5);
        }
        if (parameter) {
            parameter.parameter = ssparameter;
        }

        const diff = _tempVec3.subVectors(_tempVec4, _tempVec5);
        return diff.dot(diff);
    }

    /**
     * 
     * @param segment
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToSegment(segment: LineSegment2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Segment2Segment2Distance> = null): number {
        return Math.sqrt(this.distanceSqToSegment(segment, closestPoint0, closestPoint1, parameter));
    }

    /**
     * 
     * @param box
     */
    public intersectsBox(box: Box2): boolean {

        const boxCenter = _tempVec1;
        const boxExtent = _tempVec2;
        box.getCenter(boxCenter);
        box.getExtent(boxExtent);

        const transformedP0 = _tempVec3.subVectors(this.p[0], boxCenter);
        const transformedP1 = _tempVec4.subVectors(this.p[1], boxCenter);
        const transformedSegment = new LineSegment2(transformedP0, transformedP1);

        const segOrigin = _tempVec3;
        const segDirection = _tempVec4;
        const segExtent = transformedSegment.getCenteredForm(segOrigin, segDirection);

        return LineSegment2.intersectsBox(segOrigin, segDirection, segExtent, boxExtent);
    }

    /**
     * @ignore
     * @param segOrigin
     * @param segDirection
     * @param segExtent
     * @param boxExtent
     */
    public static intersectsBox(segOrigin: Vector2, segDirection: Vector2, segExtent: number, boxExtent: Vector2): boolean {

        for (let i = 0; i < 2; ++i) {
            const lhs = Math.abs(segOrigin.p[i]);
            const rhs = boxExtent.p[i] + segExtent * Math.abs(segDirection.p[i]);
            if (lhs > rhs) {
                return false;
            }
        }

        return Line2.intersectsBox(segOrigin, segDirection, boxExtent);
    }

    /**
     * 
     * @param box
     */
    public intersectBox(box: Box2): Segment2Box2Intersect {

        const boxCenter = _tempVec1;
        const boxExtent = _tempVec2;
        box.getCenter(boxCenter);
        box.getExtent(boxExtent);

        const transformedP0 = _tempVec3.subVectors(this.p[0], boxCenter);
        const transformedP1 = _tempVec4.subVectors(this.p[1], boxCenter);
        const transformedSegment = new LineSegment2(transformedP0, transformedP1);

        const segOrigin = _tempVec3;
        const segDirection = _tempVec4;
        const segExtent = transformedSegment.getCenteredForm(segOrigin, segDirection);

        const result = {
            intersect: false,
            numIntersections: 0,
            parameter: new Array<number>(2),
            point: new Array<Vector2>(2),
            cdeParameter: new Array<number>(2),
        };

        LineSegment2.intersectBox(segOrigin, segDirection, segExtent, boxExtent, result);

        for (let i = 0; i < result.numIntersections; ++i)
        {
            // Compute the segment in the aligned-box coordinate system
            // and then translate it back to the original coordinates
            // using the box cener.
            result.point[i] = new Vector2().copy(segDirection).multiplyScalar(result.parameter[i]).add(segOrigin).add(boxCenter);
            result.cdeParameter[i] = result.parameter[i];

            // Convert the parameters from the centered form to the
            // endpoint form.
            result.parameter[i] = (result.parameter[i] / segExtent + 1) * 0.5;
        }

        return result;
    }

    /**
     * @ignore
     * @param segOrigin
     * @param segDirection
     * @param segExtent
     * @param boxExtent
     * @param result
     */
    public static intersectBox(segOrigin: Vector2, segDirection: Vector2, segExtent: number, boxExtent: Vector2, result: Segment2Box2Intersect) {

        Line2.intersectBox(segOrigin, segDirection, boxExtent, result);

        if (result.intersect)
        {
            // The line containing the segment intersects the box; the
            // t-interval is [t0,t1].  The segment intersects the box as
            // long as [t0,t1] overlaps the segment t-interval
            // [-segExtent,+segExtent].
            const segInterval = [ -segExtent, segExtent ];

            const iiResult = Intervals.staticQuery(result.parameter, segInterval);
            result.intersect = iiResult.intersect;
            result.numIntersections = iiResult.numIntersections;
            result.parameter = iiResult.overlap;
        }
    }

    /**
     * 
     * @param segment
     */
    public intersectsSegment(segment: LineSegment2): boolean {

        const segment0 = this;
        const segment1 = segment;

        const seg0Origin = _tempVec1;
        const seg0Direction = _tempVec2;
        const seg1Origin = _tempVec3;
        const seg1Direction = _tempVec4;

        const seg0Extent = segment0.getCenteredForm(seg0Origin, seg0Direction);
        const seg1Extent = segment1.getCenteredForm(seg1Origin, seg1Direction);

        const line0 = new Line2(seg0Origin, seg0Direction);
        const line1 = new Line2(seg1Origin, seg1Direction);

        const llResult = line0.intersectLine(line1);
        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the segments.
            if (Math.abs(llResult.line0Parameter[0]) <= seg0Extent && Math.abs(llResult.line1Parameter[0]) <= seg1Extent) {
                return true;
            }
            else {
                return false;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            // Compute the location of segment1 endpoints relative to
            // segment0.
            const diff = _tempVec5.subVectors(seg1Origin, seg0Origin);
            const t = seg0Direction.dot(diff);

            // Get the parameter intervals of the segments relative to
            // segment0.
            const interval0 = [ -seg0Extent, seg0Extent ];
            const interval1 = [ t - seg1Extent, t + seg1Extent ];

            // Compute the intersection of the intervals.
            const iiResult = Intervals.staticQuery(interval0, interval1);
            return iiResult.intersect;
        }
        else
        {
            return false;
        }
    }

    /**
     * 
     * @param segment
     */
    public intersectSegment(segment: LineSegment2): Segment2Segment2Intersect {

        const result = {
            intersect: false,
            numIntersections: 0,
            segment0Parameter: new Array<number>(2),
            segment1Parameter: new Array<number>(2),
            point: new Array<Vector2>(2),
        };

        const segment0 = this;
        const segment1 = segment;

        const seg0Origin = _tempVec1;
        const seg0Direction = _tempVec2;
        const seg1Origin = _tempVec3;
        const seg1Direction = _tempVec4;

        const seg0Extent = segment0.getCenteredForm(seg0Origin, seg0Direction);
        const seg1Extent = segment1.getCenteredForm(seg1Origin, seg1Direction);

        const line0 = new Line2(seg0Origin, seg0Direction);
        const line1 = new Line2(seg1Origin, seg1Direction);

        const llResult = line0.intersectLine(line1);

        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the segments.
            if (Math.abs(llResult.line0Parameter[0]) <= seg0Extent && Math.abs(llResult.line1Parameter[0]) <= seg1Extent)
            {
                result.intersect = true;
                result.numIntersections = 1;
                result.segment0Parameter[0] = llResult.line0Parameter[0];
                result.segment1Parameter[0] = llResult.line1Parameter[0];
                result.point[0] = llResult.point;
            }
            else
            {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            // Compute the location of segment1 endpoints relative to
            // segment0.
            const diff = _tempVec5.subVectors(seg1Origin, seg0Origin);
            const t = seg0Direction.dot(diff);

            // Get the parameter intervals of the segments relative to
            // segment0.
            const interval0 = [ -seg0Extent, seg0Extent ];
            const interval1 = [ t - seg1Extent, t + seg1Extent ];

            // Compute the intersection of the intervals.
            const iiResult = Intervals.staticQuery(interval0, interval1);
            if (iiResult.intersect)
            {
                result.intersect = true;
                result.numIntersections = iiResult.numIntersections;
                for (let i = 0; i < iiResult.numIntersections; ++i)
                {
                    result.segment0Parameter[i] = iiResult.overlap[i];
                    result.segment1Parameter[i] = iiResult.overlap[i] - t;
                    result.point[i] = new Vector2().copy(seg0Direction).multiplyScalar(result.segment0Parameter[i]).add(seg0Origin);
                }
            }
            else
            {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else
        {
            result.intersect = false;
            result.numIntersections = 0;
        }

        return result;
    }

    /**
     * 
     * @param segment
     */
    public equals(segment: LineSegment2): boolean {
        return segment.start.equals(this.start) && segment.end.equals(this.end);
    }
}

/**
 * @ignore
 * @param slope
 * @param h0
 * @param h1
 */
function getClampedRoot(slope: number, h0: number, h1: number): number {
    let r;
    if (h0 < 0) {
        if (h1 > 0) {
            r = -h0 / slope;
            if (r > 1) {
                r = 0.5;
            }
        }
        else {
            r = 1;
        }
    }
    else {
        r = 0;
    }
    return r;
}

/**
 * @ignore
 * @param sValue
 * @param classify
 * @param edge
 * @param end
 */
function computeIntersection(sValue: number[], classify: number[], edge: number[], end: number[][]) {

    if (classify[0] < 0)
    {
        edge[0] = 0;
        end[0][0] = 0;
        end[0][1] = mF00 / mB;
        if (end[0][1] < 0 || end[0][1] > 1)
        {
            end[0][1] = 0.5;
        }

        if (classify[1] == 0)
        {
            edge[1] = 3;
            end[1][0] = sValue[1];
            end[1][1] = 1;
        }
        else  // classify[1] > 0
        {
            edge[1] = 1;
            end[1][0] = 1;
            end[1][1] = mF10 / mB;
            if (end[1][1] < 0 || end[1][1] > 1)
            {
                end[1][1] = 0.5;
            }
        }
    }
    else if (classify[0] == 0)
    {
        edge[0] = 2;
        end[0][0] = sValue[0];
        end[0][1] = 0;

        if (classify[1] < 0)
        {
            edge[1] = 0;
            end[1][0] = 0;
            end[1][1] = mF00 / mB;
            if (end[1][1] < 0 || end[1][1] > 1)
            {
                end[1][1] = 0.5;
            }
        }
        else if (classify[1] == 0)
        {
            edge[1] = 3;
            end[1][0] = sValue[1];
            end[1][1] = 1;
        }
        else
        {
            edge[1] = 1;
            end[1][0] = 1;
            end[1][1] = mF10 / mB;
            if (end[1][1] < 0 || end[1][1] > 1)
            {
                end[1][1] = 0.5;
            }
        }
    }
    else  // classify[0] > 0
    {
        edge[0] = 1;
        end[0][0] = 1;
        end[0][1] = mF10 / mB;
        if (end[0][1] < 0 || end[0][1] > 1)
        {
            end[0][1] = 0.5;
        }

        if (classify[1] == 0)
        {
            edge[1] = 3;
            end[1][0] = sValue[1];
            end[1][1] = 1;
        }
        else
        {
            edge[1] = 0;
            end[1][0] = 0;
            end[1][1] = mF00 / mB;
            if (end[1][1] < 0 || end[1][1] > 1)
            {
                end[1][1] = 0.5;
            }
        }
    }
}

/**
 * @ignore
 * @param edge
 * @param end
 * @param parameter
 */
function computeMinimumParameters(edge: number[], end: number[][], parameter: number[]) {

    const delta = end[1][1] - end[0][1];
    const h0 = delta * (-mB * end[0][0] + mC * end[0][1] - mE);
    if (h0 >= 0)
    {
        if (edge[0] == 0)
        {
            parameter[0] = 0;
            parameter[1] = getClampedRoot(mC, mG00, mG01);
        }
        else if (edge[0] == 1)
        {
            parameter[0] = 1;
            parameter[1] = getClampedRoot(mC, mG10, mG11);
        }
        else
        {
            parameter[0] = end[0][0];
            parameter[1] = end[0][1];
        }
    }
    else
    {
        const h1 = delta * (-mB * end[1][0] + mC * end[1][1] - mE);
        if (h1 <= 0)
        {
            if (edge[1] == 0)
            {
                parameter[0] = 0;
                parameter[1] = getClampedRoot(mC, mG00, mG01);
            }
            else if (edge[1] == 1)
            {
                parameter[0] = 1;
                parameter[1] = getClampedRoot(mC, mG10, mG11);
            }
            else
            {
                parameter[0] = end[1][0];
                parameter[1] = end[1][1];
            }
        }
        else  // h0 < 0 and h1 > 0
        {
            const z = Math.min(Math.max(h0 / (h0 - h1), 0), 1);
            const omz = 1 - z;
            parameter[0] = omz * end[0][0] + z * end[1][0];
            parameter[1] = omz * end[0][1] + z * end[1][1];
        }
    }
}
