import { Texture } from "./Texture";
import { Constants } from "../Constants";
import { Nullable } from "../../types";

/**
 * Mipmap
 */
export interface MipmapRawBuffer {
    buffer: ArrayBuffer | ArrayBufferView;
    width: number;
    height: number;
    depth?: number;
}

/**
 * 
 */
export class TextureRaw extends Texture {

    public buffer: Nullable<ArrayBuffer | ArrayBufferView>;
    public deleteBuffer: boolean;
    public mipmaps: MipmapRawBuffer[];

    /**
     * 
     * @param data
     * @param width
     * @param height
     * @param deleteBuffer
     * @param generateMipmaps
     * @param format
     * @param type
     * @param flipY
     */
    constructor(data: Nullable<ArrayBuffer | ArrayBufferView>, width: number, height: number, deleteBuffer: boolean = false,
                generateMipmaps: boolean = true, format: number = Constants.RGBAFormat, type: number = Constants.UnsignedByteType,
                flipY: boolean = false) {
        super();

        this.buffer = data;
        this.deleteBuffer = deleteBuffer;

        this.width = width;
        this.height = height;

        this.unpackAlignment = (this.width % 4 != 0) ? 1 : 4;
        this.generateMipmaps = generateMipmaps;
        this.format = format;
        this.type = type;

        this._flipY = flipY;
    }

    /**
     * 
     */
    public clone(): TextureRaw {
        return new TextureRaw(this.buffer, this.width, this.height, this.deleteBuffer).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TextureRaw): TextureRaw {
        super.copy(source);

        return this;
    }

    /**
     * 
     */
    public dispose() {
        super.dispose();

        if (this.deleteBuffer) {
            delete this.buffer;
        }
    }
}
