import { Curve } from "./Curve";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { BinaryBuffer } from "../../Misc";

/**
 * 
 * @category Curve
 */
export class EllipseCurve extends Curve<Vector2> {

    public center: Vector2;
    public xRadius: number;
    public yRadius: number;
    public aStartAngle: number;
    public aEndAngle: number;
    public aClockwise: boolean;
    public aRotation: number;

    /**
     * 
     * @param aX
     * @param aY
     * @param xRadius
     * @param yRadius
     * @param aStartAngle
     * @param aEndAngle
     * @param aClockwise
     * @param aRotation
     */
    constructor(aX: number = 0, aY: number = 0,
                xRadius: number = 1, yRadius: number = 1,
                aStartAngle: number = 0, aEndAngle: number = 2 * Math.PI,
                aClockwise: boolean = false,
                aRotation: number = 0) {
        super();

        this.type = 'EllipseCurve';
        this.center = new Vector2(aX || 0, aY || 0);
        this.xRadius = xRadius;
        this.yRadius = yRadius;
        this.aStartAngle = aStartAngle;
        this.aEndAngle = aEndAngle;
        this.aClockwise = aClockwise ;
        this.aRotation = aRotation ;
    }

    /**
     * X
     */
    public get aX() {
        return this.center.x;
    }
    public set aX(value) {
        this.center.x = value;
    }

    /**
     * Y
     */
    public get aY() {
        return this.center.y;
    }
    public set aY(value) {
        this.center.y = value;
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector2> = null): Vector2 {

        let point = optionalTarget || new Vector2();

        let twoPi = Math.PI * 2;
        let deltaAngle = this.aEndAngle - this.aStartAngle;
        let samePoints = Math.abs(deltaAngle) < Number.EPSILON;

        // ensures that deltaAngle is 0 .. 2 PI
        while (deltaAngle < 0) { deltaAngle += twoPi; }
        while (deltaAngle > twoPi) { deltaAngle -= twoPi; }

        if (deltaAngle < Number.EPSILON) {
            if (samePoints) {
                deltaAngle = 0;
            } else {
                deltaAngle = twoPi;
            }
        }

        if (this.aClockwise == true && !samePoints) {
            if (deltaAngle == twoPi) {
                deltaAngle = -twoPi;
            } else {
                deltaAngle = deltaAngle - twoPi;
            }
        }

        let angle = this.aStartAngle + t * deltaAngle;
        let x = this.aX + this.xRadius * Math.cos(angle);
        let y = this.aY + this.yRadius * Math.sin(angle);

        if (this.aRotation != 0) {
            let cos = Math.cos(this.aRotation);
            let sin = Math.sin(this.aRotation);

            let tx = x - this.aX;
            let ty = y - this.aY;

            // Rotate the point about the center of the ellipse.
            x = tx * cos - ty * sin + this.aX;
            y = tx * sin + ty * cos + this.aY;
        }

        return point.set(x, y);
    }

    /**
     * 
     */
    public clone() {
        return new EllipseCurve().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: EllipseCurve): EllipseCurve {
        super.copy(source);

        this.aX = source.aX;
        this.aY = source.aY;
        this.xRadius = source.xRadius;
        this.yRadius = source.yRadius;
        this.aStartAngle = source.aStartAngle;
        this.aEndAngle = source.aEndAngle;
        this.aClockwise = source.aClockwise;
        this.aRotation = source.aRotation;

        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();
        data.aX = this.aX;
        data.aY = this.aY;
        data.xRadius = this.xRadius;
        data.yRadius = this.yRadius;
        data.aStartAngle = this.aStartAngle;
        data.aEndAngle = this.aEndAngle;
        data.aClockwise = this.aClockwise;
        data.aRotation = this.aRotation;

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): EllipseCurve {
        super.fromJSON(json);

        this.aX = json.aX;
        this.aY = json.aY;
        this.xRadius = json.xRadius;
        this.yRadius = json.yRadius;
        this.aStartAngle = json.aStartAngle;
        this.aEndAngle = json.aEndAngle;
        this.aClockwise = json.aClockwise;
        this.aRotation = json.aRotation;

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): EllipseCurve {
        super.toDataBuffer(dataBuffer);

        dataBuffer.writeVector2(this.center);
        dataBuffer.writeFloat32(this.xRadius);
        dataBuffer.writeFloat32(this.yRadius);
        dataBuffer.writeFloat32(this.aStartAngle);
        dataBuffer.writeFloat32(this.aEndAngle);
        dataBuffer.writeFloat32(this.aRotation);
        dataBuffer.writeBoolean(this.aClockwise);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): EllipseCurve {
        super.fromDataBuffer(dataBuffer);

        this.center = dataBuffer.readVector2(this.center);
        this.xRadius = dataBuffer.readFloat32();
        this.yRadius = dataBuffer.readFloat32();
        this.aStartAngle = dataBuffer.readFloat32();
        this.aEndAngle = dataBuffer.readFloat32();
        this.aRotation = dataBuffer.readFloat32();
        this.aClockwise = dataBuffer.readBoolean();

        return this;
    }
}
