import { DeepImmutable, FloatArray, int, Nullable } from "../types";
import { InVector3, Vector3 } from "./Vector3";
import { Matrix4 } from "./Matrix4";
import { BinaryBuffer, BufferAttribute } from "../Misc";
import { Quaternion } from "./Quaternion";
import { MathUtils } from "./MathUtils";
import { MathTmp } from "./MathTmp";

type InMatrix3 = Matrix3 | DeepImmutable<Matrix3>;

/**
 * 3x3
 * @category Math
 */
export class Matrix3 {

    /**
     * @ignore
     */
    private static _identityReadOnly = new Matrix3() as DeepImmutable<Matrix3>;

    /**
     * @ignore
     */
    private _notify: boolean;
    /**
     * @ignore
     */
    private _onNotify: any;

    /**
     * 
     */
    public elements: number[];

    /**
     * 
     * @param elements
     */
    constructor(elements: number[] = []) {
        this._notify = true;
        this._onNotify = null;

        this.elements = [
            1, 0, 0,
            0, 1, 0,
            0, 0, 1,
        ];
        if (elements.length > 0) {
            this.fromArray(elements);
        }
    }

    /**
     * 
     */
    public static Build() {
        return new Matrix3();
    }

    /**
     * 
     * @param column
     * @param row
     */
    public static getElementIndex(column: int, row: int): int {
        return column * 3 + row;
    }

    /**
     * 
     */
    public static get IDENTITY(): DeepImmutable<Matrix3> {
        return Matrix3._identityReadOnly;
    }

    /**
     * 
     */
    public get notify() {
        return this._notify;
    }
    public set notify(value: boolean) {
        this._notify = value;
    }

    /**
     * 
     */
    public setNotifyFunc(value: any): Matrix3 {
        this._onNotify = value;
        return this;
    }

    /**
     * 
     */
    private triggerNotify(): void {
        if (this._notify) {
            if (this._onNotify) {
                this._onNotify();
            }
        }
    }

    /**
     * 
     * @param n11
     * @param n12
     * @param n13
     * @param n21
     * @param n22
     * @param n23
     * @param n31
     * @param n32
     * @param n33
     */
    public set(n11: number, n12: number, n13: number, n21: number, n22: number, n23: number, n31: number, n32: number, n33: number): Matrix3 {

        const te = this.elements;
        te[0] = n11; te[1] = n21; te[2] = n31;
        te[3] = n12; te[4] = n22; te[5] = n32;
        te[6] = n13; te[7] = n23; te[8] = n33;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public isIdentity(): boolean {
        return this.equals(Matrix3.IDENTITY);
    }

    /**
     * 
     */
    public identity(): Matrix3 {
        this.set(
            1, 0, 0,
            0, 1, 0,
            0, 0, 1,
        );
        return this;
    }

    /**
     * 
     */
    public clone(): Matrix3 {
        return new Matrix3().copy(this);
    }

    /**
     * 
     * @param matrix3
     */
    public copy(matrix3: InMatrix3): Matrix3 {
        const te = this.elements;
        const me = matrix3.elements;
        te[0] = me[0]; te[1] = me[1]; te[2] = me[2];
        te[3] = me[3]; te[4] = me[4]; te[5] = me[5];
        te[6] = me[6]; te[7] = me[7]; te[8] = me[8];
        this.triggerNotify();
        return this;
    }

    /**
     * X
     * @param angle
     */
    public setFromRotationX(angle: number): Matrix3 {
        const cosAngle = Math.cos(angle);
        const sinAngle = Math.sin(angle);

        const te = this.elements;

        te[0] = 1.0;
        te[1] = 0.0;
        te[2] = 0.0;
        te[3] = 0.0;
        te[4] = cosAngle;
        te[5] = sinAngle;
        te[6] = 0.0;
        te[7] = -sinAngle;
        te[8] = cosAngle;
        this.triggerNotify();
        return this;
    }

    /**
     * Y
     * @param angle
     */
    public setFromRotationY(angle: number): Matrix3 {
        const cosAngle = Math.cos(angle);
        const sinAngle = Math.sin(angle);

        const te = this.elements;

        te[0] = cosAngle;
        te[1] = sinAngle;
        te[2] = 0.0;
        te[3] = -sinAngle;
        te[4] = cosAngle;
        te[5] = 0.0;
        te[6] = 0.0;
        te[7] = 0.0;
        te[8] = 1.0;
        this.triggerNotify();
        return this;
    }

    /**
     * Z
     * @param angle
     */
    public setFromRotationZ(angle: number): Matrix3 {
        const cosAngle = Math.cos(angle);
        const sinAngle = Math.sin(angle);

        const te = this.elements;

        te[0] = cosAngle;
        te[1] = 0.0;
        te[2] = -sinAngle;
        te[3] = 0.0;
        te[4] = 1.0;
        te[5] = 0.0;
        te[6] = sinAngle;
        te[7] = 0.0;
        te[8] = cosAngle;
        this.triggerNotify();
        return this;
    }

    /**
     * Matrix4
     * @param matrix4
     */
    public setFromMatrix4(matrix4: Matrix4): Matrix3 {
        const me = matrix4.elements;
        this.set(
            me[0], me[4], me[8],
            me[1], me[5], me[9],
            me[2], me[6], me[10],
        );
        return this;
    }

    /**
     * XYZ
     * @param xAxis
     * @param yAxis
     * @param zAxis
     */
    public extractBasis(xAxis = new Vector3(), yAxis = new Vector3(), zAxis = new Vector3()): Matrix3 {
        const te = this.elements;
        xAxis.fromArray(te, 0);
        yAxis.fromArray(te, 3);
        zAxis.fromArray(te, 6);
        return this;
    }

    /**
     * XYZ
     * @param xAxis
     * @param yAxis
     * @param zAxis
     */
    public makeBasis(xAxis: InVector3, yAxis: InVector3, zAxis: InVector3): Matrix3 {
        this.set(
            xAxis.x, yAxis.x, zAxis.x,
            xAxis.y, yAxis.y, zAxis.y,
            xAxis.z, yAxis.z, zAxis.z,
        );
        return this;
    }

    /**
     * X
     * @param x
     */
    public getX(x = new Vector3()): Vector3 {
        return x.set(this.elements[0], this.elements[1], this.elements[2]);
    }

    /**
     * Y
     * @param y
     */
    public getY(y = new Vector3()): Vector3 {
        return y.set(this.elements[3], this.elements[4], this.elements[5]);
    }

    /**
     * Z
     * @param z
     */
    public getZ(z = new Vector3()): Vector3 {
        return z.set(this.elements[6], this.elements[7], this.elements[8]);
    }

    /**
     * 
     * @param column
     * @param row
     */
    public getElement(column: int, row: int): number {
        const index = Matrix3.getElementIndex(column, row);
        return this.elements[index];
    }

    /**
     * 
     * @param column
     * @param row
     * @param value
     */
    public setElement(column: int, row: int, value: number): Matrix3 {
        const index = Matrix3.getElementIndex(column, row);
        this.elements[index] = value;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param vec3
     */
    public getColumn(index: int, vec3 = new Vector3()): Vector3 {
        const startIndex = index * 3;
        const x = this.elements[startIndex];
        const y = this.elements[startIndex + 1];
        const z = this.elements[startIndex + 2];

        vec3.x = x;
        vec3.y = y;
        vec3.z = z;
        return vec3;
    }

    /**
     * 
     * @param index
     * @param vec3
     */
    public setColumn(index: int, vec3: InVector3): Matrix3 {
        const startIndex = index * 3;
        this.elements[startIndex] = vec3.x;
        this.elements[startIndex + 1] = vec3.y;
        this.elements[startIndex + 2] = vec3.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param vec3
     * @returns {Vector3}
     * @memberOf Matrix3.prototype
     */
    public getRow(index: int, vec3 = new Vector3()): Vector3 {
        const x = this.elements[index];
        const y = this.elements[index + 3];
        const z = this.elements[index + 6];
        vec3.x = x;
        vec3.y = y;
        vec3.z = z;
        return vec3;
    }

    /**
     * 
     * @param index
     * @param vec3
     */
    public setRow(index: int, vec3: InVector3): Matrix3 {
        this.elements[index] = vec3.x;
        this.elements[index + 3] = vec3.y;
        this.elements[index + 6] = vec3.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     * @param z
     */
    public makeScale(x: number, y: number, z: number): Matrix3 {
        this.set(
            x, 0, 0,
            0, y, 0,
            0, 0, z,
        );
        return this;
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public multiply(matrix3: InMatrix3): Matrix3 {
        return this.multiplyMatrices(this, matrix3);
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public premultiply(matrix3: InMatrix3) {
        return this.multiplyMatrices(matrix3, this);
    }

    /**
     * 
     * @param aMat
     * @param bMat
     */
    public multiplyMatrices(aMat: InMatrix3, bMat: InMatrix3): Matrix3 {
        const ae = aMat.elements;
        const be = bMat.elements;
        const te = this.elements;

        const a11 = ae[0]; const a12 = ae[3]; const a13 = ae[6];
        const a21 = ae[1]; const a22 = ae[4]; const a23 = ae[7];
        const a31 = ae[2]; const a32 = ae[5]; const a33 = ae[8];

        const b11 = be[0]; const b12 = be[3]; const b13 = be[6];
        const b21 = be[1]; const b22 = be[4]; const b23 = be[7];
        const b31 = be[2]; const b32 = be[5]; const b33 = be[8];

        te[0] = a11 * b11 + a12 * b21 + a13 * b31;
        te[3] = a11 * b12 + a12 * b22 + a13 * b32;
        te[6] = a11 * b13 + a12 * b23 + a13 * b33;

        te[1] = a21 * b11 + a22 * b21 + a23 * b31;
        te[4] = a21 * b12 + a22 * b22 + a23 * b32;
        te[7] = a21 * b13 + a22 * b23 + a23 * b33;

        te[2] = a31 * b11 + a32 * b21 + a33 * b31;
        te[5] = a31 * b12 + a32 * b22 + a33 * b32;
        te[8] = a31 * b13 + a32 * b23 + a33 * b33;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public multiplyScalar(scalar: number): Matrix3 {
        const te = this.elements;
        te[0] *= scalar; te[3] *= scalar; te[6] *= scalar;
        te[1] *= scalar; te[4] *= scalar; te[7] *= scalar;
        te[2] *= scalar; te[5] *= scalar; te[8] *= scalar;

        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public multiplyScalarVector(vec: InVector3): Matrix3 {
        const te = this.elements;
        te[0] *= vec.x;
        te[1] *= vec.x;
        te[2] *= vec.x;
        te[3] *= vec.y;
        te[4] *= vec.y;
        te[5] *= vec.y;
        te[6] *= vec.z;
        te[7] *= vec.z;
        te[8] *= vec.z;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @returns {number}
     */
    public determinant(): number {
        const te = this.elements;
        const a = te[0]; const b = te[1]; const c = te[2];
        const d = te[3]; const e = te[4]; const f = te[5];
        const g = te[6]; const h = te[7]; const i = te[8];
        return a * e * i - a * f * h - b * d * i + b * f * g + c * d * h - c * e * g;
    }

    /**
     * 
     */
    public inverse(): Matrix3 {
        return this.getInverse(this);
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public getInverse(matrix3: InMatrix3): Matrix3 {

        const me = matrix3.elements;
        const te = this.elements;

        const n11 = me[0]; const n21 = me[1]; const n31 = me[2];
        const n12 = me[3]; const n22 = me[4]; const n32 = me[5];
        const n13 = me[6]; const n23 = me[7]; const n33 = me[8];

        const t11 = n33 * n22 - n32 * n23;
        const t12 = n32 * n13 - n33 * n12;
        const t13 = n23 * n12 - n22 * n13;

        const det = n11 * t11 + n21 * t12 + n31 * t13;

        if (det == 0) {
            return this.identity();
        }

        const detInv = 1 / det;

        te[0] = t11 * detInv;
        te[1] = (n31 * n23 - n33 * n21) * detInv;
        te[2] = (n32 * n21 - n31 * n22) * detInv;

        te[3] = t12 * detInv;
        te[4] = (n33 * n11 - n31 * n13) * detInv;
        te[5] = (n31 * n12 - n32 * n11) * detInv;

        te[6] = t13 * detInv;
        te[7] = (n21 * n13 - n23 * n11) * detInv;
        te[8] = (n22 * n11 - n21 * n12) * detInv;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public transpose(): Matrix3 {
        let tmp;
        let m = this.elements;
        tmp = m[1]; m[1] = m[3]; m[3] = tmp;
        tmp = m[2]; m[2] = m[6]; m[6] = tmp;
        tmp = m[5]; m[5] = m[7]; m[7] = tmp;

        this.triggerNotify();
        return this;
    }

    /**
     * Matrix4
     * @param matrix4
     */
    public getNormalMatrix(matrix4: Matrix4) {
        return this.setFromMatrix4(matrix4).getInverse(this).transpose();
    }

    /**
     * UV
     * @param tx
     * @param ty
     * @param sx
     * @param sy
     * @param rotation
     * @param cx
     * @param cy
     */
    public setUvTransform(tx: number, ty: number, sx: number, sy: number, rotation: number, cx: number, cy: number): Matrix3 {
        const c = Math.cos(rotation);
        const s = Math.sin(rotation);
        return this.set(
            sx * c, sx * s, -sx * (c * cx + s * cy) + cx + tx,
            -sy * s, sy * c, -sy * (-s * cx + c * cy) + cy + ty,
            0, 0, 1,
        );
    }

    /**
     * 
     * @param x
     * @param y
     */
    public scale(x: number, y: number): Matrix3 {
        const te = this.elements;
        const sx = x;
        const sy = y;
        te[0] *= sx; te[3] *= sx; te[6] *= sx;
        te[1] *= sy; te[4] *= sy; te[7] *= sy;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param theta
     */
    public rotate(theta: number): Matrix3 {
        const c = Math.cos(theta);
        const s = Math.sin(theta);

        const te = this.elements;

        const a11 = te[0];
        const a12 = te[3];
        const a13 = te[6];
        const a21 = te[1];
        const a22 = te[4];
        const a23 = te[7];

        te[0] = c * a11 + s * a21;
        te[3] = c * a12 + s * a22;
        te[6] = c * a13 + s * a23;

        te[1] = -s * a11 + c * a21;
        te[4] = -s * a12 + c * a22;
        te[7] = -s * a13 + c * a23;

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public translate(x: number, y: number): Matrix3 {
        const te = this.elements;

        const tx = x;
        const ty = y;

        te[0] += tx * te[2]; te[3] += tx * te[5]; te[6] += tx * te[8];
        te[1] += ty * te[2]; te[4] += ty * te[5]; te[7] += ty * te[8];

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public makeRotationFromQuaternion(quaternion: Quaternion): Matrix3 {
        const te = this.elements;

        const x = quaternion.x;
        const y = quaternion.y;
        const z = quaternion.z;
        const w = quaternion.w;

        const x2 = x + x;
        const y2 = y + y;
        const z2 = z + z;

        const xx = x * x2;
        const xy = x * y2;
        const xz = x * z2;
        const yy = y * y2;
        const yz = y * z2;
        const zz = z * z2;
        const wx = w * x2;
        const wy = w * y2;
        const wz = w * z2;

        te[0] = (1 - (yy + zz));
        te[1] = (xy + wz);
        te[2] = (xz - wy);

        te[3] = (xy - wz);
        te[4] = (1 - (xx + zz));
        te[5] = (yz + wx);

        te[6] = (xz + wy);
        te[7] = (yz - wx);
        te[8] = (1 - (xx + yy));

        this.triggerNotify();
        return this;
    }

    /**
     * Buffer
     * @param attribute
     */
    public applyToBufferAttribute(attribute: BufferAttribute): BufferAttribute {
        for (let i = 0, l = attribute.count; i < l; i++) {
            MathTmp.Vector3[0].x = attribute.getX(i);
            MathTmp.Vector3[0].y = attribute.getY(i);
            MathTmp.Vector3[0].z = attribute.getZ(i);
            MathTmp.Vector3[0].applyMatrix3(this);
            attribute.setXYZ(i, MathTmp.Vector3[0].x, MathTmp.Vector3[0].y, MathTmp.Vector3[0].z);
        }
        return attribute;
    }

    /**
     * 
     * @param matrix3
     * @returns {boolean}
     */
    public equals(matrix3: InMatrix3): boolean {
        const te = this.elements;
        const me = matrix3.elements;
        for (let i = 0; i < 9; i++) {
            if (te[i] != me[i]) { return false; }
        }
        return true;
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: InMatrix3, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        const te = this.elements;
        const me = right.elements;
        for (let i = 0; i < 9; i++) {
            if (!MathUtils.equalsEpsilon(te[i], me[i], relativeEpsilon, absoluteEpsilon)) { return false; }
        }
        return true;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset = 0): Matrix3 {
        for (let i = 0; i < 9; i++) {
            this.elements[i] = array[i + offset];
        }

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset = 0): FloatArray {
        const te = this.elements;

        array[offset] = te[0];
        array[offset + 1] = te[1];
        array[offset + 2] = te[2];

        array[offset + 3] = te[3];
        array[offset + 4] = te[4];
        array[offset + 5] = te[5];

        array[offset + 6] = te[6];
        array[offset + 7] = te[7];
        array[offset + 8] = te[8];

        return array;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Matrix3 {
        for (let i = 0; i < 9; i++) {
            this.elements[i] = dataBuffer.readFloat32();
        }

        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Matrix3 {
        for (let i = 0; i < 9; i++) {
            dataBuffer.writeFloat32(this.elements[i]);
        }
        return this;
    }
}
