import { Path } from "./Path";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { BinaryBuffer } from "../../Misc";
import { MathUtils } from "../MathUtils";

/**
 * 2D
 * @category Shape
 */
export class Shape extends Path {

    public uuid: string;
    public holes: Path[];

    /**
     * 
     * @param points
     */
    constructor(points: Nullable<Vector2[]> = null) {
        super(points);

        this.uuid = MathUtils.generateUUID();
        this.type = 'Shape';
        this.holes = [];
    }

    /**
     * 
     * @param divisions
     */
    public getPointsHoles(divisions: number): Vector2[][] {
        const holesPts = [];
        for (let i = 0, l = this.holes.length; i < l; i++) {
            holesPts[i] = this.holes[i].getPoints(divisions);
        }
        return holesPts;
    }

    /**
     * 
     * @param divisions
     */
    public extractPoints(divisions: number) {
        return {
            shape: this.getPoints(divisions),
            holes: this.getPointsHoles(divisions),
        };
    }

    /**
     * 
     */
    public clone(): Shape {
        return new Shape().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: Shape): Shape {
        super.copy(source);

        this.holes = [];

        for (let i = 0, l = source.holes.length; i < l; i++) {
            let hole = source.holes[i];
            this.holes.push(hole.clone());
        }
        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();
        data.uuid = this.uuid;
        data.holes = [];

        for (let i = 0, l = this.holes.length; i < l; i++) {
            let hole = this.holes[i];
            data.holes.push(hole.toJSON());
        }
        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): Shape {
        super.fromJSON(json);

        this.uuid = json.uuid;
        this.holes = [];

        for (let i = 0, l = json.holes.length; i < l; i++) {
            let hole = json.holes[i];
            this.holes.push(new Path().fromJSON(hole));
        }
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Shape {
        super.toDataBuffer(dataBuffer);

        dataBuffer.writeUint16(this.holes.length);

        this.holes.forEach((path) => {
            path.toDataBuffer(dataBuffer);
        });
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Shape {
        super.fromDataBuffer(dataBuffer);

        const count = dataBuffer.readUint16();
        for (let i = 0; i < count; ++i) {
            const path = new Path().fromDataBuffer(dataBuffer);
            this.holes.push(path);
        }
        return this;
    }
}
