import { Vector3 } from "./Vector3";

/**
 * 3D
 * @category Math
 */
export class SphericalHarmonics3 {

    /**
     * 
     */
    public coefficients: Vector3[] = [];

    /**
     * 
     */
    constructor() {
        for (let i = 0; i < 9; i++) {
            this.coefficients.push(new Vector3());
        }
    }

    /**
     * 
     * @param coefficients
     */
    public set(coefficients: Vector3[]): SphericalHarmonics3 {
        for (let i = 0; i < 9; i++) {
            this.coefficients[i].copy(coefficients[i]);
        }
        return this;
    }

    /**
     * 
     */
    public zero(): SphericalHarmonics3 {
        for (let i = 0; i < 9; i++) {
            this.coefficients[i].set(0, 0, 0);
        }
        return this;
    }

    /**
     * 
     * @param normal
     * @param target
     */
    public getAt(normal: Vector3, target: Vector3 = new Vector3()): Vector3 {

        const x = normal.x;
        const y = normal.y;
        const z = normal.z;

        const coeff = this.coefficients;

        // band 0
        target.copy(coeff[0]).multiplyScalar(0.282095);

        // band 1
        target.addScaledVector(coeff[ 1 ], 0.488603 * y);
        target.addScaledVector(coeff[ 2 ], 0.488603 * z);
        target.addScaledVector(coeff[ 3 ], 0.488603 * x);

        // band 2
        target.addScaledVector(coeff[ 4 ], 1.092548 * (x * y));
        target.addScaledVector(coeff[ 5 ], 1.092548 * (y * z));
        target.addScaledVector(coeff[ 6 ], 0.315392 * (3.0 * z * z - 1.0));
        target.addScaledVector(coeff[ 7 ], 1.092548 * (x * z));
        target.addScaledVector(coeff[ 8 ], 0.546274 * (x * x - y * y));

        return target;
    }

    /**
     * ()
     * @param normal
     * @param target
     */
    public getIrradianceAt(normal: Vector3, target: Vector3 = new Vector3()): Vector3 {

        const x = normal.x;
        const y = normal.y;
        const z = normal.z;

        const coeff = this.coefficients;

        // band 0
        target.copy(coeff[0]).multiplyScalar(0.886227); //  * 0.282095

        // band 1
        target.addScaledVector(coeff[ 1 ], 2.0 * 0.511664 * y); // ( 2 *  / 3 ) * 0.488603
        target.addScaledVector(coeff[ 2 ], 2.0 * 0.511664 * z);
        target.addScaledVector(coeff[ 3 ], 2.0 * 0.511664 * x);

        // band 2
        target.addScaledVector(coeff[ 4 ], 2.0 * 0.429043 * x * y); // (  / 4 ) * 1.092548
        target.addScaledVector(coeff[ 5 ], 2.0 * 0.429043 * y * z);
        target.addScaledVector(coeff[ 6 ], 0.743125 * z * z - 0.247708); // (  / 4 ) * 0.315392 * 3
        target.addScaledVector(coeff[ 7 ], 2.0 * 0.429043 * x * z);
        target.addScaledVector(coeff[ 8 ], 0.429043 * (x * x - y * y)); // (  / 4 ) * 0.546274

        return target;
    }

    /**
     * SphericalHarmonics3
     * @param sh
     */
    public add(sh: SphericalHarmonics3): SphericalHarmonics3 {
        for (let i = 0; i < 9; i++) {
            this.coefficients[i].add(sh.coefficients[i]);
        }
        return this;
    }

    /**
     * SphericalHarmonics3
     * @param sh
     * @param scalar
     */
    public addScaledSH(sh: SphericalHarmonics3, scalar: number): SphericalHarmonics3 {
        for (let i = 0; i < 9; i++) {
            this.coefficients[ i ].addScaledVector(sh.coefficients[ i ], scalar);
        }
        return this;
    }

    /**
     * 
     * @param sh
     * @param alpha
     */
    public lerp(sh: SphericalHarmonics3, alpha: number): SphericalHarmonics3 {
        for (let i = 0; i < 9; i++) {
            this.coefficients[i].lerp(sh.coefficients[i], alpha);
        }
        return this;
    }

    /**
     * 
     * @param sh
     */
    public equals(sh: SphericalHarmonics3): boolean {
        for (let i = 0; i < 9; i++) {
            if (!this.coefficients[i].equals(sh.coefficients[i])) {
                return false;
            }
        }
        return true;
    }

    /**
     * 
     * @param sh
     */
    public copy(sh: SphericalHarmonics3): SphericalHarmonics3 {
        return this.set(sh.coefficients);
    }

    /**
     * 
     */
    public clone(): SphericalHarmonics3 {
        return new SphericalHarmonics3().copy(this);
    }
}
