import { Light } from "./Light";
import { ColorRGB } from "../Math";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";

/**
 * 
 */
export class HemisphereLight extends Light {

    /**
     * @ignore
     */
    protected _groundColor: ColorRGB  = new ColorRGB(1.0, 1.0, 1.0);

    /**
     * 
     * @param scene
     * @param parent
     * @param name
     */
    constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '') {
        super(scene, parent, name);
    }

    /**
     * @ignore
     */
    public set castShadow(value: boolean) {
        this._castShadow = false;
    }

    /**
     * 
     */
    public get groundColor() {
        return this._groundColor;
    }
    public set groundColor(value) {
        this._groundColor.copy(value);
    }
}
