import { Light } from "./Light";
import { AbstractObject, Scene } from "../Scene";
import { Nullable } from "../types";

/**
 * 
 */
export class RectAreaLight extends Light {

    /**
     * @ignore
     */
    protected _width: number = 10;
    /**
     * @ignore
     */
    protected _height: number = 10;

    /**
     * 
     * @param scene
     * @param parent
     * @param name
     */
    constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '') {
        super(scene, parent, name);
    }

    public get width() {
        return this._width;
    }
    public set width(value) {
        if (this._width != value) {
            this._width = value;
        }
    }

    public get height() {
        return this._height;
    }
    public set height(value) {
        if (this._height != value) {
            this._height = value;
        }
    }
}
