import { int } from "../types";

/**
 * 
 */
export class VisibleLayers {

    public mask: int;

    /**
     * 
     */
    constructor() {
        this.mask = 1 | 0;
    }

    /**
     * 
     * @param channel
     */
    public set(channel: int) {
        this.mask = 1 << channel | 0;
    }

    /**
     * 
     * @param channel
     */
    public enable(channel: int) {
        this.mask |= 1 << channel | 0;
    }

    /**
     * 
     */
    public enableAll() {
        this.mask = 0xffffffff | 0;
    }

    /**
     * 
     * @param channel
     */
    public toggle(channel: int) {
        this.mask ^= 1 << channel | 0;
    }

    /**
     * 
     * @param channel
     */
    public disable(channel: int) {
        this.mask &= ~(1 << channel | 0);
    }

    /**
     * 
     */
    public disableAll() {
        this.mask = 0;
    }

    /**
     * 
     * @param layers
     */
    public test(layers: VisibleLayers) {
        return (this.mask & layers.mask) != 0;
    }

}
