import { MathUtils } from "../Math/MathUtils";
import { Euler, InVector3, Matrix3, Matrix4, Quaternion, Vector3 } from "../Math";
import { MathTmp } from "../Math/MathTmp";
import { AbstractObject } from "./AbstractObject";
import { Scene } from "./Scene";
import { Nullable } from "../types";

/**
 * 3D
 */
export class TransformObject extends AbstractObject {

    /**
     * @ignore
     */
    [key: string]: any;

    /**
     * @ignore
     */
    protected _position = new Vector3();
    /**
     * @ignore
     */
    protected _rotation = new Euler();
    /**
     * @ignore
     */
    protected _quaternion = new Quaternion();
    /**
     * @ignore
     */
    protected _scale = new Vector3(1, 1, 1);
    /**
     * @ignore
     */
    protected _matrix = new Matrix4();
    /**
     * @ignore
     */
    protected _matrixWorld = new Matrix4();
    /**
     * @ignore
     */
    protected _pivotMatrix = new Matrix4();

    /**
     * @ignore
     */
    protected _matrixWorldInverse = new Matrix4();
    /**
     * @ignore
     */
    protected _pivotMatrixInverse = new Matrix4();
    /**
     * @ignore
     */
    protected _normalMatrix = new Matrix3();

    /**
     * @ignore
     */
    protected _modelViewMatrix = new Matrix4();
    /**
     * @ignore
     */
    protected _modelViewProjectionMatrix = new Matrix4();
    /**
     * @ignore
     */
    protected _transModelViewMatrix = new Matrix4();
    /**
     * @ignore
     */
    protected _transModelViewMatrixInverse = new Matrix4();
    /**
     * @ignore
     */
    protected _modelViewRelativeMatrix = new Matrix4();
    /**
     * @ignore
     */
    protected _modelViewProjectionRelativeMatrix = new Matrix4();

    /**
     * @ignore
     */
    private _lastPosition = new Vector3();
    /**
     * @ignore
     */
    private _lastRotation = new Euler();
    /**
     * @ignore
     */
    private _lastQuaternion = new Quaternion();
    /**
     * @ignore
     */
    private _lastScale = new Vector3(1, 1, 1);
    /**
     * @ignore
     */
    private _lastMatrix = new Matrix4();
    /**
     * @ignore
     */
    private _lastMatrixWorld = new Matrix4();
    /**
     * @ignore
     */
    private _lastPivotMatrix = new Matrix4();

    /**
     * @ignore
     */
    private _tempPosition = new Vector3();
    /**
     * @ignore
     */
    private _tempRotation = new Euler();
    /**
     * @ignore
     */
    private _tempQuaternion = new Quaternion();
    /**
     * @ignore
     */
    private _tempScale = new Vector3(1, 1, 1);
    /**
     * @ignore
     */
    private _tempMatrix = new Matrix4();
    /**
     * @ignore
     */
    private _tempMatrixWorld = new Matrix4();
    /**
     * @ignore
     */
    private _tempPivotMatrix = new Matrix4();

    /**
     * @ignore
     */
    protected _usePivotMatrix = false;
    /**
     * @ignore
     */
    protected _matrixNeedsUpdate = true;
    /**
     * @ignore
     */
    protected _matrixWorldNeedsUpdate = true;

    /**
     * 
     * @param scene
     * @param parent
     * @param name
     * @param isPure
     */
    constructor(scene: Scene, parent: Nullable<AbstractObject> = null, name: string = '', isPure: boolean = true) {
        super(scene, parent, name);

        this._position.setNotifyFunc(this._onPositionChange.bind(this));
        this._rotation.setNotifyFunc(this._onRotationChange.bind(this));
        this._quaternion.setNotifyFunc(this._onQuaternionChange.bind(this));
        this._scale.setNotifyFunc(this._onScaleChange.bind(this));
        this._matrix.setNotifyFunc(this._onMatrixChange.bind(this));
        this._matrixWorld.setNotifyFunc(this._onMatrixWorldChange.bind(this));
        this._pivotMatrix.setNotifyFunc(this._onPivotMatrixChange.bind(this));

        if (isPure) {
            this.scene.addTransform(this);
        }
    }

    /**
     * 
     */
    get position() {
        return this._position;
    }
    set position(value: InVector3) {
        this._position.copy(value);
    }

    /**
     * 
     */
    get scale() {
        return this._scale;
    }
    set scale(value: InVector3) {
        this._scale.copy(value);
    }

    /**
     * 
     */
    get quaternion() {
        return this._quaternion;
    }
    set quaternion(value: Quaternion) {
        this._quaternion.copy(value);
    }

    /**
     * 
     */
    get rotation() {
        return this._rotation;
    }
    set rotation(value: Euler) {
        this._rotation.copy(value);
    }

    /**
     * 
     */
    get visibleLayers() {
        return this._visibleLayers;
    }

    /**
     * 
     */
    get matrix() {
        return this._matrix;
    }

    /**
     * 
     */
    get matrixWorld() {
        return this._matrixWorld;
    }

    /**
     * 
     */
    get matrixWorldInverse() {
        return this._matrixWorldInverse;
    }

    /**
     * 
     */
    get usePivotMatrix() {
        return this._usePivotMatrix;
    }

    /**
     * 
     */
    get pivotMatrix() {
        return this._pivotMatrix;
    }

    /**
     * 
     */
    get pivotMatrixInverse() {
        return this._pivotMatrixInverse;
    }

    /**
     * 
     */
    get normalMatrix() {
        return this._normalMatrix;
    }
    set normalMatrix(value: Matrix3) {
        this._normalMatrix.copy(value);
    }

    /**
     * MV
     */
    get modelViewMatrix() {
        return this._modelViewMatrix;
    }
    set modelViewMatrix(value: Matrix4) {
        this._modelViewMatrix.copy(value);
    }

    /**
     * MVP
     */
    get modelViewProjectionMatrix() {
        return this._modelViewProjectionMatrix;
    }
    set modelViewProjectionMatrix(value: Matrix4) {
        this._modelViewProjectionMatrix.copy(value);
    }

    /**
     * TMV
     */
    get transModelViewMatrix() {
        return this._transModelViewMatrix;
    }
    set transModelViewMatrix(value: Matrix4) {
        this._transModelViewMatrix.copy(value);
    }

    /**
     * TMV
     */
    get transModelViewMatrixInverse() {
        return this._transModelViewMatrixInverse;
    }
    set transModelViewMatrixInverse(value: Matrix4) {
        this._transModelViewMatrixInverse.copy(value);
    }

    /**
     * MVR
     */
    get modelViewRelativeMatrix() {
        return this._modelViewRelativeMatrix;
    }
    set modelViewRelativeMatrix(value: Matrix4) {
        this._modelViewRelativeMatrix.copy(value);
    }

    /**
     * MVPR
     */
    get modelViewProjectionRelativeMatrix() {
        return this._modelViewProjectionRelativeMatrix;
    }
    set modelViewProjectionRelativeMatrix(value: Matrix4) {
        this._modelViewProjectionRelativeMatrix.copy(value);
    }

    /**
     * @ignore
     * @private
     */
    private _onRotationChange() {
        this._quaternion.notify = false;
        this._quaternion.setFromEuler(this._rotation);
        this._quaternion.notify = true;

        if (!this._rotation.equals(this._lastRotation)) {
            this._tempRotation.copy(this._lastRotation);
            this._lastRotation.copy(this._rotation);

            this._matrixNeedsUpdate = true;

            this.onRotationChanged(this._tempRotation, this._lastRotation);
        }
    }

    /**
     * @ignore
     * @private
     */
    private _onQuaternionChange() {
        this._rotation.notify = false;
        this._rotation.setFromQuaternion(this._quaternion, null);
        this._rotation.notify = true;

        if (!this._quaternion.equals(this._lastQuaternion)) {
            this._tempQuaternion.copy(this._lastQuaternion);
            this._lastQuaternion.copy(this._quaternion);

            this._matrixNeedsUpdate = true;

            this.onQuaternionChanged(this._tempQuaternion, this._lastQuaternion);
        }
    }

    /**
     * @ignore
     * @private
     */
    private _onPositionChange() {
        if (!this._position.equals(this._lastPosition)) {
            this._tempPosition.copy(this._lastPosition);
            this._lastPosition.copy(this._position);

            this._matrixNeedsUpdate = true;

            this.onPositionChanged(this._tempPosition, this._lastPosition);
        }
    }

    /**
     * @ignore
     * @private
     */
    private _onScaleChange() {
        if (!this._scale.equals(this._lastScale)) {
            this._tempScale.copy(this._lastScale);
            this._lastScale.copy(this._scale);

            this._matrixNeedsUpdate = true;

            this.onScaleChanged(this._tempScale, this._lastScale);
        }
    }

    /**
     * @ignore
     * @private
     */
    private _onMatrixChange() {
        if (!this._matrix.equals(this._lastMatrix)) {
            this._tempMatrix.copy(this._lastMatrix);
            this._lastMatrix.copy(this._matrix);

            this.onMatrixChanged(this._tempMatrix, this._lastMatrix);
        }
    }

    /**
     * @ignore
     * @private
     */
    private _onMatrixWorldChange() {
        if (!this._matrixWorld.equals(this._lastMatrixWorld)) {
            this._tempMatrixWorld.copy(this._lastMatrixWorld);
            this._lastMatrixWorld.copy(this._matrixWorld);

            this.onMatrixWorldChanged(this._tempMatrixWorld, this._lastMatrixWorld);
        }
    }

    /**
     * @ignore
     * @private
     */
    private _onPivotMatrixChange() {
        if (!this._pivotMatrix.equals(this._lastPivotMatrix)) {
            this._tempPivotMatrix.copy(this._lastPivotMatrix);
            this._lastPivotMatrix.copy(this._pivotMatrix);

            this._pivotMatrixInverse.getInverse(this._pivotMatrix);
            this._usePivotMatrix = !this._pivotMatrix.isIdentity();

            this._matrixNeedsUpdate = true;
        }
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onRotationChanged(oldValue: Euler, newValue: Euler): void {
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onQuaternionChanged(oldValue: Quaternion, newValue: Quaternion): void {
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onPositionChanged(oldValue: Vector3, newValue: Vector3): void {
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onScaleChanged(oldValue: Vector3, newValue: Vector3): void {
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onMatrixChanged(oldValue: Matrix4, newValue: Matrix4): void {
    }

    /**
     * 
     * @param oldValue
     * @param newValue
     * @ignore
     */
    protected onMatrixWorldChanged(oldValue: Matrix4, newValue: Matrix4): void {
    }

    /**
     * 
     * @param matrix
     */
    public applyMatrix(matrix: Matrix4): TransformObject {
        this.matrix.premultiply(matrix);
        this.matrix.decompose(this._position, this._quaternion, this._scale);
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public applyQuaternion(quaternion: Quaternion): TransformObject {
        this._quaternion.premultiply(quaternion);
        return this;
    }

    /**
     * 
     * @param axis
     * @param angle
     */
    public setRotationFromAxisAngle(axis: InVector3, angle: number): TransformObject {
        this._quaternion.setFromAxisAngle(axis, angle);
        return this;
    }

    /**
     * 
     * @param euler
     */
    public setRotationFromEuler(euler: Euler): TransformObject {
        this._quaternion.setFromEuler(euler);
        return this;
    }

    /**
     * 
     * @param matrix
     */
    public setRotationFromMatrix(matrix: Matrix3 | Matrix4): TransformObject {
        this._quaternion.setFromRotationMatrix(matrix);
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public setRotationFromQuaternion(quaternion: Quaternion): TransformObject {
        this._quaternion.copy(quaternion);
        return this;
    }

    /**
     * 
     * @param axis
     * @param angle
     */
    public rotateOnAxis(axis: InVector3, angle: number): TransformObject {
        this._tempQuaternion.setFromAxisAngle(axis, angle);
        this._quaternion.multiply(this._tempQuaternion);
        return this;
    }

    /**
     * 
     * @param axis
     * @param angle
     */
    public rotateOnWorldAxis(axis: InVector3, angle: number): TransformObject {
        this._tempQuaternion.setFromAxisAngle(axis, angle);
        this._quaternion.premultiply(this._tempQuaternion);
        return this;
    }

    /**
     * X
     * @param angle
     */
    public rotateX(angle: number): TransformObject {
        return this.rotateOnAxis(Vector3.UNIT_X, angle);
    }

    /**
     * Y
     * @param angle
     */
    public rotateY(angle: number): TransformObject {
        return this.rotateOnAxis(Vector3.UNIT_Y, angle);
    }

    /**
     * Z
     * @param angle
     */
    public rotateZ(angle: number): TransformObject {
        return this.rotateOnAxis(Vector3.UNIT_Z, angle);
    }

    /**
     * 
     * @param axis
     * @param distance
     */
    public translateOnAxis(axis: InVector3, distance: number): TransformObject {
        this._tempPosition.copy(axis).applyQuaternion(this._quaternion);
        this.position.add(this._tempPosition.multiplyScalar(distance));
        return this;
    }

    /**
     * X
     * @param distance
     */
    public translateX(distance: number): TransformObject {
        return this.translateOnAxis(Vector3.UNIT_X, distance);
    }

    /**
     * Y
     * @param distance
     */
    public translateY(distance: number): TransformObject {
        return this.translateOnAxis(Vector3.UNIT_Y, distance);
    }

    /**
     * Z
     * @param distance
     */
    public translateZ(distance: number): TransformObject {
        return this.translateOnAxis(Vector3.UNIT_Z, distance);
    }

    /**
     * 
     * @param position
     */
    public setPosition(position: Vector3): TransformObject {
        this._position.copy(position);
        return this;
    }

    /**
     * 
     * @param scale
     */
    public setScale(scale: Vector3): TransformObject {
        this._scale.copy(scale);
        return this;
    }

    /**
     * 
     * @param position
     */
    public setWorldPosition(position: Vector3): TransformObject {
        if (!this._parent) {
            this._position.copy(position);
        } else {
            this._parent.matrixWorldInverse.transformPoint(position, this._position);
        }
        return this;
    }

    /**
     * 
     * @param quaternion
     */
    public setWorldRotation(quaternion: Quaternion): TransformObject {
        if (!this._parent) {
            this._quaternion.copy(quaternion);
        } else {
            this._tempQuaternion.copy(this._parent.quaternion).inverse();
            this._quaternion.notify = false;
            this._quaternion.copy(this._tempQuaternion);
            this._quaternion.notify = true;
            this._quaternion.multiply(quaternion);
        }
        return this;
    }

    /**
     * 
     * @param position
     * @param result
     */
    public localToWorld(position: Vector3, result = new Vector3()): Vector3 {
        result.copy(position);
        return result.applyMatrix4(this.matrixWorld);
    }

    /**
     * 
     * @param position
     * @param result
     */
    public worldToLocal(position: Vector3, result = new Vector3()): Vector3 {
        result.copy(position);
        return result.applyMatrix4(this.matrixWorld);
    }

    /**
     * 
     * @param target
     */
    public lookAt(target: Vector3): TransformObject {

        this._tempPosition.setFromMatrixPosition(this.matrixWorld);
        this._tempMatrix.lookAt(target, this._tempPosition, Vector3.UNIT_Y);

        if (this._parent) {
            this._quaternion.notify = false;
            this._quaternion.setFromRotationMatrix(this._tempMatrix);
            this._parent.matrixWorld.getRotationMatrix4(this._tempMatrix);
            this._tempQuaternion.setFromRotationMatrix(this._tempMatrix);
            this._quaternion.notify = true;
            this._quaternion.premultiply(this._tempQuaternion.inverse());
        }
        else {
            this._quaternion.setFromRotationMatrix(this._tempMatrix);
        }
        return this;
    }

    /**
     * 
     * @param target
     */
    public getPivotPosition(target = new Vector3()): Vector3 {
        target.x = -this._pivotMatrix.elements[12];
        target.y = -this._pivotMatrix.elements[13];
        target.z = -this._pivotMatrix.elements[14];
        return target;
    }

    /**
     * 
     * @param target
     */
    public getWorldPivotPosition(target = new Vector3()): Vector3 {
        target = this.getPivotPosition(target);
        return target.setFromMatrixPosition(this.matrixWorld);
    }

    /**
     * 
     * @param target
     */
    public getWorldPosition(target = new Vector3()): Vector3 {
        return target.setFromMatrixPosition(this.matrixWorld);
    }

    /**
     * 
     * @param target
     */
    public getWorldQuaternion(target = new Quaternion()): Quaternion {
        this.matrixWorld.decompose(null, target, null);
        return target;
    }

    /**
     * 
     * @param target
     */
    public getWorldRotaion(target = new Euler()): Euler {
        this.matrixWorld.decompose(null, this._tempQuaternion, null);
        target.setFromQuaternion(this._tempQuaternion, this._rotation.order);
        return target;
    }

    /**
     * 
     * @param target
     */
    public getWorldScale(target = new Vector3()): Vector3 {
        this.matrixWorld.decompose(null, null, target);
        return target;
    }

    /**
     * 
     * @param target
     */
    public getWorldDirection(target = new Vector3()): Vector3 {
        const e = this.matrixWorld.elements;
        return target.set(e[8], e[9], e[10]).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getLocalUp(dir = new Vector3()): Vector3 {
        dir.set(0, 1, 0);
        return dir.applyQuaternion(this.quaternion).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getLocalDown(dir = new Vector3()): Vector3 {
        dir.set(0, -1, 0);
        return dir.applyQuaternion(this.quaternion).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getLocalForward(dir = new Vector3()): Vector3 {
        dir.set(0, 0, 1);
        return dir.applyQuaternion(this.quaternion).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getLocalBackward(dir = new Vector3()): Vector3 {
        dir.set(0, 0, -1);
        return dir.applyQuaternion(this.quaternion).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getLocalLeft(dir = new Vector3()): Vector3 {
        dir.set(-1, 0, 0);
        return dir.applyQuaternion(this.quaternion).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getLocalRight(dir = new Vector3()): Vector3 {
        dir.set(1, 0, 0);
        return dir.applyQuaternion(this.quaternion).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getWorldUp(dir = new Vector3()): Vector3 {
        dir.set(0, 1, 0);
        return this.matrixWorld.transformVector3(dir, dir).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getWorldDown(dir = new Vector3()): Vector3 {
        dir.set(0, -1, 0);
        return this.matrixWorld.transformVector3(dir, dir).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getWorldForward(dir = new Vector3()): Vector3 {
        dir.set(0, 0, 1);
        return this.matrixWorld.transformVector3(dir, dir).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getWorldBackward(dir = new Vector3()): Vector3 {
        dir.set(0, 0, -1);
        return this.matrixWorld.transformVector3(dir, dir).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getWorldLeft(dir = new Vector3()): Vector3 {
        dir.set(-1, 0, 0);
        return this.matrixWorld.transformVector3(dir, dir).normalize();
    }

    /**
     * 
     * @param dir
     */
    public getWorldRight(dir = new Vector3()): Vector3 {
        dir.set(1, 0, 0);
        return this.matrixWorld.transformVector3(dir, dir).normalize();
    }

    /**
     * 
     */
    public getHeading(): number {
        return MathUtils.TWO_PI - MathUtils.zeroToTwoPi(this.rotation.y);
    }

    /**
     * 
     */
    public getPitch(): number {
        return MathUtils.clamp(this.rotation.x, -1.0, 1.0);
    }

    /**
     * 
     */
    public getRoll(): number {
        return MathUtils.zeroToTwoPi(this.rotation.z + MathUtils.TWO_PI);
    }

    /**
     * 
     * @param force
     */
    public updateMatrix(force: boolean = false): TransformObject {

        if (this._matrixNeedsUpdate || force) {

            this._matrix.notify = false;

            this._tempMatrix.copy(this._matrix);

            if (this._usePivotMatrix) {
                const pivotScaleM = MathTmp.Matrix4[0].makeScaleXYZ(this.scale.x, this.scale.y, this.scale.z);
                const pivotRotationM = MathTmp.Matrix4[1].makeRotationFromQuaternion(this.quaternion);

                this._matrix.multiplyMatrices(pivotScaleM, this._pivotMatrix);
                this._matrix.premultiply(pivotRotationM);
                this._matrix.premultiply(this.pivotMatrixInverse);
                this._matrix.translateVector(this.position);
            }
            else {
                this._matrix.compose(this.position, this.quaternion, this.scale);
            }

            this._matrix.notify = true;

            this._matrixNeedsUpdate = false;

            this._matrixWorldNeedsUpdate = true;

            this.onMatrixChanged(this._tempMatrix, this._matrix);
        }

        return this;
    }

    /**
     * 
     * @param force
     */
    public updateMatrixWorld(force: boolean = false): TransformObject {

        this.updateMatrix();

        if (this._matrixWorldNeedsUpdate || force) {

            this._matrixWorld.notify = false;

            this._tempMatrixWorld.copy(this._matrixWorld);

            if (!this._parent) {

                this._matrixWorld.copy(this._matrix);

            } else {

                this._matrixWorld.multiplyMatrices(this._parent.matrixWorld, this._matrix);

            }

            this._matrixWorld.notify = true;

            this._matrixWorldInverse.getInverse(this._matrixWorld);

            this._matrixWorldNeedsUpdate = false;

            force = true;

            this.onMatrixWorldChanged(this._tempMatrixWorld, this._matrixWorld);
        }

        const children = this.children;
        for (let i = 0, l = children.length; i < l; i++) {
            children[i].updateMatrixWorld(force);
        }

        return this;
    }

    /**
     * 
     * @param updateParents
     * @param updateChildren
     * @param force
     */
    public updateWorldMatrix(updateParents: boolean = false, updateChildren: boolean = false, force: boolean = false): TransformObject {

        if (updateParents && this._parent != null) {
            this._parent.updateWorldMatrix(true, false, force);
        }

        this.updateMatrix();

        if (this._matrixWorldNeedsUpdate || force) {

            this._matrixWorld.notify = false;

            this._tempMatrixWorld.copy(this._matrixWorld);

            if (!this._parent) {

                this._matrixWorld.copy(this._matrix);

            } else {

                this._matrixWorld.multiplyMatrices(this._parent.matrixWorld, this._matrix);

            }

            this._matrixWorld.notify = true;

            this._matrixWorldInverse.getInverse(this._matrixWorld);

            this._matrixWorldNeedsUpdate = false;

            force = true;

            this.onMatrixWorldChanged(this._tempMatrixWorld, this._matrixWorld);
        }

        if (updateChildren) {
            const children = this.children;
            for (let i = 0, l = children.length; i < l; i++) {
                children[i].updateWorldMatrix(false, true, force);
            }
        }

        return this;
    }

    /**
     * 
     */
    public clone(): TransformObject {
        return new TransformObject(this._scene, this._parent).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TransformObject): TransformObject {
        return this;
    }
}
