/**
 * 
 * @category Math
 */
export enum IntervalsType {
    /**
     * 
     */
    isEmpty,
    /**
     * [t0,t0]
     */
    isPoint,
    /**
     * [t0,t1]
     */
    isFinite,
    /**
     * [t0+][0]t0[1]++1()
     */
    isPositiveInfinite,
    /**
     * (-t1)[0]=1-()[1]t1
     */
    isNegativeInfinite,
    /**
     * isDynamicQuery
     */
    isDynamicQuery
}

/**
 * 
 * @category Math
 */
export interface IntervalsResult {
    intersect: boolean;
    numIntersections: number;
    overlap: number[];
    type: IntervalsType;
}

/**
 * 
 * @category Math
 */
export abstract class Intervals {

    public static staticQuery(interval0: number[], interval1: number[]): IntervalsResult {

        const result = {
            intersect: false,
            numIntersections: 0,
            overlap: [0, 0],
            type: IntervalsType.isEmpty
        };

        if (interval0[1] < interval1[0] || interval0[0] > interval1[1])
        {
            result.numIntersections = 0;
            result.overlap[0] = 0;
            result.overlap[1] = 0;
            result.type = IntervalsType.isEmpty;
        }
        else if (interval0[1] > interval1[0])
        {
            if (interval0[0] < interval1[1])
            {
                result.overlap[0] = (interval0[0] < interval1[0] ? interval1[0] : interval0[0]);
                result.overlap[1] = (interval0[1] > interval1[1] ? interval1[1] : interval0[1]);
                if (result.overlap[0] < result.overlap[1])
                {
                    result.numIntersections = 2;
                    result.type = IntervalsType.isFinite;
                }
                else
                {
                    result.numIntersections = 1;
                    result.type = IntervalsType.isPoint;
                }
            }
            else  // interval0[0] == interval1[1]
            {
                result.numIntersections = 1;
                result.overlap[0] = interval0[0];
                result.overlap[1] = result.overlap[0];
                result.type = IntervalsType.isPoint;
            }
        }
        else  // interval0[1] == interval1[0]
        {
            result.numIntersections = 1;
            result.overlap[0] = interval0[1];
            result.overlap[1] = result.overlap[0];
            result.type = IntervalsType.isPoint;
        }

        result.intersect = (result.numIntersections > 0);

        return result;
    }
}
