import { FloatArray, Nullable } from "../types";

/**
 * 
 * @category Libs
 */
export abstract class Earcut {

    /**
     * 
     * @param data
     * @param holeIndices
     * @param dim
     */
    public static earcut(data: number[], holeIndices: Nullable<FloatArray> = null, dim: number = 2): number[] {

        let hasHoles = holeIndices && holeIndices.length,
            outerLen = hasHoles && holeIndices ? holeIndices[0] * dim : data.length,
            outerNode = linkedList(data, 0, outerLen, dim, true),
            triangles = new Array<number>();

        if (!outerNode || outerNode.next == outerNode.prev) { return triangles; }

        let minX, minY, maxX, maxY, x, y, invSize;

        if (hasHoles && holeIndices) { outerNode = eliminateHoles(data, holeIndices, outerNode, dim); }

        // if the shape is not too simple, we'll use z-order curve hash later; calculate polygon bbox
        if (data.length > 80 * dim) {
            minX = maxX = data[0];
            minY = maxY = data[1];

            for (let i = dim; i < outerLen; i += dim) {
                x = data[i];
                y = data[i + 1];
                if (x < minX) { minX = x; }
                if (y < minY) { minY = y; }
                if (x > maxX) { maxX = x; }
                if (y > maxY) { maxY = y; }
            }

            // minX, minY and invSize are later used to transform coords into integers for z-order calculation
            invSize = Math.max(maxX - minX, maxY - minY);
            invSize = invSize != 0 ? 1 / invSize : 0;
        }

        // @ts-ignore
        earcutLinked(outerNode, triangles, dim, minX, minY, invSize);

        return triangles;

    }

    /**
     * 
     * @param data
     * @param dim
     * @param triangles
     */
    public static trianglesArea(data: number[], triangles: number[], dim: number = 2): number {
        let trianglesArea = 0;
        for (let i = 0; i < triangles.length; i += 3) {
            let a = triangles[i] * dim;
            let b = triangles[i + 1] * dim;
            let c = triangles[i + 2] * dim;
            trianglesArea += Math.abs((data[a] - data[c]) * (data[b + 1] - data[a + 1]) - (data[a] - data[b]) * (data[c + 1] - data[a + 1]));
        }
        return trianglesArea * 0.5;
    }

    /**
     * ;
     * @param data
     * @param holeIndices
     * @param dim
     * @param triangles
     */
    public static deviation(data: number[], holeIndices: FloatArray, dim: number = 2, triangles: FloatArray): number {

        let hasHoles = holeIndices && holeIndices.length;
        let outerLen = hasHoles ? holeIndices[0] * dim : data.length;

        let polygonArea = Math.abs(signedArea(data, 0, outerLen, dim));
        if (hasHoles) {
            for (let i = 0, len = holeIndices.length; i < len; i++) {
                let start = holeIndices[i] * dim;
                let end = i < len - 1 ? holeIndices[i + 1] * dim : data.length;
                polygonArea -= Math.abs(signedArea(data, start, end, dim));
            }
        }

        let trianglesArea = 0;
        for (let i = 0; i < triangles.length; i += 3) {
            let a = triangles[i] * dim;
            let b = triangles[i + 1] * dim;
            let c = triangles[i + 2] * dim;
            trianglesArea += Math.abs((data[a] - data[c]) * (data[b + 1] - data[a + 1]) - (data[a] - data[b]) * (data[c + 1] - data[a + 1]));
        }

        return polygonArea == 0 && trianglesArea == 0 ? 0 : Math.abs((trianglesArea - polygonArea) / polygonArea);
    }

    /**
     * Earcut
     * @param data
     */
    public static flatten(data: number[][][]) {
        let dim = data[0][0].length,
            result = {vertices: new Array<number>(), holes: new Array<number>(), dimensions: dim},
            holeIndex = 0;

        for (let i = 0; i < data.length; i++) {
            for (let j = 0; j < data[i].length; j++) {
                for (let d = 0; d < dim; d++) {
                    result.vertices.push(data[i][j][d]);
                }
            }
            if (i > 0) {
                holeIndex += data[i - 1].length;
                result.holes.push(holeIndex);
            }
        }
        return result;
    }
}

/**
 * @ignore
 */
class Node {
    public i: number;
    public x: number;
    public y: number;

    public prev: Nullable<Node>;
    public next: Nullable<Node>;

    public z: Nullable<number>;
    public prevZ: Nullable<Node>;
    public nextZ: Nullable<Node>;

    public steiner: boolean;

    constructor(i: number, x: number, y: number) {
        this.i = i;

        // vertex coordinates
        this.x = x;
        this.y = y;

        // previous and next vertex nodes in a polygon ring
        this.prev = null;
        this.next = null;

        // z-order curve value
        this.z = null;

        // previous and next nodes in z-order
        this.prevZ = null;
        this.nextZ = null;

        // indicates whether this is a steiner point
        this.steiner = false;
    }
}

/**
 * @ignore
 * @param data
 * @param start
 * @param end
 * @param dim
 * @param clockwise
 */
function linkedList(data: number[], start: number, end: number, dim: number, clockwise: boolean) {
    let last: Nullable<Node> = null;

    if (clockwise == (signedArea(data, start, end, dim) > 0)) {
        for (let i = start; i < end; i += dim) {
            last = insertNode(i, data[i], data[i + 1], last);
        }
    } else {
        for (let i = end - dim; i >= start; i -= dim) {
            last = insertNode(i, data[i], data[i + 1], last);
        }
    }

    // @ts-ignore
    if (last && equals(last, last.next)) {
        removeNode(last);
        last = last.next;
    }

    return last;
}

/**
 * @ignore
 */
function filterPoints(start: Node, end: Nullable<Node> = null): Nullable<Node> {
    if (!start) { return start; }
    if (!end) { end = start; }

    let p = start, again;
    do {
        again = false;

        // @ts-ignore
        if (!p.steiner && (equals(p, p.next) || area(p.prev, p, p.next) == 0)) {
            removeNode(p);
            // @ts-ignore
            p = end = p.prev;
            if (p == p.next) { break; }
            again = true;

        } else {
            // @ts-ignore
            p = p.next;
        }
    } while (again || p != end);

    return end;
}

/**
 * @ignore
 */
function earcutLinked(ear: Node, triangles: number[], dim: number, minX: number, minY: number, invSize: number, pass: number = 0) {
    if (!ear) { return; }

    // interlink polygon nodes in z-order
    if (!pass && invSize) { indexCurve(ear, minX, minY, invSize); }

    let stop = ear, prev, next;

    // iterate through ears, slicing them one by one
    while (ear.prev != ear.next) {
        prev = ear.prev;
        next = ear.next;

        if (invSize ? isEarHashed(ear, minX, minY, invSize) : isEar(ear)) {
            // cut off the triangle
            // @ts-ignore
            triangles.push(prev.i / dim);
            triangles.push(ear.i / dim);
            // @ts-ignore
            triangles.push(next.i / dim);

            removeNode(ear);

            // skipping the next vertex leads to less sliver triangles
            // @ts-ignore
            ear = next.next;
            // @ts-ignore
            stop = next.next;

            continue;
        }

        // @ts-ignore
        ear = next;

        // if we looped through the whole remaining polygon and can't find any more ears
        if (ear == stop) {
            // try filtering points and slicing again
            if (!pass) {
                // @ts-ignore
                earcutLinked(filterPoints(ear), triangles, dim, minX, minY, invSize, 1);

                // if this didn't work, try curing all small self-intersections locally
            } else if (pass == 1) {
                // @ts-ignore
                ear = cureLocalIntersections(filterPoints(ear), triangles, dim);
                earcutLinked(ear, triangles, dim, minX, minY, invSize, 2);

                // as a last resort, try splitting the remaining polygon into two
            } else if (pass == 2) {
                splitEarcut(ear, triangles, dim, minX, minY, invSize);
            }

            break;
        }
    }
}

/**
 * @ignore
 */
function isEar(ear: Node): boolean {
    let a = ear.prev,
        b = ear,
        c = ear.next;

    // @ts-ignore
    if (area(a, b, c) >= 0) { return false; } // reflex, can't be an ear

    // now make sure we don't have other points inside the potential ear
    // @ts-ignore
    let p = ear.next.next;

    while (p != ear.prev) {
        // @ts-ignore
        if (pointInTriangle(a.x, a.y, b.x, b.y, c.x, c.y, p.x, p.y) && area(p.prev, p, p.next) >= 0) { return false; }
        // @ts-ignore
        p = p.next;
    }

    return true;
}

/**
 * @ignore
 */
function isEarHashed(ear: Node, minX: number, minY: number, invSize: number): boolean {
    let a = ear.prev,
        b = ear,
        c = ear.next;

    // @ts-ignore
    if (area(a, b, c) >= 0) { return false; } // reflex, can't be an ear

    // triangle bbox; min & max are calculated like this for speed
    // @ts-ignore
    let minTX = a.x < b.x ? (a.x < c.x ? a.x : c.x) : (b.x < c.x ? b.x : c.x), minTY = a.y < b.y ? (a.y < c.y ? a.y : c.y) : (b.y < c.y ? b.y : c.y), maxTX = a.x > b.x ? (a.x > c.x ? a.x : c.x) : (b.x > c.x ? b.x : c.x), maxTY = a.y > b.y ? (a.y > c.y ? a.y : c.y) : (b.y > c.y ? b.y : c.y);

    // z-order range for the current triangle bbox;
    let minZ = zOrder(minTX, minTY, minX, minY, invSize), maxZ = zOrder(maxTX, maxTY, minX, minY, invSize);

    let p = ear.prevZ, n = ear.nextZ;

    // look for points inside the triangle in both directions
    // @ts-ignore
    while (p && p.z >= minZ && n && n.z <= maxZ) {
        // @ts-ignore
        if (p != ear.prev && p != ear.next && pointInTriangle(a.x, a.y, b.x, b.y, c.x, c.y, p.x, p.y) && area(p.prev, p, p.next) >= 0) { return false; }
        p = p.prevZ;

        // @ts-ignore
        if (n != ear.prev && n != ear.next && pointInTriangle(a.x, a.y, b.x, b.y, c.x, c.y, n.x, n.y) && area(n.prev, n, n.next) >= 0) { return false; }
        n = n.nextZ;
    }

    // look for remaining points in decreasing z-order
    // @ts-ignore
    while (p && p.z >= minZ) {
        // @ts-ignore
        if (p != ear.prev && p != ear.next && pointInTriangle(a.x, a.y, b.x, b.y, c.x, c.y, p.x, p.y) && area(p.prev, p, p.next) >= 0) { return false; }
        p = p.prevZ;
    }

    // look for remaining points in increasing z-order
    // @ts-ignore
    while (n && n.z <= maxZ) {
        // @ts-ignore
        if (n != ear.prev && n != ear.next && pointInTriangle(a.x, a.y, b.x, b.y, c.x, c.y, n.x, n.y) && area(n.prev, n, n.next) >= 0) { return false; }
        n = n.nextZ;
    }

    return true;
}

/**
 * @ignore
 */
function cureLocalIntersections(start: Node, triangles: number[], dim: number): Nullable<Node> {
    let p = start;
    do {
        // @ts-ignore
        let a = p.prev, b = p.next.next;

        // @ts-ignore
        if (!equals(a, b) && intersects(a, p, p.next, b) && locallyInside(a, b) && locallyInside(b, a)) {

            // @ts-ignore
            triangles.push(a.i / dim);
            // @ts-ignore
            triangles.push(p.i / dim);
            // @ts-ignore
            triangles.push(b.i / dim);

            // remove two nodes involved
            removeNode(p);
            // @ts-ignore
            removeNode(p.next);

            // @ts-ignore
            p = start = b;
        }
        // @ts-ignore
        p = p.next;
    } while (p != start);

    return filterPoints(p);
}

/**
 * @ignore
 */
function splitEarcut(start: Node, triangles: number[], dim: number, minX: number, minY: number, invSize: number) {
    // look for a valid diagonal that divides the polygon into two
    let a = start;
    do {
        // @ts-ignore
        let b = a.next.next;
        while (b != a.prev) {
            // @ts-ignore
            if (a.i != b.i && isValidDiagonal(a, b)) {
                // split the polygon in two by the diagonal
                // @ts-ignore
                let c = splitPolygon(a, b);

                // filter colinear points around the cuts
                // @ts-ignore
                a = filterPoints(a, a.next);
                // @ts-ignore
                c = filterPoints(c, c.next);

                // run earcut on each half
                earcutLinked(a, triangles, dim, minX, minY, invSize);
                earcutLinked(c, triangles, dim, minX, minY, invSize);
                return;
            }
            // @ts-ignore
            b = b.next;
        }
        // @ts-ignore
        a = a.next;
    } while (a != start);
}

/**
 * @ignore
 */
function eliminateHoles(data: number[], holeIndices: FloatArray, outerNode: Node, dim: number) {
    let queue = [],
        i, len, start, end, list;

    for (i = 0, len = holeIndices.length; i < len; i++) {
        start = holeIndices[i] * dim;
        end = i < len - 1 ? holeIndices[i + 1] * dim : data.length;
        list = linkedList(data, start, end, dim, false);
        // @ts-ignore
        if (list == list.next) { list.steiner = true; }
        // @ts-ignore
        queue.push(getLeftmost(list));
    }

    queue.sort(compareX);

    // process holes from left to right
    for (i = 0; i < queue.length; i++) {
        eliminateHole(queue[i], outerNode);
        // @ts-ignore
        outerNode = filterPoints(outerNode, outerNode.next);
    }

    return outerNode;
}

/**
 * @ignore
 */
function compareX(a: Node, b: Node): number {
    return a.x - b.x;
}

/**
 * @ignore
 */
function eliminateHole(hole: Node, outerNode: Node) {
    const outerNode2 = findHoleBridge(hole, outerNode);
    if (outerNode2) {
        const b = splitPolygon(outerNode2, hole);

        // filter collinear points around the cuts
        // @ts-ignore
        filterPoints(outerNode2, outerNode2.next);
        // @ts-ignore
        filterPoints(b, b.next);
    }
}

/**
 * @ignore
 */
function findHoleBridge(hole: Node, outerNode: Node): Nullable<Node> {
    let p = outerNode,
        hx = hole.x,
        hy = hole.y,
        qx = -Infinity,
        m;

    // find a segment intersected by a ray from the hole's leftmost point to the left;
    // segment's endpoint with lesser x will be potential connection point
    do {
        // @ts-ignore
        if (hy <= p.y && hy >= p.next.y && p.next.y != p.y) {
            // @ts-ignore
            var x = p.x + (hy - p.y) * (p.next.x - p.x) / (p.next.y - p.y);
            if (x <= hx && x > qx) {
                qx = x;
                if (x == hx) {
                    if (hy == p.y) { return p; }
                    // @ts-ignore
                    if (hy == p.next.y) { return p.next; }
                }
                // @ts-ignore
                m = p.x < p.next.x ? p : p.next;
            }
        }
        // @ts-ignore
        p = p.next;
    } while (p != outerNode);

    if (!m) { return null; }

    if (hx == qx) { return m; } // hole touches outer segment; pick leftmost endpoint

    // look for points inside the triangle of hole point, segment intersection and endpoint;
    // if there are no points found, we have a valid connection;
    // otherwise choose the point of the minimum angle with the ray as connection point

    let stop = m,
        mx = m.x,
        my = m.y,
        tanMin = Infinity,
        tan;

    p = m;

    do {
        if (hx >= p.x && p.x >= mx && hx != p.x &&
            pointInTriangle(hy < my ? hx : qx, hy, mx, my, hy < my ? qx : hx, hy, p.x, p.y)) {

            tan = Math.abs(hy - p.y) / (hx - p.x); // tangential

            if (locallyInside(p, hole) &&
                (tan < tanMin || (tan == tanMin && (p.x > m.x || (p.x == m.x && sectorContainsSector(m, p)))))) {
                m = p;
                tanMin = tan;
            }
        }

        // @ts-ignore
        p = p.next;

    } while (p != stop);

    return m;
}

/**
 * @ignore
 */
function sectorContainsSector(m: Node, p: Node): boolean {
    // @ts-ignore
    return area(m.prev, m, p.prev) < 0 && area(p.next, m, m.next) < 0;
}

/**
 * @ignore
 */
function indexCurve(start: Node, minX: number, minY: number, invSize: number) {
    let p = start;
    do {
        if (p.z == null) { p.z = zOrder(p.x, p.y, minX, minY, invSize); }
        p.prevZ = p.prev;
        p.nextZ = p.next;
        // @ts-ignore
        p = p.next;
    } while (p != start);

    // @ts-ignore
    p.prevZ.nextZ = null;
    p.prevZ = null;

    sortLinked(p);
}

/**
 * @ignore
 */
function sortLinked(list: Nullable<Node>) {
    let i, p, q, e, tail, numMerges, pSize, qSize, inSize = 1;

    do {
        p = list;
        list = null;
        tail = null;
        numMerges = 0;

        while (p) {
            numMerges++;
            q = p;
            pSize = 0;
            for (i = 0; i < inSize; i++) {
                pSize++;
                // @ts-ignore
                q = q.nextZ;
                if (!q) { break; }
            }
            qSize = inSize;

            while (pSize > 0 || (qSize > 0 && q)) {

                // @ts-ignore
                if (pSize != 0 && (qSize == 0 || !q || p.z <= q.z)) {
                    e = p;
                    // @ts-ignore
                    p = p.nextZ;
                    pSize--;
                } else {
                    e = q;
                    // @ts-ignore
                    q = q.nextZ;
                    qSize--;
                }

                if (tail) { tail.nextZ = e; }
                else { list = e; }

                // @ts-ignore
                e.prevZ = tail;
                tail = e;
            }

            p = q;
        }

        // @ts-ignore
        tail.nextZ = null;
        inSize *= 2;

    } while (numMerges > 1);

    return list;
}

/**
 * @ignore
 */
function zOrder(x: number, y: number, minX: number, minY: number, invSize: number): number {
    // coords are transformed into non-negative 15-bit integer range
    x = 32767 * (x - minX) * invSize;
    y = 32767 * (y - minY) * invSize;

    x = (x | (x << 8)) & 0x00FF00FF;
    x = (x | (x << 4)) & 0x0F0F0F0F;
    x = (x | (x << 2)) & 0x33333333;
    x = (x | (x << 1)) & 0x55555555;

    y = (y | (y << 8)) & 0x00FF00FF;
    y = (y | (y << 4)) & 0x0F0F0F0F;
    y = (y | (y << 2)) & 0x33333333;
    y = (y | (y << 1)) & 0x55555555;

    return x | (y << 1);
}

/**
 * @ignore
 */
function getLeftmost(start: Node) {
    let p = start,
        leftmost = start;
    do {
        if (p.x < leftmost.x || (p.x == leftmost.x && p.y < leftmost.y)) { leftmost = p; }
        // @ts-ignore
        p = p.next;
    } while (p != start);

    return leftmost;
}

/**
 * @ignore
 */
function pointInTriangle(ax: number, ay: number, bx: number, by: number, cx: number, cy: number, px: number, py: number): boolean {
    return (cx - px) * (ay - py) - (ax - px) * (cy - py) >= 0 &&
        (ax - px) * (by - py) - (bx - px) * (ay - py) >= 0 &&
        (bx - px) * (cy - py) - (cx - px) * (by - py) >= 0;
}

/**
 * @ignore
 */
function isValidDiagonal(a: Node, b: Node): boolean {
    // @ts-ignore
    return a.next.i != b.i && a.prev.i != b.i && !intersectsPolygon(a, b) && (locallyInside(a, b) && locallyInside(b, a) && middleInside(a, b) && (area(a.prev, a, b.prev) || area(a, b.prev, b)) || equals(a, b) && area(a.prev, a, a.next) > 0 && area(b.prev, b, b.next) > 0);
}

/**
 * @ignore
 */
function area(p: Node, q: Node, r: Node): number {
    return (q.y - p.y) * (r.x - q.x) - (q.x - p.x) * (r.y - q.y);
}

/**
 * @ignore
 */
function equals(p1: Node, p2: Node): boolean {
    return p1.x == p2.x && p1.y == p2.y;
}

/**
 * @ignore
 */
function intersects(p1: Node, q1: Node, p2: Node, q2: Node): boolean {

    const o1 = sign(area(p1, q1, p2));
    const o2 = sign(area(p1, q1, q2));
    const o3 = sign(area(p2, q2, p1));
    const o4 = sign(area(p2, q2, q1));

    if (o1 != o2 && o3 != o4) { return true; } // general case

    if (o1 == 0 && onSegment(p1, p2, q1)) { return true; } // p1, q1 and p2 are collinear and p2 lies on p1q1
    if (o2 == 0 && onSegment(p1, q2, q1)) { return true; } // p1, q1 and q2 are collinear and q2 lies on p1q1
    if (o3 == 0 && onSegment(p2, p1, q2)) { return true; } // p2, q2 and p1 are collinear and p1 lies on p2q2
    if (o4 == 0 && onSegment(p2, q1, q2)) { return true; } // p2, q2 and q1 are collinear and q1 lies on p2q2

    return false;
}

/**
 * @ignore
 */
function onSegment(p: Node, q: Node, r: Node): boolean {
    return q.x <= Math.max(p.x, r.x) && q.x >= Math.min(p.x, r.x) && q.y <= Math.max(p.y, r.y) && q.y >= Math.min(p.y, r.y);
}

/**
 * @ignore
 */
function sign(num: number): number {
    return num > 0 ? 1 : num < 0 ? -1 : 0;
}

/**
 * @ignore
 */
function intersectsPolygon(a: Node, b: Node) {

    let p = a;
    do {
        // @ts-ignore
        if (p.i != a.i && p.next.i != a.i && p.i != b.i && p.next.i != b.i && intersects(p, p.next, a, b)) { return true; }
        // @ts-ignore
        p = p.next;
    } while (p != a);

    return false;
}

/**
 * @ignore
 */
function locallyInside(a: Node, b: Node): boolean {
    // @ts-ignore
    return area(a.prev, a, a.next) < 0 ? area(a, b, a.next) >= 0 && area(a, a.prev, b) >= 0 : area(a, b, a.prev) < 0 || area(a, a.next, b) < 0;
}

/**
 * @ignore
 */
function middleInside(a: Node, b: Node) {
    let p = a,
        inside = false,
        px = (a.x + b.x) / 2,
        py = (a.y + b.y) / 2;
    do {
        // @ts-ignore
        if (((p.y > py) != (p.next.y > py)) && p.next.y != p.y && (px < (p.next.x - p.x) * (py - p.y) / (p.next.y - p.y) + p.x)) {
            inside = !inside;
        }
        // @ts-ignore
        p = p.next;

    } while (p != a);

    return inside;
}

/**
 * @ignore
 */
function splitPolygon(a: Node, b: Node): Node {
    let a2 = new Node(a.i, a.x, a.y),
        b2 = new Node(b.i, b.x, b.y),
        an = a.next,
        bp = b.prev;

    a.next = b;
    b.prev = a;

    a2.next = an;
    // @ts-ignore
    an.prev = a2;

    b2.next = a2;
    a2.prev = b2;

    // @ts-ignore
    bp.next = b2;
    b2.prev = bp;

    return b2;
}

/**
 * @ignore
 */
function insertNode(i: number, x: number, y: number, last: Nullable<Node>): Node {
    let p = new Node(i, x, y);

    if (!last) {
        p.prev = p;
        p.next = p;

    } else {
        p.next = last.next;
        p.prev = last;
        // @ts-ignore
        last.next.prev = p;
        last.next = p;
    }
    return p;
}

/**
 * @ignore
 */
function removeNode(p: Node) {
    if (p.next) { p.next.prev = p.prev; }
    if (p.prev) { p.prev.next = p.next; }

    if (p.prevZ) { p.prevZ.nextZ = p.nextZ; }
    if (p.nextZ) { p.nextZ.prevZ = p.prevZ; }
}

/**
 * @ignore
 */
function signedArea(data: FloatArray, start: number, end: number, dim: number): number {
    let sum = 0;
    for (let i = start, j = end - dim; i < end; i += dim) {
        sum += (data[j] - data[i]) * (data[i + 1] + data[j + 1]);
        j = i;
    }
    return sum;
}
