import { Vector3 } from "./Vector3";
import { FloatArray, Nullable } from "../types";
import { BufferAttribute } from "../Misc";
import { AbstractObject } from "../Scene/AbstractObject";
import { Sphere } from "./Sphere";
import { Intervals } from "./Algorithm/Intervals";
import { OBB } from "./OBB";
import { LCPSolver } from "./Algorithm/LCPSolver";
import { Matrix4 } from "./Matrix4";
import { Box3Box3Intersect } from "./InterfaceType/IntersectResult";
import { Box3OBBDistance } from "./InterfaceType/DistanceResult";
import { MathTmp } from "./MathTmp";
import { InterleavedBufferAttribute } from "../Scene/Geometries/InterleavedBufferAttribute";

/**
 * @ignore
 */
const _tempArray1 = new Array<number>(3);
/**
 * @ignore
 */
const _tempArray2 = new Array<number>(3);

/**
 * 3D
 * @category Math
 */
export class Box3 {
    /**
     * 
     */
    public min: Vector3;
    /**
     * 
     */
    public max: Vector3;

    /**
     * 
     * @param min
     * @param max
     */
    constructor(min: Nullable<Vector3> = null, max: Nullable<Vector3> = null) {
        this.min = new Vector3(+Infinity, +Infinity, +Infinity);
        this.max = new Vector3(-Infinity, -Infinity, -Infinity);

        if (min) {
            this.min.copy(min);
        }
        if (max) {
            this.min.copy(max);
        }
    }

    /**
     * 
     * @constructor
     */
    public static Build() {
        return new Box3();
    }

    /**
     * 
     */
    public clone(): Box3 {
        return new Box3(this.min, this.max);
    }

    /**
     * 
     * @param box
     */
    public copy(box: Box3): Box3 {
        this.min.copy(box.min);
        this.max.copy(box.max);
        return this;
    }

    /**
     * Min,Max
     * @param min
     * @param max
     */
    public set(min: Vector3, max: Vector3): Box3 {
        this.min.copy(min);
        this.max.copy(max);
        return this;
    }

    /**
     * Number
     * @param array
     */
    public setFromArray(array: number[]): Box3 {
        let minX = +Infinity;
        let minY = +Infinity;
        let minZ = +Infinity;

        let maxX = -Infinity;
        let maxY = -Infinity;
        let maxZ = -Infinity;

        for (let i = 0, l = array.length; i < l; i += 3) {
            const x = array[i];
            const y = array[i + 1];
            const z = array[i + 2];

            if (x < minX) { minX = x; }
            if (y < minY) { minY = y; }
            if (z < minZ) { minZ = z; }

            if (x > maxX) { maxX = x; }
            if (y > maxY) { maxY = y; }
            if (z > maxZ) { maxZ = z; }
        }

        this.min.set(minX, minY, minZ);
        this.max.set(maxX, maxY, maxZ);

        return this;
    }

    /**
     * Buffer
     * @param attribute
     */
    public setFromBufferAttribute(attribute: BufferAttribute | InterleavedBufferAttribute): Box3 {
        let minX = +Infinity;
        let minY = +Infinity;
        let minZ = +Infinity;

        let maxX = -Infinity;
        let maxY = -Infinity;
        let maxZ = -Infinity;

        for (let i = 0, l = attribute.count; i < l; i++) {
            const x = attribute.getX(i);
            const y = attribute.getY(i);
            const z = attribute.getZ(i);

            if (x < minX) { minX = x; }
            if (y < minY) { minY = y; }
            if (z < minZ) { minZ = z; }

            if (x > maxX) { maxX = x; }
            if (y > maxY) { maxY = y; }
            if (z > maxZ) { maxZ = z; }
        }

        this.min.set(minX, minY, minZ);
        this.max.set(maxX, maxY, maxZ);

        return this;
    }

    /**
     * Vector3
     * @param points
     */
    public setFromPoints(points: Vector3[]): Box3 {

        this.makeEmpty();

        for (let i = 0, il = points.length; i < il; i++) {
            this.expandByPoint(points[i]);
        }
        return this;
    }

    /**
     * 
     * @param center
     * @param size
     */
    public setFromCenterAndSize(center: Vector3, size: Vector3): Box3 {

        const halfSize = MathTmp.Vector3[0].copy(size).multiplyScalar(0.5);
        this.min.copy(center).sub(halfSize);
        this.max.copy(center).add(halfSize);
        return this;
    }

    /**
     * 
     * @param object
     */
    public setFromObject(object: AbstractObject): Box3 {

        this.makeEmpty();

        return this.expandByObject(object);
    }

    /**
     * 
     */
    public makeEmpty(): Box3 {
        this.min.x = this.min.y = this.min.z = +Infinity;
        this.max.x = this.max.y = this.max.z = -Infinity;
        return this;
    }

    /**
     * 
     */
    public isEmpty(): boolean {
        return (this.max.x < this.min.x) || (this.max.y < this.min.y) || (this.max.z < this.min.z);
    }

    /**
     * 
     * @param target
     * @returns {Vector3}
     */
    public getCenter(target = new Vector3()): Vector3 {
        return this.isEmpty() ? target.set(0, 0, 0) : target.addVectors(this.min, this.max).multiplyScalar(0.5);
    }

    /**
     * 
     * @param target
     */
    public getSize(target = new Vector3()): Vector3 {
        return this.isEmpty() ? target.set(0, 0, 0) : target.subVectors(this.max, this.min);
    }

    /**
     * 
     * @param target
     */
    public getExtent(target = new Vector3()): Vector3 {
        return this.isEmpty() ? target.set(0, 0, 0) : target.subVectors(this.max, this.min).multiplyScalar(0.5);
    }

    /**
     * 
     * @param point
     */
    public expandByPoint(point: Vector3): Box3 {
        this.min.min(point);
        this.max.max(point);
        return this;
    }

    /**
     * 
     * @param vector
     */
    public expandByVector(vector: Vector3): Box3 {
        this.min.sub(vector);
        this.max.add(vector);
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public expandByScalar(scalar: number): Box3 {
        this.min.addScalar(-scalar);
        this.max.addScalar(scalar);
        return this;
    }

    /**
     * Mesh
     * @param object
     */
    public expandByObject(object: AbstractObject): Box3 {
        return this;
    }

    /**
     * 
     * @param target
     */
    public getBoundingSphere(target = new Sphere()): Sphere {
        this.getCenter(target.center);
        target.radius = this.getSize(MathTmp.Vector3[0]).length() * 0.5;
        return target;
    }

    /**
     * 
     * @param point
     */
    public containsPoint(point: Vector3): boolean {
        return !(point.x < this.min.x || point.x > this.max.x ||
            point.y < this.min.y || point.y > this.max.y ||
            point.z < this.min.z || point.z > this.max.z);
    }

    /**
     * 
     * @param box
     */
    public containsBox(box: Box3): boolean {
        return this.min.x <= box.min.x && box.max.x <= this.max.x &&
            this.min.y <= box.min.y && box.max.y <= this.max.y &&
            this.min.z <= box.min.z && box.max.z <= this.max.z;
    }

    /**
     * 
     * @param sphere
     */
    public containsSphere(sphere = new Sphere()): boolean {
        const boxSphere = new Sphere();
        this.getBoundingSphere(boxSphere);
        return boxSphere.containsSphere(sphere);
    }

    /**
     * Box
     * @param point
     * @param target
     */
    public getParameter(point: Vector3, target = new Vector3()): Vector3 {

        return target.set(
            (point.x - this.min.x) / (this.max.x - this.min.x),
            (point.y - this.min.y) / (this.max.y - this.min.y),
            (point.z - this.min.z) / (this.max.z - this.min.z),
        );
    }

    /**
     * 
     * @param point
     * @param target
     */
    public clampPoint(point: Vector3, target = new Vector3()): Vector3 {

        return target.copy(point).clamp(this.min, this.max);
    }

    /**
     * 
     * @param box
     */
    public intersect(box: Box3): Box3 {
        this.min.max(box.min);
        this.max.min(box.max);
        if (this.isEmpty()) { this.makeEmpty(); }
        return this;
    }

    /**
     * 
     * @param box
     */
    public union(box: Box3): Box3 {
        this.min.min(box.min);
        this.max.max(box.max);
        return this;
    }

    /**
     * Matrix4
     * @param matrix
     */
    public applyMatrix4(matrix: Matrix4): Box3 {

        if (this.isEmpty()) { return this; }

        MathTmp.Vector3[ 0 ].set(this.min.x, this.min.y, this.min.z).applyMatrix4(matrix); // 000
        MathTmp.Vector3[ 1 ].set(this.min.x, this.min.y, this.max.z).applyMatrix4(matrix); // 001
        MathTmp.Vector3[ 2 ].set(this.min.x, this.max.y, this.min.z).applyMatrix4(matrix); // 010
        MathTmp.Vector3[ 3 ].set(this.min.x, this.max.y, this.max.z).applyMatrix4(matrix); // 011
        MathTmp.Vector3[ 4 ].set(this.max.x, this.min.y, this.min.z).applyMatrix4(matrix); // 100
        MathTmp.Vector3[ 5 ].set(this.max.x, this.min.y, this.max.z).applyMatrix4(matrix); // 101
        MathTmp.Vector3[ 6 ].set(this.max.x, this.max.y, this.min.z).applyMatrix4(matrix); // 110
        MathTmp.Vector3[ 7 ].set(this.max.x, this.max.y, this.max.z).applyMatrix4(matrix); // 111

        this.setFromPoints(MathTmp.Vector3);

        return this;
    }

    /**
     * 
     * @param offset
     */
    public translate(offset: Vector3): Box3 {
        this.min.add(offset);
        this.max.add(offset);
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public scale(scalar: number): Box3 {
        this.min.multiplyScalar(scalar);
        this.max.multiplyScalar(scalar);
        return this;
    }

    /**
     * 
     * @param vec
     */
    public multiply(vec: Vector3): Box3 {
        this.min.multiply(vec);
        this.max.multiply(vec);
        return this;
    }

    /**
     * 
     * @param offset1
     * @param offset2
     */
    public translates(offset1: Vector3, offset2: Vector3): Box3 {
        this.min.add(offset1);
        this.max.add(offset2);
        return this;
    }

    /**
     * 
     * @param scalar1
     * @param scalar2
     */
    public scales(scalar1: number, scalar2: number): Box3 {
        this.min.multiplyScalar(scalar1);
        this.max.multiplyScalar(scalar2);
        return this;
    }

    /**
     * 
     * @param aVec
     * @param bVec
     */
    public multiplies(aVec: Vector3, bVec: Vector3): Box3 {
        this.min.multiply(aVec);
        this.max.multiply(bVec);
        return this;
    }

    /**
     * SQ
     * @param point
     * @param closestPoint
     */
    public distanceSqToPoint(point: Vector3, closestPoint: Nullable<Vector3> = null): number {

        const boxCenter = this.getCenter(MathTmp.Vector3[0]);
        const boxExtent = this.getExtent(MathTmp.Vector3[1]);
        const closest = MathTmp.Vector3[2].subVectors(point, boxCenter);

        let sqDistance = 0;

        for (let i = 0; i < 3; ++i) {
            if (closest.p[i] < -boxExtent.p[i]) {
                const delta = closest.p[i] + boxExtent.p[i];
                sqDistance += delta * delta;
                closest.p[i] =  -boxExtent.p[i];
            }
            else if (closest.p[i] > boxExtent.p[i]) {
                const delta = closest.p[i] - boxExtent.p[i];
                sqDistance += delta * delta;
                closest.p[i] =  boxExtent.p[i];
            }
        }

        if (closestPoint) {
            closestPoint.addVectors(closest, boxCenter);
        }

        return sqDistance;
    }

    /**
     * 
     * @param point
     * @param closestPoint
     */
    public distanceToPoint(point: Vector3, closestPoint: Nullable<Vector3> = null): number {
        return Math.sqrt(this.distanceSqToPoint(point, closestPoint));
    }

    /**
     * SQ
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     */
    public distanceSqToBox(box: Box3, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null): number {

        MathTmp.Box3[0].makeEmpty();
        MathTmp.Box3[1].makeEmpty();

        let sqrDistance = 0;

        for (let i = 0; i < 3; ++i) {

            if (this.min.p[i] >= box.max.p[i]) {

                const delta = this.min.p[i] - box.min.p[i];
                sqrDistance += delta * delta;

                if (closestPoint0) {
                    MathTmp.Box3[0].min.p[i] =  this.min.p[i];
                    MathTmp.Box3[0].max.p[i] =  this.min.p[i];
                }
                if (closestPoint1) {
                    MathTmp.Box3[1].min.p[i] =  box.max.p[i];
                    MathTmp.Box3[1].max.p[i] =  box.max.p[i];
                }
            }
            else if (box.min.p[i] >= this.max.p[i]) {

                const delta = box.min.p[i] - this.max.p[i];
                sqrDistance += delta * delta;

                if (closestPoint0) {
                    MathTmp.Box3[0].min.p[i] =  this.max.p[i];
                    MathTmp.Box3[0].max.p[i] =  this.max.p[i];
                }
                if (closestPoint1) {
                    MathTmp.Box3[1].min.p[i] =  box.min.p[i];
                    MathTmp.Box3[1].max.p[i] =  box.min.p[i];
                }
            }
            else if (closestPoint0 || closestPoint1) {

                const intr0 = [ this.min.p[i], this.max.p[i] ];
                const intr1 = [ box.min.p[i], box.max.p[i] ];
                const iiResult = Intervals.staticQuery(intr0, intr1);
                if (closestPoint0) {
                    MathTmp.Box3[0].min.p[i] =  iiResult.overlap[0];
                    MathTmp.Box3[0].max.p[i] =  iiResult.overlap[1];
                }
                if (closestPoint1) {
                    MathTmp.Box3[1].min.p[i] =  iiResult.overlap[0];
                    MathTmp.Box3[1].max.p[i] =  iiResult.overlap[1];
                }
            }
        }

        if (closestPoint0) {
            MathTmp.Box3[0].getCenter(closestPoint0);
        }
        if (closestPoint1) {
            MathTmp.Box3[1].getCenter(closestPoint1);
        }

        return sqrDistance;
    }

    /**
     * 
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     */
    public distanceToBox(box: Box3, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null): number {
        return Math.sqrt(this.distanceSqToBox(box, closestPoint0, closestPoint1));
    }

    /**
     * OBBSQ
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToOBB(box: OBB, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null, parameter: Nullable<Box3OBBDistance> = null): number {

        const box0 = this;
        const box1 = box;

        const K = MathTmp.Vector3[0].subVectors(box0.max, box0.min);
        const delta = MathTmp.Vector3[1].subVectors(box1.center, box0.min);

        for (let i = 0; i < 3; ++i) {
            delta.sub(MathTmp.Vector3[2].copy(box1.axis[i]).multiplyScalar(box1.extent.p[i]));
        }

        const rotDelta = MathTmp.Vector3[2];
        for (let i = 0; i < 3; ++i) {
            rotDelta.p[i] =  box1.axis[i].dot(delta);
        }

        const twoExtent = MathTmp.Vector3[3].copy(box1.extent).multiplyScalar(2);

        const q = [
            -delta.x, -delta.y, -delta.z, rotDelta.x, rotDelta.y, rotDelta.z,
            K.x, K.y, K.z, twoExtent.x, twoExtent.y, twoExtent.z
        ];

        const z = 0;
        const p = 1;
        const m = -1;
        const U0 = box1.axis[0];
        const U1 = box1.axis[1];
        const U2 = box1.axis[2];

        const M = [
            [p, z, z, -U0.x, -U1.x, -U1.z, p, z, z, z, z, z],
            [z, p, z, -U0.y, -U1.y, -U1.y, z, p, z, z, z, z],
            [z, z, p, -U0.z, -U1.z, -U1.z, z, z, p, z, z, z],
            [-U0.x, -U0.y, -U0.z, p, z, z, z, z, z, p, z, z],
            [-U1.x, -U1.y, -U1.z, z, p, z, z, z, z, z, p, z],
            [-U2.x, -U2.y, -U2.z, z, z, p, z, z, z, z, z, p],
            [m, z, z, z, z, z, z, z, z, z, z, z],
            [z, m, z, z, z, z, z, z, z, z, z, z],
            [z, z, m, z, z, z, z, z, z, z, z, z],
            [z, z, z, m, z, z, z, z, z, z, z, z],
            [z, z, z, z, m, z, z, z, z, z, z, z],
            [z, z, z, z, z, m, z, z, z, z, z, z]
        ];

        const w = new Array<number>(12);
        const zz = new Array<number>(12);

        const mLCP = new LCPSolver(12);
        if (mLCP.solve(q, M, w, zz)) {

            if (parameter) {
                parameter.queryIsSuccessful = true;
                parameter.numLCPIterations = mLCP.getNumIterations();
            }

            for (let i = 0; i < 3; ++i) {

                const boxParameter = zz[i] + box0.min.p[i];
                if (parameter) {
                    parameter.box0Parameter[i] = boxParameter;
                }
                MathTmp.Vector3[0].p[i] = boxParameter;
            }

            MathTmp.Vector3[1].copy(box1.center);
            for (let i = 0, j = 3; i < 3; ++i, ++j) {

                const boxParameter = zz[j] - box1.extent.p[i];
                if (parameter) {
                    parameter.box1Parameter[i] = boxParameter;
                }
                MathTmp.Vector3[1].add(MathTmp.Vector3[2].copy(box1.axis[i]).multiplyScalar(boxParameter));
            }

            if (closestPoint0) {
                closestPoint0.copy(MathTmp.Vector3[0]);
            }

            if (closestPoint1) {
                closestPoint1.copy(MathTmp.Vector3[1]);
            }

            const diff = MathTmp.Vector3[2].subVectors(MathTmp.Vector3[1], MathTmp.Vector3[0]);
            return diff.dot(diff);
        }
        else {
            if (parameter) {
                parameter.queryIsSuccessful = false;
                parameter.numLCPIterations = 0;
            }
        }

        return NaN;
    }

    /**
     * OBB
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     */
    public distanceToOBB(box: OBB, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null, parameter: Nullable<Box3OBBDistance> = null): number {
        return Math.sqrt(this.distanceSqToOBB(box, closestPoint0, closestPoint1, parameter));
    }

    /**
     * 
     * @param box
     */
    public intersectsBox(box: Box3): boolean {
        return !(box.max.x < this.min.x || box.min.x > this.max.x ||
            box.max.y < this.min.y || box.min.y > this.max.y ||
            box.max.z < this.min.z || box.min.z > this.max.z);
    }

    /**
     * 
     * @param box
     */
    public intersectBox(box: Box3): Box3Box3Intersect {

        const box0 = this;
        const box1 = box;

        const result = {
            intersect: false,
            box: new Box3()
        };

        for (let i = 0; i < 3; i++) {
            if (box0.max.p[i] < box1.min.p[i] || box0.min.p[i] > box1.max.p[i]) {
                result.intersect = false;
                return result;
            }
        }

        for (let i = 0; i < 3; i++) {
            if (box0.max.p[i] <= box1.max.p[i]) {
                result.box.max.p[i] =  box0.max.p[i];
            }
            else {
                result.box.max.p[i] =  box1.max.p[i];
            }

            if (box0.min.p[i] <= box1.min.p[i]) {
                result.box.min.p[i] =  box1.min.p[i];
            }
            else {
                result.box.min.p[i] =  box0.min.p[i];
            }
        }

        result.intersect = true;
        return result;
    }

    /**
     * 
     * @param sphere
     */
    public intersectsSphere(sphere: Sphere): boolean {
        this.clampPoint(sphere.center, MathTmp.Vector3[0]);
        return MathTmp.Vector3[0].distanceToSquared(sphere.center) <= (sphere.radius * sphere.radius);
    }

    /**
     * OBB
     * @param box
     */
    public intersectsOBB(box: OBB): boolean {

        const box0 = this;
        const box1 = box;

        // Get the centered form of the aligned box.  The axes are implicitly
        // A0[0] = (1,0,0), A0[1] = (0,1,0), and A0[2] = (0,0,1).
        let C0 = box0.getCenter(MathTmp.Vector3[0]);
        let E0 = box0.getExtent(MathTmp.Vector3[1]).toArray(_tempArray1);

        // Convenience variables.
        let C1 = box1.center;
        let A1 = box1.axis;
        let E1 = box1.extent.toArray(_tempArray2);

        let cutoff = 1;
        let existsParallelPair = false;

        // Compute the difference of box centers.
        let D = MathTmp.Vector3[2].subVectors(C1, C0);

        let dot01: number[][] = [[], [], []];      // dot01[i][j] = Dot(A0[i],A1[j]) = A1[j][i]
        let absDot01: number[][] = [[], [], []];   // |dot01[i][j]|
        let r0; let r1; let r;                      // interval radii and distance between centers
        let r01;                                    // r0 + r1

        // Test for separation on the axis C0 + t*A0[0].
        for (let i = 0; i < 3; ++i) {
            dot01[0][i] = A1[i].x;
            absDot01[0][i] = Math.abs(A1[i].x);
            if (absDot01[0][i] >= cutoff) {
                existsParallelPair = true;
            }
        }
        r = Math.abs(D.x);
        r1 = E1[0] * absDot01[0][0] + E1[1] * absDot01[0][1] + E1[2] * absDot01[0][2];
        r01 = E0[0] + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1].
        for (let i = 0; i < 3; ++i) {
            dot01[1][i] = A1[i].y;
            absDot01[1][i] = Math.abs(A1[i].y);
            if (absDot01[1][i] >= cutoff) {
                existsParallelPair = true;
            }
        }
        r = Math.abs(D.y);
        r1 = E1[0] * absDot01[1][0] + E1[1] * absDot01[1][1] + E1[2] * absDot01[1][2];
        r01 = E0[1] + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2].
        for (let i = 0; i < 3; ++i) {
            dot01[2][i] = A1[i].z;
            absDot01[2][i] = Math.abs(A1[i].z);
            if (absDot01[2][i] >= cutoff) {
                existsParallelPair = true;
            }
        }
        r = Math.abs(D.z);
        r1 = E1[0] * absDot01[2][0] + E1[1] * absDot01[2][1] + E1[2] * absDot01[2][2];
        r01 = E0[2] + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A1[0].
        r = Math.abs(D.dot(A1[0]));
        r0 = E0[0] * absDot01[0][0] + E0[1] * absDot01[1][0] + E0[2] * absDot01[2][0];
        r01 = r0 + E1[0];
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A1[1].
        r = Math.abs(D.dot(A1[1]));
        r0 = E0[0] * absDot01[0][1] + E0[1] * absDot01[1][1] + E0[2] * absDot01[2][1];
        r01 = r0 + E1[1];
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A1[2].
        r = Math.abs(D.dot(A1[2]));
        r0 = E0[0] * absDot01[0][2] + E0[1] * absDot01[1][2] + E0[2] * absDot01[2][2];
        r01 = r0 + E1[2];
        if (r > r01) {
            return false;
        }

        // At least one pair of box axes was parallel, so the separation is
        // effectively in 2D.  The edge-edge axes do not need to be tested.
        if (existsParallelPair) {
            return true;
        }

        // Test for separation on the axis C0 + t*A0[0]xA1[0].
        r = Math.abs(D.z * dot01[1][0] - D.y * dot01[2][0]);
        r0 = E0[1] * absDot01[2][0] + E0[2] * absDot01[1][0];
        r1 = E1[1] * absDot01[0][2] + E1[2] * absDot01[0][1];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[0]xA1[1].
        r = Math.abs(D.z * dot01[1][1] - D.y * dot01[2][1]);
        r0 = E0[1] * absDot01[2][1] + E0[2] * absDot01[1][1];
        r1 = E1[0] * absDot01[0][2] + E1[2] * absDot01[0][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[0]xA1[2].
        r = Math.abs(D.z * dot01[1][2] - D.y * dot01[2][2]);
        r0 = E0[1] * absDot01[2][2] + E0[2] * absDot01[1][2];
        r1 = E1[0] * absDot01[0][1] + E1[1] * absDot01[0][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1]xA1[0].
        r = Math.abs(D.x * dot01[2][0] - D.z * dot01[0][0]);
        r0 = E0[0] * absDot01[2][0] + E0[2] * absDot01[0][0];
        r1 = E1[1] * absDot01[1][2] + E1[2] * absDot01[1][1];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1]xA1[1].
        r = Math.abs(D.x * dot01[2][1] - D.z * dot01[0][1]);
        r0 = E0[0] * absDot01[2][1] + E0[2] * absDot01[0][1];
        r1 = E1[0] * absDot01[1][2] + E1[2] * absDot01[1][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1]xA1[2].
        r = Math.abs(D.x * dot01[2][2] - D.z * dot01[0][2]);
        r0 = E0[0] * absDot01[2][2] + E0[2] * absDot01[0][2];
        r1 = E1[0] * absDot01[1][1] + E1[1] * absDot01[1][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2]xA1[0].
        r = Math.abs(D.y * dot01[0][0] - D.x * dot01[1][0]);
        r0 = E0[0] * absDot01[1][0] + E0[1] * absDot01[0][0];
        r1 = E1[1] * absDot01[2][2] + E1[2] * absDot01[2][1];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2]xA1[1].
        r = Math.abs(D.y * dot01[0][1] - D.x * dot01[1][1]);
        r0 = E0[0] * absDot01[1][1] + E0[1] * absDot01[0][1];
        r1 = E1[0] * absDot01[2][2] + E1[2] * absDot01[2][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2]xA1[2].
        r = Math.abs(D.y * dot01[0][2] - D.x * dot01[1][2]);
        r0 = E0[0] * absDot01[1][2] + E0[1] * absDot01[0][2];
        r1 = E1[0] * absDot01[2][1] + E1[1] * absDot01[2][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        return true;
    }

    /**
     * 
     * @param box
     */
    public equals(box: Box3): boolean {
        return box.min.equals(this.min) && box.max.equals(this.max);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset = 0): FloatArray {
        this.min.toArray(array, offset);
        this.max.toArray(array, offset + 3);
        return array;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset = 0): Box3 {
        this.min.fromArray(array, offset);
        this.max.fromArray(array, offset + 3);
        return this;
    }
}
