import { MathUtils } from "../../Math";
import { BufferAttrUpdate } from "./BufferAttrUpdate";
import { Constants } from "../../Engines";
import { BufferArray, int, Nullable } from "../../types";
import { DataTypeFormat } from "../../Misc";
import { InterleavedBufferClone } from "./InterleavedBufferClone";
import { IDisposable } from "../../Misc/IDisposable";
import { GLDataBuffer } from "../../Engines/GLDataBuffer";

/**
 * Buffer
 */
export class InterleavedBuffer implements IDisposable {

    public uuid: string;
    public array: BufferArray;
    public type: number;
    public stride: number;
    public count: number;
    public usage: number;
    public target: number;
    public updateRange: BufferAttrUpdate;

    protected _version: number;
    protected _reference: number;
    protected _glBuffer: Nullable<GLDataBuffer>;

    /**
     * 
     * @param array
     * @param stride
     */
    constructor(array: BufferArray, stride: number) {

        this.uuid = MathUtils.generateUUID();

        this.array = array;
        this.stride = stride;
        this.count = array.length / stride;
        this.type = DataTypeFormat.dataTypeFromTypedArray(array);
        this.target = Constants.ArrayBufferTarget;
        this.usage = Constants.StaticDrawUsage;
        this.updateRange = {offset: 0, count: -1};

        this._version = 0;
        this._reference = 0;
        this._glBuffer = null;
    }

    public get glBuffer() {
        return this._glBuffer;
    }
    public set glBuffer(value) {
        if (this._glBuffer != value) {
            if (this._glBuffer) {
                this._glBuffer.dispose();
            }
            this._glBuffer = value;
            if (this._glBuffer) {
                this._glBuffer.addReference();
            }
        }
    }

    /**
     * 
     */
    public get version() {
        return this._version;
    }

    /**
     * 
     */
    public get reference() {
        return this._reference;
    }

    /**
     * 
     */
    public set needsUpdate(value: boolean) {
        if (value) {
            this._version++;
        }
    }

    /**
     * 
     */
    public dispose(): void {
        this._reference--;
        if (this._reference == 0) {
            if (this._glBuffer) {
                this._glBuffer.dispose();
                this._glBuffer = null;
            }
        }
    }

    /**
     * 
     */
    public addReference(): void {
        this._reference++;
    }

    /**
     * 
     * @param array
     */
    public setArray(array: BufferArray): InterleavedBuffer {
        this.array = array;
        this.count = this.array.length / this.stride;
        return this;
    }

    /**
     * 
     * @param value
     */
    public setUsage(value: number): InterleavedBuffer {
        this.usage = value;
        return this;
    }

    /**
     * 
     */
    public clone(cache: InterleavedBufferClone): InterleavedBuffer {

        let uuid = cache.cacheKey.get(this.array);
        if (!uuid) {
            uuid = MathUtils.generateUUID();
            cache.cacheKey.set(this.array, uuid);
        }

        let cloneArray = cache.arrayBuffers[uuid];
        if (!cloneArray) {
            cloneArray = this.array.slice(0).buffer;
            cache.arrayBuffers[uuid] = cloneArray;
        }

        const array = DataTypeFormat.createTypedArrayFromDataTypeValues(this.type, cloneArray)!;

        const ib = new InterleavedBuffer(array, this.stride);
        ib.setUsage(this.usage);

        return this;
    }

    /**
     * 
     * @param source
     */
    public copy(source: InterleavedBuffer): InterleavedBuffer {
        this.usage = source.usage;
        return this;
    }

    /**
     * 
     * @param index1
     * @param attribute
     * @param index2
     */
    public copyAt(index1: int, attribute: InterleavedBuffer, index2: int): InterleavedBuffer {
        index1 *= this.stride;
        index2 *= attribute.stride;

        for (let i = 0, l = this.stride; i < l; i++) {
            this.array[index1 + i] = attribute.array[index2 + i];
        }
        return this;
    }

    /**
     * 
     * @param value
     * @param offset
     */
    public set(value: number[], offset: int = 0) {
        this.array.set(value, offset);
        return this;
    }
}
