    protected _initGLContext(): void {
    }

    /**
     * GPU
     * @protected
     */
    protected _identifyGPUVendor(vendor: string, renderer: string): string {
        if (vendor.match(/NVIDIA/i) || renderer.match(/NVIDIA/i)) {
            return 'NVIDIA';
        }
        if (vendor.match(/INTEL/i) || renderer.match(/INTEL/i)) {
            return 'INTEL';
        }
        if (vendor.match(/AMD/i) ||
            renderer.match(/AMD/i) ||
            vendor.match(/ATI/i) ||
            renderer.match(/ATI/i)) {
            return 'AMD';
        }
        return 'UNKNOWN GPU';
    }

