import { Nullable } from "../types";
import { Vector2 } from "./Vector2";
import { Box2 } from "./Box2";

/**
 * 
 * @category Math
 */
export class Polygon {

    public polygon: number[];

    /**
     * 
     * @param points
     */
    constructor(points: Nullable<number[]> = null) {
        if (points) {
            this.polygon = points.slice(0);
        }
        else {
            this.polygon = [];
        }
    }

    /**
     * 
     * @param points
     */
    public set(points: number[]): Polygon {
        this.polygon = points.slice(0);
        return this;
    }

    /**
     * Vector2
     * @param polygon
     */
    public setFromVector2(polygon: Vector2[]): Polygon {
        this.polygon = [];
        for (let i = 0; i < polygon.length; ++i) {
            this.polygon.push(polygon[i].x, polygon[i].y);
        }
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public add(x: number, y: number): Polygon {
        this.polygon.push(x, y);
        return this;
    }

    /**
     * Vector2
     * @param vec2
     */
    public addFromVector2(vec2: Vector2): Polygon {
        this.polygon.push(vec2.x, vec2.y);
        return this;
    }

    /**
     * 
     */
    public isSimple(): boolean {
        let p = this.polygon;
        let n = p.length >> 1;
        if (n < 4) {
            return true;
        }

        let a1 = Point();
        let a2 = Point();
        let b1 = Point();
        let b2 = Point();
        let c = Point();

        for (let i = 0; i < n; i++) {
            a1.x = p[2 * i];
            a1.y = p[2 * i + 1];

            if (i == n - 1) {
                a2.x = p[0];
                a2.y = p[1];
            } else {
                a2.x = p[2 * i + 2];
                a2.y = p[2 * i + 3];
            }

            for (let j = 0; j < n; j++) {
                if (Math.abs(i - j) < 2) { continue; }
                if (j == n - 1 && i == 0) { continue; }
                if (i == n - 1 && j == 0) { continue; }

                b1.x = p[2 * j];
                b1.y = p[2 * j + 1];

                if (j == n - 1) {
                    b2.x = p[0];
                    b2.y = p[1];
                } else {
                    b2.x = p[2 * j + 2];
                    b2.y = p[2 * j + 3];
                }

                if (getLineIntersection(a1, a2, b1, b2, c) != null) { return false; }
            }
        }
        return true;
    }

    /**
     * 
     */
    public isConvex(): boolean {
        const p = this.polygon;
        if (p.length < 6) { return true; }
        const l = p.length - 4;
        for (let i = 0; i < l; i += 2) {
            if (!isConvex(p[i], p[i + 1], p[i + 2], p[i + 3], p[i + 4], p[i + 5])) { return false; }
        }
        if (!isConvex(p[l], p[l + 1], p[l + 2], p[l + 3], p[0], p[1])) { return false; }
        if (!isConvex(p[l + 2], p[l + 3], p[0], p[1], p[2], p[3])) { return false; }
        return true;
    }

    /**
     * 
     */
    public getArea(): number {
        const p = this.polygon;
        if (p.length < 6) { return 0; }
        const l = p.length - 2;
        let sum = 0;
        for (let i = 0; i < l; i += 2) {
            sum += (p[i + 2] - p[i]) * (p[i + 1] + p[i + 3]);
        }
        sum += (p[0] - p[l]) * (p[l + 1] + p[1]);
        return -sum * 0.5;
    }

    /**
     * 
     * @param box
     */
    public getAABB(box = new Box2()): Box2 {
        let p = this.polygon;
        let minx = Infinity;
        let miny = Infinity;
        let maxx = -minx;
        let maxy = -miny;
        for (let i = 0; i < p.length; i += 2) {
            minx = Math.min(minx, p[i]);
            maxx = Math.max(maxx, p[i]);
            miny = Math.min(miny, p[i + 1]);
            maxy = Math.max(maxy, p[i + 1]);
        }
        box.min.x = minx;
        box.min.y = miny;
        box.max.x = maxx;
        box.max.y = maxy;
        return box;
    }

    /**
     * 
     * @param pointX
     * @param pointY
     */
    public containsPoint(pointX: number, pointY: number): boolean {
        let p = this.polygon;
        let px = pointX;
        let py = pointY;
        let n = p.length >> 1;
        let ax;
        let ay = p[2 * n - 3] - py;
        let bx = p[2 * n - 2] - px;
        let by = p[2 * n - 1] - py;

        let lup;

        for (let ii = 0; ii < n; ii++) {
            ax = bx;
            ay = by;
            bx = p[2 * ii] - px;
            by = p[2 * ii + 1] - py;
            if (ay == by) { continue; }
            lup = by > ay;
        }

        let depth = 0;
        for (let i = 0; i < n; i++) {
            ax = bx;
            ay = by;
            bx = p[2 * i] - px;
            by = p[2 * i + 1] - py;
            if (ay < 0 && by < 0) { continue; } // both "up" or both "down"
            if (ay > 0 && by > 0) { continue; } // both "up" or both "down"
            if (ax < 0 && bx < 0) { continue; } // both points on the left

            if (ay == by && Math.min(ax, bx) <= 0) { return true; }
            if (ay == by) { continue; }

            let lx = ax + (bx - ax) * (-ay) / (by - ay);
            if (lx == 0) { return true; } // point on edge
            if (lx > 0) { depth++; }
            if (ay == 0 && lup && by > ay) { depth--; } // hit vertex, both up
            if (ay == 0 && !lup && by < ay) { depth--; } // hit vertex, both down
            lup = by > ay;
        }
        return (depth & 1) == 1;
    }

    /**
     * 
     */
    public triangulate(): number[] {
        let p = this.polygon;
        let n = p.length >> 1;
        if (n < 3) { return []; }
        let tgs = [];
        let avl = [];
        for (let count = 0; count < n; count++) {
            avl.push(count);
        }

        let i = 0;
        let al = n;
        while (al > 3) {
            let i0 = avl[(i + 0) % al];
            let i1 = avl[(i + 1) % al];
            let i2 = avl[(i + 2) % al];

            let ax = p[2 * i0];
            let ay = p[2 * i0 + 1];
            let bx = p[2 * i1];
            let by = p[2 * i1 + 1];
            let cx = p[2 * i2];
            let cy = p[2 * i2 + 1];

            let earFound = false;
            if (isConvex(ax, ay, bx, by, cx, cy)) {
                earFound = true;
                for (let j = 0; j < al; j++) {
                    let vi = avl[j];
                    if (vi == i0 || vi == i1 || vi == i2) { continue; }
                    if (isPointInTriangle(p[2 * vi], p[2 * vi + 1], ax, ay, bx, by, cx, cy)) {
                        earFound = false;
                        break;
                    }
                }
            }
            if (earFound) {
                tgs.push(i0, i1, i2);
                avl.splice((i + 1) % al, 1);
                al--;
                i = 0;
            } else if (i++ > 3 * al) { break; }
        }
        tgs.push(avl[0], avl[1], avl[2]);
        return tgs;
    }

    /**
     * 
     * @param startX
     * @param startY
     * @param endX
     * @param endY
     */
    public slice(startX: number, startY: number, endX: number, endY: number): number[][] {
        let p = this.polygon;
        let ax = startX;
        let ay = startY;
        let bx = endX;
        let by = endY;

        if (this.containsPoint(ax, ay) || this.containsPoint(bx, by)) {
            return [p.slice(0)];
        }

        let a = Point(ax, ay);
        let b = Point(bx, by);
        let iscs: Point[] = []; // intersections
        let ps: Point[] = []; // points

        for (let count = 0; count < p.length; count += 2) {
            ps.push(Point(p[count], p[count + 1]));
        }

        for (let i = 0; i < ps.length; i++) {
            let isc: Nullable<Point> = Point();
            isc = getLineIntersection(a, b, ps[i], ps[(i + 1) % ps.length], isc);

            let fisc = iscs[0];
            let lisc = iscs[iscs.length - 1];
            if (isc && (fisc == null || pointDistance(isc, fisc) > 1e-10) && (lisc == null || pointDistance(isc, lisc) > 1e-10)) {
                isc.flag = true;
                iscs.push(isc);
                ps.splice(i + 1, 0, isc);
                i++;
            }
        }

        if (iscs.length < 2) { return [p.slice(0)]; }
        iscs.sort((u, v) => pointComp(a, u, v));

        let pgs = [];
        let dir = 0;
        while (iscs.length > 0) {

            let i0: Point = iscs[0];
            let i1: Point = iscs[1];

            let index0: number = ps.indexOf(i0);
            let index1: number = ps.indexOf(i1);
            let solved: boolean = false;

            if (firstWithFlag(ps, index0) == index1) {
                solved = true;
            } else {
                i0 = iscs[1];
                i1 = iscs[0];
                index0 = ps.indexOf(i0);
                index1 = ps.indexOf(i1);
                if (firstWithFlag(ps, index0) == index1) { solved = true; }
            }

            if (solved) {
                dir--;
                let pgn = getPoints(ps, index0, index1);
                pgs.push(pgn);
                ps = getPoints(ps, index1, index0);
                i0.flag = i1.flag = false;
                iscs.splice(0, 2);
                if (iscs.length == 0) { pgs.push(ps); }
            } else {
                dir++;
                iscs.reverse();
            }
            if (dir > 1) { break; }
        }

        let result: number[][] = [];
        for (let ii = 0; ii < pgs.length; ii++) {
            let pg = pgs[ii];
            let npg: number[] = [];
            for (let j = 0; j < pg.length; j++) {
                npg.push(pg[j].x, pg[j].y);
            }
            result.push(npg);
        }
        return result;
    }

    /**
     * 
     */
    public reverse(): number[] {
        let p = this.polygon;
        let np = [];
        for (let j = p.length - 2; j >= 0; j -= 2) {
            np.push(p[j], p[j + 1]);
        }
        return np;
    }
}

/**
 * @ignore
 */
interface Point {
    x: number;
    y: number;
    flag: boolean;
}

/**
 * @ignore
 */
function Point(x: number = 0, y: number = 0): Point {
    return {
        x,
        y,
        flag: false
    };
}

/**
 * @ignore
 * @param a1
 * @param a2
 * @param b1
 * @param b2
 * @param c
 */
function getLineIntersection(a1: Point, a2: Point, b1: Point, b2: Point, c: Point): Nullable<Point> {
    let dax = (a1.x - a2.x);
    let dbx = (b1.x - b2.x);
    let day = (a1.y - a2.y);
    let dby = (b1.y - b2.y);

    let Den = dax * dby - day * dbx;

    if (Den == 0) {
        return null;
    } // parallel

    let A = (a1.x * a2.y - a1.y * a2.x);
    let B = (b1.x * b2.y - b1.y * b2.x);

    let I = c;
    I.x = (A * dbx - dax * B) / Den;
    I.y = (A * dby - day * B) / Den;

    if (inRectangle(I, a1, a2) && inRectangle(I, b1, b2)) {
        return I;
    }
    return null;
}

/**
 * @ignore
 * @param a
 * @param b
 * @param c
 */
function inRectangle(a: Point, b: Point, c: Point): boolean {
    let minx = Math.min(b.x, c.x);
    let maxx = Math.max(b.x, c.x);
    let miny = Math.min(b.y, c.y);
    let maxy = Math.max(b.y, c.y);

    if (minx == maxx) {
        return (miny <= a.y && a.y <= maxy);
    }
    if (miny == maxy) {
        return (minx <= a.x && a.x <= maxx);
    }

    // return (minx <= a.x && a.x <= maxx && miny <= a.y && a.y <= maxy)
    return (minx <= a.x + 1e-10 && a.x - 1e-10 <= maxx && miny <= a.y + 1e-10 && a.y - 1e-10 <= maxy);
}

/**
 * @ignore
 * @param ax
 * @param ay
 * @param bx
 * @param by
 * @param cx
 * @param cy
 */
function isConvex(ax: number, ay: number, bx: number, by: number, cx: number, cy: number) {
    return (ay - by) * (cx - bx) + (bx - ax) * (cy - by) >= 0;
}

/**
 * @ignore
 * @param px
 * @param py
 * @param ax
 * @param ay
 * @param bx
 * @param by
 * @param cx
 * @param cy
 */
function isPointInTriangle(px: number, py: number, ax: number, ay: number, bx: number, by: number, cx: number, cy: number) {
    let v0x = cx - ax;
    let v0y = cy - ay;
    let v1x = bx - ax;
    let v1y = by - ay;
    let v2x = px - ax;
    let v2y = py - ay;

    let dot00 = v0x * v0x + v0y * v0y;
    let dot01 = v0x * v1x + v0y * v1y;
    let dot02 = v0x * v2x + v0y * v2y;
    let dot11 = v1x * v1x + v1y * v1y;
    let dot12 = v1x * v2x + v1y * v2y;

    let invDenom = 1 / (dot00 * dot11 - dot01 * dot01);
    let u = (dot11 * dot02 - dot01 * dot12) * invDenom;
    let v = (dot00 * dot12 - dot01 * dot02) * invDenom;

    // Check if point is in triangle
    return (u >= 0) && (v >= 0) && (u + v < 1);
}

/**
 * @ignore
 * @param a
 * @param b
 */
function pointDistance(a: Point, b: Point): number {
    const dx = b.x - a.x;
    const dy = b.y - a.y;
    return Math.sqrt(dx * dx + dy * dy);
}

/**
 * @ignore
 * @param a
 * @param u
 * @param v
 */
function pointComp(a: Point, u: Point, v: Point): number {
    return pointDistance(a, u) - pointDistance(a, v);
}

/**
 * @ignore
 * @param points
 * @param index0
 * @param index1
 */
function getPoints(points: Point[], index0: number, index1: number): Point[] {
    let n = points.length;
    let result = [];
    if (index1 < index0) { index1 += n; }
    for (let i = index0; i <= index1; i++) {
        result.push(points[i % n]);
    }
    return result;
}

/**
 * @ignore
 * @param points
 * @param index
 */
function firstWithFlag(points: Point[], index: number): number {
    let n = points.length;
    while (true) {
        index = (index + 1) % n;
        if (points[index].flag) {
            return index;
        }
    }
}
