import { BufferArray, int, Nullable } from "../../types";
import { Constants, GLConstants } from "../../Engines";
import { DataTypeFormat } from "../../Misc/DataTypeFormat";
import { ColorRGB } from "../../Math/ColorRGB";
import { ColorRGBA } from "../../Math/ColorRGBA";
import { Vector2 } from "../../Math/Vector2";
import { Vector3 } from "../../Math/Vector3";
import { Vector4 } from "../../Math/Vector4";
import { Matrix3 } from "../../Math/Matrix3";
import { Matrix4 } from "../../Math/Matrix4";
import { BinaryBuffer } from "../../Misc/BinaryBuffer";
import { BufferAttrUpdate } from "./BufferAttrUpdate";
import { GLDataBuffer } from "../../Engines/GLDataBuffer";
import { IDisposable } from "../../Misc/IDisposable";

/**
 * @ignore
 */
const _vector3 = new Vector3();
/**
 * @ignore
 */
const _vector2 = new Vector2();

/**
 * BufferJSON
 * @category Misc
 */
export interface BufferAttrJSON {
    /**
     * 
     */
    type: number;
    /**
     * 
     */
    itemSize: number;
    /**
     * 
     */
    array: number[];
    /**
     * 
     */
    normalized: boolean;
}

/**
 * Buffer
 * @category Misc
 */
export class BufferAttribute implements IDisposable {

    /**
     * 
     */
    public name: string;
    public type: number;
    public count: int;
    public itemSize: int;
    public normalized: boolean;
    public usage: number;
    public updateRange: BufferAttrUpdate;
    public array: BufferArray;
    public target: number;

    protected _version: number;
    protected _reference: number;
    protected _glBuffer: Nullable<GLDataBuffer>;

    /**
     * 
     * @param array
     * @param itemSize
     * @param normalized
     */
    constructor(array: BufferArray, itemSize: number, normalized: boolean = false) {

        this.name = '';
        this.type = 0;

        this.array = array;
        this.itemSize = itemSize;
        this.count = array != undefined ? array.length / itemSize : 0;
        this.normalized = normalized;
        this.type = DataTypeFormat.dataTypeFromTypedArray(array);
        this.target = Constants.ArrayBufferTarget;
        this.usage = Constants.StaticDrawUsage;
        this.updateRange = {offset: 0, count: -1};

        this._version = 0;
        this._reference = 0;
        this._glBuffer = null;
    }

    public get glBuffer() {
        return this._glBuffer;
    }
    public set glBuffer(value) {
        if (this._glBuffer != value) {
            if (this._glBuffer) {
                this._glBuffer.dispose();
            }
            this._glBuffer = value;
            if (this._glBuffer) {
                this._glBuffer.addReference();
            }
        }
    }

    /**
     * 
     */
    public get version() {
        return this._version;
    }

    /**
     * 
     */
    public get reference() {
        return this._reference;
    }

    /**
     * 
     */
    public set needsUpdate(value: boolean) {
        if (value) {
            this._version++;
        }
    }

    /**
     * 
     */
    public dispose(): void {
        this._reference--;
        if (this._reference == 0) {
            if (this._glBuffer) {
                this._glBuffer.dispose();
                this._glBuffer = null;
            }
        }
    }

    /**
     * 
     */
    public addReference(): void {
        this._reference++;
    }

    /**
     * 
     * @param array
     */
    public setArray(array: BufferArray): BufferAttribute {
        this.array = array;
        this.count = this.array.length / this.itemSize;
        return this;
    }

    /**
     * 
     * @param value
     */
    public setUsage(value: number): BufferAttribute {
        this.usage = value;
        return this;
    }

    /**
     * 
     */
    public clone(): BufferAttribute {
        return new BufferAttribute(this.array, this.itemSize, this.normalized).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: BufferAttribute): BufferAttribute {
        const type = DataTypeFormat.typedArrayFromDataType(source.type);

        this.name = source.name;
        this.itemSize = source.itemSize;
        this.count = source.count;
        this.normalized = source.normalized;
        this.usage = source.usage;

        if (type) {
            this.array = new type(source.array);
            this.type = source.type;
        }

        return this;
    }

    /**
     * 
     * @param index1
     * @param attribute
     * @param index2
     */
    public copyAt(index1: int, attribute: BufferAttribute, index2: int): BufferAttribute {
        index1 *= this.itemSize;
        index2 *= attribute.itemSize;
        for (let i = 0, l = this.itemSize; i < l; i++) {
            this.array[index1 + i] = attribute.array[index2 + i];
        }
        return this;
    }

    /**
     * 
     * @param array
     */
    public copyArray(array: BufferArray): BufferAttribute {
        this.array.set(array);
        this.count = this.array.length / this.itemSize;
        return this;
    }

    /**
     * ColorRGB
     * @param colors
     */
    public copyColorRGBsArray(colors: ColorRGB[]): BufferAttribute {
        const array = this.array;

        let offset = 0;
        for (let i = 0, l = colors.length; i < l; i++) {
            const color = colors[i];
            array[offset++] = color.r;
            array[offset++] = color.g;
            array[offset++] = color.b;
        }
        return this;
    }

    /**
     * ColorRGBA
     * @param colors
     */
    public copyColorRGBAsArray(colors: ColorRGBA[]): BufferAttribute {
        const array = this.array;
        let offset = 0;

        for (let i = 0, l = colors.length; i < l; i++) {
            const color = colors[i];
            array[offset++] = color.r;
            array[offset++] = color.g;
            array[offset++] = color.b;
            array[offset++] = color.a;
        }
        return this;
    }

    /**
     * Vector2
     * @param vectors
     */
    public copyVector2sArray(vectors: Vector2[]): BufferAttribute {
        const array = this.array;
        let offset = 0;

        for (let i = 0, l = vectors.length; i < l; i++) {
            const vector = vectors[i];
            array[offset++] = vector.x;
            array[offset++] = vector.y;
        }
        return this;
    }

    /**
     * Vector3
     * @param vectors
     */
    public copyVector3sArray(vectors: Vector3[]): BufferAttribute {
        const array = this.array;
        let offset = 0;

        for (let i = 0, l = vectors.length; i < l; i++) {
            const vector = vectors[i];
            array[offset++] = vector.x;
            array[offset++] = vector.y;
            array[offset++] = vector.z;
        }
        return this;
    }

    /**
     * Vector4
     * @param vectors
     */
    public copyVector4sArray(vectors: Vector4[]): BufferAttribute {
        const array = this.array;
        let offset = 0;

        for (let i = 0, l = vectors.length; i < l; i++) {
            const vector = vectors[i];
            array[offset++] = vector.x;
            array[offset++] = vector.y;
            array[offset++] = vector.z;
            array[offset++] = vector.w;
        }
        return this;
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public applyMatrix3(matrix3: Matrix3): BufferAttribute {

        if (this.itemSize == 2) {

            for (let i = 0, l = this.count; i < l; i++) {
                _vector2.fromBufferAttribute(this, i);
                _vector2.applyMatrix3(matrix3);
                this.setXY(i, _vector2.x, _vector2.y);
            }

        } else if (this.itemSize == 3) {

            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                _vector3.applyMatrix3(matrix3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }

        }
        return this;
    }

    /**
     * Matrix4
     * @param matrix4
     */
    public applyMatrix4(matrix4: Matrix4): BufferAttribute {

       if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                _vector3.applyMatrix4(matrix4);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;
    }

    /**
     * 
     * @param matrix3
     */
    public applyNormalMatrix(matrix3: Matrix3): BufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                _vector3.applyNormalMatrix(matrix3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;

    }

    /**
     * 
     * @param matrix4
     */
    public transformDirection(matrix4: Matrix4): BufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                matrix4.transformVector3(_vector3, _vector3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;
    }

    /**
     * 
     * @param matrix4
     */
    public transformPoint(matrix4: Matrix4): BufferAttribute {

        if (this.itemSize == 3) {
            for (let i = 0, l = this.count; i < l; i++) {
                _vector3.fromBufferAttribute(this, i);
                matrix4.transformPoint(_vector3, _vector3);
                this.setXYZ(i, _vector3.x, _vector3.y, _vector3.z);
            }
        }
        return this;
    }

    /**
     * Value
     * @param offset
     */
    public get(offset: number): number {
        return this.array[offset];
    }

    /**
     * Value
     * @param value
     * @param offset
     */
    public set(value: number[], offset: int = 0): BufferAttribute {
        this.array.set(value, offset);
        return this;
    }

    /**
     * itemSizeX
     * @param index
     */
    public getX(index: int): number {
        return this.array[index * this.itemSize];
    }

    /**
     * itemSizeX
     * @param index
     * @param x
     */
    public setX(index: int, x: number): BufferAttribute {
        this.array[index * this.itemSize] = x;
        return this;
    }

    /**
     * itemSizeY
     * @param index
     */
    public getY(index: int): number {
        return this.array[index * this.itemSize + 1];
    }

    /**
     * itemSizeY
     * @param index
     * @param y
     */
    public setY(index: int, y: number): BufferAttribute {
        this.array[index * this.itemSize + 1] = y;
        return this;
    }

    /**
     * itemSizeZ
     * @param index
     */
    public getZ(index: int): number {
        return this.array[index * this.itemSize + 2];
    }

    /**
     * itemSizeZ
     * @param index
     * @param z
     */
    public setZ(index: int, z: number): BufferAttribute {
        this.array[index * this.itemSize + 2] = z;
        return this;
    }

    /**
     * itemSizeW
     * @param index
     */
    public getW(index: int): number {
        return this.array[index * this.itemSize + 3];
    }

    /**
     * itemSizeW
     * @param index
     * @param W
     */
    public setW(index: int, W: number): BufferAttribute {
        this.array[index * this.itemSize + 3] = W;
        return this;
    }

    /**
     * itemSizeXY
     * @param index
     * @param x
     * @param y
     */
    public setXY(index: int, x: number, y: number): BufferAttribute {
        index *= this.itemSize;
        this.array[index + 0] = x;
        this.array[index + 1] = y;
        return this;
    }

    /**
     * itemSizeXYZ
     * @param index
     * @param x
     * @param y
     * @param z
     */
    public setXYZ(index: int, x: number, y: number, z: number): BufferAttribute {
        index *= this.itemSize;
        this.array[index + 0] = x;
        this.array[index + 1] = y;
        this.array[index + 2] = z;
        return this;
    }

    /**
     * itemSizeXYZW
     * @param index
     * @param x
     * @param y
     * @param z
     * @param w
     */
    public setXYZW(index: int, x: number, y: number, z: number, w: number): BufferAttribute {
        index *= this.itemSize;
        this.array[index + 0] = x;
        this.array[index + 1] = y;
        this.array[index + 2] = z;
        this.array[index + 3] = w;
        return this;
    }

    /**
     * JSON
     */
    public toJSON(): BufferAttrJSON {
        return {
            itemSize: this.itemSize,
            type: this.type,
            array: Array.prototype.slice.call(this.array),
            normalized: this.normalized,
        };
    }

    /**
     * 
     * @param dataBuffer
     */
    public serializeBinary(dataBuffer: BinaryBuffer) {
        dataBuffer.writeUint8(1);
        dataBuffer.writeUint8(this.itemSize);
        dataBuffer.writeBoolean(this.normalized);
        dataBuffer.writeUint32(this.type);
        dataBuffer.writeString(this.name);

        dataBuffer.writeUint32(this.array.length);

        switch (this.type) {
            case Constants.ByteType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeInt8(this.array[i]);
                }
                break;
            case Constants.UnsignedByteType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeUint8(this.array[i]);
                }
                break;
            case Constants.ShortType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeInt16(this.array[i]);
                }
                break;
            case Constants.UnsignedShortType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeUint16(this.array[i]);
                }
                break;
            case Constants.IntType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeInt32(this.array[i]);
                }
                break;
            case Constants.UnsignedIntType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeUint32(this.array[i]);
                }
                break;
            case Constants.FloatType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeFloat32(this.array[i]);
                }
                break;
            case Constants.DoubleType:
                for (let i = 0; i < this.array.length; ++i) {
                    dataBuffer.writeFloat64(this.array[i]);
                }
                break;
        }
    }

    /**
     * 
     * @param dataBuffer
     */
    public deserializeBinary(dataBuffer: BinaryBuffer) {

        // @ts-ignore
        const dataVersion = dataBuffer.readUint8();

        this.itemSize = dataBuffer.readUint8();
        this.normalized = dataBuffer.readBoolean();
        this.type = dataBuffer.readUint32();
        this.name = dataBuffer.readString();

        const length = dataBuffer.readUint32();
        this.count = length / this.itemSize;

        switch (this.type) {
            case Constants.ByteType:
                this.array = new Int8Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readInt8();
                }
                break;
            case Constants.UnsignedByteType:
                this.array = new Uint8Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readUint8();
                }
                break;
            case Constants.ShortType:
                this.array = new Int8Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readInt16();
                }
                break;
            case Constants.UnsignedShortType:
                this.array = new Int16Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readUint16();
                }
                break;
            case Constants.IntType:
                this.array = new Int32Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readInt32();
                }
                break;
            case Constants.UnsignedIntType:
                this.array = new Uint32Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readUint32();
                }
                break;
            case Constants.FloatType:
                this.array = new Float32Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readFloat32();
                }
                break;
            case Constants.DoubleType:
                this.array = new Float64Array(length);
                for (let i = 0; i < length; ++i) {
                    this.array[i] = dataBuffer.readFloat64();
                }
                break;
        }
    }
}
