import { Vector2 } from "./Vector2";
import { Nullable } from "../types";
import { LineSegment2 } from "./LineSegment2";
import { Ray2Ray2Distance, Ray2Segment2Distance } from "./InterfaceType/DistanceResult";
import { Box2 } from "./Box2";
import { Line2 } from "./Line2";
import {
    Ray2Box2Intersect,
    Ray2Ray2Intersect,
    Ray2Segment2Intersect
} from "./InterfaceType/IntersectResult";
import { Intervals } from "./Algorithm/Intervals";

/**
 * @ignore
 */
const _tempVec1 = new Vector2();
/**
 * @ignore
 */
const _tempVec2 = new Vector2();
/**
 * @ignore
 */
const _tempVec3 = new Vector2();
/**
 * @ignore
 */
const _tempVec4 = new Vector2();
/**
 * @ignore
 */
const _tempVec5 = new Vector2();
/**
 * @ignore
 */
const _tempVec6 = new Vector2();
/**
 * @ignore
 */
const _tempVec7 = new Vector2();
/**
 * @ignore
 */
const _tempArray = new Array<number>(2);

/**
 * 2D
 * @category Math
 */
export class Ray2 {

    /**
     * 
     */
    public origin: Vector2;
    /**
     * 
     */
    public direction: Vector2;

    /**
     * 
     * @param origin
     * @param direction
     */
    constructor(origin: Nullable<Vector2> = null, direction: Nullable<Vector2> = null) {
        this.origin = new Vector2();
        this.direction = new Vector2(-1, 0);

        if (origin) {
            this.origin.copy(origin);
        }
        if (direction) {
            this.direction.copy(direction);
        }
    }

    /**
     * 
     * @param origin
     * @param direction
     */
    public set(origin: Vector2, direction: Vector2): Ray2 {
        this.origin.copy(origin);
        this.direction.copy(direction);
        return this;
    }

    /**
     * 
     */
    public clone(): Ray2 {
        return new Ray2().copy(this);
    }

    /**
     * 
     * @param ray
     */
    public copy(ray: Ray2): Ray2 {
        this.origin.copy(ray.origin);
        this.direction.copy(ray.direction);
        return this;
    }

    /**
     * 
     * @param t
     * @param target
     */
    public at(t: number, target = new Vector2()): Vector2 {
        return target.copy(this.direction).multiplyScalar(t).add(this.origin);
    }

    /**
     * 
     * @param vec
     */
    public lookAt(vec: Vector2): Ray2 {
        this.direction.copy(vec).sub(this.origin).normalize();
        return this;
    }

    /**
     * 
     * @param t
     */
    public recast(t: number) {
        this.origin.copy(this.at(t, _tempVec1));
        return this;
    }

    /**
     * 
     * @param point
     * @param target
     */
    public closestPointToPoint(point: Vector2, target = new Vector2()): Vector2 {
        target.subVectors(point, this.origin);

        const directionDistance = target.dot(this.direction);
        if (directionDistance < 0) {
            return target.copy(this.origin);
        }
        return target.copy(this.direction).multiplyScalar(directionDistance).add(this.origin);
    }

    /**
     * SQ
     * @param point
     * @param rayClosest
     */
    public distanceSqToPoint(point: Vector2, rayClosest: Nullable<Vector2> = null): number {

        const diff = _tempVec1.subVectors(point, this.origin);
        const rayParameter = this.direction.dot(diff);

        if (rayParameter > 0) {
            _tempVec2.copy(this.direction).multiplyScalar(rayParameter).add(this.origin);
        }
        else {
            _tempVec2.copy(this.origin);
        }

        if (rayClosest) {
            rayClosest.copy(_tempVec2);
        }

        diff.subVectors(point, _tempVec2);
        return diff.dot(diff);
    }

    /**
     * 
     * @param point
     * @param rayClosest
     */
    public distanceToPoint(point: Vector2, rayClosest: Nullable<Vector2> = null): number {
        return Math.sqrt(this.distanceSqToPoint(point, rayClosest));
    }

    /**
     * SQ
     * @param ray
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToRay(ray: Ray2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Ray2Ray2Distance> = null): number {

        const ray0 = this;
        const ray1 = ray;

        let diff = _tempVec1.subVectors(ray0.origin, ray1.origin);
        let a01 = -ray0.direction.dot(ray1.direction);
        let b0 = diff.dot(ray0.direction), b1;
        let s0, s1;

        if (Math.abs(a01) < 1) {
            // Rays are not parallel.
            b1 = -diff.dot(ray1.direction);
            s0 = a01 * b1 - b0;
            s1 = a01 * b0 - b1;

            if (s0 >= 0) {
                if (s1 >= 0) {
                    const det = 1 - a01 * a01;
                    s0 /= det;
                    s1 /= det;
                }
                else {
                    s1 = 0;
                    if (b0 >= 0) {
                        s0 = 0;
                    }
                    else {
                        s0 = -b0;
                    }
                }
            }
            else {
                if (s1 >= 0) {
                    s0 = 0;
                    if (b1 >= 0) {
                        s1 = 0;
                    }
                    else {
                        s1 = -b1;
                    }
                }
                else {
                    if (b0 < 0) {
                        s0 = -b0;
                        s1 = 0;
                    }
                    else {
                        s0 = 0;
                        if (b1 >= 0) {
                            s1 = 0;
                        }
                        else {
                            s1 = -b1;
                        }
                    }
                }
            }
        }
        else {
            if (a01 > 0) {
                s1 = 0;
                if (b0 >= 0) {
                    s0 = 0;
                }
                else {
                    s0 = -b0;
                }
            }
            else {
                if (b0 >= 0) {
                    b1 = -diff.dot(ray1.direction);
                    s0 = 0;
                    s1 = -b1;
                }
                else {
                    s0 = -b0;
                    s1 = 0;
                }
            }
        }

        _tempVec2.copy(ray0.direction).multiplyScalar(s0).add(ray0.origin);
        _tempVec3.copy(ray1.direction).multiplyScalar(s1).add(ray1.origin);

        if (closestPoint0) {
            closestPoint0.copy(_tempVec2);
        }
        if (closestPoint1) {
            closestPoint1.copy(_tempVec3);
        }
        if (parameter) {
            parameter.parameter = [s0, s1];
        }

        diff.subVectors(_tempVec2, _tempVec3);
        return diff.dot(diff);
    }

    /**
     * 
     * @param ray
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToRay(ray: Ray2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Ray2Ray2Distance> = null): number {
        return this.distanceSqToRay(ray, closestPoint0, closestPoint1, parameter);
    }

    /**
     * SQ
     * @param segment
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToSegment(segment: LineSegment2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Ray2Segment2Distance> = null): number {

        let segCenter = _tempVec1;
        let segDirection = _tempVec2;
        let segExtent = segment.getCenteredForm(segCenter, segDirection);

        let diff = _tempVec3.subVectors(this.origin, segCenter);
        let a01 = -this.direction.dot(segDirection);
        let b0 = diff.dot(this.direction);
        let s0, s1;

        if (Math.abs(a01) < 1) {
            let det = 1 - a01 * a01;
            let extDet = segExtent * det;
            let b1 = -diff.dot(segDirection);
            s0 = a01 * b1 - b0;
            s1 = a01 * b0 - b1;

            if (s0 >= 0) {
                if (s1 >= -extDet) {
                    if (s1 <= extDet) {
                        s0 /= det;
                        s1 /= det;
                    }
                    else {
                        s1 = segExtent;
                        s0 = Math.max(-(a01 * s1 + b0), 0);
                    }
                }
                else {
                    s1 = -segExtent;
                    s0 = Math.max(-(a01 * s1 + b0), 0);
                }
            }
            else {
                if (s1 <= -extDet) {
                    s0 = -(-a01 * segExtent + b0);
                    if (s0 > 0) {
                        s1 = -segExtent;
                    }
                    else {
                        s0 = 0;
                        s1 = -b1;
                        if (s1 < -segExtent) {
                            s1 = -segExtent;
                        }
                        else if (s1 > segExtent) {
                            s1 = segExtent;
                        }
                    }
                }
                else if (s1 <= extDet) {
                    s0 = 0;
                    s1 = -b1;
                    if (s1 < -segExtent) {
                        s1 = -segExtent;
                    }
                    else if (s1 > segExtent) {
                        s1 = segExtent;
                    }
                }
                else {
                    s0 = -(a01 * segExtent + b0);
                    if (s0 > 0) {
                        s1 = segExtent;
                    }
                    else {
                        s0 = 0;
                        s1 = -b1;
                        if (s1 < -segExtent) {
                            s1 = -segExtent;
                        }
                        else if (s1 > segExtent) {
                            s1 = segExtent;
                        }
                    }
                }
            }
        }
        else {
            if (a01 > 0) {
                s1 = -segExtent;
            }
            else {
                s1 = segExtent;
            }

            s0 = Math.max(-(a01 * s1 + b0), 0);
        }

        _tempVec4.copy(this.direction).multiplyScalar(s0).add(this.origin);
        _tempVec5.copy(segDirection).multiplyScalar(s1).add(segCenter);

        if (closestPoint0) {
            closestPoint0.copy(_tempVec4);
        }
        if (closestPoint1) {
            closestPoint1.copy(_tempVec5);
        }

        if (parameter) {
            parameter.parameter = [s0, s1];
        }

        diff.subVectors(_tempVec4, _tempVec5);
        return diff.dot(diff);
    }

    /**
     * 
     * @param segment
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToSegment(segment: LineSegment2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null, parameter: Nullable<Ray2Segment2Distance> = null): number {
        return Math.sqrt(this.distanceSqToSegment(segment, closestPoint0, closestPoint1, parameter));
    }

    /**
     * 
     * @param box
     */
    public intersectsBox(box: Box2): boolean {

        const boxCenter = box.getCenter(_tempVec1);
        const boxExtent = box.getExtent(_tempVec2);

        const rayOrigin = _tempVec3.subVectors(this.origin, boxCenter);
        return Ray2.intersectsBox(rayOrigin, this.direction, boxExtent);
    }

    /**
     * @ignore
     * @param rayOrigin
     * @param rayDirection
     * @param boxExtent
     */
    public static intersectsBox(rayOrigin: Vector2, rayDirection: Vector2, boxExtent: Vector2): boolean {

        for (let i = 0; i < 2; ++i) {
            if (Math.abs(rayOrigin.p[i]) > boxExtent.p[i] && rayOrigin.p[i] * rayDirection.p[i] >= 0) {
                return false;
            }
        }
        return Line2.intersectsBox(rayOrigin, rayDirection, boxExtent);
    }

    /**
     * 
     * @param box
     */
    public intersectBox(box: Box2): Ray2Box2Intersect {

        const boxCenter = box.getCenter(_tempVec1);
        const boxExtent = box.getExtent(_tempVec2);

        const rayOrigin = _tempVec3.subVectors(this.origin, boxCenter);

        const result = {
            intersect: false,
            numIntersections: 0,
            parameter: new Array<number>(2),
            point: new Array<Vector2>(2)
        };

        Ray2.intersectBox(rayOrigin, this.direction, boxExtent, result);

        for (let i = 0; i < result.numIntersections; ++i) {
            result.point[i] = new Vector2().copy(this.direction).multiplyScalar(result.parameter[i]).add(this.origin);
        }

        return result;
    }

    /**
     * @ignore
     * @param rayOrigin
     * @param rayDirection
     * @param boxExtent
     * @param result
     */
    public static intersectBox(rayOrigin: Vector2, rayDirection: Vector2, boxExtent: Vector2, result: Ray2Box2Intersect): void {

        Line2.intersectBox(rayOrigin, rayDirection, boxExtent, result);

        if (result.intersect)
        {
            // The line containing the ray intersects the box; the
            // t-interval is [t0,t1].  The ray intersects the box as long
            // as [t0,t1] overlaps the ray t-interval [0,+infinity).
            const rayInterval = [ 0, Infinity ];
            const iiResult = Intervals.staticQuery(result.parameter, rayInterval);
            result.intersect = iiResult.intersect;
            result.numIntersections = iiResult.numIntersections;
            result.parameter = iiResult.overlap;
        }
    }

    /**
     * 
     * @param segment
     */
    public intersectsSegment(segment: LineSegment2): boolean {

        const segOrigin = _tempVec1;
        const segDirection = _tempVec2;
        const segExtent = segment.getCenteredForm(segOrigin, segDirection);

        const line0 = new Line2(this.origin, this.direction);
        const line1 = new Line2(segOrigin, segDirection);

        const llResult = line0.intersectLine(line1);
        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the ray and
            // segment.
            if (llResult.line0Parameter[0] >= 0 && Math.abs(llResult.line1Parameter[0]) <= segExtent)
            {
                return true;
            }
            else
            {
                return false;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            // Compute the location of the right-most point of the segment
            // relative to the ray direction.
            const  diff = _tempVec3.subVectors(segOrigin, this.origin);
            const t = this.direction.dot(diff) + segExtent;
            if (t > 0)
            {
                return true;
            }
            else if (t < 0)
            {
                return false;
            }
            else
            {
                return true;
            }
        }
        else
        {
            return false;
        }
    }

    /**
     * 
     * @param segment
     */
    public intersectSegment(segment: LineSegment2): Ray2Segment2Intersect {

        const segOrigin = _tempVec1;
        const segDirection = _tempVec2;
        const segExtent = segment.getCenteredForm(segOrigin, segDirection);

        const result = {
            intersect: false,
            numIntersections: 0,
            rayParameter: new Array<number>(2),
            segmentParameter: new Array<number>(2),
            point: new Array<Vector2>(2),
        };

        const line0 = new Line2(this.origin, this.direction);
        const line1 = new Line2(segOrigin, segDirection);

        const llResult = line0.intersectLine(line1);

        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the ray and
            // segment.
            if (llResult.line0Parameter[0] >= 0 && Math.abs(llResult.line1Parameter[0]) <= segExtent)
            {
                result.intersect = true;
                result.numIntersections = 1;
                result.rayParameter[0] = llResult.line0Parameter[0];
                result.segmentParameter[0] = llResult.line1Parameter[0];
                result.point[0] = llResult.point;
            }
            else
            {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            // Compute t for which segment.origin =
            // ray.origin + t*ray.direction.
            const diff = _tempVec3.subVectors(segOrigin, this.origin);
            const t = this.direction.dot(diff);

            // Get the ray interval.
            const interval0 = [0, Infinity];

            // Compute the location of the segment endpoints relative to
            // the ray.
            const interval1 = [ t - segExtent, t + segExtent ];

            // Compute the intersection of [0,+infinity) and [tmin,tmax].
            const iiResult = Intervals.staticQuery(interval0, interval1);
            if (iiResult.intersect) {
                result.intersect = true;
                result.numIntersections = iiResult.numIntersections;
                for (let i = 0; i < iiResult.numIntersections; ++i) {
                    result.rayParameter[i] = iiResult.overlap[i];
                    result.segmentParameter[i] = iiResult.overlap[i] - t;
                    result.point[i] = new Vector2().copy(this.direction).multiplyScalar(result.rayParameter[i]).add(this.origin);
                }
            }
            else {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else {
            result.intersect = false;
            result.numIntersections = 0;
        }
        return result;
    }

    /**
     * 
     * @param ray
     */
    public intersectsRay(ray: Ray2): boolean {

        const line0 = new Line2(this.origin, this.direction);
        const line1 = new Line2(ray.origin, ray.direction);

        const llResult = line0.intersectLine(line1);

        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the rays.
            if (llResult.line0Parameter[0] >= 0 && llResult.line1Parameter[0] >= 0)
            {
                return true;
            }
            else
            {
                return false;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            if (this.direction.dot(ray.direction) > 0)
            {
                return true;
            }
            else
            {
                // The rays are collinear but in opposite directions.
                // Test whether they overlap.  Ray0 has interval
                // [0,+infinity) and ray1 has interval (-infinity,t]
                // relative to ray0.direction.
                const diff = _tempVec1.subVectors(ray.origin, this.origin);
                const t = this.direction.dot(diff);
                if (t > 0) {
                    return true;
                }
                else if (t < 0) {
                    return false;
                }
                else {
                    return true;
                }
            }
        }
        else
        {
            return false;
        }
    }

    /**
     * 
     * @param ray
     */
    public intersectRay(ray: Ray2): Ray2Ray2Intersect {

        const ray0 = this;
        const ray1 = ray;

        const result = {
            intersect: false,
            numIntersections: 0,
            ray0Parameter: new Array<number>(2),
            ray1Parameter: new Array<number>(2),
            point: new Array<Vector2>(2),
        };

        const line0 = new Line2(this.origin, this.direction);
        const line1 = new Line2(ray.origin, ray.direction);

        const llResult = line0.intersectLine(line1);

        if (llResult.numIntersections == 1)
        {
            // Test whether the line-line intersection is on the rays.
            if (llResult.line0Parameter[0] >= 0 && llResult.line1Parameter[0] >= 0)
            {
                result.intersect = true;
                result.numIntersections = 1;
                result.ray0Parameter[0] = llResult.line0Parameter[0];
                result.ray1Parameter[0] = llResult.line1Parameter[0];
                result.point[0] = llResult.point;
            }
            else
            {
                result.intersect = false;
                result.numIntersections = 0;
            }
        }
        else if (llResult.numIntersections == Infinity)
        {
            // Compute t for which ray1.origin =
            // ray0.origin + t*ray0.direction.
            const maxReal = Infinity;
            const diff = _tempVec1.subVectors(ray1.origin, ray0.origin);
            const t = ray0.direction.dot(diff);
            if (ray0.direction.dot(ray1.direction) > 0)
            {
                // The rays are collinear and in the same direction, so
                // they must overlap.
                result.intersect = true;
                result.numIntersections = Infinity;
                if (t >= 0)
                {
                    result.ray0Parameter[0] = t;
                    result.ray0Parameter[1] = maxReal;
                    result.ray1Parameter[0] = 0;
                    result.ray1Parameter[1] = maxReal;
                    result.point[0] = ray1.origin;
                }
                else
                {
                    result.ray0Parameter[0] = 0;
                    result.ray0Parameter[1] = maxReal;
                    result.ray1Parameter[0] = -t;
                    result.ray1Parameter[1] = maxReal;
                    result.point[0] = ray0.origin;
                }
            }
            else
            {
                // The rays are collinear but in opposite directions.
                // Test whether they overlap.  Ray0 has interval
                // [0,+infinity) and ray1 has interval (-infinity,t1]
                // relative to ray0.direction.
                if (t >= 0)
                {
                    result.intersect = true;
                    result.numIntersections = 2;
                    result.ray0Parameter[0] = 0;
                    result.ray0Parameter[1] = t;
                    result.ray1Parameter[0] = 0;
                    result.ray1Parameter[1] = t;
                    result.point[0] = ray0.origin;
                    result.point[1] = ray1.origin;
                }
                else
                {
                    result.intersect = false;
                    result.numIntersections = 0;
                }
            }
        }
        else
        {
            result.intersect = false;
            result.numIntersections = 0;
        }
        return result;
    }

    /**
     * 
     * @param ray
     */
    public equals(ray: Ray2): boolean {
        return ray.origin.equals(this.origin) && ray.direction.equals(this.direction);
    }
}
