import { Vector3 } from "./Vector3";

/**
 * 
 * @category Math
 */
export class Cylindrical {

    /**
     * 
     */
    public radius: number;
    /**
     * zx-z
     */
    public theta: number;
    /**
     * x-z
     */
    public y: number;

    /**
     * 
     * @param radius
     * @param theta
     * @param y
     */
    constructor(radius: number = 1, theta: number = 0, y: number = 0) {
        this.radius = radius;
        this.theta = theta;
        this.y = y;
    }

    /**
     * 
     * @param radius
     * @param theta
     * @param y
     */
    public set(radius: number, theta: number, y: number): Cylindrical {
        this.radius = radius;
        this.theta = theta;
        this.y = y;
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     * @param z
     */
    public setFromCartesianCoords(x: number, y: number, z: number): Cylindrical {
        this.radius = Math.sqrt(x * x + z * z);
        this.theta = Math.atan2(x, z);
        this.y = y;
        return this;
    }

    /**
     * 
     * @param vec
     */
    public setFromVector3(vec: Vector3): Cylindrical {
        return this.setFromCartesianCoords(vec.x, vec.y, vec.z);
    }

    /**
     * 
     */
    public clone(): Cylindrical {
        return new Cylindrical().copy(this);
    }

    /**
     * 
     * @param other
     */
    public copy(other: Cylindrical): Cylindrical {
        this.radius = other.radius;
        this.theta = other.theta;
        this.y = other.y;
        return this;
    }
}
