import { WebGLEngine } from "../WebGLEngine";
import { Vector4 } from "../../Math/Vector4";
import { ColorRGB, ColorRGBA } from "../../Math";
import { Constants } from "../Constants";
import { MathTmp } from "../../Math/MathTmp";
import { Nullable } from "../../types";

/**
 * @ignore
 */
declare module "../WebGLEngine" {

    export interface WebGLEngine {

        currentBlending: number;
        currentBlendEquation: number;
        currentBlendSrc: number;
        currentBlendDst: number;
        currentBlendEquationAlpha: number;
        currentBlendSrcAlpha: number;
        currentBlendDstAlpha: number;
        currentBlendColor: Vector4;
        equationToGL: any;
        factorToGL: any;

        _initBlendingState(): WebGLEngine;
        setBlending(blending: number): WebGLEngine;
        setBlendingColor(blendColor: ColorRGB | ColorRGBA): WebGLEngine;
        setBlendingEquation(blendEquation: number, blendEquationAlpha: number): WebGLEngine;
        setBlendingFunc(blendSrc: number, blendDst: number, blendSrcAlpha: number, blendDstAlpha: number): WebGLEngine;
    }
}

WebGLEngine.prototype._initBlendingState = function(): WebGLEngine {

    this.currentBlending = Constants.NoBlending;
    this.currentBlendEquation = -1;
    this.currentBlendSrc = -1;
    this.currentBlendDst = -1;
    this.currentBlendEquationAlpha = -1;
    this.currentBlendSrcAlpha = -1;
    this.currentBlendDstAlpha = -1;
    this.currentBlendColor = new Vector4();

    const gl = this.gl;

    gl.disable(gl.BLEND);

    this.equationToGL = {
        [ Constants.AddEquation ]: gl.FUNC_ADD,
        [ Constants.SubtractEquation ]: gl.FUNC_SUBTRACT,
        [ Constants.ReverseSubtractEquation ]: gl.FUNC_REVERSE_SUBTRACT,
        [ Constants.MinEquation ]: gl.MIN,
        [ Constants.MaxEquation ]: gl.MAX,
    };

    this.factorToGL = {
        [ Constants.ZeroFactor ]: gl.ZERO,
        [ Constants.OneFactor ]: gl.ONE,
        [ Constants.SrcColorFactor ]: gl.SRC_COLOR,
        [ Constants.SrcAlphaFactor ]: gl.SRC_ALPHA,
        [ Constants.SrcAlphaSaturateFactor ]: gl.SRC_ALPHA_SATURATE,
        [ Constants.DstColorFactor ]: gl.DST_COLOR,
        [ Constants.DstAlphaFactor ]: gl.DST_ALPHA,
        [ Constants.OneMinusSrcColorFactor ]: gl.ONE_MINUS_SRC_COLOR,
        [ Constants.OneMinusSrcAlphaFactor ]: gl.ONE_MINUS_SRC_ALPHA,
        [ Constants.OneMinusDstColorFactor ]: gl.ONE_MINUS_DST_COLOR,
        [ Constants.OneMinusDstAlphaFactor ]: gl.ONE_MINUS_DST_ALPHA
    };
    return this;
};

WebGLEngine.prototype.setBlending = function(blending: number): WebGLEngine {

    const gl = this.gl;

    if (this.currentBlending != blending) {

        if (blending == Constants.NoBlending) {
            gl.disable(gl.BLEND);
        }
        else {
            gl.enable(gl.BLEND);
        }

        this.currentBlending = blending;

        switch (blending) {
            case Constants.PremultipliedBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE_MINUS_SRC_ALPHA, this.gl.ONE, this.gl.ONE);
                break;
            case Constants.PremultipliedPorterDuffBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE_MINUS_SRC_ALPHA, this.gl.ONE, this.gl.ONE_MINUS_SRC_ALPHA);
                break;
            case Constants.CombineBlending:
                this.setBlendingFunc(this.gl.SRC_ALPHA, this.gl.ONE_MINUS_SRC_ALPHA, this.gl.ONE, this.gl.ONE);
                break;
            case Constants.OneOneBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE, this.gl.ZERO, this.gl.ONE);
                break;
            case Constants.AddBlending:
                this.setBlendingFunc(this.gl.SRC_ALPHA, this.gl.ONE, this.gl.ZERO, this.gl.ONE);
                break;
            case Constants.SubtractBlending:
                this.setBlendingFunc(this.gl.ZERO, this.gl.ONE_MINUS_SRC_COLOR, this.gl.ONE, this.gl.ONE);
                break;
            case Constants.MultiplyBlending:
                this.setBlendingFunc(this.gl.DST_COLOR, this.gl.ZERO, this.gl.ONE, this.gl.ONE);
                break;
            case Constants.MaximizedBlending:
                this.setBlendingFunc(this.gl.SRC_ALPHA, this.gl.ONE_MINUS_SRC_COLOR, this.gl.ONE, this.gl.ONE);
                break;
            case Constants.InterpolateBlending:
                this.setBlendingFunc(this.gl.CONSTANT_COLOR, this.gl.ONE_MINUS_CONSTANT_COLOR, this.gl.CONSTANT_ALPHA, this.gl.ONE_MINUS_CONSTANT_ALPHA);
                break;
            case Constants.ScreenModeBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE_MINUS_SRC_COLOR, this.gl.ONE, this.gl.ONE_MINUS_SRC_ALPHA);
                break;
            case Constants.OneOneOneOneBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE, this.gl.ONE, this.gl.ONE);
                break;
            case Constants.AlphaToColorBlending:
                this.setBlendingFunc(this.gl.DST_ALPHA, this.gl.ONE, this.gl.ZERO, this.gl.ZERO);
                break;
            case Constants.ReverseOneMinusBlending:
                this.setBlendingFunc(this.gl.ONE_MINUS_DST_COLOR, this.gl.ONE_MINUS_SRC_COLOR, this.gl.ONE_MINUS_DST_ALPHA, this.gl.ONE_MINUS_SRC_ALPHA);
                break;
            case Constants.SrcDstOneMinusSrcAlphaBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE_MINUS_SRC_ALPHA, this.gl.ONE, this.gl.ONE_MINUS_SRC_ALPHA);
                break;
            case Constants.OneOneOneZeroBlending:
                this.setBlendingFunc(this.gl.ONE, this.gl.ONE, this.gl.ONE, this.gl.ZERO);
                break;
            case Constants.ExclusionBlending:
                this.setBlendingFunc(this.gl.ONE_MINUS_DST_COLOR, this.gl.ONE_MINUS_SRC_COLOR, this.gl.ZERO, this.gl.ONE);
                break;
        }
    }
    return this;
};

WebGLEngine.prototype.setBlendingColor = function(blendColor: ColorRGB | ColorRGBA): WebGLEngine {

    if (blendColor instanceof ColorRGBA) {
        MathTmp.Vector4[0].set(blendColor.r, blendColor.g, blendColor.b, blendColor.a);
    }
    else {
        MathTmp.Vector4[0].set(blendColor.r, blendColor.g, blendColor.b, 1.0);
    }

    const gl = this.gl;

    if (!MathTmp.Vector4[0].equals(this.currentBlendColor)) {
        this.currentBlendColor.copy(MathTmp.Vector4[0]);
        gl.blendColor(this.currentBlendColor.x, this.currentBlendColor.y, this.currentBlendColor.z, this.currentBlendColor.w);
    }
    return this;
};

WebGLEngine.prototype.setBlendingEquation = function(blendEquation: number, blendEquationAlpha: Nullable<number> = null): WebGLEngine {

    const gl = this.gl;

    if (this.currentBlendEquation != blendEquation || this.currentBlendEquationAlpha != blendEquationAlpha) {

        if (blendEquationAlpha != null) {
            gl.blendEquationSeparate(this.equationToGL[blendEquation], this.equationToGL[blendEquationAlpha]);
        } else {
            gl.blendEquation(this.equationToGL[blendEquation]);
        }

        this.currentBlendEquation = blendEquation;
        this.currentBlendEquationAlpha = blendEquationAlpha ?? -1;
    }

    return this;
};

WebGLEngine.prototype.setBlendingFunc = function(blendSrc: number, blendDst: number, blendSrcAlpha: Nullable<number> = null, blendDstAlpha: Nullable<number> = null): WebGLEngine {

    const gl = this.gl;

    if (this.currentBlendSrc != blendSrc || this.currentBlendDst != blendDst || this.currentBlendSrcAlpha != blendSrcAlpha || this.currentBlendDstAlpha != blendDstAlpha) {

        if (blendSrcAlpha != null && blendDstAlpha != null) {
            gl.blendFuncSeparate(this.factorToGL[blendSrc], this.factorToGL[blendDst], this.factorToGL[blendSrcAlpha], this.factorToGL[blendDstAlpha]);
        } else {
            gl.blendFunc(this.factorToGL[blendSrc], this.factorToGL[blendDst]);
        }

        this.currentBlendSrc = blendSrc;
        this.currentBlendDst = blendDst;
        this.currentBlendSrcAlpha = blendSrcAlpha ?? -1;
        this.currentBlendDstAlpha = blendDstAlpha ?? -1;
    }

    return this;
};
