import { Nullable } from "../types";

/**
 * GL
 * @ignore
 */
let queryPoolId = 0;

/**
 * @ignore
 */
export class GLQuery {

    public id: number;
    public handler: any;
    public target: number;
    public pending: boolean;
    public result: Nullable<boolean>;

    constructor(handler: any, target: number) {
        this.id = queryPoolId++;
        this.handler = handler;
        this.target = target;
        this.pending = false;
        this.result = null;
    }
}
