import { Vector2 } from "./Vector2";
import { FloatArray, Nullable } from "../types";
import { BufferAttribute } from "../Misc";
import { Intervals } from "./Algorithm/Intervals";
import { Box2Box2Intersect } from "./InterfaceType/IntersectResult";
import { MathTmp } from "./MathTmp";

/**
 * 2D
 * @category Math
 */
export class Box2 {

    /**
     * 
     */
    public min: Vector2;

    /**
     * 
     */
    public max: Vector2;

    /**
     * 
     * @param min
     * @param max
     */
    constructor(min: Nullable<Vector2> = null, max: Nullable<Vector2> = null) {
        this.min = new Vector2(+Infinity, +Infinity);
        this.max = new Vector2(-Infinity, -Infinity);

        if (min) {
            this.min.copy(min);
        }
        if (max) {
            this.min.copy(max);
        }
    }

    /**
     * 
     * @constructor
     */
    public static Build() {
        return new Box2();
    }

    /**
     * X
     */
    public get x() {
        return this.min.x;
    }
    public set x(value) {
        const width = this.width;
        this.min.x = value;
        this.max.x = this.min.x + width;
    }

    /**
     * Y
     */
    public get y() {
        return this.min.y;
    }
    public set y(value) {
        const height = this.height;
        this.min.y = value;
        this.max.y = this.min.y + height;
    }

    /**
     * 
     */
    public get width() {
        return this.max.x - this.min.x;
    }
    public set width(value) {
        this.max.x = this.min.x + value;
    }

    /**
     * 
     */
    public get height() {
        return this.max.y - this.min.y;
    }
    public set height(value) {
        this.max.y = this.min.y + value;
    }

    /**
     * 
     */
    public get center() {
        return this.getCenter();
    }
    public set center(value) {
        const extent = this.getExtent();
        this.min.x = value.x - extent.x;
        this.min.y = value.y - extent.y;
        this.max.x = value.x + extent.x;
        this.max.y = value.y + extent.y;
    }

    /**
     * 
     */
    public get position() {
        return this.min;
    }
    public set position(value) {
        this.x = value.x;
        this.y = value.y;
    }

    /**
     * 
     */
    public get size() {
        return this.getSize();
    }
    public set size(value) {
        this.width = value.x;
        this.height = value.y;
    }

    /**
     * 
     */
    public clone(): Box2 {
        return new Box2(this.min, this.max);
    }

    /**
     * 
     * @param box
     */
    public copy(box: Box2): Box2 {
        this.min.copy(box.min);
        this.max.copy(box.max);
        return this;
    }

    /**
     * Min,Max
     * @param min
     * @param max
     */
    public set(min: Vector2, max: Vector2): Box2 {
        this.min.copy(min);
        this.max.copy(max);
        return this;
    }

    /**
     * Number
     * @param array
     */
    public setFromArray(array: number[]): Box2 {
        let minX = +Infinity;
        let minY = +Infinity;

        let maxX = -Infinity;
        let maxY = -Infinity;

        for (let i = 0, l = array.length; i < l; i += 3) {
            const x = array[i];
            const y = array[i + 1];

            if (x < minX) { minX = x; }
            if (y < minY) { minY = y; }

            if (x > maxX) { maxX = x; }
            if (y > maxY) { maxY = y; }
        }

        this.min.set(minX, minY);
        this.max.set(maxX, maxY);

        return this;
    }

    /**
     * Buffer
     * @param attribute
     */
    public setFromBufferAttribute(attribute: BufferAttribute): Box2 {
        let minX = +Infinity;
        let minY = +Infinity;

        let maxX = -Infinity;
        let maxY = -Infinity;

        for (let i = 0, l = attribute.count; i < l; i++) {
            const x = attribute.getX(i);
            const y = attribute.getY(i);

            if (x < minX) { minX = x; }
            if (y < minY) { minY = y; }

            if (x > maxX) { maxX = x; }
            if (y > maxY) { maxY = y; }
        }

        this.min.set(minX, minY);
        this.max.set(maxX, maxY);

        return this;
    }

    /**
     * Vector2
     * @param points
     */
    public setFromPoints(points: Vector2[]): Box2 {

        this.makeEmpty();

        for (let i = 0, il = points.length; i < il; i++) {
            this.expandByPoint(points[i]);
        }
        return this;
    }

    /**
     * 
     * @param center
     * @param size
     */
    public setFromCenterAndSize(center: Vector2, size: Vector2): Box2 {

        const halfSize = MathTmp.Vector2[0].copy(size).multiplyScalar(0.5);
        this.min.copy(center).sub(halfSize);
        this.max.copy(center).add(halfSize);
        return this;
    }

    /**
     * 
     */
    public makeEmpty(): Box2 {
        this.min.x = this.min.y = +Infinity;
        this.max.x = this.max.y = -Infinity;
        return this;
    }

    /**
     * 
     */
    public isEmpty(): boolean {
        return (this.max.x < this.min.x) || (this.max.y < this.min.y);
    }

    /**
     * 
     * @param target
     * @returns {Vector2}
     */
    public getCenter(target = new Vector2()): Vector2 {
        return this.isEmpty() ? target.set(0, 0) : target.addVectors(this.min, this.max).multiplyScalar(0.5);
    }

    /**
     * 
     * @param target
     */
    public getSize(target = new Vector2()): Vector2 {
        return this.isEmpty() ? target.set(0, 0) : target.subVectors(this.max, this.min);
    }

    /**
     * 
     * @param target
     */
    public getExtent(target = new Vector2()): Vector2 {
        return this.isEmpty() ? target.set(0, 0) : target.subVectors(this.max, this.min).multiplyScalar(0.5);
    }

    /**
     * 
     * @param point
     */
    public expandByPoint(point: Vector2): Box2 {
        this.min.min(point);
        this.max.max(point);
        return this;
    }

    /**
     * 
     * @param vector
     */
    public expandByVector(vector: Vector2): Box2 {
        this.min.sub(vector);
        this.max.add(vector);
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public expandByScalar(scalar: number): Box2 {
        this.min.addScalar(-scalar);
        this.max.addScalar(scalar);
        return this;
    }

    /**
     * 
     * @param point
     */
    public containsPoint(point: Vector2): boolean {
        return !(point.x < this.min.x || point.x > this.max.x ||
            point.y < this.min.y || point.y > this.max.y);
    }

    /**
     * 
     * @param box
     */
    public containsBox(box: Box2): boolean {
        return this.min.x <= box.min.x && box.max.x <= this.max.x &&
            this.min.y <= box.min.y && box.max.y <= this.max.y;
    }

    /**
     * Box
     * @param point
     * @param target
     */
    public getParameter(point: Vector2, target = new Vector2()): Vector2 {

        return target.set(
            (point.x - this.min.x) / (this.max.x - this.min.x),
            (point.y - this.min.y) / (this.max.y - this.min.y)
        );
    }

    /**
     * 
     * @param point
     * @param target
     */
    public clampPoint(point: Vector2, target = new Vector2()): Vector2 {

        return target.copy(point).clamp(this.min, this.max);
    }

    /**
     * 
     * @param box
     */
    public intersect(box: Box2): Box2 {
        this.min.max(box.min);
        this.max.min(box.max);
        if (this.isEmpty()) { this.makeEmpty(); }
        return this;
    }

    /**
     * 
     * @param box
     */
    public union(box: Box2): Box2 {
        this.min.min(box.min);
        this.max.max(box.max);
        return this;
    }

    /**
     * 
     * @param offset
     */
    public translate(offset: Vector2): Box2 {
        this.min.add(offset);
        this.max.add(offset);
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public scale(scalar: number): Box2 {
        this.min.multiplyScalar(scalar);
        this.max.multiplyScalar(scalar);
        return this;
    }

    /**
     * 
     * @param vec
     */
    public multiply(vec: Vector2): Box2 {
        this.min.multiply(vec);
        this.max.multiply(vec);
        return this;
    }

    /**
     * 
     * @param offset1
     * @param offset2
     */
    public translates(offset1: Vector2, offset2: Vector2): Box2 {
        this.min.add(offset1);
        this.max.add(offset2);
        return this;
    }

    /**
     * 
     * @param scalar1
     * @param scalar2
     */
    public scales(scalar1: number, scalar2: number): Box2 {
        this.min.multiplyScalar(scalar1);
        this.max.multiplyScalar(scalar2);
        return this;
    }

    /**
     * 
     * @param aVec
     * @param bVec
     */
    public multiplies(aVec: Vector2, bVec: Vector2): Box2 {
        this.min.multiply(aVec);
        this.max.multiply(bVec);
        return this;
    }

    /**
     * SQ
     * @param point
     * @param closestPoint
     */
    public distanceSqToPoint(point: Vector2, closestPoint: Nullable<Vector2> = null): number {

        const boxCenter = this.getCenter(MathTmp.Vector2[0]);
        const boxExtent = this.getExtent(MathTmp.Vector2[1]);
        const closest = MathTmp.Vector2[2].subVectors(point, boxCenter);

        let sqDistance = 0;

        for (let i = 0; i < 2; ++i) {
            if (closest.p[i] < -boxExtent.p[i]) {
                const delta = closest.p[i] + boxExtent.p[i];
                sqDistance += delta * delta;
                closest.p[i] =  -boxExtent.p[i];
            }
            else if (closest.p[i] > boxExtent.p[i]) {
                const delta = closest.p[i] - boxExtent.p[i];
                sqDistance += delta * delta;
                closest.p[i] =  boxExtent.p[i];
            }
        }

        if (closestPoint) {
            closestPoint.addVectors(closest, boxCenter);
        }

        return sqDistance;
    }

    /**
     * 
     * @param point
     * @param closestPoint
     */
    public distanceToPoint(point: Vector2, closestPoint: Nullable<Vector2> = null): number {
        return Math.sqrt(this.distanceSqToPoint(point, closestPoint));
    }

    /**
     * SQ
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     */
    public distanceSqToBox(box: Box2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null): number {

        MathTmp.Box2[0].makeEmpty();
        MathTmp.Box2[1].makeEmpty();

        let sqrDistance = 0;

        for (let i = 0; i < 2; ++i) {

            if (this.min.p[i] >= box.max.p[i]) {

                const delta = this.min.p[i] - box.min.p[i];
                sqrDistance += delta * delta;

                if (closestPoint0) {
                    MathTmp.Box2[0].min.p[i] =  this.min.p[i];
                    MathTmp.Box2[0].max.p[i] =  this.min.p[i];
                }
                if (closestPoint1) {
                    MathTmp.Box2[1].min.p[i] =  box.max.p[i];
                    MathTmp.Box2[1].max.p[i] =  box.max.p[i];
                }
            }
            else if (box.min.p[i] >= this.max.p[i]) {

                const delta = box.min.p[i] - this.max.p[i];
                sqrDistance += delta * delta;

                if (closestPoint0) {
                    MathTmp.Box2[0].min.p[i] =  this.max.p[i];
                    MathTmp.Box2[0].max.p[i] =  this.max.p[i];
                }
                if (closestPoint1) {
                    MathTmp.Box2[1].min.p[i] =  box.min.p[i];
                    MathTmp.Box2[1].max.p[i] =  box.min.p[i];
                }
            }
            else if (closestPoint0 || closestPoint1) {

                const intr0 = [ this.min.p[i], this.max.p[i] ];
                const intr1 = [ box.min.p[i], box.max.p[i] ];
                const iiResult = Intervals.staticQuery(intr0, intr1);
                if (closestPoint0) {
                    MathTmp.Box2[0].min.p[i] =  iiResult.overlap[0];
                    MathTmp.Box2[0].max.p[i] =  iiResult.overlap[1];
                }
                if (closestPoint1) {
                    MathTmp.Box2[1].min.p[i] =  iiResult.overlap[0];
                    MathTmp.Box2[1].max.p[i] =  iiResult.overlap[1];
                }
            }
        }

        if (closestPoint0) {
            MathTmp.Box2[0].getCenter(closestPoint0);
        }
        if (closestPoint1) {
            MathTmp.Box2[1].getCenter(closestPoint1);
        }

        return sqrDistance;
    }

    /**
     * 
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     */
    public distanceToBox(box: Box2, closestPoint0: Nullable<Vector2> = null, closestPoint1: Nullable<Vector2> = null): number {
        return Math.sqrt(this.distanceSqToBox(box, closestPoint0, closestPoint1));
    }

    /**
     * 
     * @param box
     */
    public intersectsBox(box: Box2): boolean {
        return !(box.max.x < this.min.x || box.min.x > this.max.x ||
            box.max.y < this.min.y || box.min.y > this.max.y);
    }

    /**
     * 
     * @param box
     */
    public intersectBox(box: Box2): Box2Box2Intersect {

        const box0 = this;
        const box1 = box;

        const result = {
            intersect: false,
            box: new Box2()
        };

        for (let i = 0; i < 2; i++) {
            if (box0.max.p[i] < box1.min.p[i] || box0.min.p[i] > box1.max.p[i]) {
                result.intersect = false;
                return result;
            }
        }

        for (let i = 0; i < 2; i++) {
            if (box0.max.p[i] <= box1.max.p[i]) {
                result.box.max.p[i] =  box0.max.p[i];
            }
            else {
                result.box.max.p[i] =  box1.max.p[i];
            }

            if (box0.min.p[i] <= box1.min.p[i]) {
                result.box.min.p[i] =  box1.min.p[i];
            }
            else {
                result.box.min.p[i] =  box0.min.p[i];
            }
        }

        result.intersect = true;
        return result;
    }

    /**
     * 
     * @param box
     */
    public equals(box: Box2): boolean {
        return box.min.equals(this.min) && box.max.equals(this.max);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset = 0): FloatArray {
        this.min.toArray(array, offset);
        this.max.toArray(array, offset + 3);
        return array;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset = 0): Box2 {
        this.min.fromArray(array, offset);
        this.max.fromArray(array, offset + 3);
        return this;
    }
}
