import { DeepImmutable, FloatArray, int, Nullable } from "../types";
import { MathUtils } from "./MathUtils";
import { Vector2 } from "./Vector2";

type InMatrix2D = Matrix2D | DeepImmutable<Matrix2D>;

/**
 * 2D
 * @category Math
 */
export interface Matrix2DDecompose {
    x: number;
    y: number;
    scaleX: number;
    scaleY: number;
    skewX: number;
    skewY: number;
    rotation: number;
}

/**
 * 2D
 * @category Math
 */
export class Matrix2D {

    /**
     * @ignore
     */
    private static _identityReadOnly = new Matrix2D() as DeepImmutable<Matrix2D>;

    public a: number = 1;
    public b: number = 0;
    public c: number = 0;
    public d: number = 1;
    public tx: number = 0;
    public ty: number = 0;

    /**
     * 
     * @param elements
     */
    constructor(elements: FloatArray = []) {
        if (elements.length > 0) {
            this.fromArray(elements);
        }
    }

    /**
     * 
     */
    public static get identity(): DeepImmutable<Matrix2D> {
        return Matrix2D._identityReadOnly;
    }

    /**
     * 
     */
    public static get IDENTITY(): DeepImmutable<Matrix2D> {
        return Matrix2D._identityReadOnly;
    }

    /**
     * 
     * @param a
     * @param b
     * @param c
     * @param d
     * @param tx
     * @param ty
     */
    public setValues(a: Nullable<number>, b: number, c: number, d: Nullable<number>, tx: number, ty: number): Matrix2D {
        this.a = (a == null) ? 1 : a;
        this.b = b || 0;
        this.c = c || 0;
        this.d = (d == null) ? 1 : d;
        this.tx = tx || 0;
        this.ty = ty || 0;
        return this;
    }

    /**
     * 
     * @param a
     * @param b
     * @param c
     * @param d
     * @param tx
     * @param ty
     */
    public append(a: number, b: number, c: number, d: number, tx: number, ty: number): Matrix2D {
        const a1 = this.a;
        const b1 = this.b;
        const c1 = this.c;
        const d1 = this.d;
        if (a != 1 || b != 0 || c != 0 || d != 1) {
            this.a = a1 * a + c1 * b;
            this.b = b1 * a + d1 * b;
            this.c = a1 * c + c1 * d;
            this.d = b1 * c + d1 * d;
        }
        this.tx = a1 * tx + c1 * ty + this.tx;
        this.ty = b1 * tx + d1 * ty + this.ty;
        return this;
    }

    /**
     * 
     * @param a
     * @param b
     * @param c
     * @param d
     * @param tx
     * @param ty
     */
    public prepend(a: number, b: number, c: number, d: number, tx: number, ty: number): Matrix2D {
        const a1 = this.a;
        const c1 = this.c;
        const tx1 = this.tx;

        this.a = a * a1 + c * this.b;
        this.b = b * a1 + d * this.b;
        this.c = a * c1 + c * this.d;
        this.d = b * c1 + d * this.d;
        this.tx = a * tx1 + c * this.ty + tx;
        this.ty = b * tx1 + d * this.ty + ty;
        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this.a;
            case 1: return this.b;
            case 2: return this.c;
            case 3: return this.d;
            case 4: return this.tx;
            case 5: return this.ty;
            default: return 0;
        }
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): Matrix2D {
        switch (index) {
            case 0: this.a = value; break;
            case 1: this.b = value; break;
            case 2: this.c = value; break;
            case 3: this.d = value; break;
            case 4: this.tx = value; break;
            case 5: this.ty = value; break;
        }
        return this;
    }

    /**
     * 
     * @param matrix
     */
    public appendMatrix(matrix: InMatrix2D): Matrix2D {
        return this.append(matrix.a, matrix.b, matrix.c, matrix.d, matrix.tx, matrix.ty);
    }

    /**
     * 
     * @param matrix
     */
    public prependMatrix(matrix: InMatrix2D): Matrix2D {
        return this.prepend(matrix.a, matrix.b, matrix.c, matrix.d, matrix.tx, matrix.ty);
    }

    /**
     * 
     * @param x
     * @param y
     * @param scaleX
     * @param scaleY
     * @param rotation
     * @param skewX
     * @param skewY
     * @param regX
     * @param regY
     */
    public appendTransform(x: number, y: number, scaleX: number, scaleY: number, rotation: number, skewX: number, skewY: number, regX: number, regY: number): Matrix2D {

        let cos;
        let sin;

        if (rotation % 360) {
            const r = rotation * MathUtils.DEG2RAD;
            cos = Math.cos(r);
            sin = Math.sin(r);
        } else {
            cos = 1;
            sin = 0;
        }

        if (skewX || skewY) {
            skewX *= MathUtils.DEG2RAD;
            skewY *= MathUtils.DEG2RAD;
            this.append(Math.cos(skewY), Math.sin(skewY), -Math.sin(skewX), Math.cos(skewX), x, y);
            this.append(cos * scaleX, sin * scaleX, -sin * scaleY, cos * scaleY, 0, 0);
        } else {
            this.append(cos * scaleX, sin * scaleX, -sin * scaleY, cos * scaleY, x, y);
        }

        if (regX || regY) {
            this.tx -= regX * this.a + regY * this.c;
            this.ty -= regX * this.b + regY * this.d;
        }
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     * @param scaleX
     * @param scaleY
     * @param rotation
     * @param skewX
     * @param skewY
     * @param regX
     * @param regY
     */
    public prependTransform(x: number, y: number, scaleX: number, scaleY: number, rotation: number, skewX: number, skewY: number, regX: number, regY: number): Matrix2D {

        let cos;
        let sin;

        if (rotation % 360) {
            const r = rotation * MathUtils.DEG2RAD;
            cos = Math.cos(r);
            sin = Math.sin(r);
        } else {
            cos = 1;
            sin = 0;
        }

        if (regX || regY) {
            this.tx -= regX; this.ty -= regY;
        }
        if (skewX || skewY) {
            skewX *= MathUtils.DEG2RAD;
            skewY *= MathUtils.DEG2RAD;
            this.prepend(cos * scaleX, sin * scaleX, -sin * scaleY, cos * scaleY, 0, 0);
            this.prepend(Math.cos(skewY), Math.sin(skewY), -Math.sin(skewX), Math.cos(skewX), x, y);
        } else {
            this.prepend(cos * scaleX, sin * scaleX, -sin * scaleY, cos * scaleY, x, y);
        }
        return this;
    }

    /**
     * 
     * @param angle
     */
    public rotate(angle: number): Matrix2D {
        angle = angle * MathUtils.DEG2RAD;
        const cos = Math.cos(angle);
        const sin = Math.sin(angle);

        const a1 = this.a;
        const b1 = this.b;

        this.a = a1 * cos + this.c * sin;
        this.b = b1 * cos + this.d * sin;
        this.c = -a1 * sin + this.c * cos;
        this.d = -b1 * sin + this.d * cos;
        return this;
    }

    /**
     * 
     * @param skewX
     * @param skewY
     */
    public skew(skewX: number, skewY: number): Matrix2D {
        skewX = skewX * MathUtils.DEG2RAD;
        skewY = skewY * MathUtils.DEG2RAD;
        this.append(Math.cos(skewY), Math.sin(skewY), -Math.sin(skewX), Math.cos(skewX), 0, 0);
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public scale(x: number, y: number): Matrix2D {
        this.a *= x;
        this.b *= x;
        this.c *= y;
        this.d *= y;
        return this;
    }

    /**
     * 
     * @param x
     * @param y
     */
    public translate(x: number, y: number): Matrix2D {
        this.tx += this.a * x + this.c * y;
        this.ty += this.b * x + this.d * y;
        return this;
    }

    /**
     * 
     */
    public identity(): Matrix2D {
        this.a = this.d = 1;
        this.b = this.c = this.tx = this.ty = 0;
        return this;
    }

    /**
     * 
     */
    public invert(): Matrix2D {
        const a1 = this.a;
        const b1 = this.b;
        const c1 = this.c;
        const d1 = this.d;
        const tx1 = this.tx;
        const n = a1 * d1 - b1 * c1;

        this.a = d1 / n;
        this.b = -b1 / n;
        this.c = -c1 / n;
        this.d = a1 / n;
        this.tx = (c1 * this.ty - d1 * tx1) / n;
        this.ty = -(a1 * this.ty - b1 * tx1) / n;
        return this;
    }

    /**
     * 
     */
    public isIdentity(): boolean {
        return this.tx == 0 && this.ty == 0 && this.a == 1 && this.b == 0 && this.c == 0 && this.d == 1;
    }

    /**
     * XY
     * @param x
     * @param y
     * @param newPos
     */
    public applyXY(x: number, y: number, newPos = new Vector2()): Vector2 {
        newPos.x = (this.a * x) + (this.c * y) + this.tx;
        newPos.y = (this.b * x) + (this.d * y) + this.ty;
        return newPos;
    }

    /**
     * Vector2
     * @param pos
     * @param newPos
     */
    public apply(pos: Vector2, newPos = new Vector2()): Vector2 {
        const x = pos.x;
        const y = pos.y;
        newPos.x = (this.a * x) + (this.c * y) + this.tx;
        newPos.y = (this.b * x) + (this.d * y) + this.ty;
        return newPos;
    }

    /**
     * XY
     * @param x
     * @param y
     * @param newPos
     */
    public applyInverseXY(x: number, y: number, newPos = new Vector2()): Vector2 {

        const id = 1 / ((this.a * this.d) + (this.c * -this.b));

        newPos.x = (this.d * id * x) + (-this.c * id * y) + (((this.ty * this.c) - (this.tx * this.d)) * id);
        newPos.y = (this.a * id * y) + (-this.b * id * x) + (((-this.ty * this.a) + (this.tx * this.b)) * id);

        return newPos;
    }

    /**
     * Vector2
     * @param pos
     * @param newPos
     */
    public applyInverse(pos: Vector2, newPos = new Vector2()): Vector2 {

        const id = 1 / ((this.a * this.d) + (this.c * -this.b));

        const x = pos.x;
        const y = pos.y;
        newPos.x = (this.d * id * x) + (-this.c * id * y) + (((this.ty * this.c) - (this.tx * this.d)) * id);
        newPos.y = (this.a * id * y) + (-this.b * id * x) + (((-this.ty * this.a) + (this.tx * this.b)) * id);

        return newPos;
    }

    /**
     * 
     * @param matrix
     */
    public equals(matrix: InMatrix2D): boolean {
        return this.tx == matrix.tx && this.ty == matrix.ty && this.a == matrix.a && this.b == matrix.b && this.c == matrix.c && this.d == matrix.d;
    }

    /**
     * 
     * @param target
     */
    public decompose(target: Matrix2DDecompose = {
        x: 0,
        y: 0,
        scaleX: 0,
        scaleY: 0,
        skewX: 0,
        skewY: 0,
        rotation: 0
    }): Matrix2DDecompose {

        target.x = this.tx;
        target.y = this.ty;
        target.scaleX = Math.sqrt(this.a * this.a + this.b * this.b);
        target.scaleY = Math.sqrt(this.c * this.c + this.d * this.d);
        target.rotation = 0;

        const skewX = Math.atan2(-this.c, this.d);
        const skewY = Math.atan2(this.b, this.a);

        const delta = Math.abs(1 - skewX / skewY);
        if (delta < 0.00001) { // effectively identical, can use rotation:
            target.rotation = skewY / MathUtils.DEG2RAD;
            if (this.a < 0 && this.d >= 0) {
                target.rotation += (target.rotation <= 0) ? 180 : -180;
            }
            target.skewX = target.skewY = 0;
        } else {
            target.skewX = skewX / MathUtils.DEG2RAD;
            target.skewY = skewY / MathUtils.DEG2RAD;
        }
        return target;
    }

    /**
     * 
     * @param matrix
     */
    public copy(matrix: InMatrix2D): Matrix2D {
        return this.setValues(matrix.a, matrix.b, matrix.c, matrix.d, matrix.tx, matrix.ty);
    }

    /**
     * 
     */
    public clone(): Matrix2D {
        return new Matrix2D([this.a, this.b, this.c, this.d, this.tx, this.ty]);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): Matrix2D {

        this.a = array[offset];
        this.b = array[1 + offset];
        this.c = array[2 + offset];
        this.d = array[3 + offset];

        this.tx = array[4 + offset];
        this.ty = array[5 + offset];

        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {

        array[offset] = this.a;
        array[offset + 1] = this.b;
        array[offset + 2] = this.c;
        array[offset + 3] = this.d;

        array[offset + 4] = this.tx;
        array[offset + 5] = this.ty;

        return array;
    }
}
