import { int, Nullable } from "../types";
import { defaultValue } from "../Misc/DefaultValue";
import { Vector3 } from "./Vector3";

/**
 * @ignore
 */
const _lut: string[] = [];

/**
 * @ignore
 */
for (let i = 0; i < 256; i++) {

    _lut[ i ] = (i < 16 ? '0' : '') + (i).toString(16);

}

/**
 * @ignore
 */
let _seed = 1234567;

/**
 * 
 * @category Math
 */
export abstract class MathUtils {

    /**
     * EPSILON1
     */
    public static readonly EPSILON1 = 0.1;

    /**
     * EPSILON2
     */
    public static readonly EPSILON2 = 0.01;

    /**
     * EPSILON3
     */
    public static readonly EPSILON3 = 0.001;

    /**
     * EPSILON4
     */
    public static readonly EPSILON4 = 0.0001;

    /**
     * EPSILON5
     */
    public static readonly EPSILON5 = 0.00001;

    /**
     * EPSILON6
     */
    public static readonly EPSILON6 = 0.000001;

    /**
     * EPSILON7
     */
    public static readonly EPSILON7 = 0.0000001;

    /**
     * EPSILON8
     */
    public static readonly EPSILON8 = 0.00000001;

    /**
     * EPSILON9
     */
    public static readonly EPSILON9 = 0.000000001;

    /**
     * EPSILON10
     */
    public static readonly EPSILON10 = 0.0000000001;

    /**
     * EPSILON11
     */
    public static readonly EPSILON11 = 0.00000000001;

    /**
     * EPSILON12
     */
    public static readonly EPSILON12 = 0.000000000001;

    /**
     * EPSILON13
     */
    public static readonly EPSILON13 = 0.0000000000001;

    /**
     * EPSILON14
     */
    public static readonly EPSILON14 = 0.00000000000001;

    /**
     * EPSILON15
     */
    public static readonly EPSILON15 = 0.000000000000001;

    /**
     * EPSILON16
     */
    public static readonly EPSILON16 = 0.0000000000000001;

    /**
     * EPSILON17
     */
    public static readonly EPSILON17 = 0.00000000000000001;

    /**
     * EPSILON18
     */
    public static readonly EPSILON18 = 0.000000000000000001;

    /**
     * EPSILON19
     */
    public static readonly EPSILON19 = 0.0000000000000000001;

    /**
     * EPSILON20
     */
    public static readonly EPSILON20 = 0.00000000000000000001;

    /**
     * EPSILON21
     */
    public static readonly EPSILON21 = 0.000000000000000000001;

    /**
     * PI
     */
    public static readonly PI = Math.PI;

    /**
     * 1/PI
     */
    public static readonly ONE_OVER_PI = 1.0 / Math.PI;

    /**
     * PI/2
     */
    public static readonly PI_OVER_TWO = Math.PI / 2.0;

    /**
     * PI/3
     */
    public static readonly PI_OVER_THREE = Math.PI / 3.0;

    /**
     * PI/4
     */
    public static readonly PI_OVER_FOUR = Math.PI / 4.0;

    /**
     * PI/6
     */
    public static readonly PI_OVER_SIX = Math.PI / 6.0;

    /**
     * 3*PI/2
     */
    public static readonly THREE_PI_OVER_TWO = 3.0 * Math.PI / 2.0;

    /**
     * 2*PI
     */
    public static readonly TWO_PI = 2.0 * Math.PI;

    /**
     * 1/2*PI
     */
    public static readonly ONE_OVER_TWO_PI = 1.0 / (2.0 * Math.PI);

    /**
     * 64 * 1024
     */
    public static readonly SIXTY_FOUR_KILOBYTES = 64 * 1024;

    /**
     * degToRad
     */
    public static readonly DEG2RAD = Math.PI / 180;

    /**
     * radToDeg
     */
    public static readonly RAD2DEG = 180 / Math.PI;

    /**
     * radToArcSecond
     */
    public static readonly RAD2ARCSECOND = Math.PI / 180 / 3600.0;

    /**
     * UUID
     */
    public static generateUUID(): string {

        // http://stackoverflow.com/questions/105034/how-to-create-a-guid-uuid-in-javascript/21963136#21963136

        const d0 = Math.random() * 0xffffffff | 0;
        const d1 = Math.random() * 0xffffffff | 0;
        const d2 = Math.random() * 0xffffffff | 0;
        const d3 = Math.random() * 0xffffffff | 0;
        const uuid = `${_lut[ d0 & 0xff ] + _lut[ d0 >> 8 & 0xff ] + _lut[ d0 >> 16 & 0xff ] + _lut[ d0 >> 24 & 0xff ]  }-${
            _lut[ d1 & 0xff ]  }${_lut[ d1 >> 8 & 0xff ]  }-${  _lut[ d1 >> 16 & 0x0f | 0x40 ]  }${_lut[ d1 >> 24 & 0xff ]  }-${
            _lut[ d2 & 0x3f | 0x80 ]  }${_lut[ d2 >> 8 & 0xff ]  }-${  _lut[ d2 >> 16 & 0xff ]  }${_lut[ d2 >> 24 & 0xff ]
        }${_lut[ d3 & 0xff ]  }${_lut[ d3 >> 8 & 0xff ]  }${_lut[ d3 >> 16 & 0xff ]  }${_lut[ d3 >> 24 & 0xff ]}`;

        return uuid.toUpperCase();
    }

    /**
     * acos(-1, 1)
     * @param value
     */
    public static acosClamped(value: number): number {
        return Math.acos(MathUtils.clamp(value, -1.0, 1.0));
    }

    /**
     * asin(-1, 1)
     * @param value
     */
    public static asinClamped(value: number): number {
        return Math.asin(MathUtils.clamp(value, -1.0, 1.0));
    }

    /**
     * 
     * @param angle
     * @param radius
     */
    public static chordLength(angle: number, radius: number): number {
        return 2.0 * radius * Math.sin(angle * 0.5);
    }

    /**
     * 
     * @param value
     * @param min
     * @param max
     */
    public static clamp(value: number, min: number, max: number): number {
        return Math.max(min, Math.min(max, value));
    }

    /**
     * 
     * @param value
     * @param min
     * @param max
     */
    public static wrap(value: number, min: number, max: number): number {
        if (value == max || value == min) {
            return value;
        }
        const d = max - min;
        const w = ((value - min) % d + d) % d + min;
        return w;
    }

    /**
     * 
     * @param x
     * @param a1
     * @param a2
     * @param b1
     * @param b2
     */
    public static mapLinear(x: number, a1: number, a2: number, b1: number, b2: number): number {
        return b1 + (x - a1) * (b2 - b1) / (a2 - a1);
    }

    /**
     * 
     * @param x
     * @param y
     * @param t
     */
    public static lerp(x: number, y: number, t: number): number {
        return (1 - t) * x + t * y;
    }

    /**
     * 
     * @param x
     * @param min
     * @param max
     */
    public static smoothstep(x: number, min: number, max: number): number {
        if (x <= min) { return 0; }
        if (x >= max) { return 1; }
        x = (x - min) / (max - min);
        return x * x * (3 - 2 * x);
    }

    /**
     * 
     * @param x
     * @param min
     * @param max
     */
    public static smootherstep(x: number, min: number, max: number): number {
        if (x <= min) { return 0; }
        if (x >= max) { return 1; }
        x = (x - min) / (max - min);
        return x * x * x * (x * (x * 6 - 15) + 10);
    }

    /**
     * (low,high)
     * @param low
     * @param high
     */
    public static randInt(low: int, high: int): int {
        return low + Math.floor(Math.random() * (high - low + 1));
    }

    /**
     * (low,high)
     * @param low
     * @param high
     */
    public static randFloat(low: number, high: number): number {
        return low + Math.random() * (high - low);
    }

    /**
     * (-rang,rang)
     * @param range
     */
    public static randFloatSpread(range: number): number {
        return range * (0.5 - Math.random());
    }

    /**
     * 
     * @param s
     * @returns {number}
     */
    public static seededRandom(s: Nullable<number> = null): number {
        if (s != null) { _seed = s % 2147483647; }
        _seed = _seed * 16807 % 2147483647;
        return (_seed - 1) / 2147483646;
    }

    /**
     * 
     * @param degrees
     */
    public static degToRad(degrees: number): number {
        return degrees * MathUtils.DEG2RAD;
    }

    /**
     * 
     * @param radians
     */
    public static radToDeg(radians: number): number {
        return radians * MathUtils.RAD2DEG;
    }

    /**
     * 2
     * @param value
     */
    public static isPowerOfTwo(value: number): boolean {
        return (value & (value - 1)) == 0 && value != 0;
    }

    /**
     * 2
     * @param value
     */
    public static ceilPowerOfTwo(value: number): number {
        return Math.pow(2, Math.ceil(Math.log(value) / Math.LN2));
    }

    /**
     * 2
     * @param value
     */
    public static floorPowerOfTwo(value: number): number {
        return Math.pow(2, Math.floor(Math.log(value) / Math.LN2));
    }

    /**
     * 
     * @param value
     */
    public static signNotZero(value: number): number {
        return value < 0.0 ? -1.0 : 1.0;
    }

    /**
     * (-1.0,1.0)(0,255)
     * @param value
     * @param rangeMax
     */
    public static toSNorm(value: number, rangeMax: number = 255): number {
        return Math.round((MathUtils.clamp(value, -1.0, 1.0) * 0.5 + 0.5) * rangeMax);
    }

    /**
     * (0,255)(-1.0,1.0)
     * @param value
     * @param rangeMax
     */
    public static fromSNorm(value: number, rangeMax: number = 255): number {
        return MathUtils.clamp(value, 0.0, rangeMax) / rangeMax * 2.0 - 1.0;
    }

    /**
     * 
     * @param m
     * @param n
     */
    public static mod(m: number, n: number): number {
        return ((m % n) + n) % n;
    }

    /**
     * 
     * @param distance
     * @param density
     */
    public static fog(distance: number, density: number): number {
        const scalar = distance * density;
        return 1.0 - Math.exp(-(scalar * scalar));
    }

    /**
     * (0, 2*PI)
     * @param angle
     */
    public static zeroToTwoPi(angle: number): number {
        const mod = MathUtils.mod(angle, MathUtils.TWO_PI);
        if (Math.abs(mod) < MathUtils.EPSILON14 && Math.abs(angle) > MathUtils.EPSILON14) {
            return MathUtils.TWO_PI;
        }
        return mod;
    }

    /**
     * (-PI, PI)
     * @param angle
     */
    public static negativePiToPi(angle: number): number {
        return MathUtils.zeroToTwoPi(angle + MathUtils.PI) - MathUtils.PI;
    }

    /**
     * 
     * @param fvalue
     */
    public static equalsZero(fvalue: number): boolean {
        return (fvalue >= -1e-4) && (fvalue <= 1e-4);
    }

    /**
     * leftright
     * @param left
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public static equalsEpsilon(left: number, right: number, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        absoluteEpsilon = defaultValue(absoluteEpsilon, relativeEpsilon);
        const absDiff = Math.abs(left - right);
        return absDiff <= absoluteEpsilon || absDiff <= relativeEpsilon * Math.max(Math.abs(left), Math.abs(right));
    }

    /**
     * leftright
     * @param left
     * @param right
     * @param absoluteEpsilon
     */
    public static lessThan(left: number, right: number, absoluteEpsilon: number): boolean {
        return left - right < -absoluteEpsilon;
    }

    /**
     * leftright
     * @param left
     * @param right
     * @param absoluteEpsilon
     */
    public static lessThanOrEquals(left: number, right: number, absoluteEpsilon: number): boolean {
        return left - right < absoluteEpsilon;
    }

    /**
     * leftright
     * @param left
     * @param right
     * @param absoluteEpsilon
     */
    public static greaterThan(left: number, right: number, absoluteEpsilon: number): boolean {
        return left - right > absoluteEpsilon;
    }

    /**
     * leftright
     * @param left
     * @param right
     * @param absoluteEpsilon
     */
    public static greaterThanOrEquals(left: number, right: number, absoluteEpsilon: number): boolean {
        return left - right > -absoluteEpsilon;
    }

    /**
     * 
     * @param cartesian
     * @param oneOverRadii
     * @param oneOverRadiiSquared
     * @param centerToleranceSquared
     * @param result
     */
    public static scaleToGeodeticSurface(cartesian: Vector3, oneOverRadii: Vector3, oneOverRadiiSquared: Vector3, centerToleranceSquared: number, result: Vector3 = new Vector3()): boolean {

        const positionX = cartesian.x;
        const positionY = cartesian.y;
        const positionZ = cartesian.z;

        const oneOverRadiiX = oneOverRadii.x;
        const oneOverRadiiY = oneOverRadii.y;
        const oneOverRadiiZ = oneOverRadii.z;

        const x2 = positionX * positionX * oneOverRadiiX * oneOverRadiiX;
        const y2 = positionY * positionY * oneOverRadiiY * oneOverRadiiY;
        const z2 = positionZ * positionZ * oneOverRadiiZ * oneOverRadiiZ;

        // Compute the squared ellipsoid norm.
        const squaredNorm = x2 + y2 + z2;
        const ratio = Math.sqrt(1.0 / squaredNorm);

        // As an initial approximation, assume that the radial intersection is the projection point.
        const intersection = cartesian.clone().multiplyScalar(ratio);

        // If the position is near the center, the iteration will not converge.
        if (squaredNorm < centerToleranceSquared) {
            if (!isFinite(ratio)) {
                return false;
            }
            else {
                result.copy(intersection);
                return true;
            }
        }

        const oneOverRadiiSquaredX = oneOverRadiiSquared.x;
        const oneOverRadiiSquaredY = oneOverRadiiSquared.y;
        const oneOverRadiiSquaredZ = oneOverRadiiSquared.z;

        // Use the gradient at the intersection point in place of the true unit normal.
        // The difference in magnitude will be absorbed in the multiplier.
        const gradient = new Vector3();
        gradient.x = intersection.x * oneOverRadiiSquaredX * 2.0;
        gradient.y = intersection.y * oneOverRadiiSquaredY * 2.0;
        gradient.z = intersection.z * oneOverRadiiSquaredZ * 2.0;

        // Compute the initial guess at the normal vector multiplier, lambda.
        let lambda = (1.0 - ratio) * cartesian.length() / (0.5 * gradient.length());
        let correction = 0.0;

        let func;
        let denominator;
        let xMultiplier;
        let yMultiplier;
        let zMultiplier;
        let xMultiplier2;
        let yMultiplier2;
        let zMultiplier2;
        let xMultiplier3;
        let yMultiplier3;
        let zMultiplier3;

        do {
            lambda -= correction;

            xMultiplier = 1.0 / (1.0 + lambda * oneOverRadiiSquaredX);
            yMultiplier = 1.0 / (1.0 + lambda * oneOverRadiiSquaredY);
            zMultiplier = 1.0 / (1.0 + lambda * oneOverRadiiSquaredZ);

            xMultiplier2 = xMultiplier * xMultiplier;
            yMultiplier2 = yMultiplier * yMultiplier;
            zMultiplier2 = zMultiplier * zMultiplier;

            xMultiplier3 = xMultiplier2 * xMultiplier;
            yMultiplier3 = yMultiplier2 * yMultiplier;
            zMultiplier3 = zMultiplier2 * zMultiplier;

            func = x2 * xMultiplier2 + y2 * yMultiplier2 + z2 * zMultiplier2 - 1.0;

            // "denominator" here refers to the use of this expression in the velocity and acceleration
            // computations in the sections to follow.
            denominator = x2 * xMultiplier3 * oneOverRadiiSquaredX + y2 * yMultiplier3 * oneOverRadiiSquaredY + z2 * zMultiplier3 * oneOverRadiiSquaredZ;

            const derivative = -2.0 * denominator;

            correction = func / derivative;

        } while (Math.abs(func) > MathUtils.EPSILON12);

        result.x = positionX * xMultiplier;
        result.y = positionY * yMultiplier;
        result.z = positionZ * zMultiplier;
        return true;
    }
}
