import { Curve } from "./Curve";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { MathTmp } from "../MathTmp";
import { BinaryBuffer } from "../../Misc";
import { CatmullRomType } from "../../Enums";

/**
 * @ignore
 */
class CubicPoly {
    private c0 = 0;
    private c1 = 0;
    private c2 = 0;
    private c3 = 0;

    constructor() {
    }

    private init(x0: number, x1: number, t0: number, t1: number) {
        this.c0 = x0;
        this.c1 = t0;
        this.c2 = -3 * x0 + 3 * x1 - 2 * t0 - t1;
        this.c3 = 2 * x0 - 2 * x1 + t0 + t1;
    }

    public initCatmullRom(x0: number, x1: number, x2: number, x3: number, tension: number) {
        this.init(x1, x2, tension * (x2 - x0), tension * (x3 - x1));
    }

    public initNonuniformCatmullRom(x0: number, x1: number, x2: number, x3: number, dt0: number, dt1: number, dt2: number) {
        // compute tangents when parameterized in [t1,t2]
        let t1 = (x1 - x0) / dt0 - (x2 - x0) / (dt0 + dt1) + (x2 - x1) / dt1;
        let t2 = (x2 - x1) / dt1 - (x3 - x1) / (dt1 + dt2) + (x3 - x2) / dt2;

        // rescale tangents for parametrization in [0,1]
        t1 *= dt1;
        t2 *= dt1;

        this.init(x1, x2, t1, t2);
    }

    public calc(t: number) {
        let t2 = t * t;
        let t3 = t2 * t;
        return this.c0 + this.c1 * t + this.c2 * t2 + this.c3 * t3;
    }
}

/**
 * @ignore
 */
const px = new CubicPoly();
/**
 * @ignore
 */
const py = new CubicPoly();

/**
 * CatmullRom2D
 * @category Curve
 */
export class CatmullRomCurve extends Curve<Vector2> {

    public points: Vector2[];
    public closed: boolean;
    public curveType: CatmullRomType;
    public tension: number;

    /**
     * 
     * @param points
     * @param closed
     * @param curveType
     * @param tension
     */
    constructor(points: Vector2[] = [], closed: boolean = false, curveType: CatmullRomType = CatmullRomType.Centripetal, tension: number = 0.5) {
        super();

        this.type = 'CatmullRomCurve';

        this.points = points;
        this.closed = closed;
        this.curveType = curveType;
        this.tension = tension;
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector2> = null): Vector2 {

        let tmp = MathTmp.Vector2[0];

        let point = optionalTarget ?? new Vector2();

        let points = this.points;
        let l = points.length;

        let p = (l - (this.closed ? 0 : 1)) * t;
        let intPoint = Math.floor(p);
        let weight = p - intPoint;

        if (this.closed) {
            intPoint += intPoint > 0 ? 0 : (Math.floor(Math.abs(intPoint) / l) + 1) * l;
        } else if (weight == 0 && intPoint == l - 1) {
            intPoint = l - 2;
            weight = 1;
        }

        let p0; let p1; let p2; let p3; // 4 points

        if (this.closed || intPoint > 0) {
            p0 = points[(intPoint - 1) % l];
        } else {
            // extrapolate first point
            tmp.subVectors(points[0], points[1]).add(points[0]);
            p0 = tmp;
        }

        p1 = points[intPoint % l];
        p2 = points[(intPoint + 1) % l];

        if (this.closed || intPoint + 2 < l) {
            p3 = points[(intPoint + 2) % l];
        } else {
            // extrapolate last point
            tmp.subVectors(points[l - 1], points[l - 2]).add(points[l - 1]);
            p3 = tmp;
        }

        if (this.curveType == CatmullRomType.Centripetal || this.curveType == CatmullRomType.Chordal) {

            // init Centripetal / Chordal Catmull-Rom
            let pow = this.curveType == CatmullRomType.Chordal ? 0.5 : 0.25;
            let dt0 = Math.pow(p0.distanceToSquared(p1), pow);
            let dt1 = Math.pow(p1.distanceToSquared(p2), pow);
            let dt2 = Math.pow(p2.distanceToSquared(p3), pow);

            // safety check for repeated points
            if (dt1 < 1e-4) { dt1 = 1.0; }
            if (dt0 < 1e-4) { dt0 = dt1; }
            if (dt2 < 1e-4) { dt2 = dt1; }

            px.initNonuniformCatmullRom(p0.x, p1.x, p2.x, p3.x, dt0, dt1, dt2);
            py.initNonuniformCatmullRom(p0.y, p1.y, p2.y, p3.y, dt0, dt1, dt2);

        } else if (this.curveType == CatmullRomType.Catmullrom) {

            px.initCatmullRom(p0.x, p1.x, p2.x, p3.x, this.tension);
            py.initCatmullRom(p0.y, p1.y, p2.y, p3.y, this.tension);
        }

        point.set(
            px.calc(weight),
            py.calc(weight)
        );

        return point;
    }

    /**
     * 
     */
    public clone(): CatmullRomCurve {
        return new CatmullRomCurve().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: CatmullRomCurve): CatmullRomCurve {
        super.copy(source);

        this.points = [];

        for (let i = 0, l = source.points.length; i < l; i++) {
            let point = source.points[i];
            this.points.push(point.clone());
        }

        this.closed = source.closed;
        this.curveType = source.curveType;
        this.tension = source.tension;

        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();

        data.points = [];

        for (let i = 0, l = this.points.length; i < l; i++) {
            let point = this.points[i];
            data.points.push(point.toArray());
        }

        data.closed = this.closed;
        data.curveType = this.curveType;
        data.tension = this.tension;

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): CatmullRomCurve {
        super.fromJSON(json);

        this.points = [];

        for (let i = 0, l = json.points.length; i < l; i++) {
            let point = json.points[i];
            this.points.push(new Vector2().fromArray(point));
        }

        this.closed = json.closed;
        this.curveType = json.curveType;
        this.tension = json.tension;

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): CatmullRomCurve {
        super.toDataBuffer(dataBuffer);

        dataBuffer.writeBoolean(this.closed);
        dataBuffer.writeInt8(this.curveType);
        dataBuffer.writeFloat32(this.tension);

        dataBuffer.writeUint16(this.points.length);
        this.points.forEach((point) => point.toDataBuffer(dataBuffer));

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): CatmullRomCurve {
        super.fromDataBuffer(dataBuffer);

        this.closed = dataBuffer.readBoolean();
        this.curveType = dataBuffer.readUint8();
        this.tension = dataBuffer.readFloat32();

        const count = dataBuffer.readUint16();
        for (let i = 0; i < count; ++i) {
            this.points.push(new Vector2().fromDataBuffer(dataBuffer));
        }

        return this;
    }
}
