import { Matrix3 } from "./Matrix3";
import { Vector3 } from "./Vector3";
import { Vector2 } from "./Vector2";
import { Nullable } from "../types";
import { Sphere } from "./Sphere";
import { Box3 } from "./Box3";
import { Quaternion } from "./Quaternion";
import { LCPSolver } from "./Algorithm/LCPSolver";
import { OBBOBBDistance } from "./InterfaceType/DistanceResult";

/**
 * @ignore
 */
const _tempVec1 = new Vector3();
/**
 * @ignore
 */
const _tempVec2 = new Vector3();
/**
 * @ignore
 */
const _tempVec3 = new Vector3();
/**
 * @ignore
 */
const _tempVec4 = new Vector3();
/**
 * @ignore
 */
const _tempVec5 = new Vector3();

/**
 * @ignore
 */
const _tempQuat1 = new Quaternion();
/**
 * @ignore
 */
const _tempQuat2 = new Quaternion();
/**
 * @ignore
 */
const _tempQuat3 = new Quaternion();
/**
 * @ignore
 */
const _tempMat3 = new Matrix3();
/**
 * @ignore
 */
const _tempArray1 = new Array<number>(3);
/**
 * @ignore
 */
const _tempArray2 = new Array<number>(3);

/**
 * OBB
 * @category Math
 */
export class OBB {
    public matrix3: Matrix3;
    public center: Vector3;
    public extent: Vector3;
    public axis: Vector3[];

    /**
     * 
     * @param center
     * @param matrix3
     * @param extent
     */
    constructor(center: Nullable<Vector3> = null, matrix3: Nullable<Matrix3> = null, extent: Nullable<Vector3> = null) {
        this.matrix3 = new Matrix3();
        this.center = new Vector3();
        this.extent = new Vector3();

        if (center) {
            this.center.copy(center);
        }
        if (matrix3) {
            this.matrix3.copy(matrix3);
        }

        const x = this.matrix3.getX();
        const y = this.matrix3.getY();
        const z = this.matrix3.getZ();

        if (extent) {
            this.extent.copy(extent);
        }
        else {
            this.extent.set(x.length(), y.length(), z.length());
        }

        this.axis = [x.normalize(), y.normalize(), z.normalize()];
    }

    /**
     * 
     */
    public clone(): OBB {
        return new OBB().copy(this);
    }

    /**
     * 
     * @param box
     */
    public copy(box: OBB): OBB {
        this.matrix3.copy(box.matrix3);
        this.center.copy(box.center);
        this.extent.copy(box.extent);
        this.axis[0].copy(box.axis[0]);
        this.axis[1].copy(box.axis[1]);
        this.axis[2].copy(box.axis[2]);
        return this;
    }

    /**
     * OBB
     * @param center
     * @param matrix3
     * @param extent
     */
    public set(center: Vector3, matrix3: Matrix3, extent: Nullable<Vector3>): OBB {
        this.matrix3.copy(matrix3);
        this.center.copy(center);

        const x = this.matrix3.getX();
        const y = this.matrix3.getY();
        const z = this.matrix3.getZ();

        if (extent) {
            this.extent.copy(extent);
        }
        else {
            this.extent.set(x.length(), y.length(), z.length());
        }

        this.axis = [x.normalize(), y.normalize(), z.normalize()];
        return this;
    }

    /**
     * 
     */
    public makeEmpty(): OBB {
        this.matrix3.identity();
        this.center.set(0, 0, 0);
        this.extent.set(0, 0, 0);
        this.axis.forEach((vec) => vec.set(0, 0, 0));
        return this;
    }

    /**
     * 
     * @param target
     */
    public getCenter(target = new Vector3()): Vector3 {

        target.copy(this.center);

        return target;
    }

    /**
     * 
     * @param target
     */
    public getExtent(target = new Vector3()): Vector3 {

        target.copy(this.extent);

        return target;
    }

    /**
     * 
     * @param result
     */
    public getBoundingSphere(result = new Sphere()): Sphere {

        const halfAxes = this.matrix3;
        const u = halfAxes.getColumn(0);
        const v = halfAxes.getColumn(1);
        const w = halfAxes.getColumn(2);

        u.add(v);
        u.add(w);

        result.center = this.center.clone();
        result.radius = u.length();

        return result;
    }

    /**
     * Box3
     * @param box
     */
    public getBoundingBox(box = new Box3()): Box3 {
        return box.setFromCenterAndSize(this.center, this.extent);
    }

    /**
     * OBB
     * @param axis
     * @param minmax
     */
    public axisProjectionMinMax(axis: Vector3, minmax: Vector2 = new Vector2()): Vector2 {

        const origin = axis.dot(this.center);
        const maximumExtent =
            Math.abs(this.extent.x * axis.dot(this.axis[0])) +
            Math.abs(this.extent.y * axis.dot(this.axis[1])) +
            Math.abs(this.extent.z * axis.dot(this.axis[2]));

        minmax.x = origin - maximumExtent;
        minmax.y = origin + maximumExtent;
        return minmax;
    }

    /**
     * OBB
     * @param obb
     */
    public union(obb: OBB): OBB {
        let center = _tempVec1.copy(this.center).add(obb.center).multiplyScalar(0.5);
        let axis = [];
        let extent = [];

        let rot0 = new Matrix3();
        let rot1 = new Matrix3();

        rot0.setColumn(0, this.axis[0]);
        rot0.setColumn(1, this.axis[1]);
        rot0.setColumn(2, this.axis[2]);

        rot1.setColumn(0, obb.axis[0]);
        rot1.setColumn(1, obb.axis[1]);
        rot1.setColumn(2, obb.axis[2]);

        let q0 = _tempQuat1.setFromRotationMatrix(rot0);
        let q1 = _tempQuat2.setFromRotationMatrix(rot1);
        if (q0.dot(q1) < 0) {
            q1.inverse();
        }

        let q = _tempQuat3.addQuaternions(q0, q1);
        q.normalize();

        let rot = _tempMat3.makeRotationFromQuaternion(q);
        for (let j = 0; j < 3; ++j) {
            axis[j] = rot.getColumn(j);
        }

        let points: Vector3[] = [];
        let pmin = [0, 0, 0];
        let pmax = [0, 0, 0];

        this._getPoints(points);
        for (let i = 0; i < 8; ++i) {
            let diff = _tempVec2.subVectors(points[i], center);
            for (let j = 0; j < 3; ++j) {
                let dot = diff.dot(axis[j]);
                if (dot > pmax[j]) {
                    pmax[j] = dot;
                }
                else if (dot < pmin[j]) {
                    pmin[j] = dot;
                }
            }
        }

        obb._getPoints(points);
        for (let i = 0; i < 8; ++i) {
            let diff = new Vector3().subVectors(points[i], center);
            for (let j = 0; j < 3; ++j) {
                let dot = diff.dot(axis[j]);
                if (dot > pmax[j]) {
                    pmax[j] = dot;
                }
                else if (dot < pmin[j]) {
                    pmin[j] = dot;
                }
            }
        }

        for (let j = 0; j < 3; ++j) {
            center.add(_tempVec3.copy(axis[j]).multiplyScalar(0.5 * (pmax[j] + pmin[j])));
            extent[j] = 0.5 * (pmax[j] - pmin[j]);
        }

        this.axis = axis;
        this.center.copy(center);
        this.extent.set(extent[0], extent[1], extent[2]);

        return this;
    }

    /**
     * 
     * @param point
     */
    public containsPoint(point: Vector3): boolean {
        const diff = _tempVec1.copy(point).sub(this.center);
        for (let i = 0; i < 3; ++i) {
            let coeff = diff.dot(this.axis[i]);
            if (Math.abs(coeff) > this.extent.p[i]) {
                return false;
            }
        }
        return true;
    }

    /**
     * SQ
     * @param point
     * @param closestPoint
     */
    public distanceSqToPoint(point: Vector3, closestPoint: Nullable<Vector3> = null): number {

        const boxExtent = this.extent;
        const diff = _tempVec1.subVectors(point, this.center);
        const closest = _tempVec2;
        for (let i = 0; i < 3; ++i) {
            closest.p[i] =  diff.dot(this.axis[i]);
        }

        let sqDistance = 0;

        for (let i = 0; i < 3; ++i) {
            if (closest.p[i] < -boxExtent.p[i]) {
                const delta = closest.p[i] + boxExtent.p[i];
                sqDistance += delta * delta;
                closest.p[i] =  -boxExtent.p[i];
            }
            else if (closest.p[i] > boxExtent.p[i]) {
                const delta = closest.p[i] - boxExtent.p[i];
                sqDistance += delta * delta;
                closest.p[i] =  boxExtent.p[i];
            }
        }

        if (closestPoint) {
            closestPoint.copy(this.center);

            for (let i = 0; i < 3; ++i) {
                closestPoint.add(_tempVec3.copy(this.axis[i]).multiplyScalar(closest.p[i]));
            }
        }

        return sqDistance;
    }

    /**
     * 
     * @param point
     * @param closestPoint
     */
    public distanceToPoint(point: Vector3, closestPoint: Nullable<Vector3> = null): number {
        return Math.sqrt(this.distanceSqToPoint(point, closestPoint));
    }

    /**
     * OBBSQ
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceSqToOBB(box: OBB, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null, parameter: Nullable<OBBOBBDistance> = null): number {

        const box0 = this;
        const box1 = box;

        let delta = _tempVec1.subVectors(box1.center, box0.center);
        for (let i = 0; i < 3; ++i) {
            delta.add(_tempVec2.copy(box0.axis[i]).multiplyScalar(box0.extent.p[i]));
            delta.sub(_tempVec3.copy(box1.axis[i]).multiplyScalar(box1.extent.p[i]));
        }

        const R0Delta = _tempVec2;
        const R1Delta = _tempVec3;
        for (let i = 0; i < 3; ++i) {
            R0Delta.p[i] =  box0.axis[i].dot(delta);
            R1Delta.p[i] =  box1.axis[i].dot(delta);
        }

        const R0TR1: number[][] = [[], [], []];
        for (let r = 0; r < 3; ++r) {
            for (let c = 0; c < 3; ++c) {
                R0TR1[r][c] = box0.axis[r].dot(box1.axis[c]);
            }
        }

        const twoExtent0 = _tempVec4.copy(box0.extent).multiplyScalar(2);
        const twoExtent1 = _tempVec5.copy(box1.extent).multiplyScalar(2);

        const q = [
            -R0Delta.x, -R0Delta.y, -R0Delta.z,
            R1Delta.x, R1Delta.y, R1Delta.z,
            twoExtent0.x, twoExtent0.y, twoExtent0.z,
            twoExtent1.x, twoExtent1.y, twoExtent1.z
        ];

        const z = 0;
        const p = 1;
        const m = -1;
        const M = [
            [p, z, z, -R0TR1[0][0], -R0TR1[0][1], -R0TR1[0][2], p, z, z, z, z, z],
            [z, p, z, -R0TR1[1][0], -R0TR1[1][1], -R0TR1[1][2], z, p, z, z, z, z],
            [z, z, p, -R0TR1[2][0], -R0TR1[2][1], -R0TR1[2][2], z, z, p, z, z, z],
            [-R0TR1[0][0], -R0TR1[1][0], -R0TR1[2][0], p, z, z, z, z, z, p, z, z],
            [-R0TR1[0][1], -R0TR1[1][1], -R0TR1[2][1], z, p, z, z, z, z, z, p, z],
            [-R0TR1[0][2], -R0TR1[1][2], -R0TR1[2][2], z, z, p, z, z, z, z, z, p],
            [m, z, z, z, z, z, z, z, z, z, z, z],
            [z, m, z, z, z, z, z, z, z, z, z, z],
            [z, z, m, z, z, z, z, z, z, z, z, z],
            [z, z, z, m, z, z, z, z, z, z, z, z],
            [z, z, z, z, m, z, z, z, z, z, z, z],
            [z, z, z, z, z, m, z, z, z, z, z, z],
        ];

        const w = new Array<number>(12);
        const zz = new Array<number>(12);

        const mLCP = new LCPSolver(12);
        if (mLCP.solve(q, M, w, zz)) {

            if (parameter) {
                parameter.queryIsSuccessful = true;
                parameter.numLCPIterations = mLCP.getNumIterations();
            }

            _tempVec1.copy(box0.center);
            for (let i = 0; i < 3; ++i) {
                const boxParameter = zz[i] - box0.extent.p[i];
                if (parameter) {
                    parameter.box0Parameter[i] = boxParameter;
                }
                _tempVec1.add(_tempVec3.copy(box0.axis[i]).multiplyScalar(boxParameter));
            }

            _tempVec2.copy(box1.center);
            for (let i = 0, j = 3; i < 3; ++i, ++j) {
                const boxParameter = zz[j] - box1.extent.p[i];
                if (parameter) {
                    parameter.box1Parameter[i] = boxParameter;
                }
                _tempVec2.add(_tempVec3.copy(box1.axis[i]).multiplyScalar(boxParameter));
            }

            if (closestPoint0) {
                closestPoint0.copy(_tempVec1);
            }
            if (closestPoint1) {
                closestPoint1.copy(_tempVec2);
            }

            const diff = _tempVec3.subVectors(_tempVec2, _tempVec1);
            return diff.dot(diff);

        }
        else {
            if (parameter) {
                parameter.queryIsSuccessful = false;
                parameter.numLCPIterations = 0;
            }
        }

        return NaN;
    }

    /**
     * OBB
     * @param box
     * @param closestPoint0
     * @param closestPoint1
     * @param parameter
     */
    public distanceToOBB(box: OBB, closestPoint0: Nullable<Vector3> = null, closestPoint1: Nullable<Vector3> = null, parameter: Nullable<OBBOBBDistance> = null): number {
        return Math.sqrt(this.distanceSqToOBB(box, closestPoint0, closestPoint1, parameter));
    }

    /**
     * OBB
     * @param box
     */
    public intersectsOBB(box: OBB): boolean {

        let C0 = this.center;
        let A0 = this.axis;
        let E0 = this.extent.toArray(_tempArray1);

        let C1 = box.center;
        let A1 = box.axis;
        let E1 = box.extent.toArray(_tempArray2);

        let cutoff = 1;
        let existsParallelPair = false;

        // Compute difference of box centers.
        let D = _tempVec1.subVectors(C1, C0);

        let dot01: number[][] = [[], [], []];       // dot01[i][j] = Dot(A0[i],A1[j]) = A1[j][i]
        let absDot01: number[][] = [[], [], []];    // |dot01[i][j]|
        let dotDA0 = [];                            // Dot(D, A0[i])
        let r0; let r1; let r;                      // interval radii and distance between centers
        let r01;                                    // r0 + r1

        // Test for separation on the axis C0 + t*A0[0].
        for (let i = 0; i < 3; ++i) {
            dot01[0][i] = A0[0].dot(A1[i]);
            absDot01[0][i] = Math.abs(dot01[0][i]);
            if (absDot01[0][i] > cutoff) {
                existsParallelPair = true;
            }
        }
        dotDA0[0] = D.dot(A0[0]);
        r = Math.abs(dotDA0[0]);
        r1 = E1[0] * absDot01[0][0] + E1[1] * absDot01[0][1] + E1[2] * absDot01[0][2];
        r01 = E0[0] + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1].
        for (let i = 0; i < 3; ++i) {
            dot01[1][i] = A0[1].dot(A1[i]);
            absDot01[1][i] = Math.abs(dot01[1][i]);
            if (absDot01[1][i] > cutoff) {
                existsParallelPair = true;
            }
        }
        dotDA0[1] = D.dot(A0[1]);
        r = Math.abs(dotDA0[1]);
        r1 = E1[0] * absDot01[1][0] + E1[1] * absDot01[1][1] + E1[2] * absDot01[1][2];
        r01 = E0[1] + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2].
        for (let i = 0; i < 3; ++i) {
            dot01[2][i] = A0[2].dot(A1[i]);
            absDot01[2][i] = Math.abs(dot01[2][i]);
            if (absDot01[2][i] > cutoff) {
                existsParallelPair = true;
            }
        }
        dotDA0[2] = D.dot(A0[2]);
        r = Math.abs(dotDA0[2]);
        r1 = E1[0] * absDot01[2][0] + E1[1] * absDot01[2][1] + E1[2] * absDot01[2][2];
        r01 = E0[2] + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A1[0].
        r = Math.abs(D.dot(A1[0]));
        r0 = E0[0] * absDot01[0][0] + E0[1] * absDot01[1][0] + E0[2] * absDot01[2][0];
        r01 = r0 + E1[0];
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A1[1].
        r = Math.abs(D.dot(A1[1]));
        r0 = E0[0] * absDot01[0][1] + E0[1] * absDot01[1][1] + E0[2] * absDot01[2][1];
        r01 = r0 + E1[1];
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A1[2].
        r = Math.abs(D.dot(A1[2]));
        r0 = E0[0] * absDot01[0][2] + E0[1] * absDot01[1][2] + E0[2] * absDot01[2][2];
        r01 = r0 + E1[2];
        if (r > r01) {
            return false;
        }

        // At least one pair of box axes was parallel, so the separation is
        // effectively in 2D.  The edge-edge axes do not need to be tested.
        if (existsParallelPair) {
            return true;
        }

        // Test for separation on the axis C0 + t*A0[0]xA1[0].
        r = Math.abs(dotDA0[2] * dot01[1][0] - dotDA0[1] * dot01[2][0]);
        r0 = E0[1] * absDot01[2][0] + E0[2] * absDot01[1][0];
        r1 = E1[1] * absDot01[0][2] + E1[2] * absDot01[0][1];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[0]xA1[1].
        r = Math.abs(dotDA0[2] * dot01[1][1] - dotDA0[1] * dot01[2][1]);
        r0 = E0[1] * absDot01[2][1] + E0[2] * absDot01[1][1];
        r1 = E1[0] * absDot01[0][2] + E1[2] * absDot01[0][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[0]xA1[2].
        r = Math.abs(dotDA0[2] * dot01[1][2] - dotDA0[1] * dot01[2][2]);
        r0 = E0[1] * absDot01[2][2] + E0[2] * absDot01[1][2];
        r1 = E1[0] * absDot01[0][1] + E1[1] * absDot01[0][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1]xA1[0].
        r = Math.abs(dotDA0[0] * dot01[2][0] - dotDA0[2] * dot01[0][0]);
        r0 = E0[0] * absDot01[2][0] + E0[2] * absDot01[0][0];
        r1 = E1[1] * absDot01[1][2] + E1[2] * absDot01[1][1];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1]xA1[1].
        r = Math.abs(dotDA0[0] * dot01[2][1] - dotDA0[2] * dot01[0][1]);
        r0 = E0[0] * absDot01[2][1] + E0[2] * absDot01[0][1];
        r1 = E1[0] * absDot01[1][2] + E1[2] * absDot01[1][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[1]xA1[2].
        r = Math.abs(dotDA0[0] * dot01[2][2] - dotDA0[2] * dot01[0][2]);
        r0 = E0[0] * absDot01[2][2] + E0[2] * absDot01[0][2];
        r1 = E1[0] * absDot01[1][1] + E1[1] * absDot01[1][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2]xA1[0].
        r = Math.abs(dotDA0[1] * dot01[0][0] - dotDA0[0] * dot01[1][0]);
        r0 = E0[0] * absDot01[1][0] + E0[1] * absDot01[0][0];
        r1 = E1[1] * absDot01[2][2] + E1[2] * absDot01[2][1];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2]xA1[1].
        r = Math.abs(dotDA0[1] * dot01[0][1] - dotDA0[0] * dot01[1][1]);
        r0 = E0[0] * absDot01[1][1] + E0[1] * absDot01[0][1];
        r1 = E1[0] * absDot01[2][2] + E1[2] * absDot01[2][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        // Test for separation on the axis C0 + t*A0[2]xA1[2].
        r = Math.abs(dotDA0[1] * dot01[0][2] - dotDA0[0] * dot01[1][2]);
        r0 = E0[0] * absDot01[1][2] + E0[1] * absDot01[0][2];
        r1 = E1[0] * absDot01[2][1] + E1[1] * absDot01[2][0];
        r01 = r0 + r1;
        if (r > r01) {
            return false;
        }

        return true;
    }

    /**
     * 
     * @param sphere
     */
    public intersectsSphere(sphere: Sphere): boolean {
        const distance = this.distanceToPoint(sphere.center);
        return distance <= sphere.radius;
    }

    /**
     * @ignore
     * @private
     */
    private _getPoints(points: Vector3[] = []): Vector3[] {
        const product = [
            _tempVec1,
            _tempVec2,
            _tempVec3
        ];

        for (let d = 0; d < 3; ++d) {
            product[d].copy(this.axis[d]).multiplyScalar(this.extent.p[d]);
        }

        const imax = 1 << 3;
        for (let i = 0; i < imax; ++i) {
            points[i] = this.center.clone();
            for (let d = 0, mask = 1; d < 3; ++d, mask <<= 1) {
                const sign = (i & mask ? 1 : -1);
                points[i].add(_tempVec4.copy(product[d]).multiplyScalar(sign));
            }
        }
        return points;
    }

    /**
     * 
     * @param obb
     */
    public equals(obb: OBB): boolean {
        let eq = true;
        for (let i = 0; i < 3; ++i) {
            if (!this.axis[i].equals(obb.axis[i])) {
                eq = false;
            }
        }
        return eq && this.center.equals(obb.center)  && this.extent.equals(obb.extent);
    }
}
