import { IDisposable } from "../../Misc/IDisposable";
import { SmartArray } from "../../Misc/SmartArray";
import { Mesh } from "../Mesh";
import { Nullable } from "../../types";
import { Scene } from "../Scene";
import { Constants } from "../../Engines";
import { Camera } from "../../Cameras";
import { Vector3 } from "../../Math";
import { MathTmp } from "../../Math/MathTmp";

export class RenderingGroup implements IDisposable {

    private _scene: Scene;

    private _opaqueMeshes = new SmartArray<Mesh>(256);
    private _transparentMeshes = new SmartArray<Mesh>(256);
    private _alphaTestMeshes = new SmartArray<Mesh>(256);
    private _depthOnlyMeshes = new SmartArray<Mesh>(256);

    private _opaqueSortCompareFn: Nullable<(a: Mesh, b: Mesh) => number>;
    private _alphaTestSortCompareFn: Nullable<(a: Mesh, b: Mesh) => number>;
    private _transparentSortCompareFn: (a: Mesh, b: Mesh) => number;

    private _renderOpaque: (subMeshes: SmartArray<Mesh>) => void;
    private _renderAlphaTest: (subMeshes: SmartArray<Mesh>) => void;
    private _renderTransparent: (subMeshes: SmartArray<Mesh>) => void;

    constructor(public index: number, scene: Scene,
                opaqueSortCompareFn: Nullable<(a: Mesh, b: Mesh) => number> = null,
                alphaTestSortCompareFn: Nullable<(a: Mesh, b: Mesh) => number> = null,
                transparentSortCompareFn: Nullable<(a: Mesh, b: Mesh) => number> = null) {

        this._scene = scene;

        this.opaqueSortCompareFn = opaqueSortCompareFn;
        this.alphaTestSortCompareFn = alphaTestSortCompareFn;
        this.transparentSortCompareFn = transparentSortCompareFn;
    }

    public set opaqueSortCompareFn(value: Nullable<(a: Mesh, b: Mesh) => number>) {
        this._opaqueSortCompareFn = value;
        if (value) {
            this._renderOpaque = this.renderOpaqueSorted;
        }
        else {
            this._renderOpaque = RenderingGroup.renderUnsorted;
        }
    }

    public set alphaTestSortCompareFn(value: Nullable<(a: Mesh, b: Mesh) => number>) {
        this._alphaTestSortCompareFn = value;
        if (value) {
            this._renderAlphaTest = this.renderAlphaTestSorted;
        }
        else {
            this._renderAlphaTest = RenderingGroup.renderUnsorted;
        }
    }

    public set transparentSortCompareFn(value: Nullable<(a: Mesh, b: Mesh) => number>) {
        if (value) {
            this._transparentSortCompareFn = value;
        }
        else {
            this._transparentSortCompareFn = RenderingGroup.defaultTransparentSortCompare;
        }
        this._renderTransparent = this.renderTransparentSorted;
    }

    public dispose() {
        this._opaqueMeshes.dispose();
        this._transparentMeshes.dispose();
        this._alphaTestMeshes.dispose();
        this._depthOnlyMeshes.dispose();
    }

    public prepare() {
        this._opaqueMeshes.reset();
        this._transparentMeshes.reset();
        this._alphaTestMeshes.reset();
        this._depthOnlyMeshes.reset();
    }

    public dispatch(mesh: Mesh): void {

        const material = mesh.material;

        if (material.needAlphaBlending()) {
            this._transparentMeshes.push(mesh);
        }
        else if (material.needAlphaTesting()) {
            this._alphaTestMeshes.push(mesh);
        }
        else {
            this._opaqueMeshes.push(mesh);
        }

        mesh._renderingGroup = this;
    }

    public render(): void {

        const engine = this._scene.engine;

        if (this._depthOnlyMeshes.length !== 0) {
            engine.setColorStateMask(false);
            this._renderAlphaTest(this._depthOnlyMeshes);
            engine.setColorStateMask(true);
        }

        if (this._opaqueMeshes.length !== 0) {
            this._renderOpaque(this._opaqueMeshes);
        }

        if (this._alphaTestMeshes.length !== 0) {
            this._renderAlphaTest(this._alphaTestMeshes);
        }

        const stencilState = engine.getStencilTest();
        engine.setStencilTest(false);

        if (this._transparentMeshes.length !== 0) {
            engine.setStencilTest(stencilState);
            this._renderTransparent(this._transparentMeshes);
            engine.setBlending(Constants.NoBlending);
        }

        engine.setStencilTest(false);

        engine.setStencilTest(stencilState);
    }

    private renderOpaqueSorted(meshs: SmartArray<Mesh>): void {
        return RenderingGroup.renderSorted(meshs, this._opaqueSortCompareFn, this._scene.activeCamera, false);
    }

    private renderAlphaTestSorted(meshs: SmartArray<Mesh>): void {
        return RenderingGroup.renderSorted(meshs, this._alphaTestSortCompareFn, this._scene.activeCamera, false);
    }

    private renderTransparentSorted(meshs: SmartArray<Mesh>): void {
        return RenderingGroup.renderSorted(meshs, this._transparentSortCompareFn, this._scene.activeCamera, true);
    }

    private static renderSorted(meshs: SmartArray<Mesh>, sortCompareFn: Nullable<(a: Mesh, b: Mesh) => number>, camera: Nullable<Camera>, transparent: boolean): void {
        let subIndex = 0;
        let subMesh: Mesh;
        let cameraPosition = camera ? camera.getWorldPosition(MathTmp.Vector3[0]) : Vector3.ZERO;
        for (; subIndex < meshs.length; subIndex++) {
            subMesh = meshs.data[subIndex];
            subMesh._distanceToCamera = subMesh.boundingSphere.center.distanceTo(cameraPosition);
        }

        let sortedArray = meshs.data.slice(0, meshs.length);
        if (sortCompareFn) {
            sortedArray.sort(sortCompareFn);
        }

        for (subIndex = 0; subIndex < sortedArray.length; subIndex++) {
            subMesh = sortedArray[subIndex];
            subMesh.render(transparent);
        }
    }

    private static renderUnsorted(meshs: SmartArray<Mesh>): void {
        for (let subIndex = 0; subIndex < meshs.length; subIndex++) {
            let mesh = meshs.data[subIndex];
            mesh.render(false);
        }
    }

    public static defaultTransparentSortCompare(a: Mesh, b: Mesh): number {
        // Alpha index first
        if (a._renderOrder > b._renderOrder) {
            return 1;
        }
        if (a._renderOrder < b._renderOrder) {
            return -1;
        }

        // Then distance to camera
        return RenderingGroup.backToFrontSortCompare(a, b);
    }

    public static backToFrontSortCompare(a: Mesh, b: Mesh): number {
        // Then distance to camera
        if (a._distanceToCamera < b._distanceToCamera) {
            return 1;
        }
        if (a._distanceToCamera > b._distanceToCamera) {
            return -1;
        }

        return 0;
    }

    public static frontToBackSortCompare(a: Mesh, b: Mesh): number {
        // Then distance to camera
        if (a._distanceToCamera < b._distanceToCamera) {
            return -1;
        }
        if (a._distanceToCamera > b._distanceToCamera) {
            return 1;
        }

        return 0;
    }
}
