import { DeepImmutable, FloatArray, int, Nullable } from "../types";
import { Euler } from "./Euler";
import { Quaternion } from "./Quaternion";
import { Matrix3 } from "./Matrix3";
import { Matrix4 } from "./Matrix4";
import { MathUtils } from "./MathUtils";
import { Spherical } from "./Spherical";
import { Cylindrical } from "./Cylindrical";
import { BinaryBuffer } from "../Misc/BinaryBuffer";
import { BufferAttribute } from "../Scene/Geometries/BufferAttribute";
import { Camera } from "../Cameras/Camera";
import { MathTmp } from "./MathTmp";
import { InterleavedBufferAttribute } from "../Scene/Geometries/InterleavedBufferAttribute";

export type InVector3 = Vector3 | DeepImmutable<Vector3>;

/**
 * 3D
 * @category Math
 */
export class Vector3 {

    /**
     * @ignore
     */
    private static _zeroReadOnly = new Vector3(0, 0, 0) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _oneReadOnly = new Vector3(1, 1, 1) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _oneInvReadOnly = new Vector3(-1, -1, -1) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _unitXReadOnly = new Vector3(1, 0, 0) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _unitYReadOnly = new Vector3(0, 1, 0) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _unitZReadOnly = new Vector3(0, 0, 1) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _unitXInvReadOnly = new Vector3(-1, 0, 0) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _unitYInvReadOnly = new Vector3(0, -1, 0) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private static _unitZInvReadOnly = new Vector3(0, 0, -1) as DeepImmutable<Vector3>;
    /**
     * @ignore
     */
    private readonly _p: number[];
    /**
     * @ignore
     */
    private get _x() {return this._p[0]; }
    /**
     * @ignore
     */
    private set _x(value) {this._p[0] = value; }
    /**
     * @ignore
     */
    private get _y() {return this._p[1]; }
    /**
     * @ignore
     */
    private set _y(value) {this._p[1] = value; }
    /**
     * @ignore
     */
    private get _z() {return this._p[2]; }
    /**
     * @ignore
     */
    private set _z(value) {this._p[2] = value; }

    /**
     * @ignore
     */
    private _notify: boolean;
    /**
     * @ignore
     */
    private _onNotify: any;

    /**
     * 
     * @param x
     * @param y
     * @param z
     */
    constructor(x: number = 0, y: number = 0, z: number = 0) {
        this._p = [];
        this._p[0] = x;
        this._p[1] = y;
        this._p[2] = z;
        this._notify = true;
        this._onNotify = null;
    }

    /**
     * 
     */
    public static Build() {
        return new Vector3();
    }

    /**
     * ZERO
     */
    public static get ZERO(): DeepImmutable<Vector3> {
        return Vector3._zeroReadOnly;
    }

    /**
     * ONE
     */
    public static get ONE(): DeepImmutable<Vector3> {
        return Vector3._oneReadOnly;
    }

    /**
     * INV_ONE
     */
    public static get INV_ONE(): DeepImmutable<Vector3> {
        return Vector3._oneInvReadOnly;
    }

    /**
     * UNIT_X
     */
    public static get UNIT_X(): DeepImmutable<Vector3> {
        return Vector3._unitXReadOnly;
    }

    /**
     * UNIT_Y
     */
    public static get UNIT_Y(): DeepImmutable<Vector3> {
        return Vector3._unitYReadOnly;
    }

    /**
     * UNIT_Z
     */
    public static get UNIT_Z(): DeepImmutable<Vector3> {
        return Vector3._unitZReadOnly;
    }

    /**
     * UNIT_INV_X
     */
    public static get UNIT_INV_X(): DeepImmutable<Vector3> {
        return Vector3._unitXInvReadOnly;
    }

    /**
     * UNIT_INV_Y
     */
    public static get UNIT_INV_Y(): DeepImmutable<Vector3> {
        return Vector3._unitYInvReadOnly;
    }

    /**
     * UNIT_INV_Z
     */
    public static get UNIT_INV_Z(): DeepImmutable<Vector3> {
        return Vector3._unitZInvReadOnly;
    }

    /**
     * 
     * 
     * ()
     * numinput12v[0]v[numInputs-1]
     * v[0]v[2]
     * @param numInputs
     * @param v
     */
    public static computeOrthogonalComplement(numInputs: number, v: Vector3[]): number {

        if (numInputs == 1) {
            if (Math.abs(v[0].x) > Math.abs(v[0].y)) {
                v[1] = new Vector3(-v[0].z, 0, +v[0].x);
            }
            else {
                v[1] = new Vector3(0, +v[0].z, -v[0].y);
            }
            numInputs = 2;
        }

        if (numInputs == 2) {
            v[2] = new Vector3().crossVectors(v[0], v[1]);
            return Vector3.orthonormalize(3, v);
        }

        return 0;
    }

    /**
     * Gram-Schmidt
     * 
     * ()
     * 1 <= numElements <= Nv[0]v[numElements-1]
     * v[0]v[numElements-1]
     * @param numInputs
     * @param v
     */
    public static orthonormalize(numInputs: number, v: Vector3[]): number {
        if (v && 1 <= numInputs && numInputs <= 3) {
            let minLength = v[0].length();
            v[0].normalize();

            for (let i = 1; i < numInputs; ++i) {
                for (let j = 0; j < i; ++j) {
                    let dot = v[i].dot(v[j]);
                    v[i].sub(v[j].multiplyScalar(dot));
                }

                const length = v[i].length();
                v[i].normalize();

                if (length < minLength) {
                    minLength = length;
                }
            }
            return minLength;
        }
        return 0;
    }

    /**
     * 
     */
    public get p() {
        return this._p;
    }

    /**
     * X
     */
    public get x() {
        return this._x;
    }
    public set x(value: number) {
        if (this._x != value) {
            this._x = value;
            this.triggerNotify();
        }
    }

    /**
     * Y
     */
    public get y() {
        return this._y;
    }
    public set y(value: number) {
        if (this._y != value) {
            this._y = value;
            this.triggerNotify();
        }
    }

    /**
     * Z
     */
    public get z() {
        return this._z;
    }
    public set z(value: number) {
        if (this._z != value) {
            this._z = value;
            this.triggerNotify();
        }
    }

    /**
     * 
     */
    public get notify() {
        return this._notify;
    }
    public set notify(value: boolean) {
        this._notify = value;
    }

    /**
     * 
     */
    public setNotifyFunc(value: any): Vector3 {
        this._onNotify = value;
        return this;
    }

    /**
     * 
     */
    private triggerNotify(): void {
        if (this._notify) {
            if (this._onNotify) {
                this._onNotify();
            }
        }
    }

    /**
     * XYZ
     * @param x
     * @param y
     * @param z
     */
    public set(x: number, y: number, z: number): Vector3 {
        this._x = x;
        this._y = y;
        this._z = z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public setScalar(scalar: number): Vector3 {
        this._x = scalar;
        this._y = scalar;
        this._z = scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * X
     * @param x
     */
    public setX(x: number): Vector3 {
        this._x = x;
        this.triggerNotify();
        return this;
    }

    /**
     * Y
     * @param y
     */
    public setY(y: number): Vector3 {
        this._y = y;
        this.triggerNotify();
        return this;
    }

    /**
     * Z
     * @param z
     */
    public setZ(z: number): Vector3 {
        this._z = z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param index
     * @param value
     */
    public setComponent(index: int, value: number): Vector3 {
        switch (index) {
            case 0: this.x = value; break;
            case 1: this.y = value; break;
            case 2: this.z = value; break;
            default: throw new Error(`index is out of range: ${index}`);
        }
        return this;
    }

    /**
     * 
     * @param index
     */
    public getComponent(index: int): number {
        switch (index) {
            case 0: return this.x;
            case 1: return this.y;
            case 2: return this.z;
            default: throw new Error(`index is out of range: ${index}`);
        }
    }

    /**
     * 
     */
    public maximumComponent(): number {
        return Math.max(this._x, Math.max(this._y, this._z));
    }

    /**
     * 
     */
    public minimumComponent(): number {
        return Math.min(this._x, Math.min(this._y, this._z));
    }

    /**
     * 
     */
    public clone(): Vector3 {
        return new Vector3(this._x, this._y, this._z);
    }

    /**
     * 
     * @param source
     */
    public copy(source: InVector3): Vector3 {
        this._x = source.x;
        this._y = source.y;
        this._z = source.z;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public add(vec: InVector3): Vector3 {
        this._x += vec.x;
        this._y += vec.y;
        this._z += vec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public addScalar(scalar: number) {
        this._x += scalar;
        this._y += scalar;
        this._z += scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param aVec
     * @param bVec
     */
    public addVectors(aVec: InVector3, bVec: InVector3): Vector3 {
        this._x = aVec.x + bVec.x;
        this._y = aVec.y + bVec.y;
        this._z = aVec.z + bVec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     * @param scalar
     */
    public addScaledVector(vec: InVector3, scalar: number): Vector3 {
        this._x += vec.x * scalar;
        this._y += vec.y * scalar;
        this._z += vec.z * scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public sub(vec: InVector3): Vector3 {
        this._x -= vec.x;
        this._y -= vec.y;
        this._z -= vec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public subScalar(scalar: number): Vector3 {
        this._x -= scalar;
        this._y -= scalar;
        this._z -= scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param aVec
     * @param bVec
     */
    public subVectors(aVec: InVector3, bVec: InVector3): Vector3 {
        this._x = aVec.x - bVec.x;
        this._y = aVec.y - bVec.y;
        this._z = aVec.z - bVec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public multiply(vec: InVector3): Vector3 {
        this._x *= vec.x;
        this._y *= vec.y;
        this._z *= vec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public multiplyScalar(scalar: number): Vector3 {
        this._x *= scalar;
        this._y *= scalar;
        this._z *= scalar;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param aVec
     * @param bVec
     */
    public multiplyVectors(aVec: InVector3, bVec: InVector3): Vector3 {
        this._x = aVec.x * bVec.x;
        this._y = aVec.y * bVec.y;
        this._z = aVec.z * bVec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public divide(vec: InVector3): Vector3 {
        this._x /= vec.x;
        this._y /= vec.y;
        this._z /= vec.z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param scalar
     */
    public divideScalar(scalar: number): Vector3 {
        return this.multiplyScalar(1 / scalar);
    }

    /**
     * 
     * @param euler
     */
    public applyEuler(euler: Euler): Vector3 {
        return this.applyQuaternion(MathTmp.Quaternion[0].setFromEuler(euler));
    }

    /**
     * 
     * @param axis
     * @param angle
     */
    public applyAxisAngle(axis: InVector3, angle: number): Vector3 {
        return this.applyQuaternion(MathTmp.Quaternion[0].setFromAxisAngle(axis, angle));
    }

    /**
     * Matrix3
     * @param matrix3
     */
    public applyMatrix3(matrix3: Matrix3): Vector3 {
        const x = this._x;
        const y = this._y;
        const z = this._z;
        const e = matrix3.elements;
        this._x = e[0] * x + e[3] * y + e[6] * z;
        this._y = e[1] * x + e[4] * y + e[7] * z;
        this._z = e[2] * x + e[5] * y + e[8] * z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param matrix3
     */
    public applyNormalMatrix(matrix3: Matrix3): Vector3 {
        return this.applyMatrix3(matrix3).normalize();
    }

    /**
     * Matrix4
     * @param matrix4
     */
    public applyMatrix4(matrix4: Matrix4): Vector3 {
        const x = this._x;
        const y = this._y;
        const z = this._z;
        const e = matrix4.elements;
        const w = 1 / (e[3] * x + e[7] * y + e[11] * z + e[15]);
        this._x = (e[0] * x + e[4] * y + e[8] * z + e[12]) * w;
        this._y = (e[1] * x + e[5] * y + e[9] * z + e[13]) * w;
        this._z = (e[2] * x + e[6] * y + e[10] * z + e[14]) * w;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param quaternion
     * @returns {Vector3}
     * @memberOf Vector3.prototype
     */
    public applyQuaternion(quaternion: Quaternion): Vector3 {
        const x = this._x;
        const y = this._y;
        const z = this._z;
        const qx = quaternion.x;
        const qy = quaternion.y;
        const qz = quaternion.z;
        const qw = quaternion.w;

        // calculate quat * vector
        const ix = qw * x + qy * z - qz * y;
        const iy = qw * y + qz * x - qx * z;
        const iz = qw * z + qx * y - qy * x;
        const iw = -qx * x - qy * y - qz * z;

        // calculate result * inverse quat
        this._x = ix * qw + iw * -qx + iy * -qz - iz * -qy;
        this._y = iy * qw + iw * -qy + iz * -qx - ix * -qz;
        this._z = iz * qw + iw * -qz + ix * -qy - iy * -qx;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param camera
     */
    public project(camera: Camera): Vector3 {
        return this.applyMatrix4(camera.matrixWorldInverse).applyMatrix4(camera.projectionMatrix);
    }

    /**
     * 
     * @param camera
     */
    public unproject(camera: Camera): Vector3 {
        return this.applyMatrix4(camera.projectionMatrixInverse).applyMatrix4(camera.matrixWorld);
    }

    /**
     * Vector3
     * @param vec
     */
    public min(vec: InVector3): Vector3 {
        this._x = Math.min(this._x, vec.x);
        this._y = Math.min(this._y, vec.y);
        this._z = Math.min(this._z, vec.z);
        this.triggerNotify();
        return this;
    }

    /**
     * Vector3
     * @param vec
     */
    public max(vec: InVector3): Vector3 {
        this._x = Math.max(this._x, vec.x);
        this._y = Math.max(this._y, vec.y);
        this._z = Math.max(this._z, vec.z);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param minVec
     * @param maxVec
     */
    public clamp(minVec: InVector3, maxVec: InVector3): Vector3 {
        this._x = Math.max(minVec.x, Math.min(maxVec.x, this._x));
        this._y = Math.max(minVec.y, Math.min(maxVec.y, this._y));
        this._z = Math.max(minVec.z, Math.min(maxVec.z, this._z));
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param minScalar
     * @param maxScalar
     */
    public clampScalar(minScalar: number, maxScalar: number): Vector3 {
        this._x = Math.max(minScalar, Math.min(maxScalar, this._x));
        this._y = Math.max(minScalar, Math.min(maxScalar, this._y));
        this._z = Math.max(minScalar, Math.min(maxScalar, this._z));
        this.triggerNotify();
        return this;
    }

    /**
     * clamp
     * @param minScalar
     * @param maxScalar
     */
    public clampLength(minScalar: number, maxScalar: number): Vector3 {
        const length = this.length();
        return this.divideScalar(length || 1).multiplyScalar(Math.max(minScalar, Math.min(maxScalar, length)));
    }

    /**
     * 
     */
    public floor(): Vector3 {
        this._x = Math.floor(this._x);
        this._y = Math.floor(this._y);
        this._z = Math.floor(this._z);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public ceil(): Vector3 {
        this._x = Math.ceil(this._x);
        this._y = Math.ceil(this._y);
        this._z = Math.ceil(this._z);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public round(): Vector3 {
        this._x = Math.round(this._x);
        this._y = Math.round(this._y);
        this._z = Math.round(this._z);
        this.triggerNotify();
        return this;
    }

    /**
     * 0
     */
    public roundToZero(): Vector3 {
        this._x = (this._x < 0) ? Math.ceil(this._x) : Math.floor(this._x);
        this._y = (this._y < 0) ? Math.ceil(this._y) : Math.floor(this._y);
        this._z = (this._z < 0) ? Math.ceil(this._z) : Math.floor(this._z);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     */
    public negate(): Vector3 {
        this._x = -this._x;
        this._y = -this._y;
        this._z = -this._z;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public dot(vec: InVector3): number {
        return this._x * vec.x + this._y * vec.y + this._z * vec.z;
    }

    /**
     * 
     * @param vec
     */
    public cross(vec: InVector3): Vector3 {
        return this.crossVectors(this, vec);
    }

    /**
     * 
     * @param aVec
     * @param bVec
     */
    public crossVectors(aVec: InVector3, bVec: InVector3): Vector3 {
        const ax = aVec.x;
        const ay = aVec.y;
        const az = aVec.z;
        const bx = bVec.x;
        const by = bVec.y;
        const bz = bVec.z;
        this._x = ay * bz - az * by;
        this._y = az * bx - ax * bz;
        this._z = ax * by - ay * bx;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param aVec
     * @param bVec
     */
    public dotCross(aVec: InVector3, bVec: InVector3): number {
        MathTmp.Vector3[0].crossVectors(aVec, bVec);
        return this.dot(MathTmp.Vector3[0]);
    }

    /**
     * SQ
     */
    public lengthSq(): number {
        return this._x * this._x + this._y * this._y + this._z * this._z;
    }

    /**
     * 
     */
    public length(): number {
        return Math.sqrt(this._x * this._x + this._y * this._y + this._z * this._z);
    }

    /**
     * 
     */
    public manhattanLength(): number {
        return Math.abs(this._x) + Math.abs(this._y) + Math.abs(this._z);
    }

    /**
     * Vector3
     * @param vec
     */
    public manhattanDistanceTo(vec: InVector3): number {
        return Math.abs(this._x - vec.x) + Math.abs(this._y - vec.y) + Math.abs(this._z - vec.z);
    }

    /**
     * 
     */
    public normalize(): Vector3 {
        return this.divideScalar(this.length() || 1);
    }

    /**
     * Vector3
     * @param vec
     */
    public angleTo(vec: InVector3): number {
        const denominator = Math.sqrt(this.lengthSq() * vec.lengthSq());
        if (denominator == 0) { return Math.PI / 2; }
        const theta = this.dot(vec) / denominator;
        return Math.acos(MathUtils.clamp(theta, -1, 1));
    }

    /**
     * Vector3
     * @param vec
     */
    public distanceTo(vec: InVector3): number {
        return Math.sqrt(this.distanceToSquared(vec));
    }

    /**
     * Vector3SQ
     * @param vec
     */
    public distanceToSquared(vec: InVector3): number {
        const dx = this._x - vec.x;
        const dy = this._y - vec.y;
        const dz = this._z - vec.z;
        return dx * dx + dy * dy + dz * dz;
    }

    /**
     * 
     * @param length
     */
    public setLength(length: number): Vector3 {
        return this.normalize().multiplyScalar(length);
    }

    /**
     * V
     * @param vec
     * @param alpha
     */
    public lerp(vec: InVector3, alpha: number): Vector3 {
        this._x += (vec.x - this._x) * alpha;
        this._y += (vec.y - this._y) * alpha;
        this._z += (vec.z - this._z) * alpha;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec1
     * @param vec2
     * @param alpha
     */
    public lerpVectors(vec1: InVector3, vec2: InVector3, alpha: number): Vector3 {
        this._x = vec1.x + (vec2.x - vec1.x) * alpha;
        this._y = vec1.y + (vec2.y - vec1.y) * alpha;
        this._z = vec1.z + (vec2.z - vec1.z) * alpha;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public angleBetween(vec: InVector3): number {
        const n1 = MathTmp.Vector3[0].copy(this).normalize();
        const n2 = MathTmp.Vector3[1].copy(vec).normalize();
        const cosine = n1.dot(n2);
        const sine = MathTmp.Vector3[2].crossVectors(n1, n2).length();
        return Math.atan2(sine, cosine);
    }

    /**
     * Vec
     * @param vec
     */
    public midpoint(vec: InVector3): Vector3 {
        this._x = (this._x + vec.x) * 0.5;
        this._y = (this._y + vec.y) * 0.5;
        this._z = (this._z + vec.z) * 0.5;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param aVec
     * @param bVec
     */
    public midpointVectors(aVec: InVector3, bVec: InVector3) {
        this._x = (aVec.x + bVec.x) * 0.5;
        this._y = (aVec.y + bVec.y) * 0.5;
        this._z = (aVec.z + bVec.z) * 0.5;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     */
    public projectOnVector(vec: InVector3): Vector3 {
        const denominator = vec.lengthSq();
        if (denominator == 0) { return this.set(0, 0, 0); }
        const scalar = vec.dot(this) / denominator;
        return this.copy(vec).multiplyScalar(scalar);
    }

    /**
     * 
     * @param planeNormal
     */
    public projectOnPlane(planeNormal: InVector3): Vector3 {
        MathTmp.Vector3[0].copy(this).projectOnVector(planeNormal);
        return this.sub(MathTmp.Vector3[0]);
    }

    /**
     * 
     * @param normal
     */
    public reflect(normal: InVector3): Vector3 {
        return this.sub(MathTmp.Vector3[0].copy(normal).multiplyScalar(2 * this.dot(normal)));
    }

    /**
     * 
     * @param axis
     */
    public mostOrthogonalAxis(axis: InVector3 = new Vector3()) {
        if (this.x < 0) { this.x = -this.x; }
        if (this.y < 0) { this.y = -this.y; }
        if (this.z < 0) { this.z = -this.z; }

        if (this.x <= this.y) {
            if (this.x <= this.z) {
                axis.copy(Vector3.UNIT_X);
            } else {
                axis.copy(Vector3.UNIT_Z);
            }
        } else if (this.y <= this.z) {
            axis.copy(Vector3.UNIT_Y);
        } else {
            axis.copy(Vector3.UNIT_Z);
        }

        return axis;
    }

    /**
     * 
     * @param spherical
     */
    public setFromSpherical(spherical: Spherical): Vector3 {
        return this.setFromSphericalCoords(spherical.radius, spherical.phi, spherical.theta);
    }

    /**
     * 
     * @param radius
     * @param phi
     * @param theta
     */
    public setFromSphericalCoords(radius: number, phi: number, theta: number): Vector3 {
        const sinPhiRadius = Math.sin(phi) * radius;
        this._x = sinPhiRadius * Math.sin(theta);
        this._y = Math.cos(phi) * radius;
        this._z = sinPhiRadius * Math.cos(theta);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param cylindrical
     */
    public setFromCylindrical(cylindrical: Cylindrical): Vector3 {
        return this.setFromCylindricalCoords(cylindrical.radius, cylindrical.theta, cylindrical.y);
    }

    /**
     * 
     * @param radius
     * @param theta
     * @param y
     */
    public setFromCylindricalCoords(radius: number, theta: number, y: number): Vector3 {
        this._x = radius * Math.sin(theta);
        this._y = y;
        this._z = radius * Math.cos(theta);
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param matrix4
     */
    public setFromMatrixPosition(matrix4: Matrix4): Vector3 {
        const e = matrix4.elements;
        this._x = e[12];
        this._y = e[13];
        this._z = e[14];
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param matrix
     */
    public setFromMatrixScale(matrix: Matrix3 | Matrix4): Vector3 {
        const sx = this.setFromMatrixColumn(matrix, 0).length();
        const sy = this.setFromMatrixColumn(matrix, 1).length();
        const sz = this.setFromMatrixColumn(matrix, 2).length();
        this._x = sx;
        this._y = sy;
        this._z = sz;
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param matrix
     * @param index
     */
    public setFromMatrixColumn(matrix: Matrix3 | Matrix4, index: int): Vector3 {
        if (matrix as Matrix3) {
            return this.fromArray(matrix.elements, index * 3);
        }
        else {
            return this.fromArray(matrix.elements, index * 4);
        }
    }

    /**
     * 
     */
    public random(): Vector3 {
        this._x = Math.random();
        this._y = Math.random();
        this._z = Math.random();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param vec
     * @returns {boolean}
     */
    public equals(vec: InVector3): boolean {
        return ((vec.x == this._x) && (vec.y == this._y) && (vec.z == this._z));
    }

    /**
     * 
     * @param right
     * @param relativeEpsilon
     * @param absoluteEpsilon
     */
    public equalsEpsilon(right: InVector3, relativeEpsilon: number, absoluteEpsilon: Nullable<number> = null): boolean {
        return MathUtils.equalsEpsilon(this._x, right.x, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._y, right.y, relativeEpsilon, absoluteEpsilon) &&
            MathUtils.equalsEpsilon(this._z, right.z, relativeEpsilon, absoluteEpsilon);
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public fromArray(array: FloatArray, offset: number = 0): Vector3 {
        this._x = array[offset];
        this._y = array[offset + 1];
        this._z = array[offset + 2];
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param array
     * @param offset
     */
    public toArray(array: FloatArray = [], offset: number = 0): FloatArray {
        array[offset] = this._x;
        array[offset + 1] = this._y;
        array[offset + 2] = this._z;
        return array;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): Vector3 {
        this._x = dataBuffer.readFloat32();
        this._y = dataBuffer.readFloat32();
        this._z = dataBuffer.readFloat32();
        this.triggerNotify();
        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): Vector3 {
        dataBuffer.writeFloat32(this._x);
        dataBuffer.writeFloat32(this._y);
        dataBuffer.writeFloat32(this._z);
        return this;
    }

    /**
     * Buffer
     * @param attribute
     * @param index
     */
    public fromBufferAttribute(attribute: BufferAttribute | InterleavedBufferAttribute, index: int): Vector3 {
        this._x = attribute.getX(index);
        this._y = attribute.getY(index);
        this._z = attribute.getZ(index);
        return this;
    }
}
