import { Curve } from "./Curve";
import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { BinaryBuffer } from "../../Misc/BinaryBuffer";

/**
 * 2D
 * @category Curve
 */
export class LineCurve extends Curve<Vector2> {

    public v1: Vector2 = new Vector2();
    public v2: Vector2 = new Vector2();

    /**
     * 
     * @param v1
     * @param v2
     */
    constructor(v1: Nullable<Vector2> = null, v2: Nullable<Vector2> = null) {
        super();

        this.type = 'LineCurve';
        this.arcLengthDivisions = 10;

        if (v1) { this.v1.copy(v1); }
        if (v2) { this.v2.copy(v2); }
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getPoint(t: number, optionalTarget: Nullable<Vector2> = null): Vector2 {
        const point = optionalTarget ?? new Vector2();

        if (t == 1) {
            point.copy(this.v2);
        } else {
            point.copy(this.v2).sub(this.v1);
            point.multiplyScalar(t).add(this.v1);
        }

        return point;
    }

    /**
     * 
     * @param u
     * @param optionalTarget
     */
    public getPointAt(u: number, optionalTarget: Nullable<Vector2> = null): Vector2 {
        return this.getPoint(u, optionalTarget);
    }

    /**
     * 
     * @param t
     * @param optionalTarget
     */
    public getTangent(t: number, optionalTarget: Nullable<Vector2> = null): Vector2 {
        const tangent = optionalTarget ?? new Vector2();
        tangent.copy(this.v2).sub(this.v1).normalize();
        return tangent;
    }

    /**
     * 
     */
    public clone(): LineCurve {
        return new LineCurve().copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: LineCurve): LineCurve {
        super.copy(source);

        this.v1.copy(source.v1);
        this.v2.copy(source.v2);

        return this;
    }

    /**
     * JSON
     */
    public toJSON(): any {
        const data = super.toJSON();
        data.v1 = this.v1.toArray();
        data.v2 = this.v2.toArray();

        return data;
    }

    /**
     * JSON
     * @param json
     */
    public fromJSON(json: any): LineCurve {
        super.fromJSON(json);

        this.v1.fromArray(json.v1);
        this.v2.fromArray(json.v2);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public toDataBuffer(dataBuffer: BinaryBuffer): LineCurve {
        super.toDataBuffer(dataBuffer);

        this.v1.toDataBuffer(dataBuffer);
        this.v2.toDataBuffer(dataBuffer);

        return this;
    }

    /**
     * 
     * @param dataBuffer
     */
    public fromDataBuffer(dataBuffer: BinaryBuffer): LineCurve {
        super.fromDataBuffer(dataBuffer);

        this.v1.fromDataBuffer(dataBuffer);
        this.v2.fromDataBuffer(dataBuffer);
        return this;
    }
}
