import { Constants } from "../Engines";

/**
 * 
 * @category Misc
 */
export abstract class PixelFormat {

    /**
     * 
     * @param pixelFormat
     */
    public static formatComponentsLength(pixelFormat: number): number {
        switch (pixelFormat) {
            case Constants.RGBFormat:
            case Constants.RGBIntegerFormat:
                return 3;
            case Constants.RGBAFormat:
            case Constants.RGBAIntegerFormat:
                return 4;
            case Constants.RGFormat:
            case Constants.RGIntegerFormat:
            case Constants.LuminanceAlphaFormat:
                return 2;
            default:
                return 1;
        }
    }

    /**
     * 
     * @param pixelFormat
     * @param width
     * @param height
     */
    public static compressedTextureSizeInBytes(pixelFormat: number, width: number, height: number): number {
        switch (pixelFormat) {
            case Constants.RGB_S3TC_DXT1_Format:
            case Constants.RGBA_S3TC_DXT1_Format:
            case Constants.RGB_ETC1_Format:
            case Constants.RGB_ETC2_Format:
                return Math.floor((width + 3) / 4) * Math.floor((height + 3) / 4) * 8;

            case Constants.RGBA_S3TC_DXT3_Format:
            case Constants.RGBA_S3TC_DXT5_Format:
                return Math.floor((width + 3) / 4) * Math.floor((height + 3) / 4) * 16;

            case Constants.RGB_PVRTC_4BPPV1_Format:
            case Constants.RGBA_PVRTC_4BPPV1_Format:
                return Math.floor(
                    (Math.max(width, 8) * Math.max(height, 8) * 4 + 7) / 8
                );

            case Constants.RGB_PVRTC_2BPPV1_Format:
            case Constants.RGBA_PVRTC_2BPPV1_Format:
                return Math.floor(
                    (Math.max(width, 16) * Math.max(height, 8) * 2 + 7) / 8
                );

            default:
                return 0;
        }
    }

    /**
     * 
     * @param pixelFormat
     * @param pixelDatatype
     * @param width
     * @param height
     */
    public static textureSizeInBytes(pixelFormat: number, pixelDatatype: number, width: number, height: number): number {
        let componentsLength = PixelFormat.formatComponentsLength(pixelFormat);
        if (PixelFormat.dataTypeIsPacked(pixelDatatype)) {
            componentsLength = 1;
        }
        return (
            componentsLength *
            PixelFormat.dataTypeSizeInBytes(pixelDatatype) *
            width *
            height
        );
    }

    /**
     * 
     * @param pixelFormat
     * @param pixelDatatype
     * @param width
     */
    public static alignmentInBytes(pixelFormat: number, pixelDatatype: number, width: number): number {
        const mod = PixelFormat.textureSizeInBytes(pixelFormat, pixelDatatype, width, 1) % 4;
        return mod == 0 ? 4 : mod == 2 ? 2 : 1;
    }

    /**
     * Packed
     * @param pixelDatatype
     */
    public static dataTypeIsPacked(pixelDatatype: number): boolean {
        return (
            pixelDatatype == Constants.UnsignedInt248Type ||
            pixelDatatype == Constants.UnsignedShort4444Type ||
            pixelDatatype == Constants.UnsignedShort5551Type ||
            pixelDatatype == Constants.UnsignedShort565Type
        );
    }

    /**
     * 
     * @param pixelDatatype
     */
    public static dataTypeSizeInBytes(pixelDatatype: number): number {
        switch (pixelDatatype) {
            case Constants.ByteType:
            case Constants.UnsignedByteType:
                return 1;
            case Constants.ShortType:
            case Constants.UnsignedShortType:
            case Constants.UnsignedShort4444Type:
            case Constants.UnsignedShort5551Type:
            case Constants.UnsignedShort565Type:
            case Constants.HalfFloatType:
                return 2;
            case Constants.IntType:
            case Constants.UnsignedIntType:
            case Constants.FloatType:
            case Constants.UnsignedInt248Type:
                return 4;
            case Constants.DoubleType:
                return 8;
        }

        return 0;
    }
}
