import { Texture } from "./Texture";

/**
 * Canvas
 */
export class TextureCanvas extends Texture {

    public canvas: HTMLCanvasElement | OffscreenCanvas;

    /**
     * 
     * @param options
     * @param generateMipmaps
     */
    constructor(options: any, generateMipmaps: boolean = false) {
        super();

        this.generateMipmaps = generateMipmaps;

        if (options.getContext) {
            this.canvas = options;
        }
        else {
            this.canvas = document.createElement("canvas");

            if (options.width || options.width == 0) {
                this.canvas.width = options.width;
                this.canvas.height = options.height;
            }
            else {
                this.canvas.width = options;
                this.canvas.height = options;
            }
        }
    }

    public get width() {
        return this.canvas?.width;
    }

    public get height() {
        return this.canvas?.height;
    }

    /**
    * 
    */
    public get isReady(): boolean {
        return this.canvas != null;
    }

    /**
     * 
     */
    public clone(): TextureCanvas {
        return new TextureCanvas(this.canvas).copy(this);
    }

    /**
     * 
     * @param source
     */
    public copy(source: TextureCanvas): TextureCanvas {
        super.copy(source);

        return this;
    }
}
