import { IDisposable } from "../../Misc/IDisposable";
import { SmartArray } from "../../Misc/SmartArray";
import { Mesh } from "../Mesh";
import { Scene } from "../Scene";
import { Camera } from "../../Cameras";

/**
 * 
 */
export class ProjectingManager implements IDisposable {

    /**
     * @ignore
     */
    private _activeMeshes = new SmartArray<Mesh>(256);
    /**
     * @ignore
     */
    private readonly _scene: Scene;

    /**
     * 
     * @param scene
     */
    constructor(scene: Scene) {
        this._scene = scene;
    }

    /**
     * 
     */
    public dispose(): void {
    }

    /**
     * 
     */
    public reset(): void {
        this._activeMeshes.reset();
    }

    public project(camera: Camera): void {
        this._projectMeshs(camera);
    }

    private _projectMeshs(camera: Camera): void {

        const meshs = this._scene.meshes;
        meshs.forEach((mesh) => {
            this._projectMesh(camera, mesh);
        });
    }

    private _projectMesh(camera: Camera, mesh: Mesh): void {

        if (!mesh.visible) {
            return;
        }

        const renderingManager = this._scene.renderingManager;

        const layerVisible = mesh.visibleLayers.test(camera.visibleLayers);
        if (layerVisible) {
            if (!mesh.frustumCulled || camera.frustum.intersectsObject(mesh)) {
                renderingManager.dispatch(mesh);
            }
        }
    }
}
