import { Vector2 } from "../Vector2";
import { Nullable } from "../../types";
import { Earcut } from "../../Libs";

/**
 * 2D
 * @category Shape
 */
export abstract class ShapeUtils {

    /**
     * 
     * @param contour
     */
    public static area(contour: Vector2[]): number {
        let n = contour.length;
        let a = 0.0;
        for (let p = n - 1, q = 0; q < n; p = q++) {
            a += contour[p].x * contour[q].y - contour[q].x * contour[p].y;
        }
        return a * 0.5;
    }

    /**
     * 
     * @param poly
     * @param pt
     * @returns {boolean}
     */
    public static isContainsPoint(poly: Vector2[], pt: Vector2) {
        let  c = false;
        for (let i = 0, j = poly.length - 1; i < poly.length; j = i++) {
            if ((((poly[i].y <= pt.y) && (pt.y < poly[j].y)) ||
                ((poly[j].y <= pt.y) && (pt.y < poly[i].y)))
                && (pt.x < (poly[j].x - poly[i].x) * (pt.y - poly[i].y) / (poly[j].y - poly[i].y) + poly[i].x)) {
                c = !c;
            }
        }

        return c;
    }

    /**
     * 
     * @param pts
     */
    public static isClockWise(pts: Vector2[]): boolean {
        return ShapeUtils.area(pts) < 0;
    }

    /**
     * 
     * @param contour
     * @param holes
     */
    public static triangulateArea(contour: Vector2[], holes: Nullable<Vector2[][]> = null): number {
        let vertices = new Array<number>(); // flat array of vertices like [ x0,y0, x1,y1, x2,y2, ... ]
        let holeIndices = []; // array of hole indices
        let faces = []; // final array of vertex indices like [ [ a,b,d ], [ b,c,d ] ]

        removeDupEndPts(contour);
        addContour(vertices, contour);

        if (holes) {
            let holeIndex = contour.length;
            holes.forEach(removeDupEndPts);

            for (let i = 0; i < holes.length; i++) {
                holeIndices.push(holeIndex);
                holeIndex += holes[i].length;
                addContour(vertices, holes[i]);
            }
        }

        const triangles = Earcut.earcut(vertices, holeIndices, 2);
        return Earcut.trianglesArea(vertices, triangles, 2);
    }

    /**
     * 
     * @param contour
     * @param holes
     */
    public static triangulateShape(contour: Vector2[], holes: Nullable<Vector2[][]> = null): number[][] {
        let vertices = new Array<number>(); // flat array of vertices like [ x0,y0, x1,y1, x2,y2, ... ]
        let holeIndices = []; // array of hole indices
        let faces = []; // final array of vertex indices like [ [ a,b,d ], [ b,c,d ] ]

        removeDupEndPts(contour);
        addContour(vertices, contour);

        if (holes) {
            let holeIndex = contour.length;
            holes.forEach(removeDupEndPts);

            for (let i = 0; i < holes.length; i++) {
                holeIndices.push(holeIndex);
                holeIndex += holes[i].length;
                addContour(vertices, holes[i]);
            }
        }

        const triangles = Earcut.earcut(vertices, holeIndices, 2);
        for (let i = 0; i < triangles.length; i += 3) {
            faces.push(triangles.slice(i, i + 3));
        }
        return faces;
    }
}

/**
 * @ignore
 * @param points
 */
function removeDupEndPts(points: Vector2[]) {
    let l = points.length;
    if (l > 2 && points[l - 1].equals(points[0])) {
        points.pop();
    }
}

/**
 * @ignore
 * @param vertices
 * @param contour
 */
function addContour(vertices: number[], contour: Vector2[]) {
    for (let i = 0; i < contour.length; i++) {
        vertices.push(contour[i].x);
        vertices.push(contour[i].y);
    }
}
